raul-sortable {
  @apply h-full w-full text-md;

  .r-sortable-list {
    @apply h-full w-full;

    .r-sortable-list__group-label {
      height: 56px;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      border-bottom: 1px solid #ebedee;
    }

    .not-draggable {
      cursor: default;
    }

    .r-sortable-list__items-container {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      cursor: move;
      height: 100%;
    }
  }

  raul-container {
    &.sortable-drag {
      box-shadow: 0 8px 16px 0 rgba(82, 97, 115, 0.18);
      padding:0;

      & > .r-container-wrapper--movable {
        padding: 0;

        & > raul-handle {
          display: none;
        }
      }
    }

    &:hover {
      box-shadow: 0 8px 16px 0 rgba(82, 97, 115, 0.18);
    }

    

    &.not-draggable {
      &:hover {
        background-color: #e5f4ff;
        &:before {
          display: none;
        }
      }
    }
  }

  .sortable-ghost {
    @apply bg-primary-highlight;
    box-shadow: none;

    & > * {
      visibility: hidden;
    }
  }
}

.sortable-drag {
  background-color: rgb(255, 255, 255);
  opacity: 1 !important;

  & > .r-list-item-wrapper {
    padding: 0;

    & > raul-handle {
      display: none;
    }
  }

  .r-list-item {
    box-shadow: 0 8px 16px 0 rgba(82, 97, 115, 0.18);
    
    &:not(:first-child) {
      display: none;
    }

    &::before {
      opacity: 0;
    }
  }

  .r-checkbox {
    .r-checkbox {
      display: none;
    }
  }
}
