@import "../../../styles/mixins/focus.scss";

raul-accordion {
  display: flex;
  flex-direction: column;
  font-size: var(--font-size-3);

  raul-accordion-item {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid var(--color-neutral-light);
  }

  raul-accordion-item-header {
    display: flex;
  }

  raul-accordion-item-header .r-accordion__item__header {
    display: flex;
    flex: 1 1 0%;
    align-items: center;
    text-align: left;
    padding: 1rem 0.25rem 1rem 0.25rem;

    @include focus-visible;
  }

  raul-accordion-item-header .r-accordion__item__header__content {
    flex: 1 1 0%;

    a {
      color: var(--color-primary);

      &:hover {
        text-decoration: underline;
      }
    }
  }

  raul-accordion-item-title {
    display: block;
    font-size: var(--font-size-4);
    font-weight: var(--font-weight-semibold);
    line-height: var(--line-height-tight);
    margin-bottom: 0;
  }

  raul-accordion-item:first-of-type {
    border-top: 1px solid var(--color-neutral-light);
  }

  raul-accordion-item raul-accordion-item-panel {
    padding: 0rem 0.25rem 1rem 0.25rem;
    display: none;
  }

  raul-accordion-item[expanded] > raul-accordion-item-panel {
    display: block;
  }

  raul-accordion-item[expanded]
    > raul-accordion-item-header
    .r-accordion__item__header__arrow-icon {
    transform: rotate(180deg);
  }

  raul-accordion-item-header .r-accordion__item__header__arrow-icon {
    color: var(--color-neutral-dark);
    transition: all 0.2s ease-in-out;
  }

  raul-icon {
    display: inline-block;
    vertical-align: middle;
  }
}
