import {
  Component,
  Element,
  Prop,
  Event,
  EventEmitter,
  h,
  Host,
} from "@stencil/core";

/**
 * @slot - Add `raul-accordion-item-title` or header content in this slot.
 */
@Component({
  tag: "raul-accordion-item-header",
})
export class RaulAccordionItemHeader {
  toggleElement: HTMLButtonElement | undefined;

  @Element() el!: HTMLRaulAccordionItemHeaderElement;

  /**
   * Disables accordion form interaction.
   */
  @Prop() disabled?: boolean;

  /**
   * Sets expanded state of accordion.
   */
  @Prop() expanded?: boolean;

  /**
   * Sets accordion ID.
   */
  @Prop() name?: string;

  /**
   * Emits when header is clicked
   */
  @Event() accordionItemHeaderClick!: EventEmitter<undefined>;

  handleClick(e: MouseEvent) {
    if (this.shouldExpand(e.target as HTMLElement)) {
      this.accordionItemHeaderClick.emit();
    }
  }

  shouldExpand(target: HTMLElement) {
    return (
      (target === this.toggleElement ||
        target.closest("button") === this.toggleElement) &&
      target.closest("raul-action")?.tagName !== "RAUL-ACTION" &&
      target.closest("raul-button")?.tagName !== "RAUL-BUTTON" &&
      target.tagName !== "A"
    );
  }

  render() {
    return (
      <Host>
        <button
          ref={(element: HTMLButtonElement | undefined) =>
            (this.toggleElement = element)
          }
          type="button"
          class="r-accordion__item__header"
          id={`${this.name}-header`}
          disabled={this.disabled}
          aria-controls={`${this.name}-panel`}
          aria-expanded={this.expanded ? "true" : "false"}
          onClick={(e) => this.handleClick(e)}
        >
          <div class="r-accordion__item__header__content">
            <slot />
          </div>

          <raul-icon
            icon="arrow-down-v"
            class="r-accordion__item__header__arrow-icon"
          />
        </button>
      </Host>
    );
  }
}
