import { Component, Element, State, Prop, h } from "@stencil/core";

/**
 * @slot label-start - Place an element before the label. For action elements use the `data-label-action` attribute on the element.
 * @slot label-end - Place an element at the end of the label. For action elements use the `data-label-action` attribute on the element.
 * @slot messaging - Place validation, hint text or any other informational text here. Use the `raul-input-helper-list` component in this slot.
 */

@Component({
  tag: "raul-textarea",
  styleUrl: "raul-textarea.scss",
})
export class RaulTextarea {
  @Element() el!: HTMLRaulTextareaElement;

  @State() inputId = `raul-textarea-${textareaIds++}`;

  /**
   * Textarea's label text.
   */
  @Prop() label?: string;

  /**
   * @deprecated Use the "messaging" slot with the raul-input-helper-list component instead
   */
  @Prop() hint?: string;

  /**
   * @deprecated Use "invalid" and the "messaging" slot with the raul-input-helper-list component instead
   */
  @Prop() error?: string;

  /**
   * Makes the textarea visually invalid.
   */
  @Prop({ reflect: true }) invalid?: boolean = false;

  componentWillLoad() {
    const textareaEl = this.el.querySelector("textarea");
    if (!textareaEl) {
      throw new Error("raul-textarea must contain a textarea element.");
    }
    if (textareaEl.id) {
      this.inputId = textareaEl.id;
    } else {
      textareaEl.id = this.inputId;
    }
    if (this.error) {
      console.warn(
        '[RAUL: Deprecated] "error" is deprecated, use "invalid" and the "messaging" slot with the raul-input-helper-list component instead',
      );
    }
    if (this.hint) {
      console.warn(
        '[RAUL: Deprecated] "hint" is deprecated, the "messaging" slot with the raul-input-helper-list component instead',
      );
    }
  }

  render() {
    return (
      <div
        data-testid="form-element"
        class={{
          "r-form-element": true,
          "r-form-element--invalid": !!this.error || !!this.invalid,
        }}
      >
        {this.label && (
          <div class="r-form-element__label">
            <slot name="label-start" />
            <label htmlFor={this.inputId}>{this.label}</label>
            <slot name="label-end" />
          </div>
        )}

        <div class="r-form-element__control">
          <slot />
        </div>
        <slot name="messaging">
          {this.hint && <small class="r-form-element__hint">{this.hint}</small>}

          {this.error && (
            <div class="r-form-element__error">
              <raul-icon
                icon="interface-alert-diamond"
                class="r-form-element__error__icon"
              />{" "}
              {this.error}
            </div>
          )}
        </slot>
      </div>
    );
  }
}

let textareaIds = 0;
