@import "../../../styles/mixins/focus.scss";

raul-timeline {
  display: block;
  font-size: var(--font-size-3);

  .r-timeline {
    padding: 0.5rem;

    &__title {
      margin-bottom: 1.5rem;
      font-weight: var(--font-weight-bold);
      font-size: var(--font-size-4);
    }

    &__event {
      display: flex;

      &--error .r-timeline__marker {
        background-color: var(--color-destructive);
      }

      &--warning .r-timeline__marker {
        background-color: var(--color-warning);
      }

      &--success .r-timeline__marker {
        background-color: var(--color-success);
      }

      &:last-child .r-timeline__line {
        display: none;
      }

      .r-timeline__outer__marker::before,
      .r-timeline__outer__marker::after {
        display: block;
        position: absolute;
        left: 7.5px;
        width: 1px;
        height: 0.25rem;
        background-color: var(--color-neutral);
        content: "";
      }

      &:first-child .r-timeline__outer__marker::before {
        display: none;
      }

      &:last-child .r-timeline__outer__marker::after {
        display: none;
      }

      .r-timeline__outer__marker::before {
        top: 0;
      }

      .r-timeline__outer__marker::after {
        bottom: 0;
      }

      &--error,
      &--success,
      &--warning {
        .r-timeline__outer__marker {
          position: relative;
          width: 1rem;
          height: 1rem;
          border: 1px solid var(--color-neutral);
          border-radius: 9999px;
        }

        .r-timeline__outer__marker::before,
        .r-timeline__outer__marker::after {
          content: none;
        }

        .r-timeline__marker {
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }

    &__marker__wrapper {
      display: flex;
      position: relative;
      flex-direction: column;
      align-items: center;
      width: 1.5rem;
    }

    &__outer__marker {
      position: relative;
      padding: 0.25rem;
    }

    &__marker {
      position: relative;
      width: 0.5rem;
      height: 0.5rem;
      border-radius: 50%;
      background-color: var(--color-neutral);
    }

    &__line {
      flex: 1 1 0%;
      width: 1px;
      background-color: var(--color-neutral);
    }

    &__content {
      flex: 1 1 0%;
      min-width: 0;
      margin-bottom: 1.5rem;
      padding-left: 1.5rem;

      &__title {
        margin-bottom: 0;
        margin: -0.25rem;
        padding: 0.25rem;
        font-weight: var(--font-weight-medium);
        font-size: var(--font-size-3);
        line-height: var(--line-height-tight);

        a {
          color: var(--color-primary);

          &:hover {
            text-decoration: underline;
          }

          @include focus-visible(1px);
        }
      }

      &__view-more__link {
        display: block;
        color: var(--color-primary);
        font-size: var(--font-size-2);

        &:hover {
          text-decoration: underline;
        }

        @include focus-visible(1px);
      }

      &__timestamp {
        margin-top: 0.25rem;
        font-size: var(--font-size-2);
      }
    }

    &__event--truncate {
      .r-timeline__content__title,
      .r-timeline__content__sub-title {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}
