import { Component, Prop, h } from "@stencil/core";

@Component({
  tag: "raul-toggle-pane",
})
export class RaulTogglePane {
  /**
   * The name of the toggle pane.
   */
  @Prop({ reflect: true }) name?: string;

  render() {
    return (
      <div
        class="r-toggles"
        role="tabpanel"
        id={this.name}
        aria-labelledby={`${this.name}-toggle`}
      >
        <slot />
      </div>
    );
  }
}
