@import "../../../styles/mixins/focus.scss";
$animation-transition: 0.25s;

body:has(.r-vertical-menu__mobile__modal__visible) {
  overflow: hidden;
}

raul-vertical-menu {
  display: inline-block;
  width: auto;

  .r-vertical-menu {
    font-size: var(--font-size-3);
    user-select: none;
    width: 280px;

    &__mobile {
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
      background-color: var(--color-neutral-lightest);
      border: 1px solid var(--color-neutral-light);
      border-radius: 0.25rem;
      width: 100%;
      padding: 0.5rem;
      height: 60px;
      text-align: left;
      @include focus-visible(-2px);

      &__icon {
        position: absolute;
        margin-left: 0.5rem;
        width: 26px;
        height: 26px;

        span {
          display: block;
          background-color: var(--charcoal-dark);
          margin-top: 5px;
          height: 2px;

          &:nth-child(1) {
            width: 16px;
          }

          &:nth-child(2) {
            width: 20px;
          }

          &:nth-child(3) {
            width: 12px;
          }
        }
      }

      &__topnav {
        height: 69px;

        .r-vertical-menu__heading {
          font-size: var(--font-size-4);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          margin-bottom: 0;
          padding-left: 1.5rem;
          width: calc(100% - 83px);
          height: 69px;
          line-height: 69px;
        }

        &__close-button {
          position: absolute;
          right: 0;
          top: 0;
          text-align: center;
          padding: 1.5rem 1.5rem;
          color: var(--color-primary);
          @include focus-visible(-3px);
        }
      }

      &__title {
        font-size: var(--font-size-3);
        line-height: var(--line-height-tight);
        margin-bottom: 0;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: var(--font-weight-medium);
      }

      &__sub-title {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: #637076;

        font-size: var(--font-size-2);
      }

      &__title,
      &__sub-title {
        pointer-events: none;
        padding-left: 40px;
        width: calc(100% - 40px);
      }

      &__modal {
        background-color: var(--color-background);
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all .2s ease-in-out;
        pointer-events: none;

        z-index: 10000;
        transform: scale(0.9);

        &:is(.r-vertical-menu__mobile__modal__hidden) {
          visibility: hidden;
        }

        &__visible {
          display: block;
          opacity: 1;
          pointer-events: auto;
          transform: scale(1);
        }

        .r-vertical-menu__container {
          border-radius: 0;
          overflow: hidden;
          height: 100% !important;
        }

        .r-vertical-menu__container__item__title,
        .r-vertical-menu__container__item__sub-title {
          padding-left: 1.5rem;
        }

        .r-vertical-menu__container__item__arrow-next {
          right: 1.5rem;
        }

        .r-vertical-menu__container__item__arrow-back {
          padding-left: 1.5rem;
          padding-right: 1.5rem;
        }

        .r-vertical-menu__container__item__title-back {
          padding-left: 3.25rem;
        }

        .r-vertical-menu__container__item__title,
        .r-vertical-menu__container__item__sub-title {
          width: calc(100% - 64px);
        }
      }
    }

    &__heading {
      font-size: var(--font-size-4);
      line-height: var(--line-height-tight);
      margin-bottom: 1rem;
      font-weight: var(--font-weight-medium);
      user-select: text;
    }

    &__container {
      border-width: 1px;
      border-style: solid;
      border-color: var(--color-neutral-light);
      position: relative;
      border-radius: 0.25rem;
      overflow: hidden;
      display: flex;
      // transition: height $animation-transition ease-in-out;

      @media (prefers-reduced-motion) {
        transition: none;
      }

      ul {
        min-width: 100%;

        li {
          border-bottom-width: 1px;
          border-color: var(--color-neutral-light);
        }

        li:last-child {
          border-right-width: 1px;
          border-color: var(--transparent);
        }

        .r-vertical-menu--active {
          background-color: var(--color-neutral-lightest);
        }

        &.r-vertical-menu--no-animation {
          opacity: 1;
        }
      }

      &__item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        position: relative;
        border: solid 1px var(--transparent);
        height: 56px;
        @include focus-visible(-2px);

        &__title {
          font-size: var(--font-size-3);
          line-height: var(--line-height-tight);
          margin-bottom: 0;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          font-weight: var(--font-weight-medium);
        }

        &__title-back {
          font-size: var(--font-size-3);
          line-height: var(--line-height-tight);
          margin-bottom: 0;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          pointer-events: none;
          font-weight: var(--font-weight-medium);
        }

        &__sub-title {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          color: #637076;
          font-size: var(--font-size-2);
        }

        &__title,
        &__sub-title {
          padding-left: 1rem;
          pointer-events: none;
        }

        &__title-back {
          padding-left: 2.75rem;
        }

        &__title,
        &__sub-title {
          width: calc(100% - 48px);
        }
        
        &__arrow-next {
          color: var(--color-neutral-dark);
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 0.5rem;
          display: block;
          width: 1rem;
          height: 1rem;
        }

        &__arrow-back {
          color: var(--charcoal-dark);
          position: absolute;
          padding-left: 1rem;
          padding-right: 1rem;
          margin-top: 0.125rem;
          top: 50%;
          transform: translateY(-50%);
          display: block;
          width: 1rem;
          height: 1rem;
        }

        &--back-button {
          text-align: left;
        }
      }
    }
  }

  .r-vertical-menu__container__ul--current {
    margin-left: 0;
  }

  .r-vertical-menu--moving .r-vertical-menu__container__ul {
    pointer-events: none;
    transition-duration: $animation-transition;
    transition-property: transform;
    transition-timing-function: ease;
  }

  .r-vertical-menu--right .r-vertical-menu__container__ul--current {
    margin-left: 0;
  }

  .r-vertical-menu--right.r-vertical-menu--moving .r-vertical-menu__container__ul--current {
    transform: translate(-100%, 0);
  }

  .r-vertical-menu--left.r-vertical-menu--moving .r-vertical-menu__container__ul--current {
    transform: translate(100%, 0);
  }

  .r-vertical-menu__container__ul--previous {
    margin-left: -100%;
    visibility: hidden;
  }

  .r-vertical-menu--right .r-vertical-menu__container__ul--previous {
    margin-left: 0;
  }

  .r-vertical-menu--right.r-vertical-menu--moving .r-vertical-menu__container__ul--previous {
    transform: translate(-100%, 0);
    visibility: visible;
  }

  .r-vertical-menu--left.r-vertical-menu--moving .r-vertical-menu__container__ul--previous {
    transform: translate(100%, 0);
    visibility: visible;
  }
}


@media only screen and (max-width: 640px) {
  raul-vertical-menu {
    width: 100%;

    .r-vertical-menu {
      width: 100%;
    }
  }
}