@import "../../../styles/mixins/focus.scss";

body:has(raul-select[opened]) {
  @media (max-width: 639px) {
    overflow: hidden;
  }
}

raul-select {
  display: block;
  font-size: var(--font-size-3);

  .r-select {
    position: relative;

    &__label {
      display: flex;
      column-gap: 0.25rem;
      align-content: center;
      margin-bottom: 0.25rem;
      font-weight: var(--font-weight-medium);
      font-size: var(--font-size-2);

      label {
        cursor: pointer;
      }
    }

    &__toggle {
      display: block;
      width: 100%;
      padding: 0;
      border-width: 0;
      outline: none;
      background-color: var(--transparent);
      text-align: left;
      cursor: pointer;

      &__content {
        display: flex;
        position: relative;
        flex-grow: 1;
        align-items: center;
        height: 2.5rem;
        border-radius: var(--raul-select-border-radius, 0.125rem);
        background-color: var(--raul-select-bg, var(--color-background));
        color: var(--raul-select-color, var(--color-base));
        font-size: var(--font-size-3);
        line-height: var(--line-height-tight);

        &__text {
          position: absolute;
          top: 0;
          left: 0;
          flex-grow: 1;
          width: 100%;
          padding: 0.75rem 34px 0.75rem 0.75rem;
          overflow: hidden;
          font-size: var(--font-size-4);
          text-overflow: ellipsis;
          white-space: nowrap;

          @media (min-width: 1024px) {
            font-size: var(--font-size-3);
          }
        }

        &__input {
          display: block;
          -webkit-appearance: none;
          appearance: none;
          position: absolute;
          top: 0;
          left: 0;
          width: calc(100% - 32px);
          height: 2.5rem;
          padding-right: 0.75rem;
          padding-left: 0.75rem;
          border: 1px solid var(--transparent);
          border-radius: var(--raul-input-border-radius, 0.125rem);
          background-color: var(--transparent);
          color: var(--raul-input-color, --color-base);
          font-size: var(--font-size-4);
          letter-spacing: 0.025em;

          &::placeholder {
            color: var(--color-neutral-dark);
          }

          &[disabled] {
            border-color: var(--color-neutral-light);
            background-color: var(--color-neutral-light);
            color: var(--color-neutral-dark);
            cursor: not-allowed;
          }

          &:focus {
            outline: none;
          }

          &:invalid {
            border-color: var(--color-destructive);
          }

          @media (min-width: 1024px) {
            font-size: var(--font-size-3);
          }

          &--masked {
            color: var(--transparent);
            text-shadow: 0 0 0 var(--charcoal);
          }
        }

        &__icon {
          margin-left: 0.5rem;
          color: var(--color-neutral-dark);
          font-size: var(--font-size-4);
        }

        &__arrow {
          display: inline-block;
          width: 7px;
          height: 7px;
          margin-top: -0.25rem;
          transform: rotate(-135deg);
          border-top-width: 1px;
          border-left-width: 1px;
          border-color: var(--color-neutral-dark);
        }

        &__label {
          display: flex;
          z-index: 10;
          position: relative;
          align-items: center;
          margin-bottom: 0;
          overflow: hidden;
          font-size: var(--font-size-3);
          text-overflow: ellipsis;
          white-space: nowrap;
          transition: all 0.15s ease;

          label {
            cursor: pointer;
          }

          &__end-accessory {
            display: inline-block;
            margin-left: 0.25rem;
          }

          &--float {
            margin-top: -38px;
            font-weight: var(--font-weight-medium);
            font-size: var(--font-size-2);
          }
        }

        &__outline__leading {
          height: 100%;
          padding-left: 0.75rem;
          border-width: 1px;
          border-right-width: 0;
          border-style: solid;
          border-radius: var(--raul-select-border-radius, 0.125rem);
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
          border-color: var(--color-neutral);

          &--float {
            padding-left: 7px;
          }
        }

        &__outline__notch {
          display: flex;
          align-items: center;
          height: 100%;
          border-width: 1px;
          border-right-width: 0;
          border-left-width: 0;
          border-style: solid;
          border-color: var(--color-neutral);

          &--float {
            padding-right: 0.25rem;
            padding-left: 0.25rem;
            border-top-width: 0;
          }
        }

        &__outline__trailing {
          display: flex;
          flex-grow: 1;
          align-items: center;
          justify-content: flex-end;
          height: 100%;
          padding-right: 0.75rem;
          border: 1px solid var(--color-neutral);
          border-left-width: 0;
          border-radius: var(--raul-select-border-radius, 0.125rem);
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
          border-color: var(--color-neutral);

          &:focus {
            outline: none;
          }
        }

        &--borderless {
          .r-select__toggle__content__outline__leading,
          .r-select__toggle__content__outline__notch,
          .r-select__toggle__content__outline__trailing {
            border-color: var(--transparent);
          }
        }

        &--header {
          margin-left: -8px;
          border-width: 1px;
          border-style: solid;
          border-color: var(--transparent);
          background-color: var(--gray-lightest);

          .r-select__toggle__content__outline__leading,
          .r-select__toggle__content__outline__notch {
            display: none;
            border-color: var(--transparent);
          }

          .r-select__toggle__content__outline__trailing {
            order: 2;
            border-color: transparent;
          }

          .r-select__toggle__content__text {
            position: relative;
            order: 1;
            padding: 0.5rem 0.75rem;
            font-weight: var(--font-weight-bold);
            font-size: var(--font-size-5);
          }

          .r-select__toggle__content__arrow {
            border-top-width: 2px;
            border-left-width: 2px;
            border-color: var(--color-neutral-dark);
          }

          &:hover {
            background-color: var(--color-neutral-light);
          }
        }

        &--focus {
          .r-select__toggle__content__outline__leading,
          .r-select__toggle__content__outline__notch,
          .r-select__toggle__content__outline__trailing {
            border-color: var(--color-primary);
          }
        }
      }

      &:focus {
        outline: none;

        .r-select__toggle__content__outline__leading,
        .r-select__toggle__content__outline__notch,
        .r-select__toggle__content__outline__trailing {
          border-color: var(--color-primary);
        }

        .r-select__toggle__content--header {
          border-color: var(--color-primary);

          .r-select__toggle__content__outline__trailing {
            border-color: var(--transparent);
          }
        }
      }

      &:disabled {
        .r-select__toggle__content {
          background-color: var(--color-neutral-light);
          color: var(--color-neutral-dark);
          cursor: not-allowed;

          .r-select__toggle__content__outline__leading,
          .r-select__toggle__content__outline__notch,
          .r-select__toggle__content__outline__trailing {
            border-color: var(--color-neutral-light);
          }
        }
      }
    }

    &__hint {
      display: block;
      margin-top: 0.5rem;
      font-size: var(--font-size-2);
    }

    &__error {
      display: flex;
      align-items: center;
      margin-top: 0.5rem;
      color: var(--color-destructive);
      font-size: var(--font-size-2);

      &__icon {
        margin-right: 0.25rem;
        font-size: var(--font-size-4);
      }
    }

    &__menu {
      display: flex;
      z-index: 100000;
      position: absolute;
      flex-direction: row;
      width: 100%;
      min-width: 10rem;
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
      border-top-width: 1px;
      border-bottom-width: 1px;
      border-radius: var(--raul-select-menu-border-radius, 0.125rem);
      border-color: var(--color-neutral-light);
      background-color: var(--raul-select-menu-bg, var(--color-background));
      box-shadow:
        0 10px 15px -3px rgba(0, 0, 0, 0.1),
        0 4px 6px -2px rgba(0, 0, 0, 0.05);
      opacity: 0;
      pointer-events: none;
      transition:
        opacity 0.2s ease,
        scale 0.2s linear;

      &__content {
        display: flex;
        flex-direction: column;
        width: 100%;
        min-height: 2.5rem;
        max-height: none;

        @media (min-width: 640px) {
          max-height: 20rem;
        }
      }

      &__body {
        flex-grow: 1;
        overflow: auto;
      }

      @media (max-width: 639px) {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        min-width: 0;
        margin: 0;
        transform: scale(0, 0);
        border: none;
        border-radius: 0;
        box-shadow: none;
      }
    }

    &__mobile-label {
      display: none;
      visibility: hidden;

      @media (max-width: 639px) {
        display: block;

        &__content {
          display: flex;
          align-items: center;
          height: 2.5rem;
          border-bottom-width: 1px;
          border-color: var(--color-neutral-light);
          background-color: var(--color-neutral-lightest);
          font-size: var(--font-size-3);

          &__text,
          &__button {
            width: 33.333333%;
          }

          &__text {
            overflow: hidden;
            font-weight: var(--font-weight-medium);
            font-size: var(--font-size-3);
            text-align: center;
            text-overflow: ellipsis;
            white-space: nowrap;
          }

          &__button {
            padding-right: 0.75rem;
            padding-left: 0.75rem;
            color: var(--color-primary);

            &--disabled {
              color: var(--color-neutral-dark);
            }

            &--right {
              margin-left: auto;
              text-align: right;
            }
          }
        }
      }
    }

    &__search {
      position: relative;
      font-size: var(font-size-2);

      &__input {
        width: 100%;
        height: 2.5rem;
        padding-right: 0.75rem;
        padding-left: 2rem;
        border: 1px solid;
        border-top-width: 0;
        border-color: var(--color-neutral-light);

        @mixin focus-outline($outline-color) {
          &:focus {
            outline: 1px solid $outline-color;
            outline-offset: -1px;
          }
        }

        &__input {
          @include focus-outline(var(--color-primary));
        }

        &::placeholder {
          color: var(--color-neutral-dark);
        }

        @media (max-width: 639px) {
          border-right-width: 0;
          border-left-width: 0;
        }
      }

      &__icon {
        position: absolute;
        top: 50%;
        left: 0.75rem;
        transform: translateY(-50%);
        color: var(--color-neutral-dark);
      }

      &__icon-reset {
        position: absolute;
        top: 50%;
        right: 0.75rem;
        transform: translateY(-50%);
        cursor: pointer;
      }
    }

    &__no-search-results,
    &__no-options {
      display: flex;
      align-items: center;
      padding: 0.5rem 1rem 0.5rem 1rem;
      font-size: var(--font-size-3);
    }

    &--opened {
      & > .r-select__menu {
        pointer-events: inherit;
      }
    }

    &--expanded {
      & > .r-select__menu {
        transform: scale(1, 1);
        opacity: 1;

        @media (min-width: 640px) {
          transform: none;
        }
      }
    }

    &--invalid {
      .r-select__toggle__content__outline__leading,
      .r-select__toggle__content__outline__notch,
      .r-select__toggle__content__outline__trailing {
        border-color: var(--color-destructive);
      }
    }

    &--header {
      .r-select__menu {
        @media (min-width: 640px) {
          width: 320px;
        }
      }
    }
  }

  &[menu-placement="start-top"] .r-select__menu {
    @media (min-width: 640px) {
      top: auto;
      bottom: 100%;
      left: 0;
    }
  }

  &[menu-placement="start-bottom"] .r-select__menu {
    @media (min-width: 640px) {
      top: 100%;
      bottom: auto;
      left: 0;
    }
  }

  &[disabled] {
    .r-select__toggle__content {
      background-color: var(--color-neutral-light);
      color: var(--color-neutral-dark);
      cursor: not-allowed;

      .r-select__toggle__content__outline__leading,
      .r-select__toggle__content__outline__notch,
      .r-select__toggle__content__outline__trailing {
        border-color: var(--color-neutral-light);
        cursor: not-allowed;
      }
    }
  }

  &[opened] {
    .r-select__mobile-label {
      visibility: visible;
    }
  }

  .r-option {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 2.5rem;
    padding: 0.5rem 1rem 0.5rem 1rem;
    border: 1px solid;
    border-top-width: 0;
    border-color: var(--color-neutral-light);
    font-size: var(--font-size-2);
    line-height: 1.25;
    text-align: left;
    white-space: nowrap;
    cursor: pointer;
    @include focus-visible(-2px);

    &__icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 2rem;
      height: 2rem;
      margin-right: 1rem;
      border-radius: 50%;
      background-color: var(--color-neutral-light);
      color: var(--color-neutral-dark);
      font-size: var(--font-size-4);

      raul-icon {
        display: inline-flex;
      }
    }

    &__text {
      flex: 1 1 0%;
      padding-right: 0.75rem;

      &,
      &__title,
      &__description {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      &__description {
        margin-top: 0.25rem;
      }
    }

    &__check-mark {
      display: inline-block;
      width: 4px;
      height: 10px;
      margin-top: -0.25rem;
      transform: rotate(-138deg);
      border-top-width: 1px;
      border-left-width: 1px;
      border-color: var(--color-primary);
    }

    &__checkbox {
      display: inline-block;
      position: relative;
      width: 14px;
      height: 14px;
      border: 1px solid var(--color-neutral);
      background-color: var(--color-background);
      transition:
        background-color 0.15s linear,
        border-color 0.15s linear;

      &--checked,
      &--indeterminate {
        border-color: var(--color-primary);
        background-color: var(--color-primary);

        &:after {
          position: absolute;
          border-top-width: 2px;
          border-left-width: 2px;
          border-color: var(--color-background);
          content: "";
        }
      }

      &--checked:after {
        top: 10px;
        left: 4px;
        width: 5px;
        height: 10px;
        transform: rotate(-135deg);
        transform-origin: 0 0;
      }

      &--indeterminate:after {
        top: 50%;
        left: 50%;
        width: 7px;
        transform: translate(-50%, -50%);
        border-left-width: 0;
      }

      &--disabled {
        border-color: var(--color-neutral-light);
        background-color: var(--color-neutral-light);

        &:after {
          border-color: var(--color-neutral-dark);
        }
      }
    }

    &:not(.r-option--group):hover {
      background-color: var(--color-neutral-lightest);
    }

    &--selected {
      .r-option__check-mark {
        opacity: 1;
      }
    }

    &--disabled {
      pointer-events: none;

      .r-option__text,
      .r-option__check-mark {
        color: var(--color-neutral-dark);
      }
    }

    &--group,
    &--heading {
      padding-top: 1.5rem;
      padding-bottom: 1rem;
      background-color: var(--transparent);

      .r-option__text__title {
        font-weight: var(--font-weight-semibold);
      }
    }

    &--group {
      &:not(.r-option--multiple) {
        cursor: auto;
      }
    }

    &--sub-option {
      padding-left: 1.5rem;
    }

    &--with-description {
      .r-option__text__title {
        font-weight: var(--font-weight-semibold);
      }
    }

    @media (max-width: 639px) {
      border-right-width: 0;
      border-left-width: 0;
    }
  }
}
