import {
  Component,
  State,
  Prop,
  Event,
  EventEmitter,
  Listen,
  h,
  Host,
} from "@stencil/core";
import { ToggleInterface } from "../../../utils/interfaces";

@Component({
  tag: "raul-toggles",
  styleUrl: "raul-toggles.scss",
})
export class RaulToggles {
  @State() xsDevice!: boolean;

  /**
   * An array of objects representing the navigation.
   */
  @Prop() toggles: ToggleInterface[] = [];

  /**
   * The name key of the active toggle.
   */
  @Prop({ reflect: true }) activeToggle?: string;

  /**
   * If `true`, toggles will render as a select on mobile devices.
   */
  @Prop({ reflect: true }) selectOnMobile?: boolean;

  /**
   * sets the size for toggles.
   */
  @Prop({ reflect: true }) size: "default" | "small" = "default";

  /**
   * If `true`, the width of the parent will be distributed equally to the toggles.
   */
  @Prop({ reflect: true }) fullWidth?: boolean;

  /**
   * Emitted when a toggle is clicked.
   */
  @Event() raulToggleChange!: EventEmitter<string>;

  connectedCallback() {
    this.xsDevice = window.innerWidth < 640;
  }

  @Listen("resize", { target: "window" })
  handleResize() {
    this.xsDevice = window.innerWidth < 640;
  }

  private selectOptions() {
    return this.toggles.map((cv) => {
      return { value: cv.name, text: cv.label };
    });
  }

  private handleClick(toggleName: string): void {
    this.raulToggleChange.emit(toggleName);
  }

  private handleRaulChange(e: CustomEvent): void {
    this.raulToggleChange.emit(e.detail.value);
  }

  render() {
    return (
      <Host>
        {!(this.selectOnMobile && this.xsDevice) ? (
          <div class="r-toggles" role="tablist">
            <div class="r-toggles__list">
              {this.toggles.map((item: ToggleInterface) => (
                <button
                  role="tab"
                  class={{
                    "r-toggles__item": true,
                    "r-toggles__item--active": item.name === this.activeToggle,
                    "r-toggles__item--disabled": item.disabled === true,
                  }}
                  id={`${item.name}-toggle`}
                  disabled={item.disabled}
                  aria-controls={item.id || item.name}
                  aria-selected={
                    item.name === this.activeToggle ? "true" : "false"
                  }
                  onClick={() => this.handleClick(item.name)}
                >
                  {item.icon && (
                    <raul-icon icon={item.icon} kind={item.iconKind} />
                  )}
                  {item.label}
                </button>
              ))}
            </div>
          </div>
        ) : (
          <raul-select
            options={this.selectOptions()}
            value={this.activeToggle}
            onRaulChange={(e) => this.handleRaulChange(e)}
          />
        )}
      </Host>
    );
  }
}
