import {
  Component,
  Prop,
  h,
  State,
  Method,
  Watch,
  Event,
  EventEmitter,
  Host,
} from "@stencil/core";

import {
  AsYouType,
  CountryCode,
  PhoneNumber,
  parseDigits,
  parsePhoneNumberFromString,
} from "libphonenumber-js";

import parseMin from "libphonenumber-js/min";

import {
  phoneCountryCodes,
  Country,
} from "../../../utils/constants/phone-country-codes";
import { SelectOptionInterface } from "../../../utils/interfaces";

/**
 * @slot label-start - Place an element before the label. For action elements use the `data-label-action` attribute on the element.
 * @slot label-end - Place an element at the end of the label. For action elements use the `data-label-action` attribute on the element.
 * @slot messaging - Place validation, hint text or any other informational text here. Use the `raul-input-helper-list` component in this slot.
 */

interface ClipboardData {
  getData(format: string): string;
  setData(format: string, data: string): void;
  clearData(format?: string): boolean;
}
interface Window {
  clipboardData?: ClipboardData;
}

export interface PhoneData {
  country?: string;
  countryCallingCode: string;
  nationalNumber: string;
  number: string;
  inputValue?: string;
  valid?: boolean;
}

@Component({
  tag: "raul-phone-input",
  styleUrl: "raul-phone-input.scss",
})
export class RaulPhoneInput {
  private inputField!: HTMLInputElement;

  /**
   * Two-letter ISO country code (for example 'GB').
   */
  @Prop({ mutable: true }) defaultCountry: CountryCode = "US";

  /**
   * Sets aria-describedby on internal button element.
   */
  @Prop({ reflect: true }) helperId: string | undefined;

  /**
   * Phone input's label text.
   */
  @Prop() label?: string;

  /**
   * Makes the Phone input visually invalid and shows the feedback message.
   */
  @Prop() error?: string;

  /**
   * The value of the Phone input.
   */
  @Prop() value?: string;

  /**
   * The name attribute for the Phone input.
   */
  @Prop() name?: string;

  /**
   * The placeholder for the Phone input.
   */
  @Prop({ mutable: true }) placeholder?: string;

  /**
   * If `true`, the user cannot interact with the phone component.
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * Emitted when the Phone input value changes.
   */
  @Event() phoneValueChanged!: EventEmitter<PhoneData>;

  @State() selectOptions!: SelectOptionInterface[];
  @State() countryCodeValue!: string;
  @State() country!: {
    name: string;
    dialCode: string;
    isoCode: string;
  };
  @State() filteredCountries: Country[] = [];
  @State() focusedOptionId!: string;
  @State() loading = true;
  @State() phoneData!: PhoneData;
  @State() prevValue!: string;

  /**
   * Get Phone input data.
   */
  @Method()
  async getPhoneData() {
    if (!this.phoneData) {
      this.phoneData = {
        country: this.defaultCountry,
        countryCallingCode: this.countryCodeValue,
        nationalNumber: "",
        number: "",
      };
    }

    const { country, countryCallingCode, nationalNumber, number } =
      this.phoneData;
    return { country, countryCallingCode, nationalNumber, number };
  }

  @Watch("defaultCountry")
  defaultCountryChanged() {
    if (this.defaultCountry) {
      const dialCode = phoneCountryCodes.find(
        (item) => item.isoCode === this.defaultCountry
      )?.dialCode;
      if (dialCode) {
        this.countryCodeValue = dialCode;
      }

      const country = phoneCountryCodes.find(
        (item) => item.isoCode === this.defaultCountry
      );
      if (country) {
        this.country = country;
      }
      this.formatPlaceHolder();
    }

    if (
      (this.value === null || this.value === "") &&
      this.inputField.value !== undefined &&
      !this.inputField.value
    ) {
      this.phoneData = {
        country: this.defaultCountry,
        countryCallingCode: this.countryCodeValue,
        nationalNumber: "",
        number: "",
        inputValue: this.inputField.value,
        valid: false,
      };
      this.phoneValueChanged.emit(this.phoneData);
    } else {
      const value =
        this.inputField.value === undefined
          ? this.value
          : this.inputField.value;
      this.formatValue(null, value);
    }
  }

  @Watch("value")
  valueChanged() {
    this.formatValue(null, this.value);
  }

  componentWillLoad() {
    if (this.defaultCountry) {
      const dialCode = phoneCountryCodes.find(
        (item) => item.isoCode === this.defaultCountry
      )?.dialCode;
      if (dialCode) {
        this.countryCodeValue = dialCode;
      }

      const country = phoneCountryCodes.find(
        (item) => item.isoCode === this.defaultCountry
      );
      if (country) {
        this.country = country;
      }

      this.filteredCountries = [...phoneCountryCodes];
    }

    this.formatPlaceHolder();
  }

  componentDidLoad() {
    if (this.value) {
      this.formatValue(null, this.value);
    }
  }

  clearSearch(e: Event) {
    const target = e.target as HTMLRaulSelectElement;
    if (target.closest(".r-select__search__icon-reset")) {
      this.filteredCountries = [...phoneCountryCodes];
    }
  }

  formatPlaceHolder() {
    const asYouType = new AsYouType(this.defaultCountry);
    try {
      if (this.placeholder) {
        this.placeholder = asYouType.input(this.placeholder);
      } else if (this.value) {
        asYouType.input(this.value);
        this.placeholder = asYouType.getTemplate();
      }
    } catch (error) {
      console.log("Error on placeholder formatting", error);
    }
  }

  handleDropdownSearch(detail) {
    const value = detail.query;
    if (value === null || value === "") {
      this.filteredCountries = [...phoneCountryCodes];
    } else {
      const valueToCompare = value.toLowerCase();

      this.filteredCountries = phoneCountryCodes.filter((item) => {
        return (
          item.name.toLowerCase().includes(valueToCompare) ||
          item.isoCode.toLowerCase().includes(valueToCompare) ||
          item.dialCode.toLowerCase().includes(valueToCompare)
        );
      });
    }
  }

  selectCountry(option) {
    this.defaultCountry = option.value.split(" ")[0] as CountryCode;
  }

  setInputValue(value: string) {
    this.inputField.value = value;
    this.prevValue = value;
  }

  emitPhoneData(valid: boolean) {
    const { nationalNumber, number } = this.phoneData;

    this.phoneValueChanged.emit({
      country: this.defaultCountry,
      countryCallingCode: this.countryCodeValue,
      nationalNumber,
      number,
      inputValue: this.inputField.value,
      valid,
    });
  }

  formatValue(event: InputEvent | null, value?: string) {
    if (event === undefined && this.value === undefined) return;
    if (
      (this.value === null || this.value === "" || value === undefined) &&
      !event
    ) {
      this.phoneData = {
        country: this.defaultCountry,
        countryCallingCode: this.countryCodeValue,
        nationalNumber: "",
        number: "",
        inputValue: "",
        valid: false,
      };
    } else {
      const valueToFormat = event ? this.inputField.value : value;
      try {
        const asYouType = new AsYouType(this.defaultCountry);
        if (!valueToFormat) return;
        let formattedValue = asYouType.input(valueToFormat);
        const phoneNumber = parsePhoneNumberFromString(
          formattedValue,
          this.defaultCountry
        );

        if (phoneNumber) {
          const maxDigitsAllowed =
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            //@ts-ignore
            phoneNumber["metadata"].countries[this.defaultCountry][3];
          const phoneNumberLength = parseDigits(
            `${phoneNumber.nationalNumber}`
          ).length;
          const isPhoneLongerThanAllowed = maxDigitsAllowed.every(
            (item: number) => item < phoneNumberLength
          );
          const valid =
            Math.min(...maxDigitsAllowed) <= phoneNumberLength &&
            phoneNumberLength <= Math.max(...maxDigitsAllowed) &&
            (parseMin(`${phoneNumber.number}`) as PhoneNumber).isPossible();
          const isValueFromInputEvent =
            (event && event.inputType === "insertText") ||
            !(
              (window as Window).clipboardData &&
              (window as Window).clipboardData?.getData("text") ===
                valueToFormat
            );

          if (isPhoneLongerThanAllowed && event && isValueFromInputEvent) {
            this.setInputValue(this.prevValue);
            this.emitPhoneData(true);
          } else {
            this.phoneData = phoneNumber;

            setTimeout(() => {
              if (this.defaultCountry === "US" && phoneNumberLength < 4) {
                formattedValue = this.phoneData.nationalNumber;
              }
              this.setInputValue(formattedValue);
              this.emitPhoneData(valid);
            }, 0);
          }

          return;
        } else {
          this.phoneData = {
            country: this.defaultCountry,
            countryCallingCode: this.countryCodeValue,
            nationalNumber: "",
            number: event ? this.inputField.value : this.value || "",
            inputValue: event ? this.inputField.value : this.value,
            valid: false,
          };
        }
      } catch (error) {
        console.log("err", error);
      }
    }
    this.phoneValueChanged.emit(this.phoneData);
  }

  render() {
    const ccDropdown = () => {
      return (
        <raul-dropdown
          value={`${this.country.isoCode} (${this.country.dialCode})`}
          placeholderText=""
          disabled={this.disabled}
          onRaulChange={({ detail }) => this.selectCountry(detail)}
        >
          <raul-dropdown-search-input
            slot="search-input"
            onRaulDropdownSearchInput={({ detail }) =>
              this.handleDropdownSearch(detail)
            }
          />
          {this.filteredCountries.length === 0 ? (
            <div class="dropdown-menu__empty">No results found.</div>
          ) : null}
          {this.filteredCountries.map((item) => (
            <raul-dropdown-item
              text={`${item.isoCode} (${item.dialCode})`}
              value={`${item.isoCode} (${item.dialCode})`}
            >
              <div class="dropdown-item-content__text">{item.name}</div>
              <div class="dropdown-item-content__subtext">{`${item.isoCode} (${item.dialCode})`}</div>
            </raul-dropdown-item>
          ))}
        </raul-dropdown>
      );
    };
    return (
      <Host>
        {this.label && (
          <div class="r-form-element__label">
            <slot name="label-start" />
            <label htmlFor={this.name}>{this.label}</label>
            <slot name="label-end" />
          </div>
        )}
        <div class="r-form-element__wrapper">
          <div class="r-form-element__dropdown-wrapper">{ccDropdown()}</div>
          <div class="r-form-element__input-wrapper">
            <raul-input error={this.error ? this.error : undefined}>
              <input
                data-testid="phone-input"
                type="text"
                id={this.name}
                name={this.name}
                value={this.value}
                ref={(el) => (this.inputField = el as HTMLInputElement)}
                onInput={this.formatValue.bind(this)}
                placeholder={this.placeholder}
                disabled={this.disabled}
                autocomplete="off"
                {...(this.helperId
                  ? { "aria-describedby": this.helperId }
                  : {})}
              />
            </raul-input>
          </div>
        </div>
        <slot name="messaging" />
      </Host>
    );
  }
}
