import { Component, Prop, h, Host } from "@stencil/core";

@Component({
  tag: "raul-popover-image",
})
export class RaulPopoverImage {
  /**
   * Background image src.
   */
  @Prop() src?: string;

  render() {
    const styles = {
      background: `url('${this.src}') no-repeat center center / cover`,
    };

    return (
      <Host>
        <div class="r-popover__image" style={styles}></div>
      </Host>
    );
  }
}
