import { h } from "@stencil/core";

interface SnackBarProps {
  variant: string;
  dismissible: boolean;
  heading: string;
  content?: string;
  ctaMessage?: string;
  ctaUrl?: string;
  showIcon: boolean;
  ctaCallback?: () => void;
  onCloseCallback: () => void;
}

export const Snackbar = (props: SnackBarProps) => {
  const {
    variant,
    dismissible,
    heading,
    content,
    ctaMessage,
    ctaUrl,
    showIcon,
    ctaCallback,
    onCloseCallback,
  } = props;

  const renderIcon = (variant) => {
    const icons = {
      success: "check-1",
      danger: "alert-diamond",
      warning: "alert-triangle",
      information: "information-circle",
    };

    return <raul-icon icon={icons[variant]} kind="consumer" />;
  };

  return (
    <div
      class="r-snackbar__container"
      role={ctaMessage ? "alert" : "log"}
      tabindex="0"
    >
      <div
        class={{
          "r-snackbar__left-bar": true,
          [`r-snackbar__left-bar--${variant}`]: true,
        }}
      />
      {showIcon ? renderIcon(variant) : null}
      <div class="r-snackbar__content-container">
        <div class="r-snackbar__heading">
          <span>{heading}</span>
          {dismissible && (
            <button
              class="r-snackbar__heading__close-button"
              type="button"
              aria-label="Dismiss"
              onClick={() => onCloseCallback()}
            >
              <raul-icon icon="close"></raul-icon>
            </button>
          )}
        </div>
        {content && <div class="r-snackbar__content">{content}</div>}

        {ctaMessage ? (
          ctaUrl ? (
            <a href={ctaUrl}>
              <div class="r-snackbar__cta">{ctaMessage}</div>
            </a>
          ) : (
            <button
              class="r-snackbar__cta__message"
              type="button"
              onClick={ctaCallback && (() => ctaCallback())}
            >
              <span>{ctaMessage}</span>
            </button>
          )
        ) : null}
      </div>
    </div>
  );
};
