import { Component, h, Host } from "@stencil/core";

@Component({
  tag: "raul-container-loader",
  styleUrl: "raul-container-loader.scss",
})
export class RaulContainerLoader {
  render() {
    const Dot = (props: { animationDelay: string }) => (
      <div
        data-testid="dot"
        class="r-container-loader__dot"
        style={{ animationDelay: props.animationDelay }}
      ></div>
    );

    return (
      <Host>
        <div class="r-container-loader__dots-container">
          {Array.from({ length: 3 }).map((_, index) => (
            <Dot animationDelay={`${index * 333}ms`} />
          ))}
        </div>
      </Host>
    );
  }
}
