raul-bulk-action-bar {
  display: block;
  bottom: 0;
  max-height: 0;
  left: 0;
  position: fixed;
  width: 100%;
  transition: max-height 0.35s;
  z-index: 1010;

  &[open] {
    max-height: 3rem;
  }

  &:not([visible]) {
    visibility: hidden;
  }

  &:not([overflow]) {
    overflow: hidden;
  }

  @media (min-width: 768px) {
    position: relative;
    bottom: auto;
  }

  .r-bulk-actions__wrapper {
    align-items: center;
    display: flex;
    flex-wrap: nowrap;
    position: relative;
    padding: 0.5rem;
    column-gap: 0.5rem;
    height: 3rem;
    background-color: var(--color-primary);
    color: var(--color-background);
  }

  .r-bulk-actions__left {
    display: flex;
    flex-wrap: nowrap;
    flex-grow: 1;
    column-gap: 0.5rem;
  }

  .r-bulk-actions__buttons-wrapper {
    display: flex;
    flex-wrap: wrap;
    column-gap: 0.5rem;
  }

  .r-bulk-actions__more {
    display: inline-block;
    position: relative;
  }

  .r-bulk-actions__right {
    align-items: center;
    display: flex;
    font-size: var(--font-size-2);
    justify-content: flex-end;
    width: 7.75rem;
    flex: 0 0 auto;
   

    @media (min-width: 768px) {
      width: 16.5625rem;
    }
  }

  .r-bulk-actions__selected-count {
    font-size: var(--font-size-2);
  }

  .r-bulk-actions__select-all {
    padding: 0rem 0.75rem 0rem 0.75rem;
    display: none;

    &:focus-visible {
      outline-color: var(--color-white);
      outline-offset: 2px;
    }

    &:hover {
      text-decoration: underline;
    }

    &::after {
      display: inline-block;
      margin-left: 0.5rem;
      content: "|";
    }

    @media (min-width: 768px) {
      display: inline-block;
    }
  }

  .r-bulk-actions__close {
    padding: var(--font-size-2);

    &:focus-visible {
      outline: 2px solid var(--color-white);
      outline-offset: 0;
    }

    raul-icon {
      font-size: var(--font-size-3);
    }
  }

  .r-bulk-actions__ghost {
    height: 0;
    overflow: hidden;
  }
  
  raul-button button {
    &:focus-visible{
      outline-color: var(--color-white);
      outline-offset: 2px;
    }
  }
}