$border: 8px solid transparent;
$border-height: 10px solid var(--color-neutral-light);
$margin-Y: -11px;

$border-after: 7px solid transparent;
$border-height-after: 9px solid var(--white);
$margin-X: -7px;

raul-popover {
  position: relative;
  display: inline-block;

  raul-popover-controller {
    display: block;
    background-color: var(--color-background);
    border-radius: 0.125rem;
    border-width: 1px;
    border-style: solid;
    border-color: var(--gray-light);
    box-shadow: var(--shadow-level-4);
    transition: opacity .15s linear;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    z-index: 9999;
    max-width: 20rem;
    position: absolute;
    z-index: 9999;
    max-width: 20rem;
  }

  raul-popover-image {
    display: block;
    overflow: hidden;
    border-top-left-radius: 0.125rem;
    border-top-right-radius: 0.125rem;

    .r-popover__image {
      height: 120px;
      min-width: 318px;
    }
  }

  raul-popover-container {
    display: block;
  }

  raul-popover-content {
    display: flex;
    padding: 2rem;
    position: relative;
  

  .r-popover-label {
    display: flex;
    flex-direction: column;
  }

  .r-popover-col {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
  }
}

&[open] raul-popover-controller {
  opacity: 1;
  pointer-events: inherit;
}

&[placement="top"] {
  raul-popover-controller {
    bottom: 100%;
    left: 50%;
    transform: translate(-50%, 0);
    margin-bottom: 0.5rem;
  }

  .r-popover-arrow {
    width: 0px;
    height: 0px;
    position: absolute;
    bottom: $margin-Y;
    left: 50%;
    transform: translate(-50%, 0);
    border-left: $border;
    border-right: $border;
    border-top: $border-height;

    &:after {
      content: " ";
      width: 0px;
      height: 0px;
      position: absolute;
      top: $margin-Y;
      left: 50%;
      transform: translate(-50%, 0);
      border-left: $border-after;
      border-right: $border-after;
      border-top: $border-height-after;
    }
  }
}

&[placement="bottom"] {
  raul-popover-controller {
    top: 100%;
    left: 50%;
    transform: translate(-50%, 0);
    margin-top: 0.5rem;
  }

  .r-popover-arrow {
    width: 0px;
    height: 0px;
    position: absolute;
    top: $margin-Y;
    left: 50%;
    transform: translate(-50%, 0);
    border-left: $border;
    border-right: $border;
    border-bottom: $border-height;

    &:after {
      content: " ";
      width: 0px;
      height: 0px;
      position: absolute;
      bottom: $margin-Y;
      left: 50%;
      transform: translate(-50%, 0);
      border-left: $border-after;
      border-right: $border-after;
      border-bottom: $border-height-after;
    }
  }
}

&[placement="left"] {
  raul-popover-controller {
    top: 50%;
    right: 100%;
    transform: translate(0, -50%);
    margin-right: 0.25rem;
  }

  .r-popover-arrow {
    width: 0px;
    height: 0px;
    position: absolute;
    top: 50%;
    right: $margin-Y;
    transform: translate(0, -50%);
    border-top: $border;
    border-bottom: $border;
    border-left: $border-height;

    &:after {
      content: " ";
      width: 0px;
      height: 0px;
      position: absolute;
      top: 50%;
      left: $margin-Y;
      transform: translate(0, -50%);
      border-top: $border-after;
      border-bottom: $border-after;
      border-left: $border-height-after;
    }
  }
}

&[placement="right"] {
  raul-popover-controller {
    top: 50%;
    left: 100%;
    transform: translate(0, -50%);
    margin-left: 0.25rem;
  }

  .r-popover-arrow {
    width: 0px;
    height: 0px;
    position: absolute;
    top: 50%;
    left: $margin-Y;
    transform: translate(0, -50%);
    border-top: 7px solid var(--transparent);
    border-bottom: 7px solid var(--transparent);
    border-right: $border-height;

    &:after {
      content: " ";
      width: 0px;
      height: 0px;
      position: absolute;
      top: 50%;
      left: 2px;
      transform: translate(0, -50%);
      border-top: $border-after;
      border-bottom: $border-after;
      border-right: $border-height-after;
    }
  }
}
}