raul-list-item {
  display: block;
  border-bottom-width: 1px;
  --border-opacity: 1;
  border-color: rgba(235, 237, 238, var(--border-opacity));
  padding: 1rem 0rem 1rem 0rem;
  font-size: var(--font-size-3);

  &.raul-list-item--movable,
  &.raul-list-item--movable-by-handle {
    padding: 0;

    &:hover {
      background-color: var(--color-primary-highlight);

      raul-handle {
        opacity: 1;
      }
    }
  }

  &.raul-list-item--movable:hover {
    cursor: move;
  }

  &.raul-list-item--movable-by-handle:hover {
    raul-handle {
      cursor: move;
    }
  }

  .r-list-item-wrapper {
    padding: 1rem 0;
    display: flex;
    align-items: stretch;

    & > div {
      flex: 1;
    }
  }
}
