@import "../../../styles/mixins/focus.scss";

raul-date-picker {
  display: block;
  position: relative;

  &[disabled] {
    button {
      background-color: rgba(235, 237, 238, 1);
      cursor: not-allowed;
    }
  }

  .raul--date-picker__input-accessory {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;

    raul-icon {
      color: rgba(155, 163, 167, 1);
    }

    raul-icon[icon="calendar-2"] {
      font-size: var(--font-size-5);
    }

    raul-icon[icon="remove-2"] {
      font-size: var(--font-size-3);
    }

    button {
      width: 2.5rem;
      height: 2.5rem;
      display: flex;
      align-items: center;
      justify-content: center;

      raul-icon[icon="remove-2"] {
        font-size: var(--font-size-3);
      }
    }
  }
}

.date-picker--calendar-container {
  height: auto;
  background-color: var(--color-background);

  &:not(.static) {
    position: fixed;
    z-index: 10000;
    width: 100%;
    height: 100vh;
    display: none;

    &[hidden] {
      display: none !important;
    }
  }

  @media screen and (max-width: 40rem) {
    &:not(.static) {
      top: 0 !important;
    }
  }

  @media screen and (min-width: 40rem) {
    max-width: 16rem;
    height: auto;

    &:not(.static) {
      position: absolute;
      margin: 0.5rem 0;
      height: auto;
      border: 1px solid var(--color-neutral-light);
      border-radius: 0.125rem;
      box-shadow: var(--menu-shadow);
    }
  }

  &.open {
    display: flex;
    flex-direction: column;
  }

  .flatpickr-innerContainer {
    padding: 0.5rem 1rem 1rem;
    margin: 0 auto;
    height: 100%;

    @media screen and (min-width: 40rem) {
      height: auto;
    }
  }

  .date-picker--header-controls {
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid var(--color-neutral-light);

    background-color: var(--color-neutral-lightest);
    font-size: var(--font-size-3);

    button {
      padding: 0.75rem;
      color: var(--color-primary);
    }

    span {
      font-weight: var(--font-weight-medium);
    }

    @media screen and (min-width: 40rem) {
      display: none;
    }
  }

  .date-picker--footer-controls {
    padding: 1rem;

    raul-button {
      width: 100%;
    }

    @media screen and (min-width: 40rem) {
      display: none;
    }
  }

  &.static .date-picker--header-controls,
  &.static .date-picker--footer-controls {
    display: none;
  }

  .date-picker--calendar-months {
    padding: 1rem 1rem 0.5rem;
    margin: 0 auto;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    & > span {
      display: block;
      width: 1rem;

      cursor: pointer;

      &.flatpickr-disabled {
        color: rgba(198, 204, 208, 1);
        cursor: not-allowed;
      }
    }

    .flatpickr-current-month {
      display: flex;
      align-items: center;
      justify-content: space-around;
      column-gap: 0.5rem;
    }

    .flatpickr-monthDropdown-months {
      appearance: none;
      margin-left: 0.75rem;
      flex: auto;

      cursor: pointer;
      font-size: var(--font-size-3);
      font-weight: var(--font-weight-semibold);
      text-align: right;
    }

    .numInputWrapper {
      position: relative;
      width: 3.5rem;

      .numInput {
        width: 100%;
        background-color: var(--transparent);
        font-size: var(--font-size-3);
        font-weight: var(--font-weight-semibold);

        &:disabled {
          cursor: not-allowed;
        }
      }
    }
  }

  .flatpickr-weekdaycontainer {
    padding: 0.5rem 0;
    width: 100%;
    display: flex;
    justify-content: space-between;

    .date-picker--calendar-weekday {
      width: 14.2857143%;
      display: block;
      font-size: var(--font-size-2);
      font-weight: var(--font-weight-semibold);
      line-height: 1.125;
      text-align: center;
    }
  }

  .dayContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    row-gap: 0.5rem;

    .flatpickr-day {
      position: relative;
      z-index: 1;
      width: 14.2857143%;
      height: 2rem;
      display: flex;
      align-items: center;
      justify-content: center;

      raul-date-picker[disabled] & {
        color: rgba(198, 204, 208, 1);
        cursor: not-allowed;
      }

      &:focus-visible {
        outline: none;

        &:before {
          @include focus-style(-2px, rgba(0, 118, 204, 1));
          background-color: rgba(229, 244, 255, 1);
        }

        &.selected:before {
          @include focus-style(-4px, rgba(255, 255, 255, 1));
        }
      }

      cursor: pointer;
      font-size: var(--font-size-2);
      text-align: center;

      &:before {
        position: absolute;
        z-index: -1;
        width: 2rem;
        height: 2rem;
        display: block;
        border-radius: 500rem;
        content: "";
      }

      &.inRange {
        background-color: rgba(229, 244, 255, 1);
      }

      &.today:before,
      &.today.flatpickr-disabled:hover:before {
        background-color: rgba(235, 237, 238, 1);
      }

      &.startRange:after {
        position: absolute;
        right: 0;
        z-index: -2;
        width: 50%;
        height: 2rem;
        background-color: rgba(229, 244, 255, 1);
        content: "";
      }

      &.endRange:before {
        background-color: rgba(229, 244, 255, 1);
      }

      &.endRange:after {
        position: absolute;
        left: 0;
        z-index: -2;
        width: 50%;
        height: 2rem;

        background-color: rgba(229, 244, 255, 1);
        content: "";
      }

      &:not(.selected):hover:before {
        background-color: rgba(229, 244, 255, 1);

        raul-date-picker[disabled] & {
          background-color: var(--transparent);
        }
      }

      &.prevMonthDay,
      &.nextMonthDay {
        color: rgba(198, 204, 208, 1);
      }

      &.flatpickr-disabled {
        color: rgba(198, 204, 208, 1);
        cursor: not-allowed;

        &:hover:before {
          background-color: var(--transparent);
        }
      }

      &.selected {
        color: var(--color-background);

        &:before {
          background-color: var(--color-primary);
        }
      }
    }
  }
}
