import {
  Component,
  Element,
  Method,
  Prop,
  Event,
  EventEmitter,
  Watch,
  Listen,
  Host,
  h,
} from "@stencil/core";
import { FOCUSABLE_ELEMENTS } from "../../../utils/keyboard";

/**
 * @slot - Place `raul-modal-header`, `raul-modal-body` and `raul-modal-footer` components in this slot.
 */
@Component({
  tag: "raul-modal",
  styleUrl: "raul-modal.scss",
})
export class RaulModal {
  private modalTrigger!: HTMLElement;
  dialogEl!: HTMLDivElement;
  dummyEl!: HTMLAnchorElement;
  focusableElements!: NodeList;

  @Element() el!: HTMLRaulModalElement;

  /**
   * Sets the on screen state of the component.
   */
  @Prop({ mutable: true, reflect: true }) active = false;

  @Watch("active")
  handleActivePropChange(newVal: boolean) {
    if (newVal) {
      this.visible = true;
      this.modalOpen.emit();
    } else {
      this.modalClose.emit();
    }
  }

  /**
   * Determines wether the modal can be closed via clicking away or the `Escape` key
   */
  @Prop({ reflect: true }) notDismissible = false;

  /**
   * A `normal` modal will have a the header and body centered and no close button. A `media` modal will have the header content aligned to the left and a close button.
   */
  @Prop({ reflect: true }) variant: "normal" | "media" = "normal";

  /**
   * Controls visibility of component.
   */
  @Prop({ mutable: true, reflect: true }) visible = false;

  /**
   * Emitted when the modal is open.
   */
  @Event({ eventName: "modalopen" }) modalOpen!: EventEmitter<undefined>;

  /**
   * Emitted when the modal is closed.
   */
  @Event({ eventName: "modalclose" }) modalClose!: EventEmitter<undefined>;

  /**
   * Emitted when the modal is ready.
   */
  @Event({ eventName: "modalready" }) modalReady!: EventEmitter<undefined>;

  componentWillLoad() {
    this.visible = this.active ? true : false;
  }

  componentDidLoad() {
    this.focusableElements = this.dialogEl?.querySelectorAll(
      FOCUSABLE_ELEMENTS.join(", ")
    );
    this.modalReady.emit();
  }

  @Listen("click")
  handleInternalClick({ target }: MouseEvent) {
    if (
      (target as HTMLElement).closest('[role="dialog"]') !== this.dialogEl &&
      !this.notDismissible
    ) {
      this.dummyEl.focus();
    }
    if ((target as HTMLElement).closest("[data-close-modal]")) {
      this.dummyEl.focus();
    }
  }

  @Listen("focusin")
  handleInternalFocus({ target, relatedTarget }: FocusEvent) {
    const related = relatedTarget as HTMLElement;
    if (
      !related?.closest("raul-modal") ||
      related?.closest("raul-modal") !== this.el
    ) {
      this.modalTrigger = related;
    }

    if (!this.notDismissible && target === this.dummyEl) this.close();
  }

  @Listen("keydown")
  handleInternalKeydown(e: KeyboardEvent) {
    const { key, target, shiftKey } = e;

    if (["Esc", "Escape"].includes(key) && !this.notDismissible) {
      this.dummyEl.focus();
    }

    if (shiftKey && key === "Tab" && this.dialogEl !== target) {
      const lastIndex = this.focusableElements.length - 1;
      if (target === this.focusableElements[0]) {
        e.preventDefault();
        (this.focusableElements[lastIndex] as HTMLElement).focus();
      }
      return;
    }

    if (key === "Tab" && this.dialogEl !== target) {
      const lastIndex = this.focusableElements.length - 1;
      if (target === this.focusableElements[lastIndex]) {
        e.preventDefault();
        (this.focusableElements[0] as HTMLElement)?.focus();
      }
    }
  }

  @Listen("transitionstart")
  handleInternalTransitionstart({ target }: TransitionEvent) {
    if (this.active && target === this.el && this.focusableElements?.length > 0)
      (this.focusableElements[0] as HTMLElement)?.focus();
  }

  @Listen("transitionend")
  handleInternalTransitionend({ target }: TransitionEvent) {
    if (!this.active && target === this.dialogEl) {
      this.visible = false;
    }
  }

  /**
   * Opens the aside.
   * @returns {Promise<void>}
   */

  @Method()
  async open(): Promise<void> {
    this.active = true;
    this.visible = true;
  }

  /**
   * Closes the aside.
   * @returns {Promise<void>}
   */

  @Method()
  async close(): Promise<void> {
    this.active = false;
    this.modalTrigger?.focus();
  }

  render() {
    return (
      <Host aria-hidden={this.active ? "false" : "true"} data-testid="modal">
        <a
          ref={(el) => (this.dummyEl = el as HTMLAnchorElement)}
          class="r-dummy-link"
          tabindex="-1"
          aria-hidden="true"
          href="javascript:undefined"
        ></a>

        <div
          ref={(el) => (this.dialogEl = el as HTMLDivElement)}
          class="r-modal__dialog"
          role="dialog"
          aria-modal="true"
          aria-labelledby="modalTitle"
        >
          <slot />
        </div>
      </Host>
    );
  }
}
