import { Component, h, Host, Element } from "@stencil/core";
import { HTMLStencilElement, State } from "@stencil/core/internal";
/**
 * @slot - Place footer copyright text in this slot.
 */
@Component({
  tag: "raul-footer-copyright",
})
export class RaulFooterCopyright {
  currentYear = new Date().getFullYear();

  @Element() host: HTMLStencilElement | undefined;

  @State() hasSlotContent;

  componentWillRender() {
    this.hasSlotContent =
      this.host?.childNodes && this.host?.childNodes.length > 1;
  }

  render() {
    return (
      <Host>
        <slot>
          {!this.hasSlotContent ? (
            <span>
              &copy; Copyright 1998-{this.currentYear} All rights reserved
            </span>
          ) : null}
        </slot>
      </Host>
    );
  }
}
