raul-slider {
  display: block;
  font-size: var(--font-size-3);

  .r-form-element {
    input[type="number"] {
      -moz-appearance: textfield !important;
      /*For FireFox*/

      &::-webkit-inner-spin-button {
        /*For Webkits like Chrome and Safari*/
        -webkit-appearance: none;
        margin: 0;
      }
    }

    &__control {
      display: flex;
      align-items: center;
      font-size: var(--font-size-3);
      height: 1.25rem;
      line-height: var(--line-height-tight);
      flex-grow: 1;
      position: relative;

      &__track {
        background-color: var(--color-neutral);
        display: flex;
        align-items: center;
        flex-grow: 1;
        height: 0.25rem;
        border-radius: 9999px;
        overflow: hidden;
        position: relative;

        &__progress {
          height: 0.25rem;

          &--transparent {
            background-color: var(--transparent);
          }

          &--primary {
            background-color: var(--color-primary);
          }

          &--warning {
            background-color: var(--color-warning);
          }

          &--error {
            background-color: var(--color-destructive);
          }

          &--success {
            background-color: var(--color-success);
          }

          &--disabled {
            background-color: var(--color-neutral);
          }
        }
      }

      &--range {
        input[type="range"] {
          pointer-events: none;
        }
      }

      &__slider {
        display: block;
        cursor: pointer;
        height: 1.25rem;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: var(--transparent);
        -webkit-appearance: none;

        &::-webkit-slider-thumb {
          height: 1.25rem;
          width: 1.25rem;
          border: 2px;
          background-color: var(--color-background);
          border-radius: 9999px;
          border-style: solid;
          -webkit-appearance: none;
          pointer-events: auto !important;
        }

        &::-moz-range-thumb {
          height: 1.25rem;
          width: 1.25rem;
          background-color: var(--color-background);
          border-radius: 9999px;
          border-style: solid;
          border-width: 2px;
          pointer-events: auto !important;
        }

        &::-ms-thumb {
          height: 1.25rem;
          width: 1.25rem;
          background-color: var(--color-background);
          border-radius: 9999px;
          border-style: solid;
          pointer-events: auto !important;
        }

        &--primary {
          &::-webkit-slider-thumb {
            border-color: var(--color-primary);
          }

          &::-moz-range-thumb {
            border-color: var(--color-primary);
          }

          &::-ms-thumb {
            border-color: var(--color-primary);
          }
        }

        &--error {
          &::-webkit-slider-thumb {
            border-color: var(--color-destructive);
          }

          &::-moz-range-thumb {
            border-color: var(--color-destructive);
          }

          &::-ms-thumb {
            border-color: var(--color-destructive);
          }
        }

        &--warning {
          &::-webkit-slider-thumb {
            border-color: var(--color-warning);
          }

          &::-moz-range-thumb {
            border-color: var(--color-warning);
          }

          &::-ms-thumb {
            border-color: var(--color-warning);
          }
        }

        &--success {
          &::-webkit-slider-thumb {
            border-color: var(--color-success);
          }

          &::-moz-range-thumb {
            border-color: var(--color-success);
          }

          &::-ms-thumb {
            border-color: var(--color-success);
          }
        }

        &:disabled {
          cursor: default !important;
        }

        &:disabled::-webkit-slider-thumb {
          border-color: var(--color-neutral-dark);
          cursor: not-allowed;
        }

        &:disabled::-moz-range-thumb {
          border-color: var(--color-neutral-dark);
          cursor: not-allowed;
        }

        &:disabled::-ms-thumb {
          border-color: var(--color-neutral-dark);
          cursor: not-allowed;
        }

        &:hover 
        + .r-form-element__control__wrapper 
        .r-form-element__control__tooltip {
          opacity: 1;
        }
      }

      &__wrapper {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        pointer-events: none;
        padding: 0px 10px;

        &__formatter {
          position: relative;
          flex-grow: 1;
        }
      }

      &__tooltip {
        position: absolute;
        background-color: var(--color-base);
        color: var(--color-background);
        font-size: var(--font-size-2);
        padding: 0.25rem 0.5rem 0.25rem 0.5rem;
        box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
        border-radius: 0.125rem;
        z-index: 50;
        opacity: 0;
        top: -30px;
        left: 0%;
        transform: translateX(-50%);
        transition: opacity 0.1s ease;

        &__pointer {
          position: absolute;
          background-color: var(--color-base);
          color: var(--white);
          font-size: var(--font-size-2);
          width: 0.5rem;
          height: 0.5rem;
          box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
          bottom: -4px;
          left: 50%;
          transform: translateX(-50%) rotate(45deg);
        }
      }
    }

    &__label {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: var(--font-size-2);
      font-weight: var(--font-weight-medium);
      margin-bottom: 0.75rem;
    }

    &__input {
      height: 1.5rem;
      position: relative;

      input[type="number"] {
        border-color: var(--color-neutral);
        border-width: 1px;
        height: 1.5rem;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: var(--transparent);
        padding-left: 0.75rem;
        padding-right: 0.75rem;
        border-radius: 0.125rem;
        -webkit-appearance: none;

        &:focus {
          outline: none;
          border-color: var(--color-primary);
        }
      }

      &--range {
        flex: 1 1 0%;
      }

      &--single {
        width: 50%;
      }

      &__dash {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: var(--font-size-5);
        width: 1.25rem;
        height: 1.5rem;
      }

      &__symbol {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);

        &-append {
          left: auto;
          right: 0.75rem !important;
        }

        &-prepend {
          right: auto;
          left: 0.75rem !important;
        }
      }
    }

    &__scale {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 0.5rem;
      font-size: var(--font-size-2);
    }

    &--symbol {
      &-prepend {
        input[type="number"] {
          padding-left: 1.75rem !important;
          text-align: right;

          &::placeholder {
            text-align: right;
          }

          &::-webkit-input-placeholder {
            text-align: right;
          }
        }
      }

      &-append {
        input[type="number"] {
          padding-right: 1.75rem !important;
        }
      }
    }

    &--disabled {
      .r-form-element__input {
        color: var(--color-neutral-dark);
      }

      input[type="number"] {
        background-color: var(--color-neutral-light);
        border-color: var(--color-neutral-light);
      }
    }

    &--compact:not(.r-form-element--search) {
      input[type="range"] {
        &::placeholder {
          color: var(--transparent);
        }
      }
    }

    &--focused {
      input[type="range"] {
        &::placeholder {
          color: var(--color-neutral-dark) !important;
        }
      }
    }
  }
}