export const VERTICAL_NAV_KEYS: string[] = ["ArrowUp", "ArrowDown"];
export const FOCUSABLE_ELEMENTS: string[] = [
  "button:enabled",
  "a",
  '[tabindex="0"]:enabled',
  "input:enabled",
  "raul-dropdown",
];

export const menuNavigation = (
  menu: HTMLDivElement,
  itemTag: string,
  toggle: HTMLElement,
  event: KeyboardEvent
) => {
  event.preventDefault();

  const { key } = event;
  const target = event.target as HTMLElement;
  const tag = itemTag.toLocaleLowerCase();
  const menuItems = menu.querySelectorAll(`${itemTag}:not([disabled])`);

  if (menuItems.length > 0) {
    let itemIndex;
    if (target.closest(tag))
      itemIndex = Array.prototype.indexOf.call(menuItems, target.closest(tag));

    if (key === "ArrowDown") {
      target === toggle || target.tagName === "INPUT"
        ? (menuItems[0].firstElementChild as HTMLElement).focus()
        : null;
      itemIndex != undefined && itemIndex + 1 !== menuItems.length
        ? (menuItems[itemIndex + 1].firstElementChild as HTMLElement).focus()
        : null;
      itemIndex != undefined && itemIndex + 1 === menuItems.length
        ? (menuItems[0].firstElementChild as HTMLElement).focus()
        : null;
    }

    if (key === "ArrowUp") {
      target === toggle || target.tagName === "INPUT"
        ? (
            menuItems[menuItems.length - 1].firstElementChild as HTMLElement
          ).focus()
        : undefined;
      itemIndex != undefined && itemIndex !== 0
        ? (menuItems[itemIndex - 1].firstElementChild as HTMLElement).focus()
        : null;
      itemIndex != undefined && itemIndex === 0
        ? (
            menuItems[menuItems.length - 1].firstElementChild as HTMLElement
          ).focus()
        : null;
    }
  }
};
