@import "../../../styles/mixins/focus.scss";

raul-menu {
  position: relative;
  display: inline-flex;

  .raul--menu__toggle-wrapper {
    & > button,
    & > *[tabindex="0"] {
      outline: 0px solid;

      @include focus-visible;
    }
  }

  .raul--menu__dropdown-menu {
    position: absolute;
    z-index: 1002;
    margin: 0.5rem 0;
    max-height: 13.75rem;
    display: flex;
    flex-direction: column;
    border-radius: 0.125rem;
    box-shadow: var(--menu-shadow);
    overflow: auto;
    background-color: var(--color-background);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.15s;
  }

  &[open] .raul--menu__dropdown-menu {
    opacity: 1;
    pointer-events: all;
  }

  &[menu-position="start-top"] .raul--menu__dropdown-menu {
    bottom: 100%;
    left: 0;
  }

  &[menu-position="start-bottom"] .raul--menu__dropdown-menu {
    top: 100%;
    left: 0;
  }

  &[menu-position="end-top"] .raul--menu__dropdown-menu {
    bottom: 100%;
    right: 0;
  }

  &[menu-position="end-bottom"] .raul--menu__dropdown-menu {
    top: 100%;
    right: 0;
  }

  &[variant="links"] {
    raul-menu-item {
      button,
      a {
        color: var(--color-primary);
      }
    }
  }

  &[text-size="md"] {
    raul-menu-item {
      button,
      a {
        font-size: var(--font-size-4);
      }
    }
  }

  raul-menu-item {
    button,
    a {
      padding: 0 1rem;
      width: 100%;
      height: 2.5rem;
      display: flex;
      align-items: center;
      column-gap: 0.5rem;
      font-size: var(--font-size-2);
      white-space: nowrap;

      &:hover {
        text-decoration: underline;
      }

      &:disabled,
      &[aria-disabled="true"] {
        color: var(--color-neutral);
        cursor: not-allowed;
        text-decoration: none;
      }

      @include focus-visible(-2px);
    }

    &[border] {
      border-top: 1px solid var(--color-neutral-light);
    }

    &[destructive] {
      button,
      a {
        color: var(--color-destructive);
      }
    }
  }
}
