import { Component, Prop, h } from "@stencil/core";
import { randomUID } from "../../../../utils/string";

@Component({
  tag: "raul-heading",
})
export class RaulHeading {
  @Prop() variant?: "page" | "section" | "content";

  render() {
    const size =
      this.variant === "page"
        ? "hero"
        : this.variant === "section"
        ? "extra-large"
        : this.variant === "content"
        ? "large"
        : "large";

    return (
      <raul-text paragraph={true} size={size} key={randomUID()}>
        <slot />
      </raul-text>
    );
  }
}
