import { Component, Prop, h } from "@stencil/core";

/**
 * @slot - Place status text in this slot.
 * @slot icon - Adds raul-icon component to the left of the text.
 */
@Component({
  tag: "raul-status",
  styleUrl: "raul-status.scss",
})
export class RaulStatus {
  /**
   * Status variant.
   */
  @Prop() variant:
    | "default"
    | "destructive"
    | "warning"
    | "success"
    | "attention" = "default";

  /**
   * sets the size for status.
   */
  @Prop({ reflect: true }) size: "default" | "large" = "default";

  render() {
    return (
      <div
        class={{
          "r-status": true,
          [`r-status--${this.variant}`]: this.variant !== "default",
        }}
      >
        <slot name="icon" />
        <slot />
      </div>
    );
  }
}
