import { Component, h, Prop, Host } from "@stencil/core";
import { TimelineEventInterface } from "../../../utils/interfaces";

/**
 * @slot - Use the default slot to compose events with the `raul-timeline-event` component.
 */
@Component({
  tag: "raul-timeline",
  styleUrl: "raul-timeline.scss",
})
export class RaulTimeline {
  /**
   * @deprecated Use the slot to compose events with the `raul-timeline-event` component instead.
   */
  @Prop() events: TimelineEventInterface[] = [];
  /**
   * Sets the title.
   */
  @Prop() titleName?: string;

  componentWillLoad() {
    if (this.events)
      console.warn(
        '[RAUL: Deprecated] "events" is deprecated, use the slot to compose events with `raul-timeline-event` components instead',
      );
  }

  render() {
    return (
      <Host>
        <div class="r-timeline__title">{this.titleName}</div>

        <ul class="r-timeline__events">
          <slot>
            {this.events?.map((event) => (
              <raul-timeline-event data={event}></raul-timeline-event>
            ))}
          </slot>
        </ul>
      </Host>
    );
  }
}
