raul-filter-bar {
  display: flex;
  align-items: center;
  width: 100%;
  font-size: var(--font-size-3);

  raul-button {
    display: inline-block;
    vertical-align: middle;
    font-size: var(--font-size-3);
  }

  raul-filter-menu {
    visibility: hidden;
    margin-left: 1.5rem;

    @media (min-width: 768px) {
      display: inline-block;
    }
  }
}