export const handleKeyPress = (
  e: KeyboardEvent,
  el: HTMLInputElement,
  variant: string
) => {
  const isDirectionalArrow =
    e.key === "ArrowLeft" ||
    e.key === "ArrowUp" ||
    e.key === "ArrowRight" ||
    e.key === "ArrowDown";
  const isDelete = e.key === "Backspace" || e.key === "Delete";
  const isTab = e.key === "Tab";
  const isShiftTab = e.shiftKey && e.key === "Tab";
  const keyIsNumber = !isNaN(Number(e.key));
  let value, lastInputPosition;

  if (
    el.selectionStart &&
    el.selectionStart <= 2 &&
    !isDirectionalArrow &&
    keyIsNumber
  ) {
    if (
      Number(e.key) == 0 &&
      Number(el.value.substr(0, 2)) == 0 &&
      variant === "time-picker"
    ) {
      value = `12:${el.value.substr(3, 5)}`;
      lastInputPosition = el.selectionStart;
      return { start: 3, end: 5, value, lastInputPosition };
    }

    if (
      Number(e.key) < 4 &&
      Number(el.value.substr(0, 2)) == 2 &&
      variant === "time-picker-twenty-four"
    ) {
      value = `${"2" + e.key}:${el.value.substr(3, 5)}`;
      lastInputPosition = el.selectionStart;
      return { start: 3, end: 5, value, lastInputPosition };
    }

    if (Number(e.key) == 2 && variant === "time-picker-twenty-four") {
      value = `${"0" + e.key}:${el.value.substr(3, 5)}`;
      lastInputPosition = el.selectionStart;
      return { start: 0, end: 2, value, lastInputPosition };
    }

    if (Number(e.key) <= 2 && Number(el.value.substr(0, 2)) == 1) {
      value = `${"1" + e.key}:${el.value.substr(3, 5)}`;
      lastInputPosition = el.selectionStart;
      return { start: 3, end: 5, value, lastInputPosition };
    }

    if (Number(e.key) <= 1) {
      value = `${"0" + e.key}:${el.value.substr(3, 5)}`;
      lastInputPosition = el.selectionStart;
      return { start: 0, end: 2, value, lastInputPosition };
    }

    if (
      Number(e.key) > 1 &&
      Number(el.value.substr(0, 2)) !== 1 &&
      variant === "time-picker"
    ) {
      value = `${"0" + e.key}:${el.value.substr(3, 5)}`;
      lastInputPosition = el.selectionStart;
      return { start: 3, end: 5, value, lastInputPosition };
    }

    if (
      Number(e.key) > 2 &&
      Number(el.value.substr(0, 2)) !== 1 &&
      variant === "time-picker-twenty-four"
    ) {
      value = `${"0" + e.key}:${el.value.substr(3, 5)}`;
      lastInputPosition = el.selectionStart;
      return { start: 3, end: 5, value, lastInputPosition };
    }
  }

  if (
    lastInputPosition === 0 &&
    Number(e.key) == 2 &&
    Number(el.value.substr(0, 2)) == 1 &&
    variant === "time-picker" &&
    !isDirectionalArrow &&
    !isDelete &&
    keyIsNumber
  ) {
    value = `${"1" + e.key}:${el.value.substr(3, 5)}`;
    lastInputPosition = el.selectionStart;
    return { start: 3, end: 5, value, lastInputPosition };
  }

  if (
    lastInputPosition === 0 &&
    Number(el.value.substr(0, 2)) == 1 &&
    variant === "time-picker-twenty-four" &&
    !isDirectionalArrow &&
    !isDelete &&
    keyIsNumber
  ) {
    value = `${"1" + e.key}:${el.value.substr(3, 5)}`;
    lastInputPosition = el.selectionStart;
    return { start: 3, end: 5, value, lastInputPosition };
  }

  if (
    lastInputPosition === 0 &&
    Number(e.key) < 4 &&
    Number(el.value.substr(0, 2)) == 2 &&
    variant === "time-picker-twenty-four" &&
    !isDirectionalArrow &&
    !isDelete &&
    keyIsNumber
  ) {
    value = `${"2" + e.key}:${el.value.substr(3, 5)}`;
    lastInputPosition = el.selectionStart;
    return { start: 3, end: 5, value, lastInputPosition };
  }

  if (
    el.selectionStart &&
    el.selectionStart >= 3 &&
    el.selectionStart < 5 &&
    !isDirectionalArrow &&
    !isDelete &&
    keyIsNumber
  ) {
    if (
      Number(el.value.substr(3, 3)) >= 0 &&
      Number(el.value.substr(3, 3)) < 6
    ) {
      value = `${el.value.substr(0, 2)}:${
        Number(el.value.substr(3, 3)) + e.key
      }${el.value.substr(5, 8)}`;
      lastInputPosition = el.selectionStart;
      return variant === "time-picker-twenty-four"
        ? { start: 3, end: 5, value, lastInputPosition }
        : { start: 6, end: 8, value, lastInputPosition };
    }
    if (Number(e.key) > 0 && Number(e.key) < 6) {
      value = `${el.value.substr(0, 2)}:${"0" + e.key}${el.value.substr(5, 8)}`;
      lastInputPosition = el.selectionStart;
      return { start: 3, end: 5, value, lastInputPosition };
    }
    if (Number(e.key) > 5) {
      value = `${el.value.substr(0, 2)}:${"0" + e.key}${el.value.substr(5, 8)}`;
      lastInputPosition = el.selectionStart;
      return variant === "time-picker-twenty-four"
        ? { start: 3, end: 5, value, lastInputPosition }
        : { start: 6, end: 8, value, lastInputPosition };
    }
    if (Number(e.key) === 0) {
      value = `${el.value.substr(0, 2)}:${"0" + e.key}${el.value.substr(5, 8)}`;
      lastInputPosition = el.selectionStart;
      return { start: 3, end: 5, value, lastInputPosition };
    }
  }

  if (
    lastInputPosition === 3 &&
    Number(el.value.substr(3, 3)) > 0 &&
    Number(el.value.substr(3, 3)) < 6 &&
    !isDirectionalArrow &&
    !isDelete &&
    keyIsNumber
  ) {
    value = `${el.value.substr(0, 2)}:${
      Number(el.value.substr(3, 3)) + e.key
    }${el.value.substr(5, 8)}`;
    lastInputPosition = null;
    return variant === "time-picker-twenty-four"
      ? { start: 3, end: 5, value, lastInputPosition }
      : { start: 6, end: 8, value, lastInputPosition };
  }

  if (
    lastInputPosition === 3 &&
    Number(el.value.substr(3, 3)) > 5 &&
    !isDirectionalArrow &&
    !isDelete &&
    keyIsNumber
  ) {
    value = `${el.value}`;
    lastInputPosition = null;
    return variant === "time-picker-twenty-four"
      ? { start: 3, end: 5, value, lastInputPosition }
      : { start: 6, end: 8, value, lastInputPosition };
  }

  if (
    el.selectionStart == 6 &&
    (e.key === "ArrowUp" ||
      e.key === "ArrowDown" ||
      e.key.toLowerCase() === "a" ||
      e.key.toLowerCase() === "p")
  ) {
    if (el.value.substr(6, 7) === "--" && e.key === "ArrowUp")
      value = `${el.value.substr(0, 2)}:${el.value.substr(3, 3)}AM`;
    if (el.value.substr(6, 7) === "--" && e.key === "ArrowDown")
      value = `${el.value.substr(0, 2)}:${el.value.substr(3, 3)}PM`;
    if (el.value.substr(6, 7) === "AM")
      value = `${el.value.substr(0, 2)}:${el.value.substr(3, 3)}PM`;
    if (el.value.substr(6, 7) === "PM")
      value = `${el.value.substr(0, 2)}:${el.value.substr(3, 3)}AM`;
    if (e.key.toLowerCase() === "a")
      value = `${el.value.substr(0, 2)}:${el.value.substr(3, 3)}AM`;
    if (e.key.toLowerCase() === "p")
      value = `${el.value.substr(0, 2)}:${el.value.substr(3, 3)}PM`;
    lastInputPosition = el.selectionStart;
    return { start: 6, end: 8, value, lastInputPosition };
  }

  if (el.selectionStart && el.selectionStart <= 2 && e.key === "ArrowUp") {
    const newHour =
      el.value.substr(0, 2) === "--"
        ? variant === "time-picker"
          ? 1
          : 0
        : variant === "time-picker" && Number(el.value.substr(0, 2)) === 12
        ? 1
        : variant === "time-picker-twenty-four" &&
          Number(el.value.substr(0, 2)) === 23
        ? 0
        : Number(el.value.substr(0, 2)) + 1;
    value = `${newHour < 10 ? "0" + newHour : newHour}:${el.value.substr(
      3,
      5
    )}`;
    lastInputPosition = null;
    return { start: 0, end: 2, value, lastInputPosition };
  }
  if (el.selectionStart && el.selectionStart <= 2 && e.key === "ArrowDown") {
    const newHour =
      el.value.substr(0, 2) === "--"
        ? variant === "time-picker"
          ? 12
          : 23
        : Number(el.value.substr(0, 2)) === 0
        ? 23
        : variant === "time-picker" && Number(el.value.substr(0, 2)) === 1
        ? 12
        : Number(el.value.substr(0, 2)) - 1;
    value = `${newHour < 10 ? "0" + newHour : newHour}:${el.value.substr(
      3,
      5
    )}`;
    lastInputPosition = null;
    return { start: 0, end: 2, value, lastInputPosition };
  }
  if (
    el.selectionStart &&
    el.selectionStart >= 3 &&
    el.selectionStart < 5 &&
    e.key === "ArrowUp"
  ) {
    const newMin =
      el.value.split("")[3] === "-" && el.value.split("")[4] === "-"
        ? 0
        : Number(el.value.substr(3, 3)) == 59
        ? 0
        : Number(el.value.substr(3, 3)) + 1;
    value = `${el.value.substr(0, 2)}:${
      newMin < 10 ? "0" + newMin : newMin
    }${el.value.substr(5, 5)}`;
    lastInputPosition = null;
    return { start: 3, end: 5, value, lastInputPosition };
  }
  if (
    el.selectionStart &&
    el.selectionStart >= 3 &&
    el.selectionStart < 5 &&
    e.key === "ArrowDown"
  ) {
    const newMin =
      el.value.split("")[3] === "-" && el.value.split("")[4] === "-"
        ? 59
        : Number(el.value.substr(3, 3)) == 0
        ? 59
        : Number(el.value.substr(3, 3)) - 1;
    value = `${el.value.substr(0, 2)}:${
      newMin < 10 ? "0" + newMin : newMin
    }${el.value.substr(5, 5)}`;
    lastInputPosition = null;
    return { start: 3, end: 5, value, lastInputPosition };
  }

  if (
    el.selectionStart == 0 &&
    (e.key === "ArrowRight" || (isTab && !isShiftTab))
  ) {
    lastInputPosition = null;
    return { start: 3, end: 5, value: el.value, lastInputPosition };
  }
  if (el.selectionStart == 3 && (e.key === "ArrowLeft" || isShiftTab)) {
    lastInputPosition = null;
    return { start: 0, end: 2, value: el.value, lastInputPosition };
  }
  if (
    variant === "time-picker" &&
    el.selectionStart == 3 &&
    (e.key === "ArrowRight" || (isTab && !isShiftTab))
  ) {
    lastInputPosition = null;
    return { start: 6, end: 8, value: el.value, lastInputPosition };
  }
  if (
    variant === "time-picker" &&
    el.selectionStart == 6 &&
    (e.key === "ArrowLeft" || isShiftTab)
  ) {
    lastInputPosition = null;
    return { start: 3, end: 5, value: el.value, lastInputPosition };
  }
  if (el.selectionStart == 0 && isDelete) {
    lastInputPosition = null;
    value = `--:${el.value.substr(3, 5)}`;
    return { start: 0, end: 2, value, lastInputPosition };
  }
  if (
    el.selectionStart &&
    el.selectionStart >= 3 &&
    el.selectionStart < 5 &&
    isDelete
  ) {
    lastInputPosition = null;
    value = `${el.value.substr(0, 2)}:--${el.value.substr(5, 5)}`;
    return { start: 3, end: 5, value, lastInputPosition };
  }
};

export const selectTimeSegment = (el: HTMLInputElement, variant: string) => {
  if (!el.selectionStart) {
    el.setSelectionRange(0, 2, "forward");
    return;
  }
  if (el.selectionStart == 5 && variant === "time-picker-twenty-four") {
    el.setSelectionRange(0, 2, "forward");
    return;
  }
  if (el.selectionStart > 2 && el.selectionStart <= 6) {
    el.setSelectionRange(3, 5, "forward");
    return;
  }
  if (el.selectionStart > 6 && el.selectionStart < 8) {
    el.setSelectionRange(6, 8, "forward");
    return;
  }
  if (el.selectionStart < 3 || el.selectionStart >= 8) {
    el.setSelectionRange(0, 2, "forward");
    return;
  }
};
