import { Component, Prop, State, Watch, h } from "@stencil/core";

const kindDirectory: KindDirectory = {
  family: "families",
  icon: "icons",
  consumer: "icons-consumer",
  illustration: "icons-illustration",
  product: "products",
  resource: "resources",
};
interface KindDirectory {
  [key: string]: string;
}

const iconCache: IconCache = {};
const requests: Requests = {};
interface IconCache {
  [key: string]: string;
}
interface Requests {
  [key: string]: string;
}

@Component({
  tag: "raul-icon",
  styleUrl: "raul-icon.scss",
})
export class RaulIcon {
  @State() svgContent!: string;
  /**
   * Sets the icon to display.
   */
  @Prop({ reflect: true }) icon?: string;

  @Watch("icon")
  iconChanged() {
    this.loadSVG();
  }
  /**
   * Sets the icon category.
   */
  @Prop({ reflect: true }) kind = "icon";

  componentWillLoad() {
    this.loadSVG();
  }

  async fetchSVG() {
    try {
      const resp = await fetch(
        `//cdn.realpage.com/images/${kindDirectory[this.kind]}/${this.icon}.svg`
        // { cache: 'no-store' } commented due to loading issues in products. Original change reason: By Denis Decu - RAUL-798 Improve error handling for Icon component
      );

      if (resp && resp.ok) {
        return resp.text();
      } else {
        throw new Error(
          `[RAUL] Failed to fetch icon ${this.kind}/${this.icon}`
        );
      }
    } catch (e) {
      console.error(e);
    }
  }

  async loadSVG() {
    if (this.icon) {
      const key = `${this.kind}-${this.icon}`;

      if (!iconCache[key]) {
        if (requests && !requests[key]) {
          const svg = await this.fetchSVG();
          if (svg) {
            requests[key] = svg;
          }
        }

        const svg = requests[key];
        delete requests[key];

        if (svg) {
          //this is because svg's can be focused in IE with the tab key and this breaks keyboard/tab navigation.
          let _svg = svg.replace(
            "<svg",
            '<svg aria-hidden="true" focusable="false" '
          );
          //replace `path-1` with an unique ID because icons are exported with the same id and it breaks the icons.
          _svg = _svg.replace(/path-1/g, `path-${pathId++}-raul`);

          iconCache[key] = _svg;
        }
      }

      this.svgContent = iconCache[key];
    }
  }

  render() {
    return <i class="r-icon" innerHTML={this.svgContent} />;
  }
}

let pathId = 0;
