@import "../../../styles/mixins/focus.scss";

raul-snackbar {
  height: auto;
  max-height: 300px;
  max-height: 0;
  margin: 0;
  font-size: var(--font-size-3);
  animation: popIn 0.3s ease-in 0s 1;
  opacity: 0;
  transition:
    opacity ease 0.5s,
    max-height 0.5s ease 0.5s,
    margin 0.5s ease 0.5s;

  @media (prefers-reduced-motion) {
    animation: none;
    transition: none;
  }

  &[show] {
    height: auto;
    max-height: 300px;
    opacity: 1;
  }

  &[hidden] {
    display: none;
  }

  .r-snackbar__container {
    display: flex;
    z-index: 50;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    width: 360px;
    border-radius: 4px;
    background-color: var(--color-base);
    box-shadow: 0px 16px 27px 0px rgba(0, 0, 0, 0.24);
    color: var(--white);
    @include focus-visible(2px);

    @media (max-width: 640px) {
      width: 100%;
    }

    min-height: 56px;

    & > raul-icon {
      margin-top: 1.25rem;
      margin-left: 1rem;
      font-size: 1rem;
    }

    .r-snackbar__left-bar {
      flex: 0;
      align-self: stretch;
      min-width: 12px;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;

      &--information {
        background-color: var(--color-primary);
      }

      &--success {
        background-color: var(--color-success);
      }

      &--warning {
        background-color: var(--color-warning);
      }

      &--danger {
        background-color: var(--color-destructive);
      }
    }

    .r-snackbar__content-container {
      flex: 1;
      align-self: stretch;
      padding: 1rem;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;

      .r-snackbar__heading {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        color: var(--white);
        font-weight: var(--font-weight-bold);
        font-size: var(--font-size-4);

        .r-snackbar__heading__close-button {
          display: flex;
          align-items: center;
          justify-content: center;
          justify-self: stretch;
          width: 8px;
          min-width: 32px;
          height: 8px;
          min-height: 32px;
          margin-top: -4px;
          margin-right: -8px;
          cursor: pointer;

          @include focus-visible(0, var(--white));
        }
      }

      .r-snackbar__content {
        padding-top: 0.25rem;
        color: var(--white);
        font-weight: var(--font-weight-normal);
        font-size: var(--font-size-3);
      }

      a {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        margin-bottom: -4px;
        margin-left: -4px;
        padding: 0.25rem;
        color: var(--white);
        font-weight: var(--font-weight-normal);
        font-weight: var(--font-weight-normal);
        font-size: var(--font-size-3);
        text-decoration: underline;
        @include focus-visible(0, var(--white));
      }

      .r-snackbar__cta__message {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        margin-bottom: -4px;
        margin-left: -4px;
        padding: 0.25rem;
        color: var(--white);
        font-weight: var(--font-weight-normal);
        font-size: var(--font-size-3);
        text-decoration: underline;
        @include focus-visible(0, var(--white));
      }
    }
  }
}

@keyframes popIn {
  from {
    max-height: 0px;
    transform: translateY(100%);
  }

  to {
    max-height: 500px;
    transform: translateY (0%);
  }
}
