raul-text {
  @apply block text-md;

  .r-text {
    @apply border-0 m-0;

    &--small {
      @apply text-sm text-muted;
      line-height: 18px !important;
    }

    &--medium {
      @apply text-md text-muted;
      line-height: 20px !important;
    }

    &--large {
      @apply text-lg font-bold;
      line-height: 20px !important;
    }

    &--extra-large {
      @apply text-xl font-bold;
      line-height: 26px !important;
    }

    &--hero {
      @apply text-hero font-bold;
      line-height: 40px !important;
    }

    &--strong {
      @apply font-semibold opacity-100;
    }

    &--primary {
      @apply text-primary;
    }

    &--danger {
      @apply text-destructive;
    }

    &--success {
      @apply text-success;
    }

    &--white {
      @apply text-white;
    }

    &--align-left {
      @apply text-left;
    }

    &--align-center {
      @apply text-center;
    }

    &--align-right {
      @apply text-right;
    }

    &--align-justify {
      @apply text-justify;
    }

    &--emphasis {
      @apply italic;
    }

    &--underline {
      @apply underline;
    }

    &--line-through {
      @apply line-through;
    }

    &--ellipsis {
      @apply truncate;
    }

    &--capitalize {
      @apply capitalize;
    }

    &--uppercase {
      @apply uppercase;
    }

    &--paragraph {
      @apply mb-3;
    }
  }
}
