:host(raul-dropdown-search-input) {
  &::part(input) {
    position: relative;
    padding: 0 0.75rem;
    display: flex;
    align-items: center;
    column-gap: 0.25rem;
    border: 1px solid transparent;
    border-bottom-color: var(--dropdown-search-input-border-color);

    &:focus-within {
      border-color: var(--dropdown-focus-color);
    }
  }

  input {
    width: 100%;
    height: 2.25rem;
    border: 1px solid var(--transparent);
    outline: none;
    
    background-color: var(--dropdown-search-input-background-color);
    color: var(--dropdown-search-input-text-color);
    font-family: var(--font-family-roboto);
    font-size: var(--font-size-3);
  }

  raul-icon {
    color: var(--color-neutral-dark);
  }

  button {
    width: 1.125rem;
    height: 1.125rem;
    border: 0;
    background: var(--transparent);
    cursor: pointer;
  }
}
