import { Component, Prop, h, Host } from "@stencil/core";

@Component({
  tag: "raul-badge",
  styleUrl: "raul-badge.scss",
  shadow: false,
})
export class RaulBadge {
  /**
   * Determines the primary appearance of the badge based on its purpose.
   */
  @Prop({ reflect: true }) variant:
    | "primary"
    | "warning"
    | "error"
    | "success" = "primary";

  /**
   * The icon to display to the left of the content.
   */
  @Prop() icon?: string;

  /**
   * The text or number to display in the badge.
   */
  @Prop() content?: string;

  render() {
    return (
      <Host>
        {this.icon && (
          <div class="r-badge__icon">
            <raul-icon icon={this.icon}></raul-icon>
          </div>
        )}
        <div class="r-badge__content">{this.content}</div>
      </Host>
    );
  }
}
