import { Component, Host, Prop, h } from "@stencil/core";
/**
 * @slot - Place helper/validation text here.
 * @slot icon - Place a raul-icon to override default variant icon.
 */
@Component({
  tag: "raul-input-helper-list-item",
  styleUrl: "raul-input-helper-list-item.scss",
  shadow: true,
})
export class RaulInputHelperListItem {
  /**
   * Type of message being conveyed. NOTE: All variants but "information" have contextual icons before the text.
   */
  @Prop({ reflect: true }) variant:
    | "information"
    | "success"
    | "warning"
    | "destructive" = "information";

  render() {
    const { variant } = this;
    let icon;
    if (variant === "success") {
      icon = "check-mark-success";
    } else if (variant === "warning") {
      icon = "interface-alert-triangle";
    } else if (variant === "destructive") {
      icon = "interface-alert-diamond";
    }
    return (
      <Host
        role="listitem"
        aria-relevant="additions removals"
        aria-live="polite"
      >
        <slot name="icon">
          {icon ? <raul-icon aria-hidden="true" icon={icon} /> : null}
        </slot>

        <slot></slot>
      </Host>
    );
  }
}
