@import "../../../styles/mixins/focus.scss";

body:has(raul-aside[active]) {
  overflow: hidden;
}

raul-aside {
  font-size: var(--font-size-3);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  z-index: 10000;
  background-color: rgba(55, 71, 79, 0);
  pointer-events: none;
  transition: background-color 0.15s linear;
  visibility: visible;

 .r-aside__dialog {
    position: fixed;
    right: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    border-color: var(--gray-light);
    border-left-width: 1px;
    background-color: var(--white);
    height: 100vh;
    transform: translateX(100%);
    transition: transform 0.35s ease, background-color 0.35s ease, width 0.35s ease;
    cursor: auto;
    @include focus-visible;
  }

  &:not([visible]) {
    .r-aside__dialog {
      visibility: hidden;
    }
  }

  &[not-dismissible] {
    cursor: default;
  }

  .r-dummy-link {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    
    &:focus {
      outline: none;
    }
  }

  &[active] {
    background-color: rgba(55, 71, 79, 0.5);
    pointer-events: initial;

    & > .r-aside__dialog {
      transform: translateX(0);
    }
  }

  &[size="small"] {
    & > .r-aside__dialog {
      width: 100%;

      @media (min-width: 640px) {
        width: 22.5rem;
      }
    }
  }

  &[size="medium"] {
    & > .r-aside__dialog {
      width: 100%;

      @media (min-width: 768px) {
        width: 37.5rem;
      }
    }
  }

  &[size="large"] {
    & > .r-aside__dialog {
      width: 100%;

      @media (min-width: 1024px){
      width: 64rem;
      }
    }
  }

  &[size="xlarge"] {
    & > .r-aside__dialog {
      width: 100%;
    }
  }

  & > raul-aside[active] {
    background-color: var(--transparent);
  }

  &:has(raul-aside[active]) {
    & > .r-aside__dialog {
      background-color: rgba(235, 237, 238, 1) !important;
    }
  }

  &:has(raul-aside[active][size="small"]) {
    &[size="small"] > .r-aside__dialog {
      @media (min-width: 640px){
        transform: translateX(-40px) !important;
      }
    }

    &[size="medium"] > .r-aside__dialog {
      @media (min-width: 640px){
        transform: translateX(200px) !important;
      }
    }

    &[size="large"] > .r-aside__dialog {
      @media (min-width: 640px){
        transform: translateX(624px) !important;
      }
    }

    &[size="xlarge"] > .r-aside__dialog {
      background-color: rgba(235, 237, 238, 1);

      @media (min-width: 640px){
        width: 400px;
      }
    }
  }

  &:has(raul-aside[active][size="medium"]) {
    &[size="small"] > .r-aside__dialog {

      @media (min-width: 768px) {
        transform: translateX(-280px);
      }
    }

    &[size="medium"] > .r-aside__dialog {

      @media (min-width: 768px){
        transform: translateX(-40px);
      }
    }

    &[size="large"] > .r-aside__dialog {

      @media (min-width: 768px) {
        transform: translateX(384px);
      }
    }

    &[size="xlarge"] > .r-aside__dialog {
      background-color: rgba(235, 237, 238, 1);

      @media (min-width: 768px){
        width: 640px;
      }
    }
  }

  &:has(raul-aside[active][size="large"]) {
    &[size="small"] > .r-aside__dialog {
      background-color: rgba(235, 237, 238, 1);
      
      @media (min-width: 1024px) {
        transform: translateX(-704px);
      }
    }

    &[size="medium"] > .r-aside__dialog {
      background-color: rgba(235, 237, 238, 1);
      
      @media (min-width: 1024px) {
        transform: translateX(-460px);
      }
    }

    &[size="large"] > .r-aside__dialog {
      background-color: rgba(235, 237, 238, 1);
      
      @media (min-width: 1024px) {
        transform: translateX(-40px);
      }
    }

    &[size="xlarge"] > .r-aside__dialog {
      background-color: rgba(235, 237, 238, 1);

      @media (min-width: 1024px){
        width: 1064px;
      }
    }
  }

}
