import { Component, Prop, Event, EventEmitter, h } from "@stencil/core";
import Sortable from "sortablejs";
import { SortableEvent } from "sortablejs";

@Component({
  tag: "raul-sortable",
  styleUrl: "raul-sortable.scss",
  shadow: false,
})
export class RaulSortable {
  el?: HTMLElement;

  /**
   * If two or more lists have the same `group` property, the user will be able to drag & drop items between them
   */
  @Prop() group?: string;

  /**
   * Label of a list group
   */
  @Prop() listGroupLabel?: string;

  /**
   * The event will be emitted at the end of a drag&drop
   */
  @Event() itemDrag!: EventEmitter<{
    from: HTMLElement;
    fromIndex?: number;
    to: HTMLElement;
    toIndex?: number;
  }>;

  emitItemDragEvent(e: SortableEvent) {
    this.itemDrag.emit({
      from: e.from,
      fromIndex: e.oldIndex,
      to: e.to,
      toIndex: e.newIndex,
    });
  }

  componentDidLoad() {
    const container = this.el?.querySelector<HTMLElement>(
      ".r-sortable-list__items-container"
    );
    if (container) {
      Sortable.create(container, {
        group: this.group,
        animation: 150,
        forceFallback: true,
        ghostClass: "sortable-ghost",
        dragClass: "sortable-drag",
        filter: ".not-draggable", // Selectors that do not lead to dragging (String or Function)
        onEnd: (e) => this.emitItemDragEvent(e),
      });
    }
  }

  render() {
    return (
      <div class="r-sortable-list" ref={(el) => (this.el = el)}>
        {this.listGroupLabel && (
          <div class="r-sortable-list__group-label">
            <raul-heading variant="content">{this.listGroupLabel}</raul-heading>
          </div>
        )}
        <div class="r-sortable-list__items-container">
          <slot />
        </div>
      </div>
    );
  }
}
