raul-footer {
  display: block;
  font-size: var(--font-size-2);
  border-bottom-width: 4px;
  padding: 1rem 0rem 1rem 0rem;
  border-color: var(--color-primary);
  color: var(--color-base);

  .r-footer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  a {
    font-size: var(--font-size-2);
    line-height: var(--line-height-tight);

    &:hover {
      text-decoration: underline;
    }
  }

  &-contact,
  &-copyright,
  &-social-icons,
  &-navigation-links {
    display: block;
    width: 100%;
    text-align: center;
    padding: 0rem 0.25rem 0rem 0.25rem;
  }

  &-contact {
    order: 2;
    margin-bottom: 1rem;
  }

  &-copyright {
    order: 3;
    margin-bottom: 0.25rem;
  }

  &-social-icons {
    order: 1;
    font-size: var(--font-size-4);
    color: var(--color-neutral-dark);
    margin-bottom: 0.75rem;

    a {
      color: var(--color-base);
      background-color: var(--transparent);

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &-navigation-links {
    order: 4;
  }

  &-social-icon,
  &-navigation-link {
    display: inline-block;
  }

  &-social-icon {
    padding: 0rem 0.75rem 0rem 0.75rem;
    vertical-align: middle;

    &:first-of-type {
      padding-left: 0;
    }

    &:last-of-type {
      padding-right: 0;
    }
  }

  &-navigation-link {
    display: inline-block;
    vertical-align: middle;
    padding-right: 2px;

    &:first-of-type {
      padding-left: 0;
    }

    &:last-of-type {
      padding-right: 0;
    }

    &:not(:last-of-type)::after {
      display: inline-block;
      margin-left: 0.25rem;
      content: "|";
    }
  }

  @media (min-width: 768px) {
    padding: 1rem 1.5rem 1rem 1.5rem;
    align-items: center;

    &-contact,
    &-copyright,
    &-social-icons,
    &-navigation-links {
      width: 50%;
    }

    &-contact {
      order: 1;
      text-align: left;
      margin-bottom: 0;
    }

    &-copyright {
      margin-bottom: 0;
      text-align: left;
    }

    &-social-icons {
      order: 2;
      text-align: right;
    }

    &-navigation-links {
      text-align: right;
    }
  }

  @media (min-width: 1280px) {
    &-contact,
    &-copyright,
    &-navigation-links {
      width: 33.333333%;
    }

    &-contact {
      margin-bottom: 1px;
    }

    &-copyright {
      text-align: center;
    }

    &-social-icons {
      order: -9999;
      width: 100%;
    }
  }
}
