import {
  Component,
  Element,
  Event,
  EventEmitter,
  Host,
  Listen,
  Prop,
  State,
  h,
} from "@stencil/core";
import { HTMLStencilElement, Watch } from "@stencil/core/internal";
import { VERTICAL_NAV_KEYS } from "../../../utils/keyboard";
import { getScreenSize } from "../../../utils/dom";
import { positionMenu } from "../../../utils/collision";
import {
  clearSelection,
  deselectItems,
  getItemGroups,
  getSelectedItems,
  handleFirstArrowPress,
  initItems,
  navigateItems,
  setGroupState,
  setMultipleValues,
  setSelectionText,
} from "./utils";

/**
 * @slot label-start - Place an element before the label. For action elements use the `data-label-action` attribute on the element.
 * @slot label-end - Place an element at the end of the label. For action elements use the `data-label-action` attribute on the element.
 * @slot search-input - Place the `raul-dropdown-search-input` component here.
 * @slot toggle-content - Place custom value text or `raul-chip` components here.
 * @slot messaging - Place validation, hint text or any other informational text here. Use the `raul-input-helper-list` component in this slot.
 *
 * @part label - Label describing the dropdown.
 * @part header - The header area at the top of the menu on smaller screens.
 * @part dropdown-container - Contains the toggle and menu.
 * @part dropdown-toggle - Toggle button to open the menu.
 * @part dropdown-menu - Menu with selectable items.
 */

@Component({
  tag: "raul-dropdown",
  styleUrl: "raul-dropdown.scss",
  shadow: { delegatesFocus: true },
})
export class RaulDropdown {
  hasFooterSlot = false;
  hasToggleContentSlot = false;
  initialPosition: "start-bottom" | "start-top" | string | undefined;
  menuEl: HTMLDivElement | undefined;
  selectAllEl: HTMLRaulDropdownItemElement | undefined;
  toggleEl: HTMLButtonElement | undefined;
  timeout: NodeJS.Timeout | undefined;

  @Element() host!: HTMLStencilElement;

  @State() items: HTMLRaulDropdownItemElement[] | undefined;
  @State() selectionText: string[] = [];

  /**
   * Makes toggle button border transparent.
   */
  @Prop({ reflect: true }) borderless = false;

  /**
   * Compact floating label. Requires label property.
   */
  @Prop({ reflect: true }) compact = false;

  /**
   * Disables interactions.
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * Sets aria-describedby on internal button element.
   */
  @Prop({ reflect: true }) helperId: string | undefined;

  /**
   * Sets static label text.
   */
  @Prop() label?: string;

  /**
   * If `true` loading overlay will be appended to select dropdown menu
   */
  @Prop() loading = false;

  /**
   * Initial placement of menu.
   */
  @Prop({ mutable: true, reflect: true }) menuPlacement:
    | "start-bottom"
    | "start-top"
    | string = "start-bottom";

  /**
   * Sets selection to multiselect.
   */
  @Prop() multiselect = false;
  /**
   * Sets identifier on the component.
   */
  @Prop() name = `raulDropdown${dropdownIds++}`;

  /**
   * Opens dropdown menu when `true`.
   */
  @Prop({ mutable: true, reflect: true }) open = false;

  @Watch("open")
  handleOpenPropChange(newVal: boolean) {
    if (newVal) {
      this.timeout = setTimeout(() => {
        const searchInput = this.host.querySelector(
          "raul-dropdown-search-input",
        ) as HTMLRaulDropdownSearchInputElement;
        searchInput?.focus();
        this.setMenuPlacement();
        this.raulDropdownOpen?.emit(true);
        return;
      }, 10);
    }
    this.raulDropdownClose?.emit(true);
  }

  /**
   * Makes the dropdown visually invalid.
   */
  @Prop({ reflect: true }) invalid?: boolean = false;

  /**
   * The text to display when the select value is empty.
   */
  @Prop({ mutable: true }) placeholderText = "Placeholder";

  /**
   * Set value of dropdown.
   */
  @Prop({ mutable: true }) value?: string | string[];

  @Watch("value")
  handleValuePropChange(newVal: string | string[]) {
    if (!newVal || newVal === "" || newVal.length < 1) {
      this.clearSelections();
    } else if (this.items) {
      if (this.multiselect) {
        const items = Array.from(this.items).filter((item) =>
          newVal.includes(item.value),
        );
        this.handleMultiselectChange(items);
      } else {
        const item = Array.from(this.items).find(
          (item) => item.value === newVal,
        );
        if (item) {
          const { text, value } = item;
          this.handleSingleSelectionChange(item, { text, value });
        }
      }
    }
  }

  /**
   * Sets the style variant.
   */
  @Prop({ reflect: true }) variant: "header" | undefined;

  /**
   * Emitted when the value changes.
   */
  @Event() raulChange:
    | EventEmitter<{ value: string | string[] | undefined }>
    | undefined;

  /**
   * Emitted when menu is scrolled to the bottom.
   */
  @Event() raulDropdownScrolledToBottom: EventEmitter<boolean> | undefined;

  /**
   * Emitted when the menu opens.
   */
  @Event() raulDropdownOpen: EventEmitter<boolean> | undefined;

  /**
   * Emitted when the menu closes.
   */
  @Event() raulDropdownClose: EventEmitter<boolean> | undefined;

  componentWillLoad(): void {
    this.hasFooterSlot = !!this.host?.querySelector(`[slot="menu-footer"]`);
    this.checkToggleSlot();
  }

  componentDidLoad() {
    this.initialPosition = this.menuPlacement;
  }

  disconnectedCallback() {
    clearTimeout(this.timeout);
  }

  @Listen("click")
  handleInternalClick(ev: Event): void {
    const target = ev.target as HTMLElement;
    // Handle slotted item group clicks
    if (target.tagName.toLowerCase() === "raul-dropdown-item-group") {
      if (!this.multiselect) {
        this.menuEl?.focus();
        return;
      }
      const itemGroupEl = target as HTMLRaulDropdownItemGroupElement;
      itemGroupEl.selected = !itemGroupEl.selected;
      const isSelected: boolean = itemGroupEl.selected;
      const children = Array.from(
        itemGroupEl.querySelectorAll("raul-dropdown-item"),
      );
      if (itemGroupEl.indeterminate) itemGroupEl.indeterminate = false;
      const { newValue, selectedTextArr } = setMultipleValues(
        children,
        this.selectionText,
        this.value,
        isSelected,
      );
      this.value = newValue.length > 0 ? newValue : undefined;
      this.selectionText = selectedTextArr;
      this.raulChange?.emit({
        value: newValue.length > 0 ? newValue : undefined,
      });
      return;
    }
    // Handle single slotted item clicks
    if (
      target.tagName.toLowerCase() === "raul-dropdown-item" ||
      target.closest("raul-dropdown-item")
    ) {
      let dropdownItem;
      if (target.tagName.toLowerCase() === "raul-dropdown-item") {
        dropdownItem = target as HTMLRaulDropdownItemElement;
      } else if (target.closest("raul-dropdown-item")) {
        dropdownItem = target.closest(
          "raul-dropdown-item",
        ) as HTMLRaulDropdownItemElement;
      }
      if (dropdownItem.disabled) return;

      const { value } = dropdownItem;
      if (this.multiselect) {
        let newValue: string[] = [];
        if (this.value && typeof this.value !== "string") {
          newValue = this.value.includes(value)
            ? this.value.filter((item) => item !== value)
            : [...this.value, value];
        } else {
          newValue = [...newValue, value];
        }

        this.value = newValue.length > 0 ? newValue : undefined;
        this.raulChange?.emit({
          value: newValue.length > 0 ? newValue : undefined,
        });
        return;
      }
      this.value = value;
      this.raulChange?.emit({ value: value });
      if (this.open) this.open = false;
      return;
    }
    // Handle elements with `data-clear` attribute click
    if (target.hasAttribute("data-clear")) {
      this.value = undefined;
      this.raulChange?.emit({ value: undefined });
      return;
    }
    // Handle elements with `data-clear-selection` attribute click
    if (
      (target.closest("[data-clear-selection]") ||
        target.hasAttribute("data-clear-selection")) &&
      !!target.closest("button") &&
      this.items
    ) {
      const { dropdownItem } = clearSelection(target, this.items);
      if (dropdownItem) {
        if (this.value) {
          const index = this.value.indexOf(dropdownItem.value);
          const newValue = [...this.value];
          newValue.splice(index, 1);
          this.value = newValue.length > 0 ? newValue : undefined;
          this.raulChange?.emit({
            value: newValue.length > 0 ? newValue : undefined,
          });
        }
      }
      return;
    }
  }

  @Listen("keydown")
  handleInternalKeydown(e: KeyboardEvent): void {
    const { key } = e;
    // Close menu with esc key
    if (this.open && key === "Escape" && this.items) {
      this.open = !this.open;
      this.toggleEl?.focus();
      return;
    }
    // Navigate items with arrow keys
    if (
      VERTICAL_NAV_KEYS.includes(key) &&
      this.items &&
      !e.composedPath().find((el) => el === this.selectAllEl)
    ) {
      navigateItems(e, this.host, this.selectAllEl, this.toggleEl);
      if (!this.open) this.open = true;
      return;
    }
  }

  @Listen("focusout")
  handleInnerBlur(ev): void {
    // Close menu when focus is outside
    if (
      !ev.relatedTarget ||
      ev.relatedTarget.closest("raul-dropdown") !== this.host
    ) {
      this.open = false;
    }
  }

  @Listen("scroll", { target: "document" })
  handleExternalScroll() {
    this.setMenuPlacement();
  }

  checkToggleSlot(): void {
    this.hasToggleContentSlot = !!this.host?.querySelector(
      `[slot="toggle-content"]`,
    );
  }

  clearSelections(): void {
    const selectedItems = getSelectedItems(this.items);
    const groups = getItemGroups(this.items);
    if (selectedItems && selectedItems?.length > 0)
      selectedItems.forEach(
        (item: HTMLRaulDropdownItemElement) => (item.selected = false),
      );

    if (groups && this.multiselect) {
      groups.forEach((groupEl) => {
        setGroupState(groupEl);
      });
    }
    this.selectionText = [];
  }

  handleLabelClick(e: MouseEvent): void {
    if (
      (e.target as HTMLElement).hasAttribute("data-label-action") ||
      (e.target as HTMLElement).closest("[data-label-action]")
    )
      return;
    if (this.compact) {
      this.open = !this.open;
    }
    this.toggleEl?.focus();
  }

  handleSingleSelectionChange(
    item,
    { text, value },
  ): { value: string; itemText: string } {
    if (this.items) {
      const selectedItems = getSelectedItems(this.items);
      if (selectedItems && selectedItems.length > 0)
        deselectItems(selectedItems);
    }
    item.selected = true;
    this.selectionText = [text];
    this.toggleEl?.focus();
    return { value, itemText: text };
  }

  handleMultiselectChange(items?: HTMLRaulDropdownItemElement[]) {
    if (this.items) {
      deselectItems(this.items);
      let selectedText: string[] = [];
      items?.forEach((item) => {
        item.selected = true;
        selectedText = [...selectedText, item.text];
        const groupParent = item.closest(
          "raul-dropdown-item-group",
        ) as HTMLRaulDropdownItemGroupElement;
        if (groupParent) {
          setGroupState(groupParent);
        }
      });
      this.selectionText = selectedText.length > 0 ? selectedText : [];
    }
  }

  handleMenuScroll(e: Event): void {
    const menuEl = e
      .composedPath()
      .find((el) =>
        (el as HTMLElement)?.classList.contains("raul-dropdown__menu-body"),
      );
    const menuHeight = (menuEl as HTMLElement).offsetHeight;
    const menuScrollHeight = (menuEl as HTMLElement).scrollHeight;
    if (menuHeight + (menuEl as HTMLElement).scrollTop === menuScrollHeight)
      this.raulDropdownScrolledToBottom?.emit(true);
  }

  handleSelectAllClick(e: MouseEvent): void {
    const target = e.target as HTMLRaulDropdownItemElement;
    target.selected = !target.selected;
    const isSelected = (target as HTMLRaulDropdownItemElement).selected;
    if (this.items) {
      const { newValue, selectedTextArr } = setMultipleValues(
        this.items.filter(
          (item) => item.tagName.toLowerCase() !== "raul-dropdown-item-group",
        ),
        this.selectionText,
        this.value,
        isSelected,
      );
      this.value = newValue.length > 0 ? newValue : undefined;
      this.raulChange?.emit({
        value: newValue.length > 0 ? newValue : undefined,
      });
      this.selectionText = selectedTextArr;

      const groups = getItemGroups(this.items);
      if (groups && this.multiselect) {
        groups.forEach((groupEl) => {
          setGroupState(groupEl);
        });
      }
    }
  }

  handleSelectAllKeydown(e: KeyboardEvent): void {
    const { key } = e;
    const items = this.items?.filter((items) => !items.disabled);
    if (items) {
      switch (key) {
        case "ArrowDown":
          handleFirstArrowPress(this.toggleEl, items, 0);
          break;
        case "ArrowUp":
          handleFirstArrowPress(this.toggleEl, items, items.length - 1);
          break;
      }
    }
  }

  reInit() {
    if (this.host) {
      const obj = initItems(
        this.host,
        this.multiselect,
        this.value,
        this.selectionText,
      );
      if (obj) {
        this.items = obj.items;
        this.value = obj.value;
        this.selectionText = obj.selectionText;
      }
      if (this.multiselect && this.value && this.selectAllEl && obj?.items) {
        const selectedItems = getSelectedItems(obj.items);
        this.selectAllEl.selected = selectedItems?.length === obj.items.length;
        this.selectAllEl.indeterminate =
          (selectedItems &&
            selectedItems.length !== 0 &&
            selectedItems?.length < obj.items.length) ||
          false;
      }
    }
  }

  setMenuPlacement() {
    this.menuEl &&
    this.toggleEl &&
    this.initialPosition &&
    this.open &&
    getScreenSize() !== "sm"
      ? (this.menuPlacement = positionMenu(
          this.toggleEl,
          this.menuEl,
          this.menuPlacement,
          this.initialPosition,
        ))
      : undefined;
  }

  render() {
    const { label, items, open, variant, value } = this;
    const toggleContent = (): void => {
      let content: string | null | undefined = "";
      content =
        this.selectionText?.length > 0
          ? setSelectionText(this.selectionText, this.multiselect)
          : variant === "header"
            ? label
            : this.compact
              ? null
              : this.placeholderText;
      return (
        <span
          class={{
            "raul-dropdown__toggle-content-container": true,
            "raul-dropdown__toggle-content-container--block":
              !this.hasToggleContentSlot,
          }}
          data-testid="toggle-content"
        >
          <slot
            onSlotchange={() => this.checkToggleSlot()}
            name="toggle-content"
          >
            {content}
          </slot>
        </span>
      );
    };

    const menuHeader = () => (
      <div part="header">
        <button
          type="button"
          class="raul-dropdown__heading-button"
          onClick={() => (this.open = false)}
        >
          Cancel
        </button>
        <span class="raul-dropdown__heading-label">{label}</span>
        <button
          type="button"
          class="raul-dropdown__heading-button"
          aria-hidden={this.multiselect ? "false" : "true"}
          onClick={() => (this.open = false)}
        >
          Done
        </button>
      </div>
    );

    const selectAllHeading = () => {
      const selectedItems = getSelectedItems(items);
      return this.multiselect ? (
        <raul-dropdown-item
          ref={(el) => (this.selectAllEl = el as HTMLRaulDropdownItemElement)}
          text="Select All"
          value="selectAll"
          indeterminate={
            items &&
            selectedItems &&
            selectedItems.length > 0 &&
            selectedItems.length !== items.length
          }
          selected={
            items &&
            selectedItems &&
            selectedItems.length > 0 &&
            selectedItems.length === items.length
          }
          heading
          multi
          onClick={(e: MouseEvent) => this.handleSelectAllClick(e)}
          onKeyDown={(e: KeyboardEvent) => this.handleSelectAllKeydown(e)}
          data-testid="select-all"
        />
      ) : null;
    };

    const dropdownMenu = () => (
      <div
        ref={(el) => (this.menuEl = el as HTMLDivElement)}
        id={`${this.name}Menu`}
        part="dropdown-menu"
        hidden={!open}
        tabindex="-1"
        data-testid="dropdown-menu"
      >
        {menuHeader()}
        <slot name="search-input" />
        <div
          role="listbox"
          class="raul-dropdown__menu-body"
          onScroll={(e) => this.handleMenuScroll(e)}
          data-testid="menu-body"
        >
          {this.loading ? <raul-container-loader /> : null}
          {selectAllHeading()}
          <slot onSlotchange={() => this.reInit()} />
        </div>
        {this.hasFooterSlot ? (
          <div class="raul-dropdown__menu-footer">
            <slot name="menu-footer" />
          </div>
        ) : null}
      </div>
    );

    return (
      <Host>
        {variant !== "header" ? (
          <div
            class={{
              "raul-dropdown__label--float":
                this.compact && !(value === undefined || value === ""),
            }}
            part="label"
            onClick={(e: MouseEvent) => this.handleLabelClick(e)}
          >
            <slot name="label-start" />
            <label id={`${this.name}Label`}>{label}</label>
            <slot name="label-end" />
          </div>
        ) : null}
        <div part="dropdown-container">
          <button
            ref={(el) => (this.toggleEl = el)}
            part="dropdown-toggle"
            aria-expanded={open.toString()}
            aria-haspopup="listbox"
            aria-controls={`${this.name}Menu`}
            aria-labelledby={`${this.name}Label`}
            disabled={this.disabled}
            onClick={() => (this.open = !open)}
            onKeyDown={({ key }: KeyboardEvent) => {
              if (["ArrowDown", "ArrowUp"].includes(key)) this.open = true;
            }}
            data-testid="toggle-btn"
            {...(this.helperId ? { "aria-describedby": this.helperId } : {})}
          >
            {toggleContent()}
            <span class="dropdown-icon" aria-hidden="true">
              <slot name="dropdown-icon">
                <i class="raul-dropdown__chevron" aria-hidden="true" />
              </slot>
            </span>
          </button>
          {dropdownMenu()}
        </div>
        <slot name="messaging" />
      </Host>
    );
  }
}

let dropdownIds = 0;
