import flatpickr from "flatpickr";

export const formatHumanDate = (
  value: string | Date | Date[],
  isShort = false,
  format = "m/d/Y"
): string => {
  const { formatDate } = flatpickr;
  let humanFormatTransform = "M j, Y";

  if (format === "d/m/Y") {
    humanFormatTransform = "j M Y";
  } else if (format === "Y/m/d") {
    humanFormatTransform = "Y M j";
  }

  if (Array.isArray(value) && value.length > 1) {
    const startDate = value[0];
    const endDate = value[1];

    let startDateFormat = format;
    if (!isShort && startDate.getFullYear() === endDate?.getFullYear()) {
      if (format === "Y/m/d") {
        startDateFormat = "Y M j";
      } else if (format === "d/m/Y") {
        startDateFormat = "j M";
      } else {
        startDateFormat = "M j";
      }
    } else if (!isShort) {
      startDateFormat = humanFormatTransform;
    }

    const startDateStr = formatDate(startDate, startDateFormat);

    let endDateStr = "";
    if (isShort) {
      endDateStr = ` - ${formatDate(endDate, format)}`;
    } else if (endDate) {
      endDateStr = ` - ${formatDate(endDate, humanFormatTransform)}`;
    }

    return `${startDateStr}${endDateStr}`;
  } else {
    const val = typeof value === "string" ? new Date(value) : value;
    return formatDate(
      Array.isArray(val) ? value[0] : val,
      isShort ? format : humanFormatTransform
    );
  }
};
