@import "../../../styles/mixins/label-styles.scss";

:host {
  --file-uploader-border-color: var(--color-neutral);
  --file-uploader-border-color-hover: var(--color-primary);
}

:host(raul-file-uploader) {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
  font-family: var(--font-family-roboto);

  input[type="file"] {
    width: 0;
    height: 0;
    overflow: hidden;
  }

  &::part(label) {
    @include label-styles;
  }

  &::part(hint) {
    margin-bottom: 0.25rem;
    display: block;
    font-size: var(--font-size-2);
    line-height: var(--line-height-tight);
  }

  &::part(drop-area) {
    box-sizing: border-box;
    padding: 0 1rem;
    width: 100%;
    min-height: 7rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 0.5rem;
    border: 2px dashed var(--file-uploader-border-color);
    border-radius: var(--border-radius-small);
    cursor: pointer;
    text-align: center;
    touch-action: none;

    &:hover {
      border-color: var(--file-uploader-border-color-hover);
    }
  }

  .raul-file-uploader--drop-area {
    &--active {
      background-color: var(--color-primary-highlight);
      border-color: var(--file-uploader-border-color-hover) !important;
    }

    span {
      font-size: var(--font-size-3);
      line-height: var(--line-height-tight);
      color: var(--color-base);
    }
  }

  &::part(icon) {
    font-size: var(--font-size-6);
    color: var(--color-primary);
  }
}
