import {
  Component,
  Element,
  Event,
  EventEmitter,
  h,
  Host,
  Listen,
  Prop,
  State,
} from "@stencil/core";
import { randomUID } from "../../../utils/string";
import {
  ColorGroupsInterface,
  ColorInterface,
} from "../../../utils/interfaces";
import { ESCAPE_KEY } from "../../../utils/constants";
import { getScreenSize } from "../../../utils/dom";
import { positionMenu } from "../../../utils/collision";
import { HTMLStencilElement, Watch } from "@stencil/core/internal";
import { menuNavigation, VERTICAL_NAV_KEYS } from "../../../utils/keyboard";
import { defaultColors } from "./utils";

@Component({
  tag: "raul-color-picker",
  styleUrl: "raul-color-picker.scss",
})
export class RaulColorPicker {
  colorPickerEl!: HTMLDivElement;
  toggleEl!: HTMLButtonElement;
  colorMenuEl!: HTMLDivElement;
  initialPlacement!: string;

  @Element() el!: HTMLStencilElement;

  @State() isNoScroll = false;
  @State() opened = false;
  @State() expanded = false;

  @Event() raulColorChange!: EventEmitter<ColorInterface>;

  /**
   * @deprecated - The Pickr plugin will no longer be supported in future versions. Use regular variant.
   */
  @Prop({ reflect: true }) variant: "regular" | "plugin" = "regular";

  /**
   * If `true`, the user cannot interact with the select.
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * The name of the control, which is submitted with the form data.
   */
  @Prop({ reflect: true }) name = `color-picker-${randomUID()}`;

  /**
   * The text label.
   */
  @Prop({ reflect: true }) label?: string;

  /**
   * Makes the color picker visually invalid and shows the feedback message.
   */
  @Prop() error?: string;

  /**
   * Sets the max height of the colors list, e.g. `500px`, `calc(100vh - 50px)`.
   */
  @Prop() menuMaxHeight?: string;

  /**
   * The text to display when the color picker value is empty.
   */
  @Prop({ reflect: true }) placeholder = "Select a color";

  /**
   * The value of the color picker.
   */
  @Prop({ reflect: true, mutable: true }) value?: string;

  @Watch("value")
  handleValuePropChange(newValue: string) {
    this.raulColorChange.emit({ value: newValue });
  }

  /**
   * Array of colors to be displayed in menu.
   */
  @Prop() colors: ColorGroupsInterface[] = defaultColors;

  /**
   * Colors menu's placement.
   */
  @Prop({ mutable: true, reflect: true }) menuPlacement = "start-bottom";

  /**
   * If `true` loading overlay will be appended to colors menu
   */
  @Prop() loading = false;

  /**
   * Enables custom color picker in footer of menu
   */
  @Prop() enableCustom = false;

  componentWillLoad() {
    if (this.variant === "plugin")
      console.warn(
        '[RAUL: Deprecated] "plugin" variant is deprecated, use the "enable-custom" attribute or "enableCustom" prop to enable the custom color options instead.'
      );

    this.initialPlacement = this.menuPlacement;
  }

  @Listen("focusout")
  handleInternalBlur({ relatedTarget }: FocusEvent) {
    if (
      (relatedTarget as HTMLElement)?.closest("raul-color-picker") !== this.el
    ) {
      this.close();
    }
  }

  @Listen("keydown")
  handleKeyDown(e: KeyboardEvent) {
    if (VERTICAL_NAV_KEYS.includes(e.key)) {
      if (!this.opened) {
        this.open();
      }
      menuNavigation(
        this.colorMenuEl,
        ".r-color__menu__item",
        this.toggleEl,
        e
      );
    }

    if (e.key === "Tab") {
      this.close();
      this.toggleEl.focus();
    }

    if (ESCAPE_KEY.includes(e.key)) {
      this.close();
      this.toggleEl.focus();
    }
  }

  @Listen("scroll", { target: "document" })
  handleExternalScroll() {
    if (this.colorMenuEl && this.opened && getScreenSize() !== "sm") {
      this.menuPlacement = positionMenu(
        this.toggleEl,
        this.colorMenuEl,
        this.menuPlacement,
        this.initialPlacement
      );
    }
  }

  private open() {
    if (getScreenSize() !== "sm") {
      const position = positionMenu(
        this.toggleEl,
        this.colorMenuEl,
        this.menuPlacement,
        this.initialPlacement
      );
      if (position) {
        this.menuPlacement = position;
      }
    }
    this.expanded = true;
    this.opened = true;
  }

  private close() {
    this.expanded = true;
    this.opened = false;
  }

  private toggle() {
    if (this.opened) {
      this.close();
    } else {
      this.toggleEl.focus();
      this.open();
    }
  }

  private handleColorChange(e: Event, color: ColorInterface) {
    e.preventDefault();

    this.value = color.value;
    this.close();
    this.toggleEl.focus();
  }

  private handleCustomColorChange(e: Event) {
    const inputValue = (e.target as HTMLInputElement).value;
    this.value = inputValue;
  }

  render() {
    return (
      <Host opened={this.opened}>
        <input type="hidden" name={this.name} value={this.value} />

        {this.label ? <label class="r-color__label">{this.label}</label> : null}

        <div
          class={{
            "r-color": true,
            "r-color--opened": this.opened,
            "r-color--expanded": this.expanded,
            "r-color--invalid": !!this.error,
          }}
          ref={(el) => (this.colorPickerEl = el as HTMLDivElement)}
        >
          <button
            type="button"
            class="r-color__toggle"
            disabled={this.disabled}
            onClick={() => this.variant === "regular" && this.toggle()}
            ref={(el) => (this.toggleEl = el as HTMLButtonElement)}
          >
            <div class="r-color__toggle__content">
              <div class="r-color__toggle__content__text">
                {this.value ? this.value.toUpperCase() : this.placeholder}
              </div>

              <div
                class="r-color__toggle__content__indicator"
                style={{ background: this.value }}
              ></div>
            </div>
          </button>

          {this.variant === "regular" && (
            <div
              class="r-color__menu"
              ref={(el) => (this.colorMenuEl = el as HTMLDivElement)}
            >
              <div class="r-color__menu__content">
                {this.loading && <raul-container-loader />}

                <div class="r-color__menu__header">
                  <div class="r-color__mobile-label">
                    <div class="r-color__mobile-label__content">
                      <div class="r-color__mobile-label__content__button">
                        <span onClick={() => this.close()}>Cancel</span>
                      </div>

                      {this.label && (
                        <div class="r-color__mobile-label__content__text">
                          {this.label}
                        </div>
                      )}
                    </div>
                  </div>
                </div>

                <div
                  class="r-color__menu__body"
                  style={{
                    maxHeight: this.menuMaxHeight,
                  }}
                >
                  {this.colors?.map((colorGroup) => {
                    return (
                      <div class="r-color__menu__group">
                        {colorGroup?.colors.map((color: ColorInterface) => {
                          return (
                            <div class="r-color__menu__item">
                              <button
                                tabindex="-1"
                                aria-label={color.value}
                                onClick={(e) =>
                                  this.handleColorChange(e, color)
                                }
                              >
                                <span
                                  id={`${color.id || randomUID()}`}
                                  style={{ background: color.value }}
                                >
                                  {color.value === this.value && (
                                    <raul-icon icon="check-mark-success"></raul-icon>
                                  )}
                                </span>
                              </button>
                            </div>
                          );
                        })}
                      </div>
                    );
                  })}
                </div>

                {this.enableCustom ? (
                  <div class="r-color__menu__footer">
                    <label htmlFor={`custom-${this.name}`}>Custom Color:</label>
                    <input
                      type="color"
                      value={this.value}
                      id={`custom-${this.name}`}
                      onChange={(e: Event) => this.handleCustomColorChange(e)}
                    />
                  </div>
                ) : null}
              </div>
            </div>
          )}
        </div>

        {this.error && (
          <div class="r-color__error">
            <raul-icon
              icon="interface-alert-diamond"
              class="r-color__error__icon"
            />{" "}
            {this.error}
          </div>
        )}
      </Host>
    );
  }
}
