import { Component, Prop, h, Host } from "@stencil/core";

@Component({
  tag: "raul-container-image",
  styleUrl: "raul-container-image.scss",
})
export class RaulContainerImage {
  /**
   * Image src.
   */
  @Prop() src?: string;
  @Prop() height = 250;

  render() {
    const styles = {
      background: `url('${this.src}') no-repeat center center / cover`,
      height: `${this.height}px`,
    };

    return (
      <Host>
        <div class="r-container__image" style={styles}></div>
      </Host>
    );
  }
}
