import { Component, Prop, h, Host } from "@stencil/core";

/**
 * @slot - Place HTML table structure in this slot.
 */
@Component({
  tag: "raul-simple-table",
  styleUrl: "raul-simple-table.scss",
})
export class RaulSimpleTable {
  /**
   * Sets size of rows.
   */
  @Prop({ reflect: true }) small?: boolean;
  /**
   * Adds hover styles to rows.
   */
  @Prop({ reflect: true }) hoverable?: boolean;
  /**
   * Adds background color styles to even rows.
   */
  @Prop({ reflect: true }) striped?: boolean;

  render() {
    return (
      <Host>
        <slot />
      </Host>
    );
  }
}
