import { Component, Host, h, Prop } from "@stencil/core";
/**
 * @slot - Place the item text, `raul-list-item-title` (optional), `raul-list-item-subtitle` (optional) and `raul-list-action` (optional) components in this slot.
 */
@Component({
  tag: "raul-list-item",
  styleUrl: "raul-list-item.scss",
})
export class RaulListItem {
  @Prop() movable = false;
  @Prop() moveByHandle = false;

  render() {
    const renderBody = () => {
      if (this.movable || this.moveByHandle) {
        return (
          <div class="r-list-item-wrapper">
            <raul-handle></raul-handle>
            <div>
              <slot />
            </div>
          </div>
        );
      } else {
        return <slot />;
      }
    };
    return (
      <Host
        role="listitem"
        class={{
          "raul-list-item--movable": this.movable,
          "raul-list-item--movable-by-handle": this.moveByHandle,
        }}
      >
        {renderBody()}
      </Host>
    );
  }
}
