import {
  Component,
  Prop,
  Element,
  EventEmitter,
  Event,
  h,
  Host,
} from "@stencil/core";
import { DELETE_KEYS } from "../../../utils/constants";
/**
 * @slot - Place text in this slot.
 */
@Component({
  tag: "raul-chip",
  styleUrl: "raul-chip.scss",
})
export class RaulChip {
  @Element() el!: HTMLElement;

  /**
   * Makes the chip removable.
   */
  @Prop() removable = false;

  /** @internal */
  @Prop({ mutable: true, reflect: true }) isRemoved = false;

  /**
   * Emitted when the removable chip is clicked or delete/backspace keys are pressed.
   */
  @Event() raulChipRemove!: EventEmitter<undefined>;

  private handleChipRemove(): void {
    this.isRemoved = true;
    this.raulChipRemove.emit();
  }

  render() {
    return (
      <Host>
        <div class="r-chip__text">
          <slot />
        </div>

        {this.removable && (
          <button
            data-testid="chip-remove-button"
            class="r-chip__remove-button"
            aria-label="Remove"
            onClick={this.removable ? () => this.handleChipRemove() : undefined}
            onKeyDown={(e) => {
              if (this.removable && DELETE_KEYS.includes(e.key)) {
                this.handleChipRemove();
              }
            }}
          >
            <raul-icon icon="close" class="r-chip__remove-icon" />
          </button>
        )}
      </Host>
    );
  }
}
