/* eslint-disable @typescript-eslint/ban-ts-comment */
//@ts-nocheck
// TODO: this file will not get type checking until we refactor.

import {
  Component,
  Element,
  State,
  Prop,
  Watch,
  Event,
  EventEmitter,
  Listen,
  Method,
  h,
  Host,
} from "@stencil/core";
import { randomUID } from "../../../utils/string";
import { DOWN_KEY, ESCAPE_KEY } from "../../../utils/constants";
import { getScreenSize } from "../../../utils/dom";
import cloneDeep from "lodash/cloneDeep";
import debounce from "lodash/debounce";
import { SelectOptionInterface } from "../../../utils/interfaces";
import { handleKeyPress, selectTimeSegment } from "./utils";
import { positionMenu } from "../../../utils/collision";
import { menuNavigation, VERTICAL_NAV_KEYS } from "../../../utils/keyboard";

/**
 * @deprecated - Use raul-dropdown instead.
 *
 * @slot label-end-accessory - Use this slot to place an accessory element at teh end of the label.
 * @slot select-menu-footer - Use this slot to add footer content to the bottom of the select menu.
 */
@Component({
  tag: "raul-select",
  styleUrl: "raul-select.scss",
})
export class RaulSelect {
  private selectEl: HTMLDivElement;
  private searchInputEl: HTMLInputElement;
  private toggleButtonEl: HTMLButtonElement;
  private selectMenuEl: HTMLDivElement;
  private selectInputEl: HTMLInputElement;
  labelEndAccessoryEl: HTMLDivElement;
  labelEl: HTMLLabelElement;
  inputStart: number;
  inputEnd: number;
  lastInputPosition: number;
  private selectAllId = `option-${randomUID()}`;
  private menuBody: HTMLDivElement;
  private menuBodyContent: HTMLDivElement;
  startPosition = 1;
  endPosition = 10;
  filteredOptions: any[] = [];
  searchInputId = `search-${randomUID()}`;
  initialPosition: string;

  @Element() el!: HTMLRaulSelectElement;

  @State() focusedOptionId: string;
  @State() opened = false;
  @State() expanded = false;
  @State() searchQuery = "";
  @State() isNoScroll = false;
  @State() processedOptions: any[] = [];
  @State() isFocused = false;
  @State() children;

  /**
   * If `true`, the user cannot interact with the select.
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * The "overflow" prop can be used in custom select to prevent custom select menu overflow be cut off.
   */
  @Prop({ reflect: true }) overflow = false;

  /**
   * If `true`, allows multiple selections.
   */
  @Prop({ reflect: true }) multiple = false;

  @Watch("multiple")
  multipleChanged() {
    if (!this.value) this.setInitialValue();
  }

  /**
   * The name of the control, which is submitted with the form data.
   */
  @Prop({ reflect: true }) name = `select-${randomUID()}`;

  /**
   * The text label.
   */
  @Prop({ reflect: true }) label: string = null;

  /**
   * Optional hint text.
   */
  @Prop() hint: string;

  /**
   * Makes the select visually invalid and shows the feedback message.
   */
  @Prop() error: string;

  /**
   * Sets the max height of the select options list, e.g. `500px`, `calc(100vh - 50px)`.
   */
  @Prop() optionsMaxHeight: string;

  /**
   * The text to display when the select value is empty.
   */
  @Prop({ reflect: true }) placeholder = "Select an option";

  /**
   * If `true`, adds a search field at the top of the select menu.
   */
  @Prop({ reflect: true }) searchable = false;

  @Watch("searchable")
  searchableChanged() {
    this.searchQuery = "";
  }

  /**
   * If `true`, removes select toggle border.
   */
  @Prop({ reflect: true }) borderless = false;

  /**
   * If `true`, allows search in options description. this prop is not supported for raul-select with groups. - available from 0.36.3 version.
   */
  @Prop({ reflect: true }) searchDescription = false;

  /**
   * If `true`, removes select toggle border and styles to match the page header style.
   */
  @Prop({ reflect: true }) header = false;

  /**
   * The value of the select. If single select, value should be a `string`. If multiselect, value should be `string[]`.
   */
  @Prop({ mutable: true }) value: string | string[] = this.multiple ? [] : "";

  /**
   * Options and groups of the select.
   */
  @Prop() options: SelectOptionInterface[] = [];

  @Watch("options")
  optionsChanged() {
    if (this.custom) {
      return;
    }
    this.selectAllId = `option-${randomUID()}`;

    this.setOptionsId();
    this.processedOptions = [];
    this.startPosition = 1;
    if (getScreenSize() === "sm" && this.endPosition === 10) {
      this.endPosition = 20;
    }
    this.searchQuery = "";
    this.processOptions(this.startPosition, this.endPosition);
  }

  /**
   * Options menu's placement.
   */
  @Prop({ mutable: true, reflect: true }) menuPlacement:
    | "start-bottom"
    | "start-bottom"
    | string = "start-bottom";

  /**
   * The look of the select field.
   */
  @Prop() variant: "select" | "time-picker" | "time-picker-twenty-four" =
    "select";

  /**
   * If `true` loading overlay will be appended to select dropdown menu
   */
  @Prop() loading = false;

  /**
   * No options message.
   */
  @Prop() noOptionsMessage = "No results found";

  /**
   * If `true` `noOptionsMessage` will be appended to select dropdown menu
   */
  @Prop() showNoOptionsMessage = false;

  /**
   * Allows to insert custom raul-options.
   * For custom raul-select the following props are not available - multiple, searchable, options, variant.
   */
  @Prop() custom = false;

  /**
   * Allows for compact floating label support. Requires label property.
   */
  @Prop() compact = false;

  /**
   * Allows for override of displayed value in the select box.
   */
  @Prop() customValue: string = null;

  /**
   * Adds inputs with hidden type for each value of select.
   */
  @Prop() hiddenInput = false;

  /**
   * Adds text input to toggle element.
   */
  @Prop() textInput = false;

  /**
   * Emitted when the select menu opens.
   */
  @Event() raulSelectOpen: EventEmitter;

  /**
   * Emitted when the select menu closes.
   */
  @Event() raulSelectClose: EventEmitter;

  /**
   * Emitted when the select value changes.
   */
  @Event() raulChange: EventEmitter;

  /**
   * Emitted Time Values are changed with arrow keys.
   */
  @Event() positionCaret: EventEmitter<any>;

  /**
   * Emitted when search input is changed.
   */
  @Event() raulSelectSearchInput: EventEmitter<any>;

  componentWillLoad() {
    console.warn(
      `[RAUL: Deprecated] "raul-select" is deprecated. Use "raul-dropdown" instead.`
    );

    const hasOptionsInSlot =
      this.el.querySelectorAll("raul-option-group, raul-option").length > 0;

    if (!this.custom && !hasOptionsInSlot) {
      this.setOptionsId();
      this.setInitialValue();
      if (getScreenSize() === "sm") {
        this.endPosition = 20;
      }
      this.processedOptions = [];
      this.processOptions(this.startPosition, this.endPosition);
    }

    if (!this.custom && hasOptionsInSlot) {
      this.children = this.el.querySelectorAll(
        "raul-option-group, raul-option"
      );
      if (this.multiple)
        this.children.forEach((child) => (child.multiple = true));
    }

    this.handleDebouncedResize = debounce(this.handleDebouncedResize, 200);
  }

  componentDidLoad() {
    this.initialPosition = this.menuPlacement;

    // TODO(Jeff): look at improving search functionality
    // console.log(this.options.map((eachOpt) => {
    //   return {...eachOpt, options: eachOpt.options.filter(({text}) => text.toLocaleLowerCase().includes('re'))}
    // }).filter(opt => opt.options.length > 0))
  }

  @Listen("keydown")
  async handleKeyDown(e) {
    const { key, target } = e;

    if (
      VERTICAL_NAV_KEYS.includes(key) &&
      !this.variant.includes("time-picker")
    ) {
      e.preventDefault();
      if (!this.opened) await this.open();
      if (this.searchable && this.opened && target === this.toggleButtonEl) {
        this.searchInputEl.focus();
        return;
      }

      menuNavigation(
        this.menuBodyContent,
        this.multiple ? "raul-option" : 'raul-option:not([variant="group"])',
        this.toggleButtonEl,
        e
      );
    }

    if (key === "Tab") {
      if (
        this.searchable &&
        this.opened &&
        ![this.toggleButtonEl, this.searchInputEl].includes(target)
      ) {
        e.preventDefault();
        this.searchInputEl.focus();
      } else {
        this.close();
      }
    }

    if (this.opened && ESCAPE_KEY.includes(key)) {
      this.close();
      this.toggleButtonEl.focus();
    }
  }

  @Listen("click", { target: "window" })
  @Listen("focusin", { target: "window" })
  handleClickAndFocusOutside(e: { composedPath: () => Element[] }) {
    // As events traverse shadow dom boundaries the Event target gets updated to the host
    // element. To find the original target we can use the first element from the composedPath.
    const originalTarget = e.composedPath()[0];
    if (
      this.opened &&
      originalTarget &&
      originalTarget !== this.labelEl &&
      !this.selectEl.contains(originalTarget)
    ) {
      this.close();
    }
  }

  @Listen("resize", { target: "window" })
  handleDebouncedResize() {
    if (this.opened) {
      if (getScreenSize() === "sm" && this.endPosition === 10) {
        this.endPosition = this.endPosition + 10;
        this.processedOptions = [];
        this.processOptions(this.startPosition, this.endPosition);
      }
    }
  }

  @Listen("positionCaret")
  handlePositionCaret() {
    this.selectInputEl.selectionStart = 3;
  }

  @Listen("raulOptionClick")
  handleRaulOptionClick(e: any) {
    const { optionId, selected, value, variant }: any = e.detail;

    if (!this.custom && !this.children) {
      if (optionId === this.selectAllId) {
        this.handleSelectAllChange({
          id: this.selectAllId,
          selected: !selected,
        });
        return;
      }

      if (variant === "group" && this.multiple) {
        const group = this.options.find((opt) => optionId === opt.id);
        this.handleOptionsGroupChange({ id: optionId, selected, group });
        return;
      }

      this.handleOptionChange({
        id: optionId,
        selected: selected,
        value,
      });
    }
  }

  @Listen("scroll", { target: "document" })
  handleExternalScroll() {
    this.selectMenuEl && this.opened && getScreenSize() !== "sm"
      ? (this.menuPlacement = positionMenu(
          this.toggleButtonEl,
          this.selectMenuEl,
          this.menuPlacement,
          this.initialPosition
        ))
      : undefined;
  }

  /**
   * Open the select menu.
   */
  @Method()
  async open() {
    getScreenSize() !== "sm"
      ? (this.menuPlacement =
          positionMenu(
            this.toggleButtonEl,
            this.selectMenuEl,
            this.menuPlacement,
            this.initialPosition
          ) || this.menuPlacement)
      : undefined;

    this.opened = true;
    this.expanded = true;
    this.toggleButtonEl?.focus();
    if (!this.custom && this.searchable) {
      this.clearSearch();
    }

    this.raulSelectOpen.emit({ value: this.value });
  }

  /**
   * Close the select menu.
   */
  @Method()
  async close() {
    this.expanded = false;
    this.opened = false;
    this.raulSelectClose.emit({ value: this.value });
  }

  /**
   * Toggle the select menu.
   */
  @Method()
  async toggle() {
    this.opened ? this.close() : this.open();
  }

  private hasSlot(slotName): boolean {
    return !!this.el.querySelector(`[slot=${slotName}]`);
  }

  private matchSearchQuery(text: string) {
    return text.toLowerCase().indexOf(this.searchQuery) !== -1;
  }

  private visibleOptionsAndGroups(): any[] {
    return this.processedOptions.filter((option: any) => {
      if (option.options && option.options.length) {
        const someOptionsVisible = option.options.some((option: any) =>
          this.matchSearchQuery(option.text)
        );

        return this.matchSearchQuery(option.text) || someOptionsVisible;
      } else {
        return this.searchDescription
          ? this.matchSearchQuery(`${option.text} ${option.description}`)
          : this.matchSearchQuery(option.text);
      }
    });
  }

  setOptionsId() {
    this.options.forEach((option) => {
      option.id = option.id ? option.id : `option-${randomUID()}`;
      if (option.options?.length) {
        option.options.forEach((subOption: any) => {
          subOption.id = subOption.id ? subOption.id : `option-${randomUID()}`;
        });
      }
    });
  }

  private optionsWithoutGroups() {
    return this.options.reduce((acc: any, cv: any) => {
      if (cv.options && cv.options.length) {
        cv.options.forEach((option) => acc.push(option));
      } else {
        acc.push(cv);
      }

      return acc;
    }, []);
  }

  private focusableOptionsAndGroups(visibleOptionsAndGroups?): any[] {
    const optionsAndGroups = visibleOptionsAndGroups
      ? visibleOptionsAndGroups
      : this.visibleOptionsAndGroups();
    const flattenedVisibleOptionsAndGroups =
      this.flattenVisibleOptionsAndGroups(optionsAndGroups);

    return this.multiple
      ? flattenedVisibleOptionsAndGroups
      : flattenedVisibleOptionsAndGroups.filter(
          (option) => !(option.options && option.options.length)
        );
  }

  private selectedOptions() {
    const isSelected = (optionValue) => {
      return this.multiple
        ? this.value.includes(optionValue)
        : this.value === optionValue;
    };

    return this.optionsWithoutGroups().filter((option: any) =>
      isSelected(option.value)
    );
  }

  private selectedOptionsText(): string {
    const selectedOptions = this.selectedOptions();
    const selectedOptionsLength = selectedOptions.length;

    return this.customValue
      ? this.customValue
      : selectedOptionsLength > 1
      ? `${selectedOptionsLength} options selected`
      : selectedOptions.map((option) => option.text).join();
  }

  private setInitialValue() {
    const optionsUseSelectedProp =
      this.options.filter((option) =>
        Object.prototype.hasOwnProperty.call(option, "selected")
      ).length > 0;

    if (optionsUseSelectedProp) {
      this.value = this.multiple ? [] : "";
    }
  }

  private processOptions(start = 1, end: number = this.options.length) {
    const optionsHaveGroups = this.options.some((item) =>
      Object.prototype.hasOwnProperty.call(item, "options")
    );

    if (optionsHaveGroups) {
      this.processGroupOptions(start, end);
    } else {
      const options = this.processedOptions;

      if (!this.searchDescription) {
        this.filteredOptions = this.options.filter((option: any) =>
          option.text?.toLowerCase().includes(this.searchQuery)
        );
      } else {
        this.filteredOptions = this.options.filter(
          (option: any) =>
            option.text?.toLowerCase().includes(this.searchQuery) ||
            (option.description &&
              option.description?.toLowerCase().includes(this.searchQuery))
        );
      }
      this.filteredOptions.forEach((option: any, i) => {
        if (option.selected)
          this.value = this.multiple
            ? this.value.concat(option.value)
            : option.value;

        if (start - 1 <= i && i < end) {
          options.push(option);
        }
      });

      this.processedOptions = [];
      this.processedOptions = options;
    }
  }

  processGroupOptions(start: number, end: number) {
    let options: any[] = [];

    this.filteredOptions = cloneDeep(this.options).filter((option) => {
      if (option.options?.length) {
        option.options = option.options.filter((item) =>
          this.matchSearchQuery(item.text)
        );
        const someOptionsVisible = option.options.some((option: any) =>
          this.matchSearchQuery(option.text)
        );

        return this.matchSearchQuery(option.text) || someOptionsVisible;
      }
    });

    cloneDeep(this.filteredOptions).map((option: any) => {
      option = Object.assign({}, option);
      if (option.options?.length) {
        option.options.forEach((subOption: any) => {
          if (subOption.selected)
            this.value = this.multiple
              ? this.value.concat(subOption.value)
              : subOption.value;
        });
      }
      if (option.selected)
        this.value = this.multiple
          ? this.value.concat(option.value)
          : option.value;
      options.push(option);
    });

    const flattenedVisibleOptionsAndGroups = (options as any).reduce(
      (acc, cv) => {
        acc.push(cv);

        if (cv.options && cv.options.length) {
          cv.options.forEach((option) => {
            option.parentId = cv.id;
            acc.push(option);
          });
        }
        return acc;
      },
      []
    );

    if (start >= flattenedVisibleOptionsAndGroups.length) {
      // the end of menu
      return;
    }

    const endId =
      end >= flattenedVisibleOptionsAndGroups.length
        ? flattenedVisibleOptionsAndGroups[
            flattenedVisibleOptionsAndGroups.length - 1
          ].id
        : flattenedVisibleOptionsAndGroups[end].id;

    let parentIndex = 0;
    let optionIndex = 0;

    options.some((parent: any, i) => {
      if (parent.id === endId) {
        parentIndex = i;
        return true;
      }
      if (parent.options && parent.options.length) {
        return parent.options.some((child: any, index) => {
          if (child.id === endId) {
            parentIndex = i;
            optionIndex = index;
            return true;
          }
        });
      }
    });

    options = options.slice(0, parentIndex + 1);
    options[parentIndex].options.length = optionIndex + 1;

    this.processedOptions = [];
    this.processedOptions = options;
  }

  private handleOptionChange({ id, selected, value }) {
    this.focusedOptionId = id;

    if (this.multiple) {
      this.value = selected
        ? [...this.value, value]
        : (this.value as string[]).filter(
            (optionValue) => optionValue !== value
          );
    } else {
      this.value = value;
      this.close();
      if (this.variant === "select") this.toggleButtonEl.focus();
    }

    this.raulChange.emit({ value: this.value });
  }

  private handleOptionsGroupChange({ id, selected, group }) {
    const isSelected = selected;
    const allOptionsOfGroup = (this.filteredOptions as any[])
      .find((item) => item.id === group.id)
      .options.filter((option) => !option.disabled)
      .map((option) => option.value);

    this.focusedOptionId = id;

    this.value = isSelected
      ? [...this.value, ...allOptionsOfGroup].filter(
          (item, i, self) => self.indexOf(item) === i
        )
      : (this.value as string[]).filter(
          (option) => !allOptionsOfGroup.includes(option)
        );

    this.raulChange.emit({ value: this.value });
  }

  private handleSearch(e) {
    this.searchQuery = e.target.value.toLowerCase();

    this.raulSelectSearchInput.emit({
      searchQuery: this.searchQuery,
    });

    if (this.menuBody) this.menuBody.scrollTop = 0;

    if (this.options) {
      this.startPosition = 1;
      this.endPosition = getScreenSize() === "sm" ? 20 : 10;
      this.processedOptions = [];
      this.processOptions(this.startPosition, this.endPosition);
    }
  }

  flattenVisibleOptionsAndGroups(options) {
    return options.reduce((acc, cv) => {
      if (!cv.disabled) {
        acc.push(cv);

        if (cv.options && cv.options.length) {
          cv.options.forEach((option) => {
            if (
              !option.disabled &&
              (this.matchSearchQuery(option.text) ||
                this.matchSearchQuery(cv.text))
            ) {
              acc.push(option);
            }
          });
        }
      }

      return acc;
    }, []);
  }

  private getValuesSelectAllGroupChange() {
    let optVals = [];
    const flattenedVisibleOptionsAndGroups =
      this.flattenVisibleOptionsAndGroups(this.filteredOptions);

    optVals = this.multiple
      ? flattenedVisibleOptionsAndGroups
      : flattenedVisibleOptionsAndGroups.filter(
          (option) => !(option.options && option.options.length)
        );
    optVals = optVals
      .filter((option) => !option.options) // exclude group options
      .map((option) => option.value);

    return optVals;
  }

  private handleSelectAllChange({ id, selected }) {
    this.focusedOptionId = id;

    const optionsHaveGroups = this.options.some((item) =>
      Object.prototype.hasOwnProperty.call(item, "options")
    );
    let optVals = [];
    if (optionsHaveGroups) {
      optVals = this.getValuesSelectAllGroupChange();
    } else {
      optVals = this.filteredOptions.map((item) => (item as any).value);
    }

    this.value = selected
      ? (this.value as string[]).filter(
          (option) => optVals.indexOf(option) === -1
        )
      : optVals;
    this.raulChange.emit({ value: this.value });
  }

  private clearSearch() {
    this.searchQuery = "";
    this.startPosition = 1;
    this.endPosition = getScreenSize() === "sm" ? 20 : 10;
    this.processedOptions = [];
    if (this.menuBody) {
      this.menuBody.scrollTop = 0;
    }
    this.processOptions(this.startPosition, this.endPosition);
    setTimeout(() => this.searchInputEl.focus(), 0);
  }

  private getVisibleOptionValues() {
    return this.focusableOptionsAndGroups()
      .filter((option) => !option.options) // exclude group options
      .map((option) => option.value);
  }

  private scroll(e: any) {
    const menuHeight = this.menuBody.offsetHeight;
    const menuContentHeight = this.menuBodyContent.offsetHeight;
    const scrollEnd = menuContentHeight - menuHeight;
    if (
      Math.ceil(e.target.scrollTop) >= Math.ceil(scrollEnd - 1) &&
      Math.ceil(e.target.scrollTop) <= Math.ceil(scrollEnd + 1)
    ) {
      this.startPosition =
        getScreenSize() === "sm"
          ? this.endPosition === 20
            ? this.startPosition + 20
            : this.startPosition + 10
          : this.startPosition + 10;
      this.endPosition = this.endPosition + 10;
      this.processOptions(this.startPosition, this.endPosition);
    }
  }

  setRange(start: number, end: number) {
    this.inputStart = start;
    this.inputEnd = end;
  }

  handleTimePickerKeyPress(e, el) {
    const vals = handleKeyPress(e, el, this.variant);
    if (vals) {
      const { start, end, value, lastInputPosition }: any = vals;
      this.setRange(start, end);
      this.value = value;
      this.lastInputPosition = lastInputPosition;
    }
  }

  handleTextInputChange() {
    this.variant.includes("time-picker")
      ? this.raulChange.emit({
          value:
            this.selectInputEl.value === "--:--" ||
            this.selectInputEl.value === "--:-- --"
              ? null
              : this.selectInputEl.value,
          onChange: true,
        })
      : null;
  }

  handleTextInputClick() {
    this.variant.includes("time-picker")
      ? selectTimeSegment(this.selectInputEl, this.variant)
      : null;
  }

  handleTextInputKeyUp(e) {
    if (this.variant.includes("time-picker")) {
      e.preventDefault();
      this.selectInputEl.setSelectionRange(
        this.inputStart,
        this.inputEnd,
        "forward"
      );
      if (
        !(
          e.key === "ArrowLeft" ||
          e.key === "ArrowRight" ||
          e.key === "Tab" ||
          (e.shiftKey && e.key === "Tab")
        )
      )
        this.raulChange.emit({
          value:
            this.selectInputEl.value === "--:--" ||
            this.selectInputEl.value === "--:-- --"
              ? null
              : this.selectInputEl.value,
        });
    }
  }

  handleTextInputKeyDown(e) {
    if (this.variant.includes("time-picker")) {
      e.preventDefault();
      this.handleTimePickerKeyPress(e, this.selectInputEl);
    }
  }

  setSelectAllGroupProps() {
    let flattenedVisibleOptionsAndGroups = this.flattenVisibleOptionsAndGroups(
      this.filteredOptions
    );

    flattenedVisibleOptionsAndGroups = this.multiple
      ? flattenedVisibleOptionsAndGroups
      : flattenedVisibleOptionsAndGroups.filter(
          (option) => !(option.options && option.options.length)
        );
    flattenedVisibleOptionsAndGroups = flattenedVisibleOptionsAndGroups
      .filter((option) => !option.options) // exclude group options
      .map((option) => option.value);
    const selected =
      flattenedVisibleOptionsAndGroups.length &&
      flattenedVisibleOptionsAndGroups.every(
        (item) => this.value.indexOf(item) !== -1
      );
    const indeterminate =
      !selected &&
      flattenedVisibleOptionsAndGroups.some(
        (item) => this.value.indexOf(item) !== -1
      );
    const disabled = !flattenedVisibleOptionsAndGroups.length;
    return { selected, indeterminate, disabled };
  }

  render() {
    const visibleOptionsAndGroups = this.visibleOptionsAndGroups();
    const valueLength = this.value.length;
    const renderHiddenInput = () => {
      if (this.multiple) {
        return this.value && valueLength ? (
          (this.value as string[]).map((value) => (
            <input type="hidden" name={this.name} value={value} />
          ))
        ) : (
          <input type="hidden" name={this.name} value={this.value} />
        );
      } else {
        return <input type="hidden" name={this.name} value={this.value} />;
      }
    };

    const renderSelectToggleContent = () => {
      const selectedOptionsText = this.selectedOptionsText();
      return (
        <div
          class={{
            "r-select__toggle__content": true,
            "r-select__toggle__content--borderless": this.borderless,
            "r-select__toggle__content--header": this.header,
            "r-select__toggle__content--focus": this.isFocused,
          }}
        >
          <div
            class={{
              "r-select__toggle__content__outline__leading": true,
              "r-select__toggle__content__outline__leading--float":
                !(selectedOptionsText === null || selectedOptionsText === "") ||
                (this.compact &&
                  (this.value.length > 0 ||
                    !(this.customValue === null || this.customValue === ""))),
            }}
          ></div>
          <div
            class={{
              "r-select__toggle__content__outline__notch": true,
              "r-select__toggle__content__outline__notch--float":
                this.compact &&
                (!(
                  selectedOptionsText === null || selectedOptionsText === ""
                ) ||
                  (this.textInput && this.value.length > 0) ||
                  !(this.customValue === null || this.customValue === "")),
            }}
          >
            {this.compact && (
              <div
                class={{
                  "r-select__toggle__content__label": true,
                  "r-select__toggle__content__label--float":
                    !(
                      selectedOptionsText === null || selectedOptionsText === ""
                    ) ||
                    (this.textInput && this.value.length > 0) ||
                    !(this.customValue === null || this.customValue === ""),
                }}
              >
                <label>{this.label}</label>
                <div
                  class={{
                    "r-select__toggle__content__label__end-accessory":
                      this.hasSlot("label-end-accessory"),
                  }}
                  ref={(el) =>
                    (this.labelEndAccessoryEl = el as HTMLDivElement)
                  }
                >
                  <slot name="label-end-accessory" />
                </div>
              </div>
            )}
          </div>
          {this.variant === "select" ? (
            <div class="r-select__toggle__content__outline__trailing">
              <span class="r-select__toggle__content__arrow"></span>
            </div>
          ) : (
            <button
              type="button"
              class="r-select__toggle__content__outline__trailing"
              disabled={this.disabled}
              onClick={() =>
                this.textInput && !this.disabled ? this.toggle() : void 1
              }
              ref={(el) => (this.toggleButtonEl = el as HTMLButtonElement)}
            >
              <raul-icon icon="clock" class="r-select__toggle__content__icon" />
            </button>
          )}

          {this.textInput ? (
            <input
              ref={(el) => (this.selectInputEl = el as HTMLInputElement)}
              class={{
                "r-select__toggle__content__input": true,
                "r-select__toggle__content__input--masked":
                  this.variant.includes("time-picker"),
              }}
              type="text"
              value={this.value}
              placeholder={this.placeholder}
              disabled={this.disabled}
              onChange={() => this.handleTextInputChange()}
              onFocus={() => (this.isFocused = true)}
              onBlur={() => (this.isFocused = false)}
              onClick={() => this.handleTextInputClick()}
              onKeyUp={(e) => this.handleTextInputKeyUp(e)}
              onKeyDown={(e) => this.handleTextInputKeyDown(e)}
            />
          ) : (
            <div class="r-select__toggle__content__text">
              {this.customValue
                ? this.customValue
                : Array.isArray(this.value) && valueLength > 1
                ? `${valueLength} options selected`
                : valueLength
                ? selectedOptionsText || this.value
                : this.compact
                ? null
                : selectedOptionsText || this.placeholder}
            </div>
          )}
        </div>
      );
    };

    const renderSelectMenuFooter = () => (
      <div class="r-select__menu__footer">
        <slot name="select-menu-footer" />
      </div>
    );

    const renderSelectMenuMobileLabelContent = () => (
      <div class="r-select__mobile-label__content">
        <div class="r-select__mobile-label__content__button">
          <button
            type="button"
            tabindex="-1"
            aria-hidden="true"
            onClick={() => this.close()}
          >
            {this.custom ? "Close" : "Cancel"}
          </button>
        </div>

        {this.label && (
          <div class="r-select__mobile-label__content__text">{this.label}</div>
        )}

        {this.multiple && (
          <div
            class={{
              "r-select__mobile-label__content__button": true,
              "r-select__mobile-label__content__button--right": true,
              "r-select__mobile-label__content__button--disabled": false,
            }}
          >
            <button
              type="button"
              tabindex="-1"
              aria-hidden="true"
              onClick={() => this.close()}
            >
              Done
            </button>
          </div>
        )}
      </div>
    );

    const renderSelectMenuSearch = () => (
      <div class="r-select__search">
        {this.opened && (
          <raul-icon icon="search" class="r-select__search__icon" />
        )}

        <input
          type="text"
          tabindex="-1"
          class="r-select__search__input"
          placeholder="Search"
          value={this.searchQuery}
          aria-label="Search Options"
          onInput={(e) => this.handleSearch(e)}
          id={this.searchInputId}
          ref={(el) => (this.searchInputEl = el as HTMLInputElement)}
        />

        {this.searchQuery && (
          <button type="button" onClick={() => this.clearSearch()}>
            <raul-icon icon="remove-2" class="r-select__search__icon-reset" />
          </button>
        )}
      </div>
    );

    const renderNoSearchResults = () => (
      <div class="r-select__no-search-results">No results found.</div>
    );

    const renderSelectMenuHeader = () => {
      return (
        <div class="r-select__menu__header">
          <div class="r-select__mobile-label">
            {!this.hasSlot("select-mobile-label") &&
              renderSelectMenuMobileLabelContent()}

            <slot name="select-mobile-label" />
          </div>

          {this.searchable && renderSelectMenuSearch()}

          {!this.custom &&
            this.processedOptions.length !== undefined &&
            visibleOptionsAndGroups?.length === 0 &&
            renderNoSearchResults()}

          {!this.custom &&
            this.showNoOptionsMessage &&
            renderNoOptionsMessage()}
        </div>
      );
    };

    const renderOption = (
      option,
      groupVisible = false,
      groupDisabled = false
    ) => {
      const {
        id,
        text,
        description,
        disabled,
        icon,
        iconKind,
        variant,
        value,
      } = option;
      const visible =
        (this.searchDescription
          ? this.matchSearchQuery(`${text} ${description}`)
          : this.matchSearchQuery(text)) || groupVisible;
      if (visible) {
        return (
          <raul-option
            multiple={this.multiple}
            selected={
              this.multiple ? this.value.includes(value) : this.value === value
            }
            focused={id === this.focusedOptionId}
            disabled={disabled || groupDisabled}
            value={value}
            text={text}
            description={description}
            icon={icon}
            iconKind={iconKind}
            optionId={id}
            variant={variant}
          />
        );
      }
    };

    const renderOptionsGroup = (group) => {
      const { id, text, disabled, value, options } = group;
      const visibleOptionValues = this.getVisibleOptionValues();

      const optionsValues = options
        .map((option) => option.value)
        .filter((option) => visibleOptionValues.includes(option));

      const groupVisible = this.matchSearchQuery(text);
      const selected =
        this.multiple && this.value.length && optionsValues.length
          ? optionsValues.every((option) => this.value.includes(option))
          : false;
      return (
        <raul-option-group
          multiple={this.multiple}
          selected={selected}
          focused={id === this.focusedOptionId}
          disabled={disabled}
          value={value}
          text={text}
          indeterminate={
            this.multiple
              ? !selected &&
                optionsValues.some((option) => this.value.includes(option))
              : false
          }
          optionId={id}
        >
          {options.map((option) =>
            renderOption(
              { ...option, variant: "sub-option" },
              groupVisible,
              disabled
            )
          )}
        </raul-option-group>
      );
    };

    const renderSelectAllOption = () => {
      const selectedVisibleOptions = this.filteredOptions.filter(
        (item: any) => this.value.indexOf(item.value) !== -1
      );
      const selectedVisibleOptionsLength = selectedVisibleOptions.length;
      const filteredOptionsLength = this.filteredOptions.length;
      let indeterminate =
        !!selectedVisibleOptionsLength &&
        filteredOptionsLength !== selectedVisibleOptionsLength;
      let selected =
        selectedVisibleOptionsLength &&
        filteredOptionsLength === selectedVisibleOptionsLength;
      const optionsHaveGroups = this.options.some((item) =>
        Object.prototype.hasOwnProperty.call(item, "options")
      );
      let disabled = false;

      if (optionsHaveGroups) {
        selected = this.setSelectAllGroupProps().selected;
        indeterminate = this.setSelectAllGroupProps().indeterminate;
        disabled = this.setSelectAllGroupProps().disabled;
      }

      return (
        <raul-option
          text="Select All"
          variant="group"
          multiple={this.multiple}
          selected={selected}
          disabled={disabled}
          indeterminate={indeterminate}
          focused={this.selectAllId === this.focusedOptionId}
          optionId={this.selectAllId}
        />
      );
    };

    const renderNoOptionsMessage = () => {
      return <div class="r-select__no-options">{this.noOptionsMessage}</div>;
    };

    const flattenedVisibleOptionsAndGroups =
      this.flattenVisibleOptionsAndGroups(this.options);

    return (
      <Host opened={this.opened}>
        {this.hiddenInput && renderHiddenInput()}

        {this.label && !this.compact ? (
          <div class="r-select__label">
            <label
              ref={(el) => (this.labelEl = el as HTMLLabelElement)}
              onClick={() => this.toggleButtonEl?.focus()}
            >
              {this.label}
            </label>
            <slot name="label-end-accessory" />
          </div>
        ) : null}

        <div
          class={{
            "r-select": true,
            "r-select--opened": this.opened,
            "r-select--expanded": this.expanded,
            "r-select--header": this.header,
            "r-select--invalid": !!this.error,
          }}
          ref={(el) => (this.selectEl = el as HTMLDivElement)}
        >
          {!this.textInput && (
            <button
              type="button"
              class={{
                "r-select__toggle": true,
                "r-select__toggle--header": this.header,
              }}
              disabled={this.disabled}
              aria-haspopup="true"
              aria-expanded={this.opened ? "true" : "false"}
              aria-activedescendant={this.focusedOptionId}
              onClick={({ target }) => {
                if (
                  !target.closest(
                    ".r-select__toggle__content__label__end-accessory"
                  )
                )
                  this.toggle();
              }}
              ref={(el) => (this.toggleButtonEl = el as HTMLButtonElement)}
            >
              {!this.hasSlot("select-toggle") && renderSelectToggleContent()}

              <slot name="select-toggle" />
            </button>
          )}

          {this.textInput &&
            !this.hasSlot("select-toggle") &&
            renderSelectToggleContent()}

          <div
            class="r-select__menu"
            ref={(el) => (this.selectMenuEl = el as HTMLDivElement)}
            aria-hidden={this.opened ? "false" : "true"}
          >
            <div class="r-select__menu__content">
              {this.loading && <raul-container-loader />}

              {renderSelectMenuHeader()}

              <div
                class={{
                  "r-select__menu__body": true,
                  "r-overflow-visible": this.overflow,
                }}
                role="listbox"
                tabindex="-1"
                aria-multiselectable={this.multiple ? "true" : null}
                ref={(el) => (this.menuBody = el)}
                style={{ maxHeight: this.optionsMaxHeight }}
                onScroll={(e) =>
                  !this.custom && flattenedVisibleOptionsAndGroups.length > 10
                    ? this.scroll(e)
                    : null
                }
                onKeyDown={(e) =>
                  !this.custom &&
                  flattenedVisibleOptionsAndGroups.length > 10 &&
                  DOWN_KEY.includes(e.key)
                    ? this.scroll(e)
                    : null
                }
              >
                <div ref={(el) => (this.menuBodyContent = el)}>
                  {this.multiple && renderSelectAllOption()}

                  {visibleOptionsAndGroups.map((option) => {
                    return option.options && option.options.length
                      ? renderOptionsGroup(option)
                      : renderOption(option);
                  })}

                  <slot />
                </div>
              </div>

              {this.hasSlot("select-menu-footer") && renderSelectMenuFooter()}
            </div>
          </div>
        </div>

        {this.hint && <small class="r-select__hint">{this.hint}</small>}

        {this.error && (
          <div class="r-select__error">
            <raul-icon
              icon="interface-alert-diamond"
              class="r-select__error__icon"
            />{" "}
            {this.error}
          </div>
        )}
      </Host>
    );
  }
}
