import { Component, Element, Prop, h, State, Host } from "@stencil/core";

/**
 * @slot label-end - Place an element at the end of the label. For action elements use the `data-label-action attribute on the element.
 */

@Component({
  tag: "raul-switch",
  styleUrl: "raul-switch.scss",
})
export class RaulSwitch {
  @Element() el!: HTMLRaulSwitchElement;

  @State() switchId = `raul-switch-${switchId++}`;

  /**
   * The text label.
   */
  @Prop() labelText?: string;

  /**
   * If `true`, the switch size will be small.
   */
  @Prop({ reflect: true }) small = false;

  componentWillLoad() {
    const inputEl = this.el.querySelector("input");
    if (!inputEl) return;
    if (inputEl.id) {
      this.switchId = inputEl.id;
    } else {
      inputEl.id = this.switchId;
    }
  }

  render() {
    return (
      <Host>
        <label class="r-switch__label" htmlFor={this.switchId}>
          <slot />
          <span class="r-switch__toggle">
            <raul-icon class="r-switch__icon" icon="lock-1"></raul-icon>
          </span>

          {this.labelText}
        </label>
        <slot name="label-end" />
      </Host>
    );
  }
}

let switchId = 0;
