@import "../../../styles/mixins/focus.scss";

raul-tooltip {
  display: inline-block;
  vertical-align: middle;
  font-size: var(--font-size-3);
  position: relative;

  .r-tooltip {
    background-color: var(--charcoal);
    color: var(--white);
    font-size: var(--font-size-2);
    padding: 0.75rem;
    border-radius: 0.125rem;
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
    position: absolute;
    width: max-content;
    max-width: 16rem;
    pointer-events: none;
  }

  .r-tooltip__arrow {
    height: 0;
    width: 0;
    border-color: var(--transparent);
    position: absolute;
    opacity: 0;
    transition: opacity .15s linear;
    z-index: 50;
    border-width: 0.5rem;
  }

  .r-tooltip__wrapper {
    position: relative;
  }

  .button,
  [tabindex="0"] {
    @include focus-visible(2px);
  }

  &[placement$="top"] .r-tooltip__arrow {
    border-bottom-width: 0;
    margin-bottom: -0.5rem;
    bottom: 0;
    bottom: 100%;
    left: 50%;
    margin-bottom: 0.125rem;
    transform: translate(-50%, 0);
    border-top-color: var(--charcoal);
  }

  &[placement="start-top"] .r-tooltip {
    bottom: 100%;
    left: calc(100% + 0.875rem);
    transform: translate(-100%, 0);
    margin-bottom: 0.5rem;
  }

  &[placement="top"] .r-tooltip {
    bottom: 100%;
    left: 50%;
    transform: translate(-50%, 0);
    margin-bottom: 0.5rem;
  }

  &[placement="end-top"] .r-tooltip {
    bottom: 100%;
    right: calc(100% + 0.875rem);
    transform: translate(100%, 0);
    margin-bottom: 0.5rem;
  }

  &[placement$="bottom"] .r-tooltip__arrow {
    border-top-width: 0;
    margin-top: -0.5rem;
    top: 0;
    top: 100%;
    left: 50%;
    margin-top: 0.125rem;
    transform: translate(-50%, 0);
    border-bottom-color: var(--charcoal);
  }

  &[placement="start-bottom"] .r-tooltip {
    top: 100%;
    left: calc(100% + 0.875rem);
    margin-top: 0.5rem;
    transform: translate(-100%, 0);
  }

  &[placement="bottom"] .r-tooltip {
    top: 100%;
    left: 50%;
    transform: translate(-50%, 0);
    margin-top: 0.5rem;
  }

  &[placement="end-bottom"] .r-tooltip {
    top: 100%;
    right: calc(100% + 0.875rem);
    margin-top: 0.5rem;
    transform: translate(100%, 0);
  }

  &[placement="left"] {
    .r-tooltip {
      top: 50%;
      right: 100%;
      margin-right: 0.5rem;
      transform: translate(0, -50%);
    }

    .r-tooltip__arrow {
      border-right-width: 0;
      margin-right: -0.5rem;
      right: 0;
      top: 50%;
      right: 100%;
      margin-right: 0.125rem;
      transform: translate(0, -50%);
      border-left-color: var(--charcoal);
    }
  }

  &[placement="right"] {
    .r-tooltip {
      top: 50%;
      left: 100%;
      margin-left: 0.5rem;
      transform: translate(0, -50%);
    }

    .r-tooltip__arrow {
      border-left-width: 0;
      margin-left: -0.5rem;
      left: 0;
      top: 50%;
      left: 100%;
      margin-left: 0.125rem;
      transform: translate(0, -50%);
      border-right-color: var(--charcoal);
    }
  }

  &[open] {
    .r-tooltip__arrow {
      opacity: 1;
    }
  }
}