import { Component, Prop, h } from "@stencil/core";
import { randomUID } from "../../../../utils/string";

@Component({
  tag: "raul-text",
  styleUrl: "raul-text.scss",
})
export class RaulText {
  @Prop() size?: "small" | "medium" | "large" | "extra-large" | "hero";
  @Prop() align: "left" | "center" | "right" | "justify" = "left";
  @Prop() color?: "primary" | "success" | "danger" | "white";
  @Prop() inline = false;
  @Prop() strong = false;
  @Prop() emphasis = false;
  @Prop() underline = false;
  @Prop() lineThrough = false;
  @Prop() ellipsis = false;
  @Prop() capitalize = false;
  @Prop() uppercase = false;
  @Prop() paragraph = false;

  render() {
    const Tag = this.inline
      ? "span"
      : this.size === "small"
      ? "p"
      : this.size === "medium"
      ? "p"
      : this.size === "large"
      ? "h3"
      : this.size === "extra-large"
      ? "h2"
      : this.size === "hero"
      ? "h1"
      : "span";

    return (
      <Tag
        class={{
          ["r-text"]: true,
          [`r-text--${this.size}`]: !!this.size,
          [`r-text--${this.color}`]: !!this.color,
          [`r-text--align-${this.align}`]: true,
          ["r-text--inline"]: this.inline,
          ["r-text--strong"]: this.strong,
          ["r-text--underline"]: this.underline,
          ["r-text--line-through"]: this.lineThrough,
          ["r-text--emphasis"]: this.emphasis,
          ["r-text--ellipsis"]: this.ellipsis,
          ["r-text--capitalize"]: this.capitalize,
          ["r-text--uppercase"]: this.uppercase,
          ["r-text--paragraph"]: this.paragraph,
        }}
        key={randomUID()}
      >
        <slot />
      </Tag>
    );
  }
}
