raul-eyebrow {
  display: flex;
  align-items: center;
  height: 1.25rem;

  .r-eyebrow-start,
  
  .r-eyebrow-end {
    background-color: var(--color-secondary);
    width: 1rem;
    height: 0.5rem;
    border-radius: 0.25rem
  }

  .r-eyebrow-end {
    background-color: var(--color-neutral-dark);
    margin-left: 0.25rem;
    width: 2rem;

  }
}
