@use "sass:math";
@import "../../../styles/mixins/focus.scss";

$paging-bar-border: 1px solid var(--color-neutral-light);
$input-border: 1px solid var(--color-neutral);

raul-paging-bar {
  font-size: var(--font-size-2);
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: space-between;
  height: 45px;
  border-bottom: $paging-bar-border;

  .r-paging-bar__col {
    width: 100%;
    display: none;

    &--entries {
      display: none;

      @media (min-width: 768px) {
        display: block;
        min-width: 40%;
      }
    }

    &--count {
      text-align: center;
      display: none;

      @media (min-width: 768px) {
        display: block;
        flex-grow: 1;
      }
    }

    &--nav {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;

      @media (min-width: 768px) {
        justify-content: flex-end;
        padding-right: 0;
        text-align: right;
        min-width: 40%;
      }

      button {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 2rem;
        height: 2rem;

        &:disabled {
          cursor: not-allowed;
          opacity: 0.3;
        }

        *:focus {
          outline: none;
        }

        *:focus-visible {
          outline: 2px solid var(--color-primary);
        }
      }
    }
  }

  .r-paging-bar__entry-select {
    display: inline-block;
    background-color: var(--transparent);
    position: relative;
    vertical-align: baseline;
    margin: 0rem 0.25rem 0rem 0.25rem;

    .r-select__arrow {
      position: absolute;
      color: var(--color-primary);
      right: 2px;
      margin-top: 0.5rem;
    }

    select {
      appearance: none;
      background-color: var(--transparent);
      position: relative;
      border-radius: 0.25rem;
      padding: 0.25rem 0.75rem 0.25rem 0.5rem;
      z-index: 10;
      border-width: 1px;
      border-color: var(--transparent);
      color: var(--color-primary);
      width: 42px;

      &::-ms-expand {
        display: none;
      }

      @mixin focus-visible() {
        &:focus {
          outline: none;
        }
      
        &:focus-visible {
          outline: 2px solid var(--color-primary);
        }
      }
    }
  }

  .r-paging-bar__nav {
    display: inline-block;
  }

  .r-paging-bar__input {
    display: inline-block;
    margin: 0 4px;
    width: 40px;

    input[type="number"] {
      text-align: center;
      width: 100%;
      border: $input-border;
      border-radius: 2px;
      color: var(--color-primary);
      padding: 2px 6px;
      transition: all 0.35s;

      &:focus {
        outline: none;
        border-color: var(--color-primary);
      }

      &::-webkit-inner-spin-button,
      &::-webkit-outer-spin-button {
        appearance: none;
        margin: 0;
      }
    }
  }
}