import {
  Component,
  Element,
  Event,
  EventEmitter,
  Prop,
  State,
  h,
} from "@stencil/core";

type PropertyName = "step" | "min" | "max" | "value" | "valueHigh";
@Component({
  styleUrl: "raul-slider.scss",
  tag: "raul-slider",
})
export class RaulSlider {
  @Element() el!: HTMLElement;

  inputTextElements!: HTMLInputElement[];

  inputRangeElements!: HTMLInputElement[];

  /**
   * Range's type.
   */
  @Prop() type: "single" | "range" = "single";

  /**
   * Range's minimum value.
   */
  @Prop() min = 0;

  /**
   * Slider's maximum value.
   */
  @Prop() max = 100;

  /**
   * Slider's interval of valid values. (integer or decimal)
   */
  @Prop() step = 1;

  /**
   * Determines the color appearance of the slider based on its purpose.
   */
  @Prop() variant: "primary" | "warning" | "error" | "success" = "primary";

  /**
   * If true, the slider will be disabled
   */
  @Prop() disabled = false;

  /**
   * Slider's label text.
   */
  @Prop() label?: string;

  /**
   * Value for a slider with type: 'single' and low value for a slider type: 'range'.
   */
  @Prop() value = this.min;

  /**
   * High value for a slider with type: 'range'.
   */
  @Prop() valueHigh = this.max;

  /**
   * Symbol in the beginning of the values.
   */
  @Prop() prependSymbol?: string;

  /**
   * Symbol in the end of the values.
   */
  @Prop() appendSymbol?: string;

  /**
   * Symbol in the end of input.
   */
  @Prop() compact = false;

  // states
  @State() sliderId = `raul-slider-${sliderIds++}`;

  @State() focused = false;

  @State() isUnsafe = false;

  @State() refreshElements = false;

  @State() reinitializeElements = false;

  @Event() raulChange!: EventEmitter<number | number[]>;

  // private functions
  getValues = () =>
    this.type == "range" ? [this.value, this.valueHigh] : [this.value];

  getPercent = (value: number): number =>
    ((value - this.min) / (this.max - this.min)) * 100;

  roundToIncrement = (value: number) =>
    (Math.round(((value - this.min) * 1000) / (this.step * 1000)) *
      (this.step * 1000)) /
      1000 +
    this.min;

  closestIncrementBelow = (value: number) =>
    (Math.floor(((value - this.min) * 1000) / (this.step * 1000)) *
      (this.step * 1000)) /
      1000 +
    this.min;

  invalidateValue = (value: number) =>
    Number.isNaN(value) || value < this.min || value > this.max;

  closestValidNumber = (value: number): number => {
    if (value < this.min) return this.min;
    if (value > this.max) return this.closestIncrementBelow(this.max);

    return this.roundToIncrement(value);
  };

  updateValues = (rawValues: string[], trigger: number, sort?: boolean) => {
    let numbers = rawValues.map(parseFloat);

    if (numbers.some(this.invalidateValue)) return;
    numbers = numbers.map(this.roundToIncrement);
    if (sort) numbers.sort((a, b) => a - b);
    if (numbers[0] > numbers[1] || numbers[0] == numbers[1]) {
      if (trigger === 0) {
        numbers[0] = numbers[1] - this.step;
      } else {
        numbers[1] = numbers[0] + this.step;
      }
    }
    this.value = numbers[0];
    if (numbers.length > 1) this.valueHigh = numbers[1];
  };

  reevaluateValues = () => {
    const rawValues =
      this.type == "range" ? [this.value, this.valueHigh] : [this.value];
    const numbers = rawValues.map(this.closestValidNumber);

    numbers.sort((a, b) => a - b);

    if (numbers[0] === numbers[1]) {
      if (numbers[0] == this.closestIncrementBelow(this.max)) {
        numbers[0] = numbers[0] - this.step;
      } else {
        numbers[1] = numbers[1] + this.step;
      }
    }
    this.value = numbers[0];
    if (numbers.length > 1) this.valueHigh = numbers[1];
    this.isUnsafe = false;
    this.updateElements(this.inputTextElements);
    if (this.max !== this.closestIncrementBelow(this.max)) {
      throw new Error(`
        Max value in incompatible with step increment.
        User will only be able to select up to ${this.closestIncrementBelow(
          this.max
        )}
        `);
    }
  };

  prepareElements = (initial: boolean) => {
    this.inputRangeElements = Array.from(this.el.querySelectorAll("input"))
      .filter((input) => input.type === "range")
      .slice(0, 2);

    this.inputRangeElements.forEach((input, index) => {
      input.id = `${this.sliderId}-range-${index}`;
      input.value = `${this.getValues()[index]}`;
      if (!initial && index == 0) return;
      input.addEventListener("focus", () => (this.focused = true));
      input.addEventListener("blur", () => (this.focused = false));
      input.addEventListener("input", () => {
        this.updateValues(
          this.inputRangeElements.map((rangeInput) => rangeInput.value),
          index
        );
        this.updateElements(this.inputRangeElements);
        if (!this.compact) {
          this.updateElements(this.inputTextElements);
        }
      });
    });

    if (!this.compact) {
      this.inputTextElements = Array.from(this.el.querySelectorAll("input"))
        .filter((input) => input.type === "number")
        .slice(0, 2);

      this.inputTextElements.forEach((input, index) => {
        input.id = `${this.sliderId}-text-${index}`;
        input.value = `${this.getValues()[index]}`;
        if (!initial && index == 0) return;
        input.addEventListener("focus", () => (this.focused = true));
        input.addEventListener("blur", (event) => {
          const currentInput = event.currentTarget as HTMLInputElement;

          this.focused = false;
          this.updateElements(this.inputTextElements);
          currentInput.classList.remove("r-border-destructive");
        });
        input.addEventListener("input", (event) => {
          const currentInput = event.currentTarget as HTMLInputElement;

          if (this.invalidateValue(parseFloat(currentInput.value))) {
            currentInput.classList.add("r-border-destructive");
          } else {
            currentInput.classList.remove("r-border-destructive");
          }
          this.updateValues(
            this.inputTextElements.map((textInput) => textInput.value),
            index,
            true
          );
          this.updateElements(this.inputRangeElements);
        });
      });
    }

    if (this.refreshElements) this.refreshElements = false;
    if (this.reinitializeElements) this.reinitializeElements = false;
  };

  updateElements = (elements: HTMLInputElement[]) => {
    elements.forEach((input, index) => {
      input.value = `${this.getValues()[index]}`;
    });
  };

  ensureValueIsNumber = (propertyName: PropertyName, fallback: number) => {
    const value = this[propertyName];

    if (Number.isNaN(Number(value))) {
      console.error(
        `'${value}' is not a number. '${propertyName}' will default to ${fallback}`
      );
      this[propertyName] = fallback;
    }
  };

  ensureValueIsInStep = (propertyName: PropertyName) => {
    const value = this[propertyName];

    if (this.closestValidNumber(value) !== value) {
      console.error(
        `'${value}' is not a valid step. '${propertyName}' will default to closest valid step '${this.closestValidNumber(
          value
        )}'`
      );
      this[propertyName] = this.closestValidNumber(value);
    }
  };

  // Lifecycle Methods
  componentWillLoad() {
    this.ensureValueIsNumber("step", 1);
    this.ensureValueIsNumber("min", 0);
    this.ensureValueIsNumber("max", 100);
    this.ensureValueIsNumber("value", this.min);
    this.ensureValueIsNumber("valueHigh", this.max);
    if (this.min > this.max) {
      throw new Error("Min value cannot be greater than max value");
    }
    this.ensureValueIsInStep("max");
    if (this.max - this.min < this.step) {
      throw new Error("Min value cannot be greater than max value");
    }
    this.ensureValueIsInStep("value");
    this.ensureValueIsInStep("valueHigh");
  }

  componentDidLoad() {
    this.prepareElements(true);
    this.el.id = this.sliderId;
  }

  componentShouldUpdate(
    newValue: number,
    oldValue: number,
    propertyName: PropertyName | "type" | "compact"
  ) {
    if (
      propertyName == "min" ||
      propertyName == "max" ||
      propertyName == "step" ||
      propertyName == "value" ||
      propertyName == "valueHigh"
    ) {
      if (Number.isNaN(newValue)) {
        this[propertyName] = oldValue;
        throw new Error(
          `${newValue} is not a number. ${propertyName} will remain ${oldValue}`
        );
      }
    }

    if (propertyName == "value" || propertyName == "valueHigh") {
      if (!this.focused && !this.compact) {
        this.updateElements(this.inputTextElements);
      }
    }

    if (
      propertyName == "min" ||
      propertyName == "max" ||
      propertyName == "step"
    ) {
      this.isUnsafe = true;
      console.error(
        `<raul-slider>: Programmatic changes to '${propertyName}' are not recommended`
      );
    }

    if (propertyName == "type") {
      this.isUnsafe = true;
      this.refreshElements = true;
    }

    if (propertyName == "compact") {
      this.reinitializeElements = true;
    }
  }

  componentDidUpdate() {
    if (this.isUnsafe) this.reevaluateValues();
    if (this.refreshElements) this.prepareElements(false);
    if (this.reinitializeElements) this.prepareElements(true);
    if (!this.isUnsafe)
      this.raulChange.emit(
        this.type == "range" ? [this.value, this.valueHigh] : this.value
      );
  }

  render() {
    return (
      <div
        class={{
          "r-form-element": true,
          "r-form-element--disabled": this.disabled,
          "r-form-element--focused": this.focused,
          "r-form-element--symbol-append": !!this.appendSymbol,
          "r-form-element--symbol-prepend": !!this.prependSymbol,
        }}
      >
        {this.label && <div class="r-form-element__label">{this.label}</div>}
        {!this.compact && (
          <div class="r-flex r-mb-3">
            <div
              class={{
                "r-form-element__input": true,
                "r-form-element__input--range": this.type === "range",
                "r-form-element__input--single": this.type === "single",
              }}
            >
              <input
                disabled={this.disabled}
                step={this.step}
                type="number"
                aria-label={`${this.label}${
                  this.type === "range" ? " Minimum" : ""
                } Value`}
              />
              {this.prependSymbol && (
                <div class="r-form-element__input__symbol r-form-element__input__symbol-prepend">
                  {this.prependSymbol}
                </div>
              )}
              {this.appendSymbol && (
                <div class="r-form-element__input__symbol r-form-element__input__symbol-append">
                  {this.appendSymbol}
                </div>
              )}
            </div>
            {this.type === "range" && (
              <div class="r-form-element__input__dash">-</div>
            )}
            {this.type === "range" && (
              <div
                class={{
                  "r-form-element__input": true,
                  "r-form-element__input--appended": !!this.appendSymbol,
                  "r-form-element__input--prepended": !!this.prependSymbol,
                  "r-form-element__input--range": true,
                }}
              >
                <input
                  disabled={this.disabled}
                  step={this.step}
                  type="number"
                  aria-label={`${this.label} Maximum Value`}
                />
                {this.prependSymbol && (
                  <div class="r-form-element__input__symbol r-form-element__input__symbol-prepend">
                    {this.prependSymbol}
                  </div>
                )}
                {this.appendSymbol && (
                  <div class="r-form-element__input__symbol r-form-element__input__symbol-append">
                    {this.appendSymbol}
                  </div>
                )}
              </div>
            )}
          </div>
        )}
        <div
          class={{
            "r-form-element__control": true,
            "r-form-element__control--disabled": this.disabled,
            "r-form-element__control--focused": this.focused,
            "r-form-element__control--range": this.type === "range",
          }}
        >
          <div class="r-form-element__control__track">
            {this.type === "single" && (
              <div
                class={`r-form-element__control__track__progress r-form-element__control__track__progress--${
                  this.disabled ? "disabled" : this.variant
                }`}
                style={{ width: `${this.getPercent(this.value)}%` }}
              />
            )}
            {this.type === "range" && (
              <div
                class="r-form-element__control__track__progress r-form-element__control__track__transparent"
                style={{ width: `${this.getPercent(this.value)}%` }}
              />
            )}
            {this.type === "range" && (
              <div
                class={`r-form-element__control__track__progress r-form-element__control__track__progress--${
                  this.disabled ? "disabled" : this.variant
                }`}
                style={{
                  width: `${
                    this.getPercent(this.valueHigh) -
                    this.getPercent(this.value)
                  }%`,
                }}
              />
            )}
          </div>

          <input
            class={`r-form-element__control__slider r-form-element__control__slider--${this.variant}`}
            disabled={this.disabled}
            max={this.max}
            min={this.min}
            step={this.step}
            type="range"
            value={this.value}
            aria-label={`${this.label}${
              this.type === "range" ? " Minimum" : ""
            } Value`}
            aria-hidden={this.compact ? "false" : "true"}
            tabindex={this.compact ? "0" : "-1"}
          />
          <div class="r-form-element__control__wrapper">
            <div class="r-form-element__control__wrapper__formatter">
              <div
                class="r-form-element__control__tooltip"
                style={{ left: `${this.getPercent(this.value)}%` }}
              >
                {this.prependSymbol}
                {this.getValues()[0]?.toLocaleString()}
                {this.appendSymbol}
                <div class="r-form-element__control__tooltip__pointer" />
              </div>
            </div>
          </div>
          {this.type === "range" && (
            <input
              class={`r-form-element__control__slider r-form-element__control__slider--${this.variant}`}
              disabled={this.disabled}
              max={this.max}
              min={this.min}
              step={this.step}
              type="range"
              value={this.valueHigh}
              aria-label={`${this.label} Maximum Value`}
              aria-hidden={this.compact ? "false" : "true"}
              tabindex={this.compact ? "0" : "-1"}
            />
          )}
          {this.type === "range" && (
            <div class="r-form-element__control__wrapper">
              <div class="r-form-element__control__wrapper__formatter">
                <div
                  class="r-form-element__control__tooltip"
                  style={{ left: `${this.getPercent(this.valueHigh)}%` }}
                >
                  {this.prependSymbol}
                  {this.getValues()[1]?.toLocaleString()}
                  {this.appendSymbol}
                  <div class="r-form-element__control__tooltip__pointer" />
                </div>
              </div>
            </div>
          )}
        </div>
        <div class="r-form-element__scale">
          <div>
            {this.prependSymbol}
            {this.min.toLocaleString()}
            {this.appendSymbol}
          </div>
          <div>
            {this.prependSymbol}
            {this.max.toLocaleString()}
            {this.appendSymbol}
          </div>
        </div>
      </div>
    );
  }
}

let sliderIds = 0;
