import { Component, h, Prop, Host } from "@stencil/core";
/**
 * @slot - Place `raul-grid-cell` components in this slot.
 */
@Component({
  tag: "raul-grid-row",
  styleUrl: "raul-grid-row.scss",
})
export class RaulGridRow {
  @Prop() movable = false;
  @Prop() moveByHandle = false;

  render() {
    const renderBody = () => {
      if (this.movable || this.moveByHandle) {
        return (
          <div class="r-flex r-items-stretch w-full">
            <raul-handle></raul-handle>
            <div class="r-flex r-flex-1">
              <slot />
            </div>
          </div>
        );
      } else {
        return <slot />;
      }
    };

    return (
      <Host
        role="row"
        class={{
          "raul-grid-row--movable": this.movable,
          "raul-grid-row--movable-by-handle": this.moveByHandle,
        }}
      >
        {renderBody()}
      </Host>
    );
  }
}
