import { Component, Host, Prop, h } from "@stencil/core";

/**
 * @slot - Add text to in this slot. NOTE: limit to two characters
 */

@Component({
  tag: "raul-avatar",
  styleUrl: "raul-avatar.scss",
})
export class RaulAvatar {
  /**
   * Displays avatar as a smaller size
   */
  @Prop({ reflect: true }) small?: boolean;

  /**
   * Sets the background of the avatar to the primary color.
   */
  @Prop({ reflect: true }) primary?: boolean;

  /**
   * Sets the avatar icon.
   */
  @Prop({ reflect: true }) variant: "profile" | "property" = "profile";

  /**
   * Sets an image as the avatar.
   */
  @Prop({ reflect: true }) src?: string;

  /**
   * Sets shape of the avatar to circle or square.
   */
  @Prop({ reflect: true }) corners: "rounded" | "square" = "rounded";

  render() {
    return (
      <Host class={this.corners === "rounded" ? "r-rounded-full" : ""}>
        <slot>
          {this.src ? (
            <div
              style={{ backgroundImage: `url(${this.src})` }}
              class="raul-avatar__image"
            />
          ) : (
            <raul-icon
              icon={this.variant === "profile" ? "user" : "building-7"}
              class="raul-avatar__icon"
            />
          )}
        </slot>
      </Host>
    );
  }
}
