import { Component, Element, Prop, h, Host } from "@stencil/core";

/**
 * @slot label-end - Place an element at the end of the label. For action elements use the `data-label-action attribute on the element.
 */

@Component({
  tag: "raul-checkbox",
  styleUrl: "raul-checkbox.scss",
})
export class RaulCheckbox {
  @Element() el!: HTMLRaulCheckboxElement;

  /**
   * If `true`, the checkbox border will become red. This can be useful for form validations.
   */
  @Prop() invalid = false;

  /**
   * The text label.
   */
  @Prop() labelText?: string;

  /**
   * If `true`, the checkbox size will be small.
   */
  @Prop({ reflect: true }) small = false;

  render() {
    return (
      <Host>
        <label class="r-checkbox__label">
          <slot />
          <div class="r-checkbox__label__icon" />
          {this.labelText && (
            <div class="r-checkbox__label__text">{this.labelText}</div>
          )}
        </label>
        <slot name="label-end" />
      </Host>
    );
  }
}
