import {
  Component,
  Prop,
  Element,
  Event,
  EventEmitter,
  h,
  Host,
} from "@stencil/core";

@Component({
  tag: "raul-modal-header",
})
export class RaulModal {
  mediaParent!: HTMLRaulModalElement | null;

  @Element() el!: HTMLRaulModalHeaderElement;

  @Prop() modalTitle?: string;
  @Prop() description?: string;

  @Event({ eventName: "modalclose" }) modalClose!: EventEmitter<undefined>;

  componentWillLoad() {
    this.mediaParent = this.el.closest('raul-modal[variant="media"]');
  }

  render() {
    return (
      <Host>
        <div class="r-modal-header__content">
          <h2 id="modalTitle">{this.modalTitle}</h2>
          {this.description ? <p>{this.description}</p> : null}
        </div>
        {this.mediaParent && !this.mediaParent.notDismissible ? (
          <button
            class="r-modal-header__close"
            type="button"
            data-close-modal
            onClick={() => this.modalClose.emit()}
          >
            <raul-icon icon="close"></raul-icon>
          </button>
        ) : null}
      </Host>
    );
  }
}
