import { Component, Element, Prop, h, Host } from "@stencil/core";

/**
 * @slot label-end - Place an element at the end of the label. For action elements use the `data-label-action attribute on the element.
 */

@Component({
  tag: "raul-radio",
  styleUrl: "raul-radio.scss",
})
export class RaulRadio {
  @Element() el!: HTMLRaulRadioElement;

  /**
   * If `true`, the radio border will become red. This can be useful for form validations.
   */
  @Prop({ reflect: true }) invalid = false;

  /**
   * The text label.
   */
  @Prop() labelText?: string;

  /**
   * If `true`, the radio size will be small.
   */
  @Prop({ reflect: true }) small = false;

  render() {
    return (
      <Host>
        <label class="r-radio__label">
          <slot />

          <div class="r-radio__label__icon" />

          {this.labelText && (
            <div class="r-radio__label__text">{this.labelText}</div>
          )}
        </label>
        <slot name="label-end" />
      </Host>
    );
  }
}
