@import "../../../styles/mixins/label-styles.scss";

:host {
  /**
  * @prop --dropdown-toggle-background-color: Background color of toggle button
  * @prop --dropdown-toggle-border-color: Border color of toggle button
  * @prop --dropdown-toggle-border-radius: Border radius of toggle button
  * @prop --dropdown-text-color: Text color of toggle button
  * @prop --dropdown-menu-shadow: Shadow of the floating menu
  * @prop --dropdown-menu-background-color: Background color of menu
  * @prop --dropdown-menu-border-color: Border color of menu
  * @prop --dropdown-chevron-color: Color of the chevron in the toggle button
  * @prop --dropdown-button-text-color: Text color of button in dropdown menu
  * @prop --dropdown-focus-color: Focus outline color
  * @prop --dropdown-search-input-background-color: Background color of search input
  * @prop --dropdown-search-input-border-color: Border color of search input
  * @prop --dropdown-search-input-text-color: Text color of search input
  * @prop --dropdown-item-border-color: Border color of item
  * @prop --dropdown-item-background-color: Background color of item
  * @prop --dropdown-item-hover-background-color: Hover background color of item
  * @prop --dropdown-item-text-color:
  */
  --dropdown-toggle-background-color: var(--color-white);
  --dropdown-toggle-border-color: var(--color-neutral);
  --dropdown-toggle-border-radius: var(--border-radius-small);
  --dropdown-text-color: var(--color-base);
  --dropdown-menu-shadow: var(--menu-shadow);
  --dropdown-menu-background-color: var(--color-background);
  --dropdown-menu-border-color: var(--color-neutral-light);
  --dropdown-chevron-color: var(--color-neutral-dark);
  --dropdown-button-text-color: var(--color-primary);
  --dropdown-focus-color: var(--color-primary);
  --dropdown-item-hover-background-color: var(--color-neutral-lightest);
  --dropdown-search-input-background-color: var(
    --dropdown-menu-background-color
  );
  --dropdown-search-input-border-color: var(--dropdown-menu-border-color);
  --dropdown-search-input-text-color: var(--dropdown-text-color);
  --dropdown-item-border-color: var(--dropdown-menu-border-color);
  --dropdown-item-background-color: var(--dropdown-menu-background-color);
  --dropdown-item-text-color: var(--dropdown-text-color);
  --dropdown-toggle-border-color-invalid: var(--color-destructive);
}

:host(raul-dropdown) {
  display: block;
  position: relative;
  width: 100%;

  .dropdown-icon {
    color: var(--dropdown-chevron-color);
  }

  label {
    cursor: pointer;
  }

  .raul-dropdown__chevron {
    display: block;
    width: 6px;
    min-height: 6px;
    margin-top: -0.25rem;
    transform: rotate(-135deg);
    border: 1px solid var(--dropdown-chevron-color);
    border-right-color: transparent;
    border-bottom-color: transparent;
  }

  .raul-dropdown__menu-body {
    position: relative;
    flex-grow: 1;
    min-height: 2.5rem;
    max-block-size: 280px;
    overflow: auto;
  }

  .raul-dropdown__menu-footer {
    display: flex;
    justify-content: flex-end;
    padding: 0.5rem 1rem;
    border-top: 1px solid var(--dropdown-menu-border-color);
    font-size: var(--font-size-3);
  }

  .raul-dropdown__toggle-content-container {
    display: inline-flex;
    column-gap: 0.5rem;
    row-gap: 0.25rem;
    flex-wrap: wrap;
    padding: 6px 0;

    &--block {
      display: block;
      flex: 1 1 0%;
      overflow: hidden;
      text-align: left;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  .raul-dropdown__heading-label {
    overflow: hidden;
    font-weight: var(--font-weight-medium);
    font-size: var(--font-size-3);

    font-family: var(--font-family-roboto);
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .raul-dropdown__heading-button {
    padding: 0.75rem;
    border: 0;
    background: transparent;
    color: var(--dropdown-button-text-color);
    font-size: var(--font-size-3);
    font-family: var(--font-family-roboto);

    &[aria-hidden="true"] {
      visibility: hidden;
    }
  }

  &::part(dropdown-container) {
    position: relative;
  }

  &::part(dropdown-toggle) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    min-height: 2.5rem;
    padding: 0 0.75rem;
    border: 1px solid var(--dropdown-toggle-border-color);
    border-radius: var(--dropdown-toggle-border-radius);

    background-color: var(--dropdown-toggle-background-color);
    color: var(--dropdown-text-color);
    font-size: var(--font-size-3);
    font-family: var(--font-family-roboto);
    white-space: nowrap;
    cursor: pointer;

    &:focus,
    &:focus-within {
      border-color: var(--dropdown-focus-color);
      outline: 0;
    }
  }

  &::part(header) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 2.5rem;
    border-bottom: 1px solid var(--color-neutral-light);

    background-color: var(--color-neutral-lightest);

    @media (min-width: 640px) {
      display: none;
    }
  }

  &::part(dropdown-menu) {
    display: none;
    z-index: 1002;
    position: fixed;
    flex-direction: column;
    width: 100%;
    min-width: 10rem;
    margin: 0.5rem 0;
    border: 1px solid var(--dropdown-menu-border-color);
    border-radius: 0.125rem;
    background-color: var(--dropdown-menu-background-color);
    box-shadow: var(--dropdown-menu-shadow);
    animation: fadeIn 0.15s;
    opacity: 0;
    pointer-events: none;

    @media (prefers-reduced-motion) {
      transition: none;
    }

    @media (max-width: 639px) {
      top: 0;
      left: 0;
      height: 100%;
      height: -webkit-fill-available;
      margin: 0;
    }

    @media (min-width: 640px) {
      position: absolute;
      min-height: 2.5rem;
      max-height: 50vh;
    }
  }

  ::slotted(button[data-clear]) {
    color: var(--dropdown-button-text-color) !important;
  }

  ::slotted(button:focus-visible) {
    outline: 2px solid var(--dropdown-focus-color) !important;
    outline-offset: 2px;
  }
}

:host(raul-dropdown[borderless]:not(:focus))::part(dropdown-toggle) {
  border-color: var(--transparent);
}

:host(raul-dropdown[variant="header"]) {
  display: inline-block;
  width: auto;
  margin-left: -0.75rem;

  .raul-dropdown__chevron {
    border-width: 2px;
    border-top-color: var(--dropdown-text-color);
    border-left-color: var(--dropdown-text-color);
  }

  &::part(dropdown-toggle) {
    column-gap: 1rem;
    border-color: var(--transparent);
    font-weight: var(--font-weight-bold);
    font-size: var(--font-size-5);

    &:hover {
      background-color: var(--color-neutral-light);
    }

    &:focus,
    &:focus-within {
      border-color: var(--dropdown-focus-color);
      outline: 0;
    }
  }

  &::part(dropdown-menu) {
    @media (min-width: 640px) {
      width: 20rem;
    }
  }
}

:host(raul-dropdown[disabled])::part(dropdown-toggle) {
  border-color: var(--color-neutral-light);
  background-color: var(--color-neutral-light);
  color: var(--color-neutral-dark);
  cursor: not-allowed;
}

:host(raul-dropdown:not([compact]))::part(label) {
  display: inline-block;
  @include label-styles;
  color: var(--dropdown-text-color);
}

:host(raul-dropdown[compact])::part(label) {
  z-index: 2;
  position: absolute;
  top: 0.625rem;
  left: 8px;
  padding: 0 4px;
  background-color: var(--color-background);
  font-size: var(--font-size-3);
  cursor: pointer;
  transition: all 0.15s ease;
}

:host(raul-dropdown[compact]) .raul-dropdown__label--float {
  top: -0.5625rem !important;
  font-weight: var(--font-weight-medium) !important;
  font-size: var(--font-size-2) !important;
}

:host(raul-dropdown[open]) {
  &::part(dropdown-menu) {
    display: flex;
    opacity: 1;
    pointer-events: initial;
  }
}

:host(raul-dropdown[invalid]) {
  &::part(dropdown-toggle):not(:focus-within) {
    border-color: var(--dropdown-toggle-border-color-invalid);
  }
}

:host(raul-dropdown[loading]) .raul-dropdown__menu-body {
  overflow: hidden;
}

@media (min-width: 640px) {
  :host(raul-dropdown[menu-placement="start-bottom"])::part(dropdown-menu) {
    top: 100%;
    bottom: auto;
    left: 0;
  }

  :host(raul-dropdown[menu-placement="start-top"])::part(dropdown-menu) {
    top: auto;
    bottom: 100%;
    left: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
