import {
  Component,
  Element,
  Prop,
  Listen,
  Method,
  h,
  Host,
  State,
} from "@stencil/core";
import { positionMenu } from "../../../utils/collision";

@Component({
  tag: "raul-tooltip",
  styleUrl: "raul-tooltip.scss",
})
export class RaulTooltip {
  toggleWrapperEl: HTMLDivElement | undefined;
  popupEl: HTMLDivElement | undefined;
  initialPosition!: string;

  @Element() el!: HTMLRaulTooltipElement;

  @State() open?: boolean;

  @Prop({ reflect: true }) text = "";
  @Prop({ mutable: true, reflect: true }) placement:
    | "top"
    | "start-top"
    | "end-top"
    | "right"
    | "left"
    | "bottom"
    | "start-bottom"
    | "end-bottom"
    | string = "top";
  @Prop({ reflect: true }) disabledHoverListener = false;
  @Prop({ reflect: true }) disabledFocusListener = false;

  componentDidLoad() {
    this.initialPosition = this.placement;
    if (
      this.toggleWrapperEl &&
      this.toggleWrapperEl.firstElementChild &&
      !this.toggleWrapperEl.querySelector("button")
    ) {
      this.toggleWrapperEl.firstElementChild.setAttribute("tabindex", "0");
    }
  }

  @Listen("focusin")
  handleFocusIn() {
    if (!this.disabledFocusListener) {
      this.show();
    }
  }

  @Listen("focusout")
  handleFocusOut() {
    if (!this.disabledFocusListener) {
      this.hide();
    }
  }

  @Listen("mouseenter")
  handleMouseEnter() {
    if (!this.disabledHoverListener) {
      this.show();
    }
  }

  @Listen("mouseleave")
  handleMouseLeave() {
    if (!this.disabledHoverListener) {
      this.hide();
    }
  }

  @Listen("scroll", { target: "document" })
  handleExternalScroll() {
    if (this.popupEl && this.toggleWrapperEl) {
      this.placement = positionMenu(
        this.toggleWrapperEl,
        this.popupEl,
        this.placement,
        this.initialPosition
      );
    }
  }

  @Method()
  async show(): Promise<void> {
    if (this.popupEl && this.toggleWrapperEl) {
      this.placement =
        positionMenu(
          this.toggleWrapperEl,
          this.popupEl,
          this.placement,
          this.initialPosition
        ) || this.placement;
      this.open = true;
    }
  }

  @Method()
  async hide(): Promise<void> {
    this.open = false;
  }

  render() {
    return (
      <Host open={this.open}>
        <div
          ref={(el) => (this.toggleWrapperEl = el)}
          class="raul--tooltip__toggle-wrapper"
          aria-haspopup="true"
        >
          <slot />
        </div>

        <div class="r-tooltip__arrow">
          <div class="r-tooltip__wrapper">
            <div
              class="r-tooltip"
              role="tooltip"
              ref={(el) => (this.popupEl = el)}
            >
              <div class="r-tooltip__content">{this.text}</div>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
