import { Component, Element, h, Prop, State } from "@stencil/core";

@Component({
  tag: "raul-simple-select",
  styleUrl: "raul-simple-select.scss",
})
/**
 * @slot messaging - Place validation, hint text or any other informational text here. Use the `raul-input-helper-list` component in this slot.
 */
export class RaulSimpleSelect {
  selectEl!: HTMLSelectElement;

  @Element() el!: HTMLRaulSimpleSelectElement;

  @State() inputId = `raul-select-${selectIds++}`;

  /**
   * Input's label text.
   */
  @Prop() label?: string;

  /**
   * Input's optional hint text.
   */
  @Prop() hint?: string;

  /**
   * Makes the input visually invalid and shows the feedback message.
   */
  @Prop() error?: string;

  componentWillLoad() {
    const select = this.el.querySelector("select");
    if (select) {
      this.selectEl = select;
    }
    if (this.selectEl?.id) this.inputId = this.selectEl.id;
    if (!this.selectEl.id) this.selectEl.id = this.inputId;
  }

  render() {
    return (
      <div
        class={{
          "r-form-element": true,
          "r-form-element--invalid": !!this.error,
        }}
      >
        {this.label && (
          <label class="r-form-element__label" htmlFor={this.inputId}>
            {this.label}
          </label>
        )}

        <div class="r-form-element__control">
          <slot />

          <raul-icon
            class="r-form-element__control__arrow"
            icon="arrow-down-v"
          />
        </div>
        <slot name="messaging">
          {this.hint && <small class="r-form-element__hint">{this.hint}</small>}

          {this.error && (
            <div class="r-form-element__error">
              <raul-icon
                icon="interface-alert-diamond"
                class="r-form-element__error__icon"
              />{" "}
              {this.error}
            </div>
          )}
        </slot>
      </div>
    );
  }
}

let selectIds = 0;
