import {
  Component,
  Prop,
  h,
  Event,
  EventEmitter,
  State,
  Watch,
} from "@stencil/core";
import { MenuItemInterface } from "../../../utils/interfaces/";

@Component({
  tag: "raul-filter-menu",
  styleUrl: "raul-filter-menu.scss",
})
export class RaulFilterMenu {
  @State() selectedItem!: MenuItemInterface;
  @State() isActive = false;

  /**
   * An array consisting of menu items.
   */
  @Prop() items: MenuItemInterface[] = [];

  @Watch("items")
  handleItemsPropChange() {
    this.updateSelectedItem();
  }

  /**
   * Sets position of dropdown menu.
   */
  @Prop() menuPosition:
    | "start-top"
    | "start-bottom"
    | "end-top"
    | "end-bottom" = "start-bottom";

  /**
   * The value of the select.
   */
  @Prop() value?: string;

  @Watch("value")
  updateValue() {
    this.updateSelectedItem();
  }

  componentWillLoad() {
    if (this.value) {
      this.updateSelectedItem();
    } else {
      this.selectedItem = this.items[0];
    }
  }

  /**
   * Event emitted when an item is selected.
   */
  @Event() raulFilterMenuChange!: EventEmitter<string>;

  private updateSelectedItem() {
    const defaultItem = this.items.find((item) => item.value === this.value);
    if (defaultItem) this.selectedItem = defaultItem;
  }

  private handleClickEvent(e: CustomEvent) {
    this.selectedItem = e.detail;
    this.raulFilterMenuChange.emit(this.selectedItem.value);
  }

  private activeState() {
    this.isActive = !this.isActive;
  }

  render() {
    return (
      <raul-menu
        items={this.items}
        textUnderline={false}
        border={true}
        textSize="sm"
        variant="items"
        menu-position={this.menuPosition}
        onRaulMenuOpen={() => this.activeState()}
        onRaulMenuClose={() => this.activeState()}
        onRaulMenuClick={(e) => this.handleClickEvent(e)}
      >
        <button
          data-testid="filter-menu-button"
          class={{
            "r-filter-menu": true,
            "r-filter-menu--active": this.isActive,
          }}
          aria-label={this.selectedItem?.text}
        >
          <raul-icon
            data-testid="filter-menu-selected-icon"
            icon={this.selectedItem?.icon}
            class="r-filter-menu__selected-icon"
          ></raul-icon>
          <raul-icon
            icon="arrow-down-v"
            class="r-filter-menu__down-arrow"
          ></raul-icon>
        </button>
      </raul-menu>
    );
  }
}
