import { Component, Host, h, Event, EventEmitter, Prop } from "@stencil/core";

@Component({
  tag: "raul-menu-item",
  shadow: false,
})
export class RaulMenuItem {
  itemEl: HTMLAnchorElement | HTMLButtonElement | undefined;

  /**
   * Enable or disable top border.
   */
  @Prop({ reflect: true }) border = false;

  /**
   * Disables item and prevents clicking.
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * Disables item and prevents clicking.
   */
  @Prop({ reflect: true }) destructive = false;

  /**
   * Sets icon and renders left of text
   */
  @Prop() icon?: string;

  /**
   * Sets aria-label attribute (use when items are icon only).
   */
  @Prop() label?: string;

  /**
   * Sets item text.
   */
  @Prop() text?: string;

  /**
   * Sets item url and renders item as an anchor tag.
   */
  @Prop() url?: string;

  /**
   * Sets item value that is detailed in the `raulMenuItemClick` event.
   */
  @Prop() value?: string;

  /**
   * Emits with item detail when item is clicked.
   */
  @Event() raulMenuItemClick!: EventEmitter<{
    text?: string;
    value?: string;
    target: HTMLAnchorElement | HTMLButtonElement | undefined;
  }>;

  private handleClick(): void {
    this.raulMenuItemClick.emit({
      text: this.text,
      value: this.value,
      target: this.itemEl,
    });
  }

  render() {
    const Tag = this.url ? "a" : "button";

    return (
      <Host role="menuitem">
        <Tag
          ref={(el) => (this.itemEl = el)}
          href={this.url ? this.url : undefined}
          disabled={this.disabled && !this.url ? true : false}
          aria-disabled={`${this.disabled}`}
          aria-label={this.label}
          tabindex="-1"
          onClick={() => this.handleClick()}
        >
          {this.icon && <raul-icon icon={this.icon}></raul-icon>}
          <slot>{this.text}</slot>
        </Tag>
      </Host>
    );
  }
}
