import { Component, Host, h, Element } from "@stencil/core";
import { HTMLStencilElement } from "@stencil/core/internal";

/**
 * @slot - Add `raul-breadcrumb` components to this slot.
 */
@Component({
  tag: "raul-breadcrumbs",
  styleUrl: "raul-breadcrumbs.scss",
})
export class RaulBreadcrumbs {
  @Element() el!: HTMLStencilElement;

  componentWillLoad() {
    const lastCrumb = this.el.querySelector("raul-breadcrumb:last-of-type");
    lastCrumb?.setAttribute("aria-current", "page");
  }

  render() {
    return (
      <Host role="list" aria-label="Breadcrumb">
        <slot />
      </Host>
    );
  }
}
