import { Component, Prop, h, Host } from "@stencil/core";
/**
 * @slot - Place container content in this slot. Content can include `raul-container-header`, `raul-container-body` and `raul-container-footer`.
 */
@Component({
  tag: "raul-container",
  styleUrl: "raul-container.scss",
})
export class RaulContainer {
  @Prop({ reflect: true }) hoverable = false;
  @Prop({ reflect: true }) addShadow = false;
  @Prop({ reflect: true }) attention?: boolean = false;

  @Prop() movable = false;
  @Prop() moveByHandle = false;

  render() {
    return (
      <Host>
        <slot name="container-image" />

        <div class="r-container-wrapper">
          {this.movable || this.moveByHandle ? (
            <raul-handle></raul-handle>
          ) : null}

          <div>
            <slot />
          </div>
        </div>
      </Host>
    );
  }
}
