import { Component, Prop, EventEmitter, Event, Host, h } from "@stencil/core";

@Component({
  tag: "raul-progress",
  styleUrl: "raul-progress.scss",
})
export class RaulProgress {
  /**
   * Presents the bar as compact
   */
  @Prop({ reflect: true }) static = false;

  /**
   * Sets the text above the bar
   */
  @Prop({ reflect: true }) label?: string;

  /**
   * Sets the text under the bar right of the value
   */
  @Prop({ reflect: true }) hint?: string;

  /**
   * Sets the value of the bar
   */
  @Prop({ reflect: true }) value = "0";

  /**
   * Use to display the progress bar only
   */

  @Prop({ reflect: true }) barOnly = false;

  /**
   * Sets the color of the progress bar
   */
  @Prop({ reflect: true }) color: "primary" | "warning" | "danger" | "success" =
    "primary";
  /** @internal */
  @Prop({ mutable: true, reflect: true }) isRemoved = false;

  @Event() raulProgressRemove!: EventEmitter<undefined>;

  private handleProgressRemove() {
    this.raulProgressRemove.emit();
    this.isRemoved = true;
  }

  render() {
    return (
      <Host>
        {!this.barOnly
          ? this.label && (
              <div class="r-progress__label">
                {this.label}

                {!this.static && (
                  <button
                    data-testid="progress-remove-btn"
                    type="button"
                    class="r-progress__remove-btn"
                    aria-label="Dismiss"
                    onClick={() => this.handleProgressRemove()}
                  >
                    <raul-icon icon="remove-2" />
                  </button>
                )}
              </div>
            )
          : null}

        <div
          class="r-progress__bar"
          role="progressbar"
          aria-label={this.barOnly ? this.label : null}
          aria-valuenow={this.barOnly ? `${this.value}` : null}
        >
          <div
            class="r-progress__bar__value"
            style={{ width: `${this.value}%` }}
          />
        </div>

        <div class="r-progress__text">
          {!this.barOnly ? `${this.value}%` : null} {!this.static && this.hint}
        </div>
      </Host>
    );
  }
}
