import { Component, Element, Host, Listen, Prop, h } from "@stencil/core";
import { HTMLStencilElement } from "@stencil/core/internal";

@Component({
  tag: "raul-dropdown-item-group",
  styleUrl: "raul-dropdown-item-group.scss",
  shadow: true,
})
export class RaulDropdownItemGroup {
  items: HTMLRaulDropdownItemElement[] | undefined;

  @Element() host: HTMLStencilElement | undefined;

  /** @internal - */
  @Prop({ mutable: true }) focused = false;

  /**
   * The name of the group.
   */
  @Prop() headingText!: string;

  /** @internal - */
  @Prop() indeterminate = false;

  /** @internal - Enables select all in group functionality */
  @Prop() multi = false;

  /** @internal -  */
  @Prop() selected = false;

  @Listen("focusout")
  handleInternalBlur() {
    this.focused = false;
  }

  handleSlotChange() {
    const slottedItems = this.host?.querySelectorAll("raul-dropdown-item");
    if (slottedItems) {
      this.items = Array.from(slottedItems);
      this.items.forEach((item) => (item.grouped = true));
    }
  }

  render() {
    const { focused, headingText, indeterminate, multi, selected } = this;
    const value = headingText?.toLowerCase().replace(" ", "-");

    return (
      <Host role="group">
        <raul-dropdown-item
          text={this.headingText}
          value={value}
          heading
          multi={multi}
          selected={selected}
          indeterminate={indeterminate}
          focused={focused}
          data-testid="group-heading"
        />
        <slot onSlotchange={() => this.handleSlotChange()}></slot>
      </Host>
    );
  }
}
