@import "../../../styles/mixins/focus.scss";

raul-switch {
  display: inline-flex;
  align-items: center;
  column-gap: 0.25rem;
  vertical-align: middle;
  font-size: var(--font-size-3);

  .r-switch__label {
    display: flex;
    cursor: pointer;
    margin-bottom: 0;
  }

  .r-switch__toggle {
    background-color: var(--color-neutral);
    border-radius: 9999px;
    display: inline-block;
    position: relative;
    height: 1.25rem;
    width: 2.25rem;
    margin-right: 0.75rem;
    transition-property: transform;
    transition-duration: 0.2s;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }

  .r-switch__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background-color: var(--color-background);
    font-size: var(--font-size-2);
    color: var(--white);
    border-width: 0;
    border-radius: 9999px;
    height: 1rem;
    width: 1rem;
    transition-property: transform;
    transition-duration: 0.2s;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    left: 2px;
    top: 2px;
  }

  input[type="checkbox"] {
    height: 0;
    width: 0;
    opacity: 0;

    &:checked ~ .r-switch__toggle {
      background-color: var(--color-primary);

      .r-switch__icon {
        transform: translateX(16px);
      }
    }

    &:focus-visible ~ .r-switch__toggle {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    &:disabled {
      ~ .r-switch__toggle .r-switch__icon {
        color: var(--color-neutral);

      }

      &:checked {
        ~ .r-switch__toggle .r-switch__icon {
          color: var(--color-primary);
        }
      }

      .r-switch__label & {
        cursor: not-allowed;
      }
    }
  }

  &[small] {
    line-height: 14px;

    .r-switch__toggle {
      width: 1.5rem;
      height: 14px;
    }

    .r-switch__icon {
      font-size: 8px;
      height: 10px;
      width: 10px;
    }

    input[type="checkbox"]:checked ~ .r-switch__toggle .r-switch__icon {
      transform: translateX(10px);
    }
  }

  *[data-label-action] {
    @include focus-visible;
  }
}