@import "../../../styles/mixins/focus.scss";

$alert-variants: (
  "information": (
    "bg": var(--color-primary-highlight),
    "color": var(--color-primary-dark),
    "border": var(--color-primary),
  ),
  "success": (
    "bg": var(--color-success-highlight),
    "color": var(--color-success-dark),
    "border": var(--color-success),
  ),
  "warning": (
    "bg": var(--color-warning-highlight),
    "color": var(--color-warning-dark),
    "border": var(--color-warning),
  ),
  "danger": (
    "bg": var(--color-destructive-highlight),
    "color": var(--color-destructive),
    "border": var(--color-destructive),
  ),
);

raul-alert {
  display: flex;
  column-gap: 0.75rem;
  width: 100%;
  height: auto;
  padding: 1rem;
  font-size: var(--font-size-3);
  opacity: 1;
  transition: opacity 350ms;

  &[is-dismissed] {
    opacity: 0;
    pointer-events: none;
  }

  &[is-removed] {
    display: none;
  }

  &[rounded] {
    border-radius: 2px;
  }

  @each $variant-name, $variant-value in $alert-variants {
    &[variant="#{$variant-name}"] {
      border-left: 12px solid map-get($variant-value, "border");
      background-color: map-get($variant-value, "bg");

      a {
        color: map-get($variant-value, "color");
      }
    }
  }

  .r-alert__content-wrapper {
    display: flex;
    flex: 1;
    flex-direction: column;
  }

  .r-alert__heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: var(--font-weight-semibold);

    .r-alert__heading__close-button {
      display: flex;
      align-items: center;
      align-self: stretch;
      justify-content: center;
      width: 2rem;
      min-width: 32px;
      height: 2rem;
      min-height: 32px;
      margin-top: -4px;
      margin-right: -8px;
      color: rgba(32, 39, 55, 0.65);
      cursor: pointer;

      &:hover {
        color: var(--charcoal-dark);
      }
      @include focus-visible;
    }
  }

  .r-alert__content {
    padding-bottom: 0.25rem;
    color: rgba(32, 39, 55, 0.8);

    a {
      display: inline-block;
      text-decoration: none;
      @include focus-visible;
    }
  }
}
