raul-snackbar-group {
  @apply text-md;
  display: flex;
  display: flex;
  z-index: 10100;
  position: fixed !important;
  right: 40px;
  bottom: 40px;
  flex-direction: column-reverse;

  @media (max-width: theme("screens.sm")) {
    right: 16px;
    left: 16px;
  }

  raul-snackbar {
    .r-snackbar__container {
      @apply mb-4;
    }
  }
}
