import {
  Component,
  Host,
  h,
  Element,
  Event,
  Listen,
  Method,
  Prop,
  Watch,
  EventEmitter,
} from "@stencil/core";
import flatpickr from "flatpickr";
import { Instance as FpInstance } from "flatpickr/dist/types/instance";
import { Options as FpOptions } from "flatpickr/dist/types/options";
import { getViewportDimensions } from "../../../utils/dom";
import { formatHumanDate } from "./utils";
import raulPickerPlugin from "./utils/raul-flatpickr-plugin";
import appendPlugin from "./utils/raul-append-plugin";

/**
 * @slot label-start - Place an element before the label. For action elements use the `data-label-action` attribute on the element.
 * @slot label-end - Place an element at the end of the label. For action elements use the `data-label-action attribute` on the element.
 * @slot messaging - Place validation, hint text or any other informational text here. Use the `raul-input-helper-list` component in this slot.
 */
@Component({
  tag: "raul-date-picker",
  styleUrl: "raul-date-picker.scss",
  shadow: false,
})
export class RaulDatePicker {
  @Element() el!: HTMLRaulDatePickerElement;

  accessoryButton!: HTMLButtonElement;
  calendar: FpInstance | null = null;
  containerEl!: HTMLElement;
  dateFormatTransform!: string;
  inputEl!: HTMLInputElement;
  raulInput?: HTMLRaulInputElement;
  observer?: MutationObserver;

  /**
   * Allows for compact floating label support. Requires label property.
   */
  @Prop() compact = false;

  /**
   * Disable the input
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * Displays date as "mm/dd/yyyy" (or whatever the format property is set to) when input is not in focus
   */
  @Prop() displayShortFormat = false;

  /**
   * Array of days to disable in yyyy-mm-dd format.
   */
  @Prop() disableDays?: (string | Date | ((date: Date) => boolean))[];
  @Watch("disableDays")
  handleDisableDaysChange() {
    if (this.calendar) {
      this.removePicker();
      this.instantiatePicker();
    }
  }

  /**
   * Prevent weekend day (Saturday and Sunday) selection.
   */
  @Prop() disableWeekends = false;

  /**
   * @deprecated Use "invalid" and the "messaging" slot with the raul-input-helper-list component instead
   */
  @Prop() error = "";

  /**
   * Makes the date picker visually invalid.
   */
  @Prop({ reflect: true }) invalid?: boolean = false;

  /**
   * Sets first day of the week (0: Sunday, 1: Monday, etc)
   */
  @Prop() firstDay: 0 | 1 | 2 | 3 | 4 | 5 | 6 = 0;

  /**
   * Sets Date format
   */
  @Prop() format: "mm/dd/yyyy" | "dd/mm/yyyy" | "yyyy/mm/dd" = "mm/dd/yyyy";

  /**
   * @deprecated Use the "messaging" slot with the raul-input-helper-list component instead
   */
  @Prop() hint = "";

  /**
   * Sets aria-describedby on internal input element.
   */
  @Prop({ reflect: true }) helperId: string | undefined;

  /**
   * Displays static calendar.
   */
  @Prop({ reflect: true }) inline = false;

  /**
   * Label text
   */
  @Prop() label = "";

  /**
   * Minimum allowed date (ISO 8601 format)
   */
  @Prop() minDate?: string;

  /**
   * Maximum allowed date (ISO 8601 format)
   */
  @Prop() maxDate?: string;

  /**
   * Input placeholder text
   */
  @Prop() placeholder = "";

  /**
   * Sets open state on picker.
   */
  @Prop({ reflect: true, mutable: true }) opened?: boolean;

  @Watch("opened")
  handleOpenedPropUpdate() {
    this.opened ? this.calendar?.open() : this.calendar?.close();
  }

  /**
   * Allow the selection of date range
   */
  @Prop({ reflect: true }) range = false;

  /**
   * Shows a loading indicator in the calendar popup.
   */
  @Prop() loading = false;
  @Watch("loading")
  handleLoadingPropChange() {
    this.removePicker();
    this.instantiatePicker();
  }

  /**
   * Sets value.
   */
  @Prop({ mutable: true }) value: string | Date | Date[] | undefined | null;

  @Watch("value")
  handleValuePropChange(newVal, oldVal) {
    let hasValidDates;

    if (!newVal) {
      this.setDate(newVal);
      return;
    }

    if (newVal?.length) {
      for (const date of newVal) {
        hasValidDates = !Number.isNaN(Date.parse(date));
        if (!hasValidDates) break;
      }
    } else {
      hasValidDates = !Number.isNaN(Date.parse(newVal));
    }

    if (newVal.length > 1 && Date.parse(newVal[0]) > Date.parse(newVal[1])) {
      hasValidDates = false;
    }

    if (hasValidDates) {
      this.setDate(newVal);
      const detail = this.createEventDetail();
      this.raulChange.emit(detail);
    } else {
      this.value = oldVal;
    }
  }

  /**
   * Emitted when input is blurred with value detail.
   */
  @Event() raulBlur!: EventEmitter<Date | { startDate: Date; endDate: Date }>;

  /**
   * Emitted when the date changes with value detail.
   */
  @Event() raulChange!: EventEmitter<Date | { startDate: Date; endDate: Date }>;

  /**
   * Emitted when the menu closes with value detail.
   */
  @Event() raulClose!: EventEmitter<Date | { startDate: Date; endDate: Date }>;

  /**
   * Emitted when input is focused with value detail.
   */
  @Event() raulFocus!: EventEmitter<Date | { startDate: Date; endDate: Date }>;

  /**
   * Emitted when the menu opens with value detail.
   */
  @Event() raulOpen!: EventEmitter<Date | { startDate: Date; endDate: Date }>;

  /**
   * Emitted when the calendar month changes.
   */
  @Event() raulDatepickerMonthChange?: EventEmitter<number>;

  /**
   * Emitted when the calendar year changes.
   */
  @Event() raulDatepickerYearChange?: EventEmitter<number>;

  connectedCallback() {
    if (!this.inline) return;
    this.observer = new MutationObserver((mutations) => {
      mutations.forEach((m) => {
        m.addedNodes.forEach((node) => {
          if (node === this.containerEl && !this.calendar)
            this.instantiatePicker();
        });
      });
    });
    this.observer.observe(this.el, { childList: true });
  }

  componentWillLoad() {
    this.dateFormatTransform =
      this.format === "dd/mm/yyyy"
        ? "d/m/Y"
        : this.format === "yyyy/mm/dd"
          ? "Y/m/d"
          : "m/d/Y";

    if (this.error) {
      console.warn(
        '[RAUL: Deprecated] "error" is deprecated, use "invalid" and the "messaging" slot with the raul-input-helper-list component instead',
      );
    }

    if (this.hint) {
      console.warn(
        '[RAUL: Deprecated] "hint" is deprecated, use the "messaging" slot with the raul-input-helper-list component instead',
      );
    }
  }

  disconnectedCallback() {
    this.removePicker();
    this.observer?.disconnect();
  }

  @Listen("change")
  handleChangeEvent(e: Event) {
    if (
      (e.target as HTMLElement).classList.contains("cur-year") &&
      this.calendar
    ) {
      let newDate = new Date(
        `${this.calendar.currentMonth + 1}/1/${(e.target as HTMLInputElement)
          ?.value}`,
      );
      if (this.maxDate && new Date(this.maxDate) < newDate) {
        newDate = new Date(this.maxDate);
      }
      if (this.minDate && new Date(this.minDate) > newDate) {
        newDate = new Date(this.minDate);
      }
      this.calendar.jumpToDate(newDate);
      this.raulDatepickerYearChange?.emit(newDate.getFullYear());
    }
  }

  @Listen("click")
  handleExternalClick({ target }: MouseEvent) {
    (target as HTMLElement)?.dataset.close !== undefined ||
    (target as HTMLElement).classList.contains("r-form-element")
      ? this.calendar?.close()
      : null;

    if (
      (target as HTMLElement).closest("[data-clear]") ||
      (target as HTMLElement).hasAttribute("data-clear")
    )
      this.clearDate();
  }

  @Listen("keydown")
  handleInternalFocus({ target, key }: KeyboardEvent) {
    if (
      target === this.calendar?.calendarContainer ||
      (key === "ArrowDown" && target === this.accessoryButton)
    )
      this.focusOnDate();
  }

  /**
   * Resets date value to undefined.
   */
  @Method()
  async clearDate(): Promise<void> {
    this.value = undefined;
    this.inputEl.value = "";
  }

  /**
   * Closes the calendar and emits `raulClose` event with details.
   */
  @Method()
  async close(): Promise<void> {
    this.opened = false;

    const detail = this.createEventDetail();
    this.raulClose.emit(detail);
  }

  /**
   * Opens the calendar an emits `raulOpen` event with details.
   */
  @Method()
  async open(): Promise<void> {
    if (!this.disabled) {
      this.opened = true;

      if (!this.displayShortFormat && this.value) {
        const value = this.calendar?.selectedDates;
        let dateStr = "";
        if (this.range && value && value[0] && value[1]) {
          dateStr = `${flatpickr.formatDate(
            value[0],
            this.dateFormatTransform,
          )} - ${flatpickr.formatDate(value[1], this.dateFormatTransform)}`;
        } else if (value && value[0]) {
          dateStr = flatpickr.formatDate(value[0], this.dateFormatTransform);
        }

        this.inputEl.value = dateStr;
      }

      const detail = this.createEventDetail();
      this.raulOpen.emit(detail);
    }
  }

  /**
   * Manually sets date (can also use the value property to set date value).
   */
  @Method()
  async setDate(date: string | Date | Date[]): Promise<void> {
    this.calendar?.setDate(date);
    if (!this.inline) {
      this.inputEl.value = date
        ? formatHumanDate(
            date,
            this.displayShortFormat,
            this.dateFormatTransform,
          )
        : "";
    }
  }

  createEventDetail() {
    if (this.value && Array.isArray(this.value)) {
      return this.range
        ? {
            startDate: this.value[0] || null,
            endDate: this.value[1] || null,
          }
        : this.value[0];
    }
  }

  focusOnDate() {
    const cal = this.calendar?.calendarContainer;
    if (!cal) return;
    const todayEl = cal.querySelector<HTMLSpanElement>(".today");
    const selectedEl = cal.querySelector<HTMLSpanElement>(".selected");
    const firstDayEl = cal.querySelector<HTMLSpanElement>(
      ".flatpickr-day:not(.prevMonthDay)",
    );
    selectedEl
      ? selectedEl?.focus()
      : todayEl
        ? todayEl?.focus()
        : firstDayEl?.focus();
  }

  handleAccessoryClick() {
    if (this.value != undefined && this.opened) {
      this.handleClearValue();
    } else {
      if (!this.calendar) {
        this.instantiatePicker();
      }
      this.calendar?.open();
    }
  }

  handleCancel() {
    this.calendar?.close();
  }

  handleChange(selectedDates: Date[]) {
    if (this.value !== selectedDates) {
      this.value = [...selectedDates];
      const { width } = getViewportDimensions();
      if (
        this.range &&
        this.calendar &&
        this.calendar.selectedDates.length < 2
      ) {
        this.focusOnDate();
      } else if (width >= 640) {
        this.calendar?.close();
      }
    }
  }

  handleClearValue() {
    this.clearDate();
    this.calendar?.close();
  }

  handleInputBlur() {
    if (this.value && this.calendar?.selectedDates.length) {
      this.inputEl.value = formatHumanDate(
        this.calendar.selectedDates,
        this.displayShortFormat,
        this.dateFormatTransform,
      );
    }
  }

  handleInputChange(e: Event | KeyboardEvent) {
    const target = e.target as HTMLInputElement;
    const { key } = e as KeyboardEvent;

    if (this.range && target.value !== "") {
      if (target.value.includes("-")) {
        const splitValue = target.value.split("-");
        const endDate = new Date(splitValue[1]);
        const startDate = new Date(
          endDate &&
          !splitValue[0].includes(",") &&
          !splitValue[0].includes("/")
            ? `${splitValue[0]}, ${endDate.getFullYear()}`
            : splitValue[0],
        );
        this.value = [startDate, endDate];
      }
    } else if (target.value === "") {
      this.clearDate();
    } else {
      const date = new Date(target.value);
      this.value = [date];
    }

    if (["Enter", "Return"].includes(key)) this.close();
  }

  handleInputFocus() {
    if (!this.disabled) {
      if (!this.calendar) this.instantiatePicker();
      this.calendar?.open();
    }
  }

  handleInputKeyDown(ev: KeyboardEvent) {
    const { key } = ev;
    if (key === "ArrowDown") this.focusOnDate();

    if (["Enter", "Return"].includes(key)) {
      this.handleInputChange(ev);
      this.inputEl.blur();
    }
  }

  instantiatePicker() {
    let pickerOptions: FpOptions = {
      appendTo: this.containerEl as HTMLDivElement,
      dateFormat: this.dateFormatTransform,
      positionElement: this.inputEl
        ? (this.inputEl as HTMLInputElement)
        : undefined,
      allowInput: true,
      disableMobile: true,
      closeOnSelect: false,
      locale: {
        firstDayOfWeek: this.firstDay,
        weekdays: {
          shorthand: ["S", "M", "T", "W", "T", "F", "S"],
          longhand: [
            "Sunday",
            "Monday",
            "Tuesday",
            "Wednesday",
            "Thursday",
            "Friday",
            "Saturday",
          ],
        },
      },
      nextArrow: `<raul-icon icon="arrow-right-v" kind="icon"></raul-icon>`,
      prevArrow: `<raul-icon icon="arrow-left-v" kind="icon"></raul-icon>`,
    };

    const plugins = [
      raulPickerPlugin(this.disabled, this.loading),
      appendPlugin({ appendTo: this.containerEl }),
    ];

    let disable: (string | ((date: Date) => boolean))[] | null = null;
    if (this.disableDays) {
      disable = this.disableDays as (string | ((date: Date) => boolean))[];
    }

    if (this.disableWeekends && disable) {
      disable.push((date) => date.getDay() === 0 || date.getDay() === 6);
    }

    if (disable) pickerOptions = { ...pickerOptions, disable };
    if (this.range) pickerOptions = { ...pickerOptions, mode: "range" };
    if (this.inline) pickerOptions = { ...pickerOptions, static: true };
    if (this.minDate)
      pickerOptions = { ...pickerOptions, minDate: this.minDate };
    if (this.maxDate)
      pickerOptions = { ...pickerOptions, maxDate: this.maxDate };

    const calendarNode = this.inline ? this.containerEl : this.raulInput;
    this.calendar = flatpickr(calendarNode as any, {
      ...pickerOptions,
      plugins,
      onOpen: () => this.open(),
      onClose: () => this.close(),
      onChange: (selectedDates) => this.handleChange(selectedDates),
      onMonthChange: (_selectedDates, _dateStr, instance) => {
        const { currentMonth } = instance;
        this.raulDatepickerMonthChange?.emit(currentMonth + 1);
      },
      onYearChange: (_selectedDates, _dateStr, instance) => {
        const { currentYear } = instance;
        this.raulDatepickerYearChange?.emit(currentYear);
      },
    });

    if (this.value) {
      this.setDate(this.value);
    }
  }

  removePicker() {
    this.calendar?.destroy();
    this.calendar = null;
  }

  render() {
    return (
      <Host>
        <div
          ref={(el) => {
            if (el) this.containerEl = el;
          }}
        ></div>
        {!this.inline ? (
          <raul-input
            ref={(el) => (this.raulInput = el)}
            label={this.label}
            hint={this.hint}
            error={this.error}
            invalid={this.invalid}
            compact={this.compact}
            not-empty={this.opened || !!this.value}
          >
            <slot name="label-start" />
            <slot name="label-end" />
            <input
              ref={(el) => {
                if (el) this.inputEl = el;
              }}
              placeholder={
                this.opened
                  ? this.range
                    ? `${this.format} - ${this.format}`
                    : this.format
                  : this.placeholder
              }
              type="text"
              autocomplete="off"
              value={
                this.value
                  ? formatHumanDate(
                      this.value,
                      this.displayShortFormat,
                      this.dateFormatTransform,
                    )
                  : undefined
              }
              disabled={this.disabled}
              onBlur={() => this.handleInputBlur()}
              onFocus={() => {
                if (!this.disabled) this.handleInputFocus();
              }}
              onKeyDown={(e: KeyboardEvent) => this.handleInputKeyDown(e)}
              onChange={(e: Event) => this.handleInputChange(e)}
              data-testid="date-picker-input"
              {...(this.helperId ? { "aria-describedby": this.helperId } : {})}
            />

            <button
              ref={(el) => {
                if (el) this.accessoryButton = el;
              }}
              type="button"
              class="raul--date-picker__input-accessory"
              slot="endAccessory"
              disabled={this.disabled}
              onClick={() => this.handleAccessoryClick()}
              data-testid="date-picker-accessory-btn"
              aria-label={
                this.inputEl?.value != "" && this.opened
                  ? "Clear"
                  : "Toggle Calendar"
              }
            >
              {this.inputEl?.value != "" && this.opened && !this.disabled ? (
                <raul-icon icon="remove-2" />
              ) : (
                <raul-icon icon="calendar-2" />
              )}
            </button>
            <slot name="messaging" />
          </raul-input>
        ) : null}
      </Host>
    );
  }
}
