interface ItemDetails {
  value: string;
  text: string;
  selected: boolean;
  disabled: boolean;
}

export const clearSelection = (
  target,
  items: HTMLRaulDropdownItemElement[]
): { itemDetails: ItemDetails; dropdownItem: HTMLRaulDropdownItemElement } => {
  const targetEl = target.hasAttribute("data-clear-selection")
    ? (target as HTMLElement)
    : (target.closest("[data-clear-selection]") as HTMLElement);
  const selectionValue = targetEl?.dataset.clearSelection;
  let dropdownItem;
  let itemDetails;
  if (items)
    dropdownItem = items.find(
      (item: HTMLRaulDropdownItemElement) => item.value === selectionValue
    );
  if (dropdownItem) {
    const { value, text, selected, disabled } = dropdownItem;
    itemDetails = {
      value,
      text,
      selected,
      disabled,
    };
  }
  return { itemDetails, dropdownItem };
};

export const deselectItems = (
  selectedItems: HTMLRaulDropdownItemElement[]
): void => {
  selectedItems.forEach(
    (item: HTMLRaulDropdownItemElement) => (item.selected = false)
  );
};

export const findItemsByValue = (
  items: HTMLRaulDropdownItemElement[] | undefined,
  isMultiselect: boolean,
  value: string | string[]
): HTMLRaulDropdownItemElement[] | undefined => {
  if (isMultiselect && value && items) {
    const itemsToSelect: HTMLRaulDropdownItemElement[] = [];
    Array.from(value)?.forEach((val: string) => {
      if (items) {
        const item = items.find(
          (opt: HTMLRaulDropdownItemElement) => opt.value === val
        );
        if (item) itemsToSelect.push(item);
      }
    });
    return itemsToSelect;
  }

  return items
    ? items.filter((opt: HTMLRaulDropdownItemElement) => opt.value === value)
    : undefined;
};

export const getItemGroups = (
  items: HTMLRaulDropdownItemElement[] | undefined
): HTMLRaulDropdownItemElement[] | undefined => {
  return items?.filter(
    (item) => item.tagName.toLowerCase() === "raul-dropdown-item-group"
  );
};

export const getSelectedItems = (
  items: HTMLRaulDropdownItemElement[] | undefined
): HTMLRaulDropdownItemElement[] | undefined => {
  return items
    ? items.filter((opt: HTMLRaulDropdownItemElement) => opt.selected === true)
    : undefined;
};

export const handleFirstArrowPress = (
  toggleEl: HTMLButtonElement | undefined,
  items: HTMLRaulDropdownItemElement[],
  focusIndex: number
): void => {
  toggleEl?.blur();
  if (items) {
    setTimeout(() => (items[focusIndex].focused = true), 5);
  }
};

export const initItems = (
  host,
  isMultiselect: boolean,
  value?: string | string[],
  selectedText?: string[]
):
  | {
      value: string | string[] | undefined;
      selectionText: string[];
      items: HTMLRaulDropdownItemElement[];
    }
  | undefined => {
  const slottedItems = host?.querySelectorAll(
    "raul-dropdown-item, raul-dropdown-item-group"
  );
  let items;
  let selectionText: string[] = [];

  if (slottedItems) items = Array.from(slottedItems);

  const groups = Array.from(
    items.filter(
      (item) => item.tagName.toLowerCase() === "raul-dropdown-item-group"
    )
  );
  if (groups && isMultiselect) {
    groups.forEach((groupEl) => {
      setGroupState(groupEl);
    });
  }

  if (isMultiselect && items)
    items.forEach((item: HTMLRaulDropdownItemElement) => (item.multi = true));

  const selectedItems = getSelectedItems(items);
  if (value) {
    const itemsToSelect = findItemsByValue(items, isMultiselect, value);
    if (selectedItems) deselectItems(selectedItems);
    if (itemsToSelect && itemsToSelect.length > 0) {
      itemsToSelect.forEach((item: HTMLRaulDropdownItemElement) => {
        item.selected = true;
        const groupParent = item.closest(
          "raul-dropdown-item-group"
        ) as HTMLRaulDropdownItemGroupElement;
        if (groupParent) {
          setGroupState(groupParent);
        }
      });
      if (selectedText?.length === 0)
        selectionText = itemsToSelect.map((item) => item.text);
    }
  }

  if (!value && selectedItems && selectedItems.length > 0) {
    const values: string[] = [];
    selectedItems.forEach((item: HTMLRaulDropdownItemElement) =>
      values.push(item.value)
    );

    value = isMultiselect ? values : values[0];
    if (selectedText?.length === 0)
      selectionText = selectedItems.map((item) => item.text);
  }

  return {
    value,
    selectionText:
      selectionText.length > 0 ? selectionText : selectedText || [],
    items,
  };
};

export const navigateItems = (
  event: KeyboardEvent,
  host,
  selectAllEl: HTMLRaulDropdownItemElement | undefined,
  toggleEl: HTMLButtonElement | undefined
) => {
  const { key, target } = event;
  event.preventDefault();
  const items: HTMLRaulDropdownItemElement[] = Array.from(
    host.querySelectorAll(
      "raul-dropdown-item:not([hidden]), raul-dropdown-item-group:not([hidden])"
    )
  );
  const allItems: HTMLRaulDropdownItemElement[] = selectAllEl
    ? [selectAllEl, ...items.filter((item) => !item.disabled)]
    : items.filter((item) => !item.disabled);
  const targetIndex = allItems.indexOf(target as HTMLRaulDropdownItemElement);
  const lastItemIndex = allItems.length - 1;
  let nextIndex;
  let indexToFocus;
  (target as HTMLRaulDropdownItemElement).focused = false;
  switch (key) {
    case "ArrowDown":
      if (targetIndex === -1) {
        handleFirstArrowPress(toggleEl, allItems, 0);
        break;
      }
      nextIndex = targetIndex + 1;
      indexToFocus = nextIndex === lastItemIndex + 1 ? 0 : nextIndex;
      allItems[indexToFocus].focused = true;
      break;
    case "ArrowUp":
      if (targetIndex === -1) {
        handleFirstArrowPress(toggleEl, allItems, lastItemIndex);
        break;
      }
      nextIndex = targetIndex - 1;
      indexToFocus = nextIndex === -1 ? lastItemIndex : nextIndex;
      allItems[indexToFocus].focused = true;
      break;
  }
};

export const setGroupState = (groupParent) => {
  const children: HTMLRaulDropdownItemElement[] = Array.from(
    groupParent.querySelectorAll("raul-dropdown-item")
  );
  const selectedChildren = children?.filter(
    (child: HTMLRaulDropdownItemElement) => child.selected
  );
  groupParent.selected = selectedChildren.length === children.length;
  groupParent.indeterminate =
    selectedChildren.length !== 0 && selectedChildren.length < children.length;
};

export const setMultipleValues = (
  items: HTMLRaulDropdownItemElement[],
  selectionText: string[],
  value: string | string[] | undefined,
  isSelected
) => {
  let newValue: string[] =
    value && value.length > 0 && typeof value !== "string" ? [...value] : [];
  let selectedTextArr: string[] = selectionText.length > 0 ? selectionText : [];
  let valuesToRemove: string[] = [];
  let textToRemove: string[] = [];
  items
    ?.filter((item) => !item.hidden)
    .forEach((item) => {
      const matchedValue =
        typeof value !== "string"
          ? value?.find((value) => value === item.value)
          : undefined;
      const matchedText = selectionText.find((text) => text === item.text);
      if (isSelected) {
        if (!matchedValue) newValue = [...newValue, item.value];
        if (!matchedText)
          selectedTextArr = [...selectedTextArr, item.text].sort();
      } else {
        if (matchedValue) valuesToRemove = [...valuesToRemove, matchedValue];
        if (matchedText) textToRemove = [...textToRemove, matchedText];
      }
    });
  if (valuesToRemove.length > 0 && value && typeof value !== "string") {
    newValue = value?.filter((value) => !valuesToRemove.includes(value));
  }

  if (textToRemove.length > 0) {
    selectedTextArr = selectionText.filter(
      (text) => !textToRemove.includes(text)
    );
  }

  return { newValue, selectedTextArr };
};

export const setSelectionText = (
  items: string[],
  isMultiselect: boolean
): string => {
  return isMultiselect
    ? `${items[0]}${items.length > 1 ? ` (+${items.length - 1})` : ""}`
    : items[0];
};
