import { Component, h, Host, Element } from "@stencil/core";
import { HTMLStencilElement, State } from "@stencil/core/internal";
/**
 * @slot - Place footer contact text in this slot.
 */
@Component({
  tag: "raul-footer-contact",
})
export class RaulFooterContact {
  @Element() host: HTMLStencilElement | undefined;

  @State() hasSlotContent;

  componentWillRender() {
    this.hasSlotContent =
      this.host?.childNodes && this.host?.childNodes.length > 1;
  }

  render() {
    return (
      <Host>
        <slot>
          {!this.hasSlotContent ? (
            <span>
              RealPage, Inc. Richardson, TX 75082{" "}
              <a href="tel:(877) 325-7243">(877) 325-7243</a>
            </span>
          ) : null}
        </slot>
      </Host>
    );
  }
}
