export function randomUID(): string {
  return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function (c) {
    const r = (Math.random() * 16) | 0,
      v = c == "x" ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
}

export function stripBaseIndent(str: string): string | undefined {
  const parseNumSpaces = /^[^\r\n]*?(?=\S)/m.exec(str);
  if (!parseNumSpaces) return;
  const numSpaces = parseNumSpaces[0].length;
  const baseIndent = new RegExp(`^\\s{${numSpaces}}`, "gm");

  return str.split(baseIndent).join("\n").trim();
}
