import {
  Component,
  Host,
  Element,
  Event,
  EventEmitter,
  Prop,
  Listen,
  h,
  Watch,
} from "@stencil/core";
import { positionMenu } from "../../../utils/collision";

/**
 * @slot - Popover content goes here. Use `raul-popover-container` component.
 * @slot trigger - Add trigger element to this slot.
 */

@Component({
  tag: "raul-popover",
  styleUrl: "raul-popover.scss",
})
export class RaulPopover {
  popoverRef?: HTMLRaulPopoverControllerElement;
  arrowRef?: HTMLDivElement;
  toggleWrapperEl?: HTMLDivElement;
  initialPosition!: string;

  @Element() el!: HTMLRaulPopoverElement;

  /**
   * Controls visibility of the popover.
   */
  @Prop({ mutable: true, reflect: true }) open = false;

  @Watch("open")
  handleOpenPropChange(newVal: boolean) {
    if (newVal && this.toggleWrapperEl && this.popoverRef) {
      this.raulPopoverOpen.emit();
      this.placement =
        positionMenu(
          this.toggleWrapperEl,
          this.popoverRef,
          this.placement,
          this.initialPosition
        ) || this.placement;
    } else {
      this.raulPopoverClose.emit();
    }
  }

  /**
   * Placement of popover.
   */
  @Prop({ mutable: true, reflect: true }) placement:
    | "top"
    | "bottom"
    | "left"
    | "right"
    | string = "top";

  /**
   * Event emitted when the popover is closed.
   */
  @Event() raulPopoverClose!: EventEmitter<undefined>;

  /**
   * Event emitted when the popover is opened.
   */
  @Event() raulPopoverOpen!: EventEmitter<undefined>;

  componentDidLoad() {
    this.initialPosition = this.placement;
  }

  @Listen("click")
  handleInternalClick({ target }: MouseEvent) {
    if (
      (target as HTMLElement).closest("div.raul--popover__toggle-wrapper") ===
      this.toggleWrapperEl
    )
      this.open = !this.open;
  }

  @Listen("click", { target: "document" })
  handleOutsideClick(e: Event) {
    const isClickOutsidePopover = !this.el.contains(e.target as HTMLElement);

    if (this.open && isClickOutsidePopover) {
      this.open = false;
    }
  }

  @Listen("scroll", { target: "document" })
  handleExternalScroll() {
    if (this.popoverRef && this.toggleWrapperEl) {
      this.placement = positionMenu(
        this.toggleWrapperEl,
        this.popoverRef,
        this.placement,
        this.initialPosition
      );
    }
  }

  render() {
    return (
      <Host>
        <div
          ref={(el) => (this.toggleWrapperEl = el)}
          class="raul--popover__toggle-wrapper"
          aria-haspopup="true"
        >
          <slot name="trigger" />
        </div>

        <raul-popover-controller
          class={{
            "r-popover": true,
            "r-popover--open": this.open,
          }}
          ref={(el) => (this.popoverRef = el)}
        >
          <div class="r-popover-arrow" ref={(el) => (this.arrowRef = el)} />
          <slot />
        </raul-popover-controller>
      </Host>
    );
  }
}
