import { Component, h, Host } from "@stencil/core";
/**
 * @slot - Place custom popover content in this slot (use to bypass the use of the other content slots for your own layout in the popover).
 * @slot icon - Place `raul-icon` or `raul-avatar` in this slot. It will be positioned top left of the main popover content.
 * @slot label - Place popover label text in this slot. It is positioned at the top of the popover content.
 * @slot sub-label - Place sub-label text in this slot. It is positioned under the label.
 * @slot status - Place the `raul-status` component in this slot. It is positioned below the labels.
 * @slot button - Place a control variant `raul-button` in this slot.
 */
@Component({
  tag: "raul-popover-content",
})
export class RaulPopoverContent {
  render() {
    return (
      <Host>
        <div class="r-popover-icon">
          <slot name="icon" />
        </div>
        <div class="r-popover-col">
          <div class="r-popover-label">
            <slot name="label" />
            <slot name="sub-label" />
          </div>
          <div class="r-popover-status">
            <slot name="status" />
          </div>
          <div class="r-popover-button">
            <slot name="button" />
          </div>
          <div class="r-popover-custom">
            <slot />
          </div>
        </div>
      </Host>
    );
  }
}
