import { Component, h, Host } from "@stencil/core";

/**
 * @slot popover-image - Place the popover "hero" image in this slot.
 * @slot popover-content - Place the popover content in this slot.
 */
@Component({
  tag: "raul-popover-container",
})
export class RaulPopoverContainer {
  render() {
    return (
      <Host>
        <slot name="popover-image" />
        <slot name="popover-content" />
      </Host>
    );
  }
}
