raul-grid {
  display: flex;
  flex-direction: column;
  background-color: var(--color-background);
  font-size: var(--font-size-3);
  color: var(--charcoal);

  raul-grid-header raul-grid-cell {
    background-color: var(--gray-lightest);
    font-size: var(--font-size-2);
    font-weight: var(--font-weight-bold);

  }

  raul-grid-body raul-grid-row[active] {
    background-color: var(--color-primary-highlight);
  }

  &[small] {
    raul-grid-body raul-grid-cell {
      font-size: var(--font-size-2);
    }
  }

  &[striped] {
    raul-grid-body raul-grid-row:nth-child(even) {
      background-color: var(--gray-lightest);
    }
  }

  &[hoverable] {
    raul-grid-body raul-grid-row:hover {
      background-color: var(--color-primary-highlight);
    }
  }
  
}
