raul-container {
  display: block;
  position: relative;
  font-size: var(--font-size-3);

  .r-container-wrapper {
    padding: 1.5rem;
    border-color: var(--color-neutral-light);
    background-color: var(--color-background);
    --border-opacity: 1;
    border-width: 1px;
    border-style: solid;
    border-radius: 0.25rem;

    @media (min-width: 768px) {
      padding: 2rem;
    }
  }

  &[add-shadow] {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  }

  &[attention]::after {
    position: absolute;
    top: 5px;
    right: 0px;
    width: 0px;
    height: 0px;
    transform: rotate(45deg);
    border-right: 12px solid transparent;
    border-bottom: 12px solid var(--color-secondary);
    border-left: 12px solid transparent;
    border-radius: 4px;
    content: "";
  }

  &[hoverable] {
    @apply transition-base;

    &:hover {
      box-shadow:
        0 20px 25px -5px rgba(0, 0, 0, 0.1),
        0 10px 10px -5px rgba(0, 0, 0, 0.04);
    }
  }

  &[movable],
  &[move-by-handle] {
    padding-left: 0;

    &:hover {
      background-color: var(--color-primary-highlight);

      raul-handle {
        min-height: 40px;
        cursor: move;
        opacity: 1;
      }
    }

    .r-container-wrapper {
      display: flex;
      align-items: center;
      padding-left: 0;

      & > div {
        flex: 1;
      }
    }
  }

  &[movable] {
    cursor: move;
  }

  raul-container-image + .r-container-wrapper {
    border-top-width: 0;
    border-radius: 0;
  }

  raul-handle {
    margin: 0rem 1rem 0rem 0.5rem;
  }
}
