import {
  Component,
  h,
  Prop,
  Event,
  EventEmitter,
  Element,
  State,
  Host,
  Listen,
} from "@stencil/core";

/**
 * @slot label-start - This slot will render content to the left of the stars.
 * @slot label-end - This slot will render content to the right of the stars.
 * @slot sub-text - This slot will render content under the stars.
 */

@Component({
  tag: "raul-rating",
  styleUrl: "raul-rating.scss",
  shadow: true,
})
export class RaulRating {
  @Element() hostElement!: HTMLElement;

  /** (optional) Label for the rating component */
  @Prop() label?: string;

  /** Size of the rating component. Defaults to "medium" */
  @Prop({ reflect: true, mutable: true }) size: "small" | "medium" | "large" =
    "medium";

  /** Determines if the rating component should be displayed in component mode. */
  @Prop({ reflect: true, mutable: true }) compact = false;

  /** (optional) The current value of the rating component. */
  @Prop({ reflect: true, mutable: true }) value = 0;

  /** Determines if the rating component is readonly or not. */
  @Prop({ reflect: true, mutable: true }) readonly = false;

  @Event({ eventName: "raulChange" }) raulChangeEvent!: EventEmitter<{
    value: number;
  }>;

  @State() hoveredStars = 0;

  componentWillRender() {
    if (this.compact) {
      this.value = 1;
      this.readonly = true;
    }
  }

  @Listen("keydown")
  handleKeyDown(event) {
    if (this.readonly) return;
    const { key } = event;
    if (key === "ArrowRight" || key === "ArrowDown") {
      event.preventDefault();
      const radioIndex = this.value === 5 ? 0 : Math.min(this.value + 1, 5) - 1;
      this.handleRadioClick(radioIndex);
      const radioToFocus = this.hostElement.shadowRoot?.querySelectorAll(
        ".raul-rating--radio",
      )[radioIndex] as HTMLElement;
      radioToFocus?.focus();
    }
    if (key === "ArrowLeft" || key === "ArrowUp") {
      event.preventDefault();
      const radioIndex = this.value <= 1 ? 4 : Math.min(this.value - 1, 5) - 1;
      this.handleRadioClick(radioIndex);
      const radioToFocus = this.hostElement.shadowRoot?.querySelectorAll(
        ".raul-rating--radio",
      )[radioIndex] as HTMLElement;
      radioToFocus?.focus();
    }
  }

  handleRadioHover(index: number) {
    if (!this.readonly) {
      this.hoveredStars = index + 1;
    }
  }

  handleRadioLeave() {
    if (!this.readonly) {
      this.hoveredStars = 0;
    }
  }

  handleRadioClick(index: number) {
    if (!this.readonly) {
      this.value = index + 1;
      this.raulChangeEvent.emit({ value: this.value });
    }
  }

  handleKeyup(event, index) {
    const { key } = event;
    if (key === "Enter" || key === " ") {
      this.handleRadioClick(index);
    }
  }

  render() {
    const radios: JSX.Element[] = [];
    const radioCount = this.compact ? 1 : 5;

    const radioContainerRole = this.readonly ? null : "radiogroup";
    for (let i = 0; i < radioCount; i++) {
      const isSelected = i < this.value;
      const isHovered = i < this.hoveredStars;
      const radioIcon = isSelected ? "vote-star" : "vote-star-empty";
      const radioRole = this.readonly ? null : "radio";
      const radioLabel = `${i + 1} ${i === 0 ? "star" : "stars"}`;
      const ariaLabel = this.readonly ? null : radioLabel;
      const ariaChecked = isSelected && i === this.value - 1 ? "true" : "false";
      const tabindex =
        this.readonly || this.value === 0 || this.value !== i + 1 ? "-1" : "0";

      radios.push(
        <raul-icon
          class={{
            "raul-rating--radio": true,
            "raul-rating--radio--selected": isSelected,
            "raul-rating--radio--hovered": isHovered && !this.readonly,
          }}
          icon={radioIcon}
          role={(radioRole as string) || undefined}
          aria-label={ariaLabel}
          aria-checked={ariaChecked}
          tabindex={tabindex}
          onMouseEnter={() => this.handleRadioHover(i)}
          onMouseLeave={() => this.handleRadioLeave()}
          onClick={() => this.handleRadioClick(i)}
          onKeyUp={(event) => this.handleKeyup(event, i)}
          onFocus={() => this.handleRadioHover(i)}
          onBlur={() => this.handleRadioLeave()}
        />,
      );
    }

    return (
      <Host>
        {this.label && <div part="label-wrapper">{this.label}</div>}
        <div part="field-wrapper">
          <slot name="label-start" />
          <div
            tabindex={this.readonly || this.value !== 0 ? "-1" : "0"}
            class="raul-rating--radio-container"
            role={(radioContainerRole as string) || undefined}
          >
            {radios}
          </div>
          <slot name="label-end" />
        </div>
        <slot name="sub-text" />
      </Host>
    );
  }
}
