raul-icon {
  display: inline-block;
  vertical-align: middle;

  svg {
    display: block;
    height: 1em;
    width: 1em;

    g[fill="none"],
    g[style*="fill:none"] {
      circle,
      ellipse,
      path,
      polygon,
      polyline,
      rect {
        fill: inherit !important;
      }
    }

    circle:not([fill="none"]):not([style*="fill:none"]),
    ellipse:not([fill="none"]):not([style*="fill:none"]),
    path:not([fill="none"]):not([style*="fill:none"]),
    polygon:not([fill="none"]):not([style*="fill:none"]),
    polyline:not([fill="none"]):not([style*="fill:none"]),
    rect:not([fill="none"]):not([style*="fill:none"]) {
      fill: currentColor !important;
    }
  }

  &[kind="product"] {
    rect:not([fill="none"]):not([style*="fill:none"]) {
      fill: none !important;
    }
  }
}
