:host(raul-dropdown-item-group) {
  display: block;

  raul-dropdown-item:not([multi])::part(option) {
    cursor: default;

    &:hover {
      background-color: var(--dropdown-item-background-color);
    }

    &:focus {
      outline-color: var(--transparent);
    }

    &:focus-visible {
      outline-color: var(--color-primary);
    }
  }
}

:host(raul-dropdown-item-group[hidden]) {
  display: none;
}
