import {
  Component,
  h,
  Prop,
  Event,
  EventEmitter,
  Watch,
  Host,
} from "@stencil/core";

@Component({
  tag: "raul-paging-bar",
  styleUrl: "raul-paging-bar.scss",
})
export class RaulPagingBar {
  /**
   * Sets current page number.
   */
  @Prop({ mutable: true, reflect: true }) currentPage = 1;

  @Watch("currentPage")
  currentPageChanged(_newVal: number, oldVal: number) {
    this.validateCurrentPage(oldVal);
  }

  /**
   * Sets the entries select options.
   */
  @Prop() entities: number[] = [10, 20, 30, 40, 50];

  /**
   * Sets rows per page.
   */
  @Prop({ reflect: true }) rowsPerPage = 10;

  @Watch("rowsPerPage")
  rowsPerPageChanged() {
    this.validateRowsPerPage();
  }

  /**
   * Sets total row number
   */
  @Prop({ reflect: true }) totalRows = 0;

  /**
   *
   */
  @Event() pagingChange!: EventEmitter<{
    event: string;
    currentPage: number;
    rowsPerPage: number;
    totalPages: number;
    startRow: number;
    endRow: number;
  }>;

  componentWillLoad() {
    this.validateRowsPerPage();
    this.validateCurrentPage();
  }

  decrement() {
    this.currentPage = this.currentPage - 1;
    this.updateBar("previousPage");
  }

  entitiesChange(event: Event) {
    this.rowsPerPage = Number((event.target as HTMLInputElement).value);
    this.currentPage = 1;
    this.updateBar("entitiesChange");
  }

  increment() {
    this.currentPage = this.currentPage + 1;
    this.updateBar("nextPage");
  }

  pageValueChange(event: Event) {
    const val = Number((event.target as HTMLOptionElement).value);

    if (val > this.totalPages() || val < 1 || Number.isNaN(val)) {
      (event.target as HTMLOptionElement).value = String(this.currentPage);
    } else {
      this.currentPage = val;
      this.updateBar("pageValueChange");
    }

    (event.target as HTMLOptionElement).blur();
  }

  private totalPages() {
    return Math.ceil(this.totalRows / this.rowsPerPage);
  }
  private startRow() {
    return (this.currentPage - 1) * this.rowsPerPage + 1;
  }
  private endRow() {
    const endRow = (this.currentPage - 1) * this.rowsPerPage + this.rowsPerPage;
    return endRow <= this.totalRows ? endRow : this.totalRows;
  }

  updateBar(event: string) {
    if (this.validateRowsPerPage()) {
      this.pagingChange.emit({
        event: event,
        currentPage: this.currentPage,
        rowsPerPage: this.rowsPerPage,
        totalPages: this.totalPages(),
        startRow: this.startRow(),
        endRow: this.endRow(),
      });
    }
  }

  validateCurrentPage(oldVal = 1) {
    if (this.currentPage < 1 || this.currentPage > this.totalPages()) {
      console.error(
        `RAUL Paging Bar Component ERROR: current page can't be less than 1 or greater than total pages number`
      );
      this.currentPage = oldVal;
    }
  }

  validateRowsPerPage() {
    if (!this.entities.includes(this.rowsPerPage)) {
      console.error(
        `RAUL Paging Bar Component ERROR: rowsPerPage does not match entities array. rowsPerPage is set to ${
          this.rowsPerPage
        } but available entities are: ${this.entities.toString()}`
      );
    }

    return this.entities.includes(this.rowsPerPage);
  }

  render() {
    return (
      <Host>
        <div
          class="r-paging-bar__col r-paging-bar__col--entries"
          data-testid="paging-bar-entries"
        >
          <span aria-hidden="true">Show</span>
          <div class="r-paging-bar__entry-select">
            <select
              aria-label="Entries per page"
              data-testid="paging-bar-entities"
              onChange={(event) => this.entitiesChange(event)}
            >
              {this.entities &&
                this.entities.map((entity) => (
                  <option value={entity} selected={this.rowsPerPage === entity}>
                    {entity}
                  </option>
                ))}
            </select>
            <raul-icon class="r-select__arrow" icon="arrow-down-v" />
          </div>
          <span aria-hidden="true">entries</span>
        </div>

        <div
          class="r-paging-bar__col r-paging-bar__col--count"
          data-testid="paging-bar-count"
        >
          {this.startRow()} - {this.endRow()} of {this.totalRows}
        </div>

        <div class="r-paging-bar__col r-paging-bar__col--nav">
          <button
            type="button"
            aria-label="Previous Entries"
            onClick={() => this.decrement()}
            disabled={this.currentPage === 1}
          >
            <raul-icon icon="arrow-left-v"></raul-icon>
          </button>

          <div class="r-paging-bar__nav">
            <span aria-hidden="true">Page</span>
            <div class="r-paging-bar__input">
              <input
                data-testid="paging-bar-input"
                type="number"
                min="1"
                max={this.totalPages()}
                value={this.currentPage}
                aria-label="Page"
                onChange={(e: Event) => this.pageValueChange(e)}
                onKeyPress={(e: KeyboardEvent) =>
                  e.key === "Enter" ? this.pageValueChange(e) : undefined
                }
              />
            </div>
            <span aria-hidden="true">of {this.totalPages()}</span>
          </div>

          <button
            type="button"
            aria-label="Next Entries"
            onClick={() => this.increment()}
            disabled={this.currentPage === this.totalPages()}
          >
            <raul-icon icon="arrow-right-v"></raul-icon>
          </button>
        </div>
      </Host>
    );
  }
}
