import {
  Component,
  Event,
  EventEmitter,
  Host,
  Prop,
  Watch,
  h,
} from "@stencil/core";

/**
 * @part input - The search input field.
 */

@Component({
  tag: "raul-dropdown-search-input",
  styleUrl: "raul-dropdown-search-input.scss",
  shadow: { delegatesFocus: true },
})
export class RaulDropdownSearchInput {
  searchInputEl: HTMLInputElement | undefined;

  /**
   * The text to display when the search input value is empty.
   */
  @Prop() placeholderText = "Search...";

  /**
   * Sets value of input.
   */
  @Prop({ mutable: true }) value = "";

  @Watch("value")
  hanndleValuePropChange(newVal: string) {
    this.raulDropdownSearchInput?.emit({ query: newVal });
  }

  /**
   * Emitted when search input is changed.
   */
  @Event() raulDropdownSearchInput:
    | EventEmitter<{ query: string | undefined }>
    | undefined;

  handleSearchClear(): void {
    this.value = "";
    this.searchInputEl?.focus();
    this.raulDropdownSearchInput?.emit({ query: this.value });
  }

  handleSearchInput(e: InputEvent): void {
    this.value = (e.target as HTMLInputElement).value;
    this.raulDropdownSearchInput?.emit({ query: this.value });
  }

  render() {
    return (
      <Host>
        <div part="input">
          <raul-icon icon="search" class="r-select__search__icon" />
          <input
            ref={(el) => (this.searchInputEl = el as HTMLInputElement)}
            type="text"
            placeholder={this.placeholderText}
            aria-label="Search Options"
            value={this.value}
            onInput={(e: InputEvent) => this.handleSearchInput(e)}
            data-testid="search-input"
          />
          <button
            type="button"
            onClick={() => this.handleSearchClear()}
            aria-label="Clear Search"
          >
            <raul-icon icon="remove-2" class="r-select__search__icon-reset" />
          </button>
        </div>
      </Host>
    );
  }
}
