import {
  Component,
  Element,
  Prop,
  Watch,
  Event,
  EventEmitter,
  Listen,
  h,
  Host,
} from "@stencil/core";

/**
 * @slot - Add the combination of `raul-accordion-item-header` and `raul-accordion-item-panel` in this slot.
 */
@Component({
  tag: "raul-accordion-item",
})
export class RaulAccordionItem {
  headerEl!: HTMLRaulAccordionItemHeaderElement | null;
  panelEl!: HTMLRaulAccordionItemPanelElement | null;
  accordionId = accordionIds++;

  @Element() el!: HTMLRaulAccordionItemElement;

  /**
   * Disables accordion form interaction.
   */
  @Prop({ reflect: true }) disabled = false;

  @Watch("disabled")
  disabledChanged() {
    this.updateHeaderElDisabled();
  }

  /**
   * Sets expanded state of accordion.
   */
  @Prop({ mutable: true, reflect: true }) expanded = false;

  @Watch("expanded")
  handleExpandedPropChange() {
    if (this.headerEl) {
      this.headerEl.expanded = this.expanded;
    }

    this.raulChange.emit({
      name: this.name,
      expanded: this.expanded,
    });
  }

  /**
   * Sets accordion ID.
   */
  @Prop({ reflect: true }) name = `accordion-${this.accordionId}`;

  @Watch("name")
  nameChanged() {
    this.updateHeaderElName();
    this.updatePanelElName();
  }

  /**
   * Emits when accordion is toggled
   */
  @Event() raulChange!: EventEmitter<{ name: string; expanded: boolean }>;

  componentDidLoad() {
    this.headerEl = this.el.querySelector("raul-accordion-item-header");
    this.panelEl = this.el.querySelector("raul-accordion-item-panel");

    if (this.headerEl) this.headerEl.expanded = this.expanded;
    this.updateHeaderElName();
    this.updateHeaderElDisabled();
    this.updatePanelElName();
  }

  @Listen("accordionItemHeaderClick")
  handleAccordionItemHeaderClick(e: MouseEvent) {
    if ((e.target as HTMLElement).closest("raul-accordion-item") === this.el) {
      this.expanded = !this.expanded;
    }
  }

  updateHeaderElName() {
    if (this.headerEl) this.headerEl.name = this.name;
  }

  updateHeaderElDisabled() {
    if (this.headerEl) this.headerEl.disabled = this.disabled;
  }

  updatePanelElName() {
    if (this.panelEl) this.panelEl.name = this.name;
  }

  render() {
    return (
      <Host>
        <slot />
      </Host>
    );
  }
}

let accordionIds = 0;
