:host(raul-file-uploader-status) {
  display: flex;
  align-items: center;
  column-gap: 0.75rem;
  padding: 0.75rem;
  border-radius: var(--border-radius-small);
  background-color: var(--color-neutral-lightest);
  font-family: var(--font-family-roboto);
  font-size: var(--font-size-2);

  .raul-file-uploader-status {
    &--content {
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    &--heading-bar {
      display: flex;
      align-items: center;
      justify-content: space-between;
      column-gap: 0.75rem;

      button {
        background-color: transparent;
        border: none;
        cursor: pointer;
        padding: 0;
        font-size: var(--font-size-2);
        color: var(--color-base);
      }
    }
  }
  &::part(icon) {
    font-size: var(--font-size-2);
  }

  &::part(file-name) {
    font-weight: var(--font-weight-bold);
  }

  &::part(progress) {
    margin-top: 0.25rem;
  }
}

:host(raul-file-uploader-status[state="success"]) {
  &::part(icon) {
    color: var(--color-success);
  }
}

:host(raul-file-uploader-status[state="error"]) {
  background-color: var(--color-destructive-highlight);

  &::part(icon) {
    color: var(--color-destructive);
    font-size: var(--font-size-3);
  }
}
