raul-column-sorter {
  @apply inline-block align-middle;

  .r-column__sorter {
    @apply flex flex-col ml-1 px-1;

    &__icon {
      @apply block w-0 h-0 border-l-4 border-r-4 my-px;
      border-left-color: transparent;
      border-right-color: transparent;

      &--ascending {
        @apply border-b-4;
        border-bottom-color: theme("colors.gray.dark");

        &.r-column__sorter__icon--active {
          border-bottom-color: theme("colors.primary.default");
        }
      }

      &--descending {
        @apply border-t-4;
        border-top-color: theme("colors.gray.dark");

        &.r-column__sorter__icon--active {
          border-top-color: theme("colors.primary.default");
        }
      }
    }
  }
}
