@import "../../../styles/mixins/focus.scss";

$button-variants: (
  "primary": (
    "bg": var(--color-primary),
    "border": var(--color-primary),
    "color": var(--white),
    "outline": var(--color-primary),
    "hover": (
      "bg": var(--color-primary-dark),
      "border": var(--color-primary-dark),
    ),
    "disabled": (
      "bg": var(--gray-light),
      "border": var(--gray-light),
      "color": var(--gray-dark),
    ),
  ),
  "secondary": (
    "bg": var(--white),
    "border": var(--color-primary),
    "color": var(--color-primary),
    "outline": var(--color-primary),
    "hover": (
      "border": var(--color-primary-dark),
      "color": var(--color-primary-dark),
    ),
    "disabled": (
      "border": var(--gray),
      "color": var(--gray-dark),
    ),
  ),
  "danger": (
    "bg": var(--color-destructive),
    "border": var(--color-destructive),
    "color": var(--white),
    "outline": var(--color-destructive),
    "hover": (
      "bg": var(--color-destructive-dark),
      "border": var(--color-destructive-dark),
    ),
    "disabled": (
      "bg": var(--gray-light),
      "border": var(--gray-light),
      "color": var(--gray-dark),
    ),
  ),
  "text": (
    "color": var(--color-primary),
    "outline": var(--color-primary),
    "hover": (
      "bg": var(--color-primary-highlight),
    ),
    "disabled": (
      "color": var(--gray-dark),
    ),
  ),
  "reverse": (
    "bg": rgba(#37474f, 0.2),
    "color": var(--white),
    "outline": var(--white),
    "hover": (
      "bg": rgba(#37474f, 0.4),
    ),
    "disabled": (
      "color": var(--gray-dark),
    ),
  ),
  "control": (
    "bg": var(--white),
    "border": var(--gray),
    "color": theme("colors.charcoal.dark"),
    "outline": var(--color-primary),
    "hover": (
      "border": var(--gray),
      "color": var(--color-primary),
    ),
    "disabled": (
      "border": var(--gray),
      "color": var(--gray-dark),
    ),
  ),
);

raul-button {
  display: inline-block;
  vertical-align: middle;

  font-size: var(--font-size-3);


  button,
  a {
    position: relative;
    padding: 0 1rem;
    width: 100%;
    min-width: 5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.375rem;
    border: 1px solid var(--transparent);
    border-radius: 9999px;
    overflow: hidden;

    font-size: var(--font-size-3);
    font-weight: var(--font-weight-medium);
    white-space: nowrap;
    transition: background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out, color 0.15s ease-in-out;

    &:disabled,
    &[aria-disabled] {
      cursor: not-allowed;
    }

    &[type="button"],
    &[type="reset"],
    &[type="submit"] {
      appearance: none;
    }

    &.r-button--icon-only {
      padding: 0;
      min-width: 2.5rem;
    }

    @include focus-visible(2px);

    raul-icon {
      font-size: var(--font-size-5);
    }
  }

  @each $variant-name, $variant-value in $button-variants {
    &[variant="#{$variant-name}"] {
      button,
      a {
        color: map-get($variant-value, "color");
        background-color: map-get($variant-value, "bg");
        border-color: map-get($variant-value, "border");

        &:hover:enabled {
          background-color: map-get(map-get($variant-value, "hover"), "bg");
          border-color: map-get(map-get($variant-value, "hover"), "border");
          color: map-get(map-get($variant-value, "hover"), "color");
        }

        &:disabled,
        &[aria-disabled] {
          background-color: map-get(
            map-get($variant-value, "disabled"),
            "bg"
          );
          border-color: map-get(
            map-get($variant-value, "disabled"),
            "border"
          );
          color: map-get(map-get($variant-value, "disabled"), "color");

          .r-ripple {
            display: none;
          }
        }

        .r-ripple {
          background-color: map-get($variant-value, "color");
        }
      }
    }
  }

  &[variant="control"] > button,
  &[variant="control"] > a {
    border-radius: 0.125rem;
  }

  &[size="small"] > button,
  &[size="small"] > a {
    height: 2rem;

    font-size: var(--font-size-2);

    raul-icon {
      font-size: var(--font-size-5);
    }
  }    
  
  &[icon="filter"],
  &[icon="edit"],
  &[icon="refresh"],
  &[icon="undo"],
  &[icon="redo"],
  &[icon="comments"],
  &[icon="email"],
  &[icon="zoom-in"],
  &[icon="zoom-out"],
  &[icon="save"],
  &[icon="settings"],
  &[icon="delete"],
  &[icon="download"],
  &[icon="upload"],
  &[icon="preview"],
  &[icon="overflow"],
  &[icon="close"],
  &[icon="sync"] {
    raul-icon {
      font-size: var(--font-size-4) !important;
    }
  }
  &[icon="right-arrow-v"],
  &[icon="left-arrow-v"],
  &[icon="today"] {
    raul-icon {
      font-size: var(--font-size-3) !important;
    }
  }
  &[icon="right-arrow"],
  &[icon="left-arrow"],
  &[icon="call"] {
    raul-icon {
      font-size:  var(--font-size-2) !important;
    }
  }
}
