import { Component, Prop, Host, h } from "@stencil/core";

/**
 * @slot - Add the main content for the accordion in this slot.
 */
@Component({
  tag: "raul-accordion-item-panel",
})
export class RaulAccordionItemPanel {
  /**
   * Sets ID of panel
   */
  @Prop({ reflect: true }) name?: string;

  render() {
    return (
      <Host id={`${this.name}-panel`}>
        <slot />
      </Host>
    );
  }
}
