import {
  Component,
  Host,
  h,
  Element,
  Event,
  EventEmitter,
  Listen,
  Prop,
  Watch,
} from "@stencil/core";
import { HTMLStencilElement } from "@stencil/core/internal";
import { positionMenu } from "../../../utils/collision";
import { MenuItemInterface } from "../../../utils/interfaces";
import { menuNavigation, VERTICAL_NAV_KEYS } from "../../../utils/keyboard";

/**
 * @slot - Place markup that will act as toggle for the menu in this slot. **NOTE: You MUST wrap any markup in this slot with the `button` element if you are not using a `raul-button` in this slot.**
 */
@Component({
  tag: "raul-menu",
  styleUrl: "raul-menu.scss",
  shadow: false,
})
export class RaulMenu {
  dropdownMenuEl?: HTMLDivElement;
  focusableToggleEl!: HTMLElement | null;
  toggleWrapperEl?: HTMLDivElement;
  initialPosition!: string;

  @Element() el!: HTMLStencilElement;

  /**
   * Enable or disable the border between menu items.
   */
  @Prop() border = false;

  /**
   * Adds a space between final and previous menu items.
   */
  @Prop() exclusive = false;

  /**
   * An array consisting of menu items.
   */
  @Prop() items: MenuItemInterface[] = [];

  /**
   * Sets position of dropdown menu.
   */
  @Prop({ reflect: true, mutable: true }) menuPosition:
    | "start-top"
    | "start-bottom"
    | "end-top"
    | "end-bottom"
    | string = "start-bottom";

  /**
   * Sets open state of the menu.
   */
  @Prop({ mutable: true, reflect: true }) open = false;

  @Watch("open")
  handleOpenPropChange(newVal: boolean) {
    if (newVal && this.dropdownMenuEl && this.toggleWrapperEl) {
      this.raulMenuOpen.emit({ value: true });
      this.menuPosition =
        positionMenu(
          this.toggleWrapperEl,
          this.dropdownMenuEl,
          this.menuPosition,
          this.initialPosition
        ) || this.menuPosition;
    } else {
      this.raulMenuClose.emit({ value: false });
    }
  }

  /**
   * Menu items text size (sm = 12px, md = 14px).
   */
  @Prop({ reflect: true }) textSize: "sm" | "md" = "sm";

  /**
   * Enable or disable text-underline on hover over menu item.
   */
  @Prop({ reflect: true }) textUnderline = true;

  /**
   * Type of variant for menu look.
   */
  @Prop({ reflect: true }) variant: "links" | "items" = "links";

  /**
   * Emitted when a menu item is clicked.
   */
  @Event() raulMenuClick!: EventEmitter<MenuItemInterface>;

  /**
   * Emitted when a menu closed.
   */
  @Event() raulMenuClose!: EventEmitter<{ value: boolean }>;

  /**
   * Emitted when a menu is opened.
   */
  @Event() raulMenuOpen!: EventEmitter<{ value: boolean }>;

  componentDidLoad() {
    this.initialPosition = this.menuPosition;
    setTimeout(() => {
      if (!this.toggleWrapperEl?.querySelector("button")) {
        this.toggleWrapperEl?.firstElementChild?.setAttribute("tabindex", "0");
      }
      if (this.toggleWrapperEl) {
        this.focusableToggleEl = this.toggleWrapperEl.querySelector(
          'button, [tabindex="0"]'
        );
      }
    });
  }

  @Listen("click")
  handleInternalClick({ target }: MouseEvent) {
    if (
      (target as HTMLElement).closest("div.raul--menu__toggle-wrapper") ===
      this.toggleWrapperEl
    ) {
      this.focusableToggleEl?.focus();
      this.open = !this.open;
    }
  }

  @Listen("focusout")
  handleInternalBlur({ relatedTarget, target }: FocusEvent) {
    const isMenuTarget = (relatedTarget as HTMLElement)?.closest("raul-menu");
    const isNotMenuTarget = isMenuTarget !== this.el;
    const isNotToggle = target !== this.focusableToggleEl;

    if (isNotMenuTarget) {
      this.open = false;

      if (this.focusableToggleEl && isNotToggle) {
        this.focusableToggleEl.focus();
      }
    }
  }

  @Listen("keydown")
  handleInternalKeydown(e: KeyboardEvent) {
    const { key, target } = e;
    if (!this.dropdownMenuEl || !this.focusableToggleEl) return;

    if (["Enter", " "].includes(key)) {
      const wrapper = (target as HTMLElement).closest(
        "div.raul--menu__toggle-wrapper"
      );
      if (
        wrapper &&
        wrapper === this.toggleWrapperEl &&
        !wrapper.querySelector("button")
      ) {
        this.open = !this.open;
      }
    }

    if (key === "Escape") {
      this.open = false;
      this.focusableToggleEl.focus();
    }

    if (key === "Tab") {
      this.focusableToggleEl.focus();
      this.open = false;
    }

    if (this.open && VERTICAL_NAV_KEYS.includes(key)) {
      menuNavigation(
        this.dropdownMenuEl,
        "raul-menu-item",
        this.focusableToggleEl,
        e
      );
    }
  }

  @Listen("scroll", { target: "document" })
  handleExternalScroll() {
    if (!this.dropdownMenuEl || !this.toggleWrapperEl) return;
    this.menuPosition = positionMenu(
      this.toggleWrapperEl,
      this.dropdownMenuEl,
      this.menuPosition,
      this.initialPosition
    );
  }

  @Listen("transitionend")
  handleTransitionEnd({ target }: TransitionEvent) {
    if (target === this.dropdownMenuEl && !this.open)
      this.menuPosition = this.initialPosition;
  }

  handleRaulMenuItemClick(e: CustomEvent, item: MenuItemInterface) {
    if (item.onClick) {
      item.onClick(e);
    }
    e.detail.target.blur();
    this.raulMenuClick.emit(item);
  }

  handleWrapperClick() {
    this.open = !this.open;
  }

  render() {
    return (
      <Host>
        <div
          ref={(el) => (this.toggleWrapperEl = el)}
          class="raul--menu__toggle-wrapper"
          aria-haspopup="true"
        >
          <slot></slot>
        </div>

        <div
          ref={(el) => (this.dropdownMenuEl = el)}
          class="raul--menu__dropdown-menu"
          role="menu"
          data-testid="raul-dropdown-menu"
        >
          {this.items?.length > 0 &&
            this.items.map((item, index) => (
              <raul-menu-item
                text={item.text}
                icon={item.icon}
                url={item.url}
                disabled={item.disabled}
                destructive={item.destructive}
                border={
                  this.exclusive && index + 1 === this.items.length
                    ? true
                    : this.border && index > 0
                    ? true
                    : false
                }
                onRaulMenuItemClick={(e) =>
                  this.handleRaulMenuItemClick(e, item)
                }
                data-testid={`menu-dropdown-item-${index}`}
              />
            ))}
        </div>
      </Host>
    );
  }
}
