import { Component, h, Prop } from "@stencil/core";
import { MenuItemInterface } from "../../../utils/interfaces";

@Component({
  tag: "raul-action",
  styleUrl: "raul-action.scss",
})
export class RaulAction {
  /**
   * Adds a space between final and previous menu items.
   */
  @Prop() exclusive = false;

  /**
   * An array consisting of `MenuItemInterface`.
   */
  @Prop() items?: MenuItemInterface[];

  /**
   * Sets discernable text on menu button.
   */
  @Prop() label = "Menu";

  /**
   * Sets position of dropdown menu.
   */
  @Prop() menuPosition:
    | "start-top"
    | "start-bottom"
    | "end-top"
    | "end-bottom" = "start-bottom";

  /**
   * Controls visibility of the menu.
   */
  @Prop({ mutable: true }) open = false;

  private openState() {
    this.open = !this.open;
  }

  render() {
    return (
      <raul-menu
        items={this.items}
        exclusive={this.exclusive}
        open={this.open}
        border={false}
        textUnderline={true}
        textSize="sm"
        variant="links"
        menu-position={this.menuPosition}
        onRaulMenuOpen={() => this.openState()}
        onRaulMenuClose={() => this.openState()}
      >
        <button
          data-testid="raul-action-button"
          class={{
            "r-action__button": true,
            "r-action__button--active": this.open,
          }}
          aria-label={this.label}
        >
          <raul-icon icon="navigation-show-more-vertical"></raul-icon>
        </button>
      </raul-menu>
    );
  }
}
