@import "../../../styles/mixins/label-styles.scss";

raul-phone-input {
  raul-dropdown::part(label) {
    display: none;
  }

  .r-form-element__label {
    @include label-styles();

    label {
      cursor: pointer;
    }
  }

  .r-form-element__wrapper {
    display: flex;
    column-gap: 1rem;
  }

  .r-form-element__dropdown-wrapper {
    width: 12rem;
    min-width: 8rem;
  }

  .r-form-element__input-wrapper {
    width: 100%;
  }

  .dropdown-item-content__text {
    flex: 1 1 0%;
    padding-right: 0.75rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
  }

  .dropdown-item-content__subtext {
    width: 5rem;
    text-align: left;
  }

  .dropdown-menu__empty {
    padding: 0.5rem 1rem;
    height: 2.5rem;
    display: flex;
    align-items: center;

    font-size: var(--font-size-2);
  }
}
