import {
  Component,
  State,
  Prop,
  Event,
  EventEmitter,
  Listen,
  h,
} from "@stencil/core";
import { TabInterface } from "../../../utils/interfaces";

@Component({
  tag: "raul-tabs",
  styleUrl: "raul-tabs.scss",
})
export class RaulTabs {
  @State() xsDevice!: boolean;

  /**
   * An array of objects representing the navigation.
   * @deprecated This prop will no longer be supported in the future.
   */
  @Prop() tabs?: TabInterface[];

  /**
   * The name key of the active tab.
   * @deprecated This prop will no longer be supported in the future.
   */
  @Prop({ reflect: true }) activeTab?: string;

  /**
   * if `true`, tabs will render as a select on mobile devices.
   * @deprecated This prop will no longer be supported in the future. Please use CSS media queries or a responsive design approach instead.
   */
  @Prop({ reflect: true }) selectOnMobile?: boolean;

  /**
   * Emitted when a tab is clicked.
   */
  @Event() raulTabChange!: EventEmitter<string>;

  connectedCallback() {
    if (this.selectOnMobile) {
      this.xsDevice = window.innerWidth < 640;
    }
  }
  @Listen("raulTabClick")
  handleRaulTabClick(event: CustomEvent) {
    const tabName = (event.target as HTMLRaulTabElement).name;

    if (tabName) {
      this.handleClick(tabName);
    }
  }
  @Listen("resize", { target: "window" })
  handleResize() {
    if (this.selectOnMobile) {
      this.xsDevice = window.innerWidth < 640;
    }
  }
  private selectOptions() {
    return this.tabs
      ? this.tabs.map((cv) => {
          return {
            value: cv.name,
            text: cv.label,
            disabled: cv.disabled,
            id: cv.id,
          };
        })
      : [];
  }

  private lastTabName: string | null = null;

  private handleClick(tabName: string) {
    if (this.lastTabName !== tabName) {
      this.raulTabChange.emit(tabName);
      this.lastTabName = tabName;
    }
  }

  private handleRaulChange(e: CustomEvent) {
    this.raulTabChange.emit(e.detail.value);
  }

  componentWillLoad() {
    if (this.selectOnMobile !== undefined) {
      console.warn('[RAUL: Deprecated] "selectOnMobile" is depricated.');
    }
    if (this.activeTab !== undefined) {
      console.warn('[RAUL: Deprecated] "activeTab" is depricated.');
    }
    if (this.tabs !== undefined) {
      console.warn('[RAUL: Deprecated] "tabs" is depricated.');
    }
  }

  render() {
    if (!(this.selectOnMobile && this.xsDevice)) {
      return (
        <div class="r-tabs" role="tablist">
          <div class="r-tabs__list">
            <slot>
              {this.tabs &&
                this.tabs.map((item) => {
                  return (
                    <raul-tab
                      disabled={item.disabled}
                      active={item.name === this.activeTab}
                      name={item.name}
                      onRaulTabClick={() => this.handleClick(item.name)}
                    >
                      {item.label}
                    </raul-tab>
                  );
                })}
            </slot>
          </div>
        </div>
      );
    } else {
      return (
        <raul-select
          options={this.selectOptions()}
          value={this.activeTab}
          onRaulChange={(e) => this.handleRaulChange(e)}
        />
      );
    }
  }
}
