import {
  Component,
  Element,
  Event,
  EventEmitter,
  Host,
  Listen,
  Prop,
  State,
  h,
} from "@stencil/core";

@Component({
  tag: "raul-file-uploader",
  styleUrl: "raul-file-uploader.scss",
  shadow: true,
})
export class RaulFileUploader {
  @Element() host!: HTMLElement;

  @State() isBeingDraggedOn = false;

  /**
   * Specifies the types of files that the server accepts (that can be submitted through a file upload). NOTE: accepted types must be comma (,) delimited and wildcards (*) are not accepted.
   */
  @Prop() accept!: string;

  /**
   * Informs the user of file restrictions.
   */
  @Prop() hintText?: string;

  /**
   * Specifies the text to inform the user of file restrictions.
   */
  @Prop() label!: string;

  /**
   * Specifies that the user is allowed to upload more than one file.
   */
  @Prop({ reflect: true }) multiple = false;

  /**
   * Specifies the name/id of the file upload element.
   */
  @Prop() name?: string;

  /**
   * Specifies upload instructions.
   */
  @Prop() uploadText?: string;

  /**
   * Fires when the value of the file uploader changes.
   */
  @Event() raulFileUploadChange!: EventEmitter<FileList>;

  @Listen("dragover", { passive: false })
  @Listen("dragleave", { passive: false })
  @Listen("drop", { passive: false })
  handleDragEvents(event: DragEvent) {
    event.preventDefault();
    event.stopPropagation();
    const { type } = event;
    if (type === "dragover" && !this.isBeingDraggedOn) {
      this.isBeingDraggedOn = true;
    }

    if (type === "dragleave" && this.isBeingDraggedOn) {
      this.isBeingDraggedOn = false;
    }

    if (type === "drop") {
      this.isBeingDraggedOn = false;
      if (event.dataTransfer) {
        const { files } = event.dataTransfer;
        const addedFiles = this.listFiles(files) as FileList;
        this.raulFileUploadChange?.emit(addedFiles);
        return;
      }
    }
  }

  handleInputChange(event: Event | DragEvent) {
    const { files } = event.target as HTMLInputElement;
    if (files) {
      const addedFiles = this.listFiles(files) as FileList;
      this.raulFileUploadChange?.emit(addedFiles);
    }
  }

  listFiles(files: FileList) {
    if (!this.accept) {
      return files;
    }

    const acceptedFiles = new Set(
      this.accept.split(",").map((type) => type.trim())
    );
    return Array.prototype.filter.call(
      files,
      ({ name, type: mimeType = "" }) => {
        const fileExtensionRegExp = /\.[^.]+$/;
        const hasFileExtension = fileExtensionRegExp.test(name);
        const [fileExtension] = !hasFileExtension
          ? [undefined]
          : fileExtensionRegExp.exec(name) ?? [];

        return (
          acceptedFiles.has(mimeType) ||
          (fileExtension && acceptedFiles.has(fileExtension))
        );
      }
    );
  }

  render() {
    return (
      <Host>
        <div part="wrapper">
          {this.label ? <span part="label">{this.label}</span> : null}
          {this.hintText ? <span part="hint">{this.hintText}</span> : null}
          <label
            htmlFor={this.name || "file-upload"}
            tabindex="0"
            data-testid="file-input"
          >
            <div
              part="drop-area"
              role="button"
              class={{
                "raul-file-uploader--drop-area": true,
                "raul-file-uploader--drop-area--active": this.isBeingDraggedOn,
              }}
            >
              <raul-icon
                icon="upload-bottom"
                kind="consumer"
                part="icon"
              ></raul-icon>
              <span>
                {this.uploadText || "Drag files or click here to upload"}
              </span>
              <input
                type="file"
                id={this.name || "file-upload"}
                name={this.name || "file-upload"}
                tabindex="-1"
                accept={this.accept}
                multiple={this.multiple}
                onChange={(event) => this.handleInputChange(event)}
              />
            </div>
          </label>
        </div>
        <slot></slot>
      </Host>
    );
  }
}
