@import "../../../styles/mixins/focus.scss";

raul-checkbox {
  display: inline-flex;
  align-items: center;
  column-gap: 0.25rem;
  vertical-align: middle;
  font-size: var(--font-size-3);

  .r-checkbox__label {
    display: flex;
    align-items: center;
    position: relative;

    input[type="checkbox"] {
      position: absolute;
      height: 0;
      opacity: 0;
      width: 0;
      z-index: -10;
      box-sizing: border-box;
      padding: 0;

      &:checked,
      &:indeterminate {
        & ~ .r-checkbox__label__icon {
          border-color: var(--color-primary);
          background-color: var(--color-primary);
        }
      }

      &:checked ~ .r-checkbox__label__icon::after {
        border-top-width: 2px;
        border-left-width: 2px;
        position: absolute;
        border-color: var(--color-background);

        width: 0.4375rem;
        height: 0.8125rem;
        left: 0.4375rem;
        top: 0.875rem;

        content: "";
        transform: rotate(-135deg);
        transform-origin: 0 0;
      }

      &:indeterminate ~ .r-checkbox__label__icon::after {
        content: "";
        position: absolute;
        border-color: var(--color-background);
        border-top-width: 2px;
        width: 0.625rem;
        height: 2px;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }

      &:disabled {
        & ~ .r-checkbox__label__icon {
          border-color: var(--color-neutral-light);
          background-color: var(--color-neutral-light);
          cursor: not-allowed;
        }

        &:checked,
        &:indeterminate {
          & ~ .r-checkbox__label__icon::after {
            border-color: var(--color-neutral-dark);
          }
        }
      }

      &:focus-visible ~ .r-checkbox__label__icon {
        outline-color: var(--color-primary);
        outline-offset: 2px;
        outline-style: solid;
        outline-width: 2px;
      }
    }

    &__icon {
      background-color: var(--color-background);
      border-width: 1px;
      border-color: var(--color-neutral);
      flex: none;
      position: relative;
      border-radius: 0.125rem;

      width: 1.25rem;
      height: 1.25rem;

      cursor: pointer;
    }

    &__text {
      font-size: var(--font-size-3);
      margin-left: 0.5rem;
      word-break: break-all;

      cursor: pointer;
    }

    &:has(input[type="checkbox"]:disabled) {
      .r-checkbox__label__text {
        cursor: not-allowed;
      }
    }
  }

  &[small] {
    .r-checkbox__label {
      &__icon {
        height: 0.875rem;
        width: 0.875rem;
      }

      input[type="checkbox"] {
        &:checked ~ .r-checkbox__label__icon::after {
          width: 0.3125rem;
          height: 0.625rem;
          left: 0.25rem;
          top: 0.625rem;
        }

        &:indeterminate ~ .r-checkbox__label__icon::after {
          width: 0.4375rem;
        }
      }
    }
  }

  &[invalid] {
    .r-checkbox__label__icon {
      border-style: solid;
      border-color: var(--color-destructive);
    }
  }

  *[data-label-action] {
    @include focus-visible;
  }
}
