raul-step-tracker {
  display: block;
  width: 100%;
  padding: 1.5rem;
  background-color: var(--color-neutral-lightest);

  .r-step-tracker {
    display: flex;

    raul-step:not(:first-child) {
      margin-left: 2px;
    }

    raul-step:first-of-type .r-step {
      padding-left: 2rem;
      border-top-left-radius: 0.125rem;
      border-bottom-left-radius: 0.125rem;

      &:before {
        content: unset;
      }
    }

    raul-step:last-of-type .r-step {
      padding-right: 2rem;
      border-top-right-radius: 0.125rem;
      border-bottom-right-radius: 0.125rem;

      &:after {
        content: unset;
      }
    }
  }
}

@media (max-width: 640px) {
  raul-step-tracker:not([compact]) {
    padding: 1rem 2rem 1rem 2rem;

    .r-step-tracker {
      margin-left: -12px;

      raul-step:not([current]) {
        display: none;
      }

      raul-step[current] {
        border-top-left-radius: 0.125rem;
        border-bottom-left-radius: 0.125rem;

        &:before {
          content: unset;
        }
      }
    }
  }
}

raul-step-tracker[compact] {
  padding: 1rem 1.5rem;
  font-size: 0.875rem;

  @media (min-width: 640px) {
    padding: 0;
    background-color: transparent;
  }

  .r-step-tracker {
    column-gap: 0.5rem;
    padding-top: 1rem;

    raul-step:first-of-type .r-step,
    raul-step:last-of-type .r-step {
      border-radius: 9999px;
    }
  }
}

raul-step .r-step {
  display: flex;
  position: relative;
  align-items: center;
  width: 100%;
  height: 2rem;
  padding-right: 1.5rem;
  border-radius: 0;
  font-weight: 500;
  font-size: var(--font-size-2);
  text-align: center;
  white-space: nowrap;
}
