import { Instance as FpInstance } from "flatpickr/dist/types/instance";
import { Plugin } from "flatpickr/dist/types/options";

export default (isDisabled: boolean, isLoading = false): Plugin =>
  (picker: FpInstance) => {
    const setCalendarClasses = () => {
      const {
        calendarContainer,
        monthsDropdownContainer,
        yearElements,
        days,
        config,
      } = picker;

      calendarContainer.classList.add("date-picker--calendar-container");
      const monthContainer =
        calendarContainer.querySelector(".flatpickr-months");
      monthContainer?.classList.add("date-picker--calendar-months");

      const weekdays = calendarContainer.querySelectorAll(".flatpickr-weekday");
      weekdays.forEach((item) => {
        item.innerHTML = item.innerHTML.replace(/\s+/g, "");
        item.classList.add("date-picker--calendar-weekday");
      });

      if (isDisabled) {
        if (!config.static) calendarContainer.setAttribute("hidden", "");

        days.parentElement?.addEventListener(
          "click",
          (e) => e.stopPropagation(),
          true
        );
      }

      if (config.static) calendarContainer.setAttribute("tabindex", "0");

      // Set testid on elements For testing
      calendarContainer.setAttribute("data-testid", "date-picker-calendar");
      monthsDropdownContainer.setAttribute(
        "data-testid",
        "date-picker-calendar-months-select"
      );
      if (yearElements[0])
        yearElements[0].setAttribute(
          "data-testid",
          "date-picker-calendar-year-input"
        );
    };
    const addControls = () => {
      const { calendarContainer, input, config }: any = picker;

      if (!config.static) {
        const labelText = input.parentNode.querySelector("label")?.innerHTML;

        const header = `
        <div class="date-picker--header-controls">
          <button data-close type="text">Cancel</button>
          <span>${labelText}</span>
          <button data-close type="text">Done</button>
        </div>
      `;
        const footer = `
        <div class="date-picker--footer-controls">
          <raul-button data-clear="true" variant="primary">Clear</raul-button>
        </div>
      `;

        calendarContainer.insertAdjacentHTML("afterbegin", header);
        calendarContainer.insertAdjacentHTML("beforeend", footer);

        if (isLoading) {
          const loaderHTML = `
          <raul-container-loader data-testid="loader-container"></raul-container-loader>`;
          calendarContainer.insertAdjacentHTML("beforeend", loaderHTML);
        }
      }
    };

    const register = () => {
      picker.loadedPlugins.push("raulPlugin");
    };
    return {
      onMonthChange: setCalendarClasses,
      onYearChange: setCalendarClasses,
      onValueUpdate: setCalendarClasses,
      onChange: setCalendarClasses,
      onOpen: setCalendarClasses,
      onReady: [register, setCalendarClasses, addControls],
    };
  };
