@import "../../../styles/mixins/focus.scss";

raul-chip {
  position: relative;
  display: inline-flex;
  align-items: flex-start;
  background-color: var(--color-primary-highlight);
  font-size: var(--font-size-2);
  font-weight: 500;
  color: var(--color-base);
  text-align: left;
  line-height: var(--line-height-tight);
  border-radius: 0.125rem;
  padding: 0.25rem 0.5rem 0.25rem 0.5rem;
  max-width: 100%;
  &[is-removed] {
    display: none;
  }

  .r-chip__remove-button {
    margin: 0.06rem 0rem 0rem 0.5rem;
    font-size: var(--font-size-2);
    width: 0.75rem;
    height: 0.75rem;
    display: flex;
    align-items: center;
    justify-content: center;

    @include focus-visible(2px);
  }

  .r-chip__remove-icon {
    margin: 0.25rem;
    padding: 0.25rem;
  }
}
