@import "../../../../styles/mixins/focus.scss";

:host(raul-dropdown-item) {
  &::part(option) {
    box-sizing: border-box;
    padding: 0.5rem 1rem;
    width: 100%;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 0;
    border-bottom: 1px solid var(--dropdown-item-border-color);
    text-align: left;

    background-color: var(--dropdown-item-background-color);
    color: var(--dropdown-item-text-color);
    cursor: pointer;
    font-family: var(--font-family-roboto);
    font-size: var(--font-size-2);

    &:hover {
      background-color: var(--dropdown-item-hover-background-color);
    }

    @include focus(-2px);
  }

  span:not([part]) {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &::part(check) {
    position: relative;
    width: 0.875rem;
    height: 0.875rem;
    display: block;
    border-width: 1px;
    border-style: solid;
    flex: 0 0 auto;
  }
}

:host(raul-dropdown-item[heading])::part(option) {
  padding-top: 1.5rem;
  padding-bottom: 1rem;
  height: 3.5rem;
  font-weight: var(--font-weight-semibold);

  &:hover {
    background-color: var(--dropdown-item-hover-background-color);
  }
}

:host(raul-dropdown-item[grouped])::part(option) {
  padding-left: 1.5rem;
}

:host(raul-dropdown-item:not([multi]))::part(check) {
  border-color: transparent;
}

:host(raul-dropdown-item[selected]:not([multi]))::part(check)::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 0.25rem;
  height: 0.625rem;
  display: block;
  border: 1px solid var(--color-primary);
  border-bottom-width: 0;
  border-right-width: 0;
  transform: rotate(-138deg);
}

:host(raul-dropdown-item[multi])::part(check) {
  border-color: var(--color-neutral);
  background-color: var(--color-background);
}

:host(raul-dropdown-item[selected][multi])::part(check) {
  border-color: var(--color-primary);
  background-color: var(--color-primary);

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0.25rem;
    width: 0.25rem;
    height: 0.625rem;
    display: block;
    border: 2px solid var(--white);
    border-bottom-width: 0;
    border-right-width: 0;
    transform: rotate(-138deg);
  }
}

:host(raul-dropdown-item[indeterminate][multi])::part(check) {
  border-color: var(--color-primary);
  background-color: var(--color-primary);

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 0.5rem;
    border-top: 2px solid var(--white);
  }
}

:host(raul-dropdown-item[disabled]) {
  &::part(option) {
    color: var(--color-neutral-dark);
    cursor: not-allowed;

    &:hover {
      background-color: var(--dropdown-menu-background-color);
    }
  }
}

:host(raul-dropdown-item[multi][disabled])::part(check) {
  border-color: var(--color-neutral-light);
  background-color: var(--color-neutral-light);
}

:host(raul-dropdown-item[selected][multi][disabled])::part(check)::after,
:host(raul-dropdown-item[indeterminate][multi][disabled])::part(check)::after {
  border-color: var(---color-neutral-dark);
}

:host(raul-dropdown-item[hidden]) {
  display: none;
}
