@import "~suneditor/dist/css/suneditor.min.css";

raul-text-editor {
  .sun-editor {
    @apply border-none;

    .se-toolbar {
      @apply p-0 outline-none bg-transparent;

      .se-btn-module-border {
        border: 1px solid theme("colors.gray.medium");
      }
    }

    .se-resizing-bar {
      @apply hidden;
    }

    .se-wrapper {
      .se-wrapper-inner {
        @apply border border-gray-medium block text-md px-3 py-2 w-full;
        -webkit-appearance: none;
        height: 5rem !important;
        min-height: theme("spacing.10");
        border-radius: var(
          --raul-input-border-radius,
          theme("borderRadius.sm")
        );
        background-color: var(--raul-input-bg, theme("colors.white"));
        color: var(--raul-input-color, theme("colors.base"));

        &:focus {
          @apply border-primary outline-none;
        }
      }

      .se-placeholder {
        @apply text-gray-dark;
        top: 8px;
        left: 12px;
        padding: inherit;
        font-size: inherit;
      }
    }
  }

  .r-text-editor-disabled {
    .sun-editor {
      .se-wrapper {
        .se-wrapper-inner {
          @apply bg-gray-light border-gray-light text-gray-dark cursor-not-allowed;
        }
      }
    }
  }

  .r-text-editor-invalid {
    .sun-editor {
      .se-wrapper {
        .se-wrapper-inner {
          @apply border-destructive;
        }
      }
    }
  }
}
