import { Component, h, Prop } from "@stencil/core";
/**
 * @deprecated - Use raul-dropdown-item-group instead.
 *
 * @slot - Place grouped `raul-options` in this slot.
 */
@Component({
  tag: "raul-option-group",
})
export class RaulOptionGroup {
  /**
   * Adds a checkbox to the group heading.
   */
  @Prop() multiple = false;
  /**
   * Sets selected state on the group heading.
   */
  @Prop() selected = false;
  /**
   * Sets focus state on the group heading.
   */
  @Prop() focused = false;
  /**
   * Sets disabled state on the group heading.
   */
  @Prop() disabled = false;
  /**
   * Sets value of option on the group heading.
   */
  @Prop() value?: string;
  /**
   * Sets text label on the group heading.
   */
  @Prop() text?: string;
  /**
   * Sets the `multiple` checkbox to indeterminate on the group heading.
   */
  @Prop() indeterminate = false;
  /**
   * Sets `id` attribute on internal button in the group heading.
   */
  @Prop() optionId?: string;

  render() {
    return (
      <div class="r-options-group" role="group">
        <raul-option
          variant="group"
          multiple={this.multiple}
          selected={this.selected}
          focused={this.focused}
          disabled={this.disabled}
          value={this.value}
          text={this.text}
          indeterminate={this.indeterminate}
          optionId={this.optionId}
        />
        <slot />
      </div>
    );
  }
}
