@import "../../../styles/mixins/focus.scss";

raul-input {
  display: block;
  font-size: var(--font-size-3);

  .r-form-element {
    &__control {
      display: flex;
      align-items: center;
      font-size: var(--font-size-3);
      height: 2.5rem;
      line-height: var(--line-height-tight);
      flex-grow: 1;
      position: relative;
      border: 1px solid var(--color-neutral);
      border-radius: 0.125rem;
      background-color: var(--color-background);

      &:focus-within {
        border-color: var(--color-primary);
        outline: 0;
      }

      input[type="text"],
      input[type="password"],
      input[type="email"],
      input[type="number"],
      input[type="search"],
      input[type="tel"],
      input[type="url"] {
        display: block;
        width: 100%;
        font-size: 1rem;
        height: 2.5rem;
        padding: 0rem 0.75rem 0rem 0.75rem;
        background-color: var(--transparent);
        flex: 1 1 auto;
        -webkit-appearance: none;

        &::placeholder {
          color: var(--color-neutral-dark);
        }

        &[disabled] {
          background-color: var(--color-neutral-light);
          border-color: var(--color-neutral-light);
          color: var(--color-neutral-dark);
          cursor: not-allowed;
        }

        &:-webkit-autofill,
        &:-webkit-autofill:hover,
        &:-webkit-autofill:focus {
          transition: background-color 600000s 0s, color 600000s 0s;
        }

        @media (min-width: 640px) {
          font-size: var(--font-size-3);
        }
      }

      &--disabled {
        cursor: not-allowed;
        border-color: var(--color-neutral-light);
      }
    }

    &__label {
      display: inline-flex;
      align-items: center;
      column-gap: 0.25rem;
      font-size: var(--font-size-2);
      font-weight: var(--font-weight-medium);
      margin-bottom: 0.25rem;

      & > label {
        cursor: pointer;
      }
    }

    &__hint {
      display: block;
      font-size: var(--font-size-2);
      margin-top: 0.5rem;
    }

    &__error {
      display: flex;
      font-size: var(--font-size-2);
      align-items: center;
      color: var(--color-destructive);
      margin-top: 0.5rem;

      &__icon {
        font-size: var(--font-size-4);
        margin-right: 0.25rem;
      }
    }

    &--invalid {
      .r-form-element__control {
        border-color: var(--color-destructive);

        &:focus-within {
          border-color: var(--color-primary);
        }
      }
    }

    &--search {
      input[type="text"],
      input[type="password"],
      input[type="email"],
      input[type="number"],
      input[type="search"],
      input[type="tel"],
      input[type="url"] {
        padding-left: 0;
        padding-right: 0;
      }

      .r-form-element__control__search-icon,
      .r-form-element__control__clear-search-icon {
        color: var(--color-neutral-dark);
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        flex: 0 0 auto;
        align-items: center;
        justify-content: center;
        font-size: var(--font-size-11);
      }

      .r-form-element__control__clear-search-icon {
        font-size: var(--font-size-3);
        left: auto;

        .focus-visible:focus {
          outline: -2px solid;
        }
      }
    }

    &--symbol {
      &-prepend {
        input[type="text"],
        input[type="password"],
        input[type="email"],
        input[type="number"],
        input[type="search"],
        input[type="tel"],
        input[type="url"] {
          padding-left: 1.75rem;
          text-align: right;

          &::placeholder {
            text-align: right;
          }

          &::-webkit-input-placeholder {
            text-align: right;
          }
        }
      }

      &-append {
        input[type="text"],
        input[type="password"],
        input[type="email"],
        input[type="number"],
        input[type="search"],
        input[type="tel"],
        input[type="url"] {
          padding-right: 0.75rem;
        }
      }

      &-prepend,
      &-append {
        .r-form-element__control__symbol-prepend,
        .r-form-element__control__symbol-append {
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 0.75rem;
        }

        .r-form-element__control__symbol-append {
          left: auto;
          right: 0.75rem;
        }
      }
    }

    &--compact:not(.r-form-element--search) {
      position: relative;

      input[type="text"],
      input[type="password"],
      input[type="email"],
      input[type="number"],
      input[type="search"],
      input[type="tel"],
      input[type="url"] {
        &::placeholder {
          color: var(--transparent);
        }
      }
    }

    &--focused {
      input[type="text"],
      input[type="password"],
      input[type="email"],
      input[type="number"],
      input[type="search"],
      input[type="tel"],
      input[type="url"] {
        &::placeholder {
          color: var(--color-neutral-dark) #{!important};
        }
      }
    }

    /* clears the 'X' for browsers*/
    input[type="search"]::-ms-clear,
    input[type="search"]::-ms-reveal,
    input[type="text"]::-ms-clear,
    input[type="text"]::-ms-reveal {
      display: none;
      width: 0;
      height: 0;
    }

    input[type="search"]::-webkit-search-decoration,
    input[type="search"]::-webkit-search-cancel-button,
    input[type="search"]::-webkit-search-results-button,
    input[type="search"]::-webkit-search-results-decoration,
    input[type="text"]::-webkit-search-decoration,
    input[type="text"]::-webkit-search-cancel-button,
    input[type="text"]::-webkit-search-results-button,
    input[type="text"]::-webkit-search-results-decoration {
      display: none;
    }
  }

  *[data-label-action] {
    @include focus-visible;
  }
}

raul-input[compact] {
  .r-form-element__label {
    position: absolute;
    top: 0.625rem;
    left: 8px;
    padding: 0 4px;
    margin-bottom: 0;
    z-index: 2;
    background-color: var(--color-background);
    font-size: var(--font-size-3);
    font-weight: var(--font-weight-normal);
    transition: all 0.15s ease;

    &--float {
      top: -9px;
      font-size: var(--font-size-2);
      font-weight: var(--font-weight-medium);
    }
  }
}
