import { Component, h, Host, Element } from "@stencil/core";
/**
 * @slot - Place `raul-grid-row` component with `raul-grid-cell` components in this slot.
 */
@Component({
  tag: "raul-grid-header",
  styleUrl: "raul-grid-header.scss",
})
export class RaulGridHeader {
  @Element() el!: HTMLElement;

  componentDidLoad() {
    const headerCells = this.el.querySelectorAll("raul-grid-cell");
    headerCells.forEach((cell: HTMLElement) =>
      cell.setAttribute("role", "columnheader")
    );
  }

  render() {
    return (
      <Host role="rowgroup">
        <slot />
      </Host>
    );
  }
}
