raul-container-loader {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
  background-color: rgba(255, 255, 255, 0.8);

  .r-container-loader__dots-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    opacity: 1;
    width: 3rem
  }

  .r-container-loader__dot {
    background-color: var(--color-neutral-light);
    border-radius: 9999px;
    width: 0.625rem;
    height: 0.625rem;
    animation: color-change 1s infinite linear;
  }
}

@keyframes color-change {
  0% {
    background-color: var(--color-primary);
  }

  20% {
    background-color: var(--color-primary);
  }

  50% {
    background-color: var(--color-neutral-light);
  }

  100% {
    background-color: var(--color-neutral-light);
  }
}