import {
  Component,
  h,
  Prop,
  State,
  Listen,
  EventEmitter,
  Event,
  Method,
  Watch,
} from "@stencil/core";
import { isScreen } from "../../../utils/dom";
import debounce from "lodash/debounce";
import Swiper, { Navigation, Pagination, SwiperOptions } from "swiper";

/**
 * @slot - Place `raul-carousel-slide` components in this slot.
 */

@Component({
  tag: "raul-carousel",
  styleUrl: "raul-carousel.scss",
})
export class RaulCarousel {
  private swiperInstance!: Swiper | null;
  private swiperContainerEl: HTMLDivElement | undefined;
  private breakpointsValues: SwiperOptions["breakpoints"] = {
    640: {
      slidesPerView: 2,
      slidesPerGroup: 2,
    },
    1000: {
      slidesPerView: 3,
      slidesPerGroup: 3,
    },
  };
  private paginationValue: SwiperOptions["pagination"] = {
    el: ".r-carousel__pagination",
    bulletClass: "r-carousel__pagination__bullet",
    bulletActiveClass: "r-carousel__pagination__bullet--active",
    modifierClass: "r-carousel__pagination__",
    clickableClass: "r-carousel__pagination__clickable",
    clickable: true,
    type: "custom",
    renderCustom: (_, current, total) => {
      let paging = ``;
      for (let i = 1; i <= total; i++) {
        const isCurrent = current === i;
        const className = `r-carousel__pagination__bullet${
          isCurrent ? " r-carousel__pagination__bullet--active" : ""
        }`;
        paging += `<button type="button" class="${className}" aria-label="Go to slide ${i}" aria-current="${
          isCurrent ? "true" : "false"
        }"></button>`;
      }

      return paging;
    },
  };
  private updateSwiperTimeout;

  @State() isEnd = false;
  @State() isStart = true;
  @State() isMobile = false;

  /**
   *  Number of items per page to display in the carousel.
   */
  @Prop() itemsPerPage = 3;

  /**
   * Enables padding around the carousel.
   */
  @Prop() padding = false;

  /**
   * Enables border  around the carousel.
   */
  @Prop() border = false;

  /**
   * If `true` Pagination will be enabled.
   */
  @Prop() pagination = false;

  /**
   * Group slides 2 by 2 for displays resolutions > 640px and 3 by 3 for displays resolutions > 1000px, on mobile only 1 slide will be visible.
   */
  @Prop() breakpoints = false;

  /**
   * If `true` Navigation Controls buttons will be enabled.
   */
  @Prop() navigationControls = false;

  /**
   * If `true` Navigation Controls buttons will be visible on hover.
   */
  @Prop() navigationControlsHover = false;

  /**
   * Index number of initial slide when the carousel is initialized.
   */
  @Prop() initialSlide = 0;

  /**
   * Slider change.
   */
  @Event() raulSlideChange!: EventEmitter<{ currentSlide: number }>;

  /**
   * Slider reached the start.
   */
  @Event() raulSlideReachedStart!: EventEmitter<{ currentSlide: number }>;

  /**
   * Slider reached the end.
   */
  @Event() raulSlideReachedEnd!: EventEmitter<{ currentSlide: number }>;

  @Watch("itemsPerPage")
  updateBreakpoints() {
    this.breakpointsValues = {
      ...this.breakpointsValues,
      1000: {
        slidesPerView: this.itemsPerPage,
        slidesPerGroup: this.itemsPerPage,
      },
    };
  }

  componentWillLoad() {
    this.handleDebouncedResize = debounce(this.handleDebouncedResize, 200);
    this.isMobile = !isScreen("sm");
    this.updateBreakpoints();
  }
  connectedCallback() {
    this.initializeSwiper();
  }

  componentDidLoad() {
    this.initializeSwiper();
  }

  initializeSwiper() {
    if (this.swiperInstance) return;
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const selfRaulCarousel: RaulCarousel = this;

    Swiper.use([Navigation, Pagination]);

    if (!this.swiperContainerEl) return;
    this.swiperInstance = new Swiper(this.swiperContainerEl, {
      wrapperClass: "r-carousel__wrapper",
      setWrapperSize: true,
      slideClass: "r-carousel__slide",
      slideActiveClass: "r-carousel__slide--active",
      slideNextClass: "r-carousel__slide--next",
      slidePrevClass: "r-carousel__slide--prev",
      pagination: this.pagination ? this.paginationValue : {},
      breakpoints: selfRaulCarousel.breakpoints
        ? this.breakpointsValues
        : undefined,
      observer: true,
      roundLengths: true,
      initialSlide: this.initialSlide,
      a11y: {
        enabled: true,
      },
      on: {
        init() {
          selfRaulCarousel.disableButtons(
            (this as unknown as Swiper).isBeginning,
            (this as unknown as Swiper).isEnd,
          );
        },
        slideChange() {
          selfRaulCarousel.disableButtons(
            (this as unknown as Swiper).isBeginning,
            (this as unknown as Swiper).isEnd,
          );
          selfRaulCarousel.raulSlideChange.emit({
            currentSlide: (this as unknown as Swiper).activeIndex,
          });

          if ((this as unknown as Swiper).isBeginning) {
            selfRaulCarousel.raulSlideReachedStart.emit({
              currentSlide: (this as unknown as Swiper).activeIndex,
            });
          }

          if ((this as unknown as Swiper).isEnd) {
            selfRaulCarousel.raulSlideReachedEnd.emit({
              currentSlide: (this as unknown as Swiper).activeIndex,
            });
          }
        },
        resize() {
          if (!selfRaulCarousel.swiperInstance) return;
          selfRaulCarousel.disableButtons(
            (this as unknown as Swiper).isBeginning,
            (this as unknown as Swiper).isEnd,
          );
          selfRaulCarousel.swiperInstance.pagination.update();
        },
      },
    });

    /*
      On first init with multiple instances of swiper the width of slides is wrong,
      trigger update to calculate the width again.
    */
    this.updateSwiperTimeout = setTimeout(() => {
      if (this.swiperInstance) this.swiperInstance.update();
    });
  }

  disconnectedCallback() {
    if (this.swiperInstance) {
      this.swiperInstance.destroy(true);
      this.swiperInstance = null;
    }
    clearTimeout(this.updateSwiperTimeout);
  }

  @Listen("resize", { target: "window" })
  handleDebouncedResize() {
    this.isMobile = !isScreen("sm");
  }

  /**
   * Go to the next slide.
   */
  @Method()
  async goToNextSlide() {
    this.swiperInstance && this.swiperInstance.slideNext();
  }

  /**
   * Go to previous slide.
   */
  @Method()
  async goToPreviousSlide() {
    this.swiperInstance && this.swiperInstance.slidePrev();
  }

  /**
   * Go to a specific slide.
   */
  @Method()
  async goToSlide(slide: number) {
    this.swiperInstance && this.swiperInstance.slideTo(slide);
  }

  private disableButtons(start: boolean, end: boolean) {
    this.isEnd = end;
    this.isStart = start;
  }

  render() {
    const renderPagination = () => {
      return (
        this.pagination && (
          <div
            class={{
              "r-carousel__pagination__container": true,
              "r-carousel__pagination__container--overlap": !this.padding,
            }}
          >
            <div data-testId="pagination" class="r-carousel__pagination"></div>
          </div>
        )
      );
    };

    return (
      <div
        class={{
          "r-carousel--navigation-hover": this.navigationControlsHover,
          "r-carousel--border": this.border,
          "r-carousel--padding": this.padding && !this.isMobile,
          "r-carousel--padding-mobile": this.padding && this.isMobile,
        }}
      >
        <div
          class={{
            "r-carousel__container": true,
            "r-carousel__container--padding": this.padding,
            "r-carousel__container--full": this.isMobile || !this.breakpoints,
          }}
          ref={(el) => (this.swiperContainerEl = el)}
        >
          <div class="r-carousel__wrapper">
            <slot></slot>
          </div>

          {renderPagination()}
        </div>

        {this.navigationControls && !this.isMobile && (
          <button
            class={{
              "r-carousel__button-prev": true,
              "r-carousel__button-disabled": this.isStart,
            }}
            type="button"
            data-testId="prev-button"
            disabled={this.isStart}
            aria-label="Previous Slide"
            onClick={() => this.goToPreviousSlide()}
          >
            <raul-icon icon="arrow-left-v"></raul-icon>
          </button>
        )}

        {this.navigationControls && !this.isMobile && (
          <button
            class={{
              "r-carousel__button-next": true,
              "r-carousel__button-disabled": this.isEnd,
            }}
            type="button"
            data-testId="next-button"
            disabled={this.isEnd}
            aria-label="Next Slide"
            onClick={() => this.goToNextSlide()}
          >
            <raul-icon icon="arrow-right-v"></raul-icon>
          </button>
        )}
      </div>
    );
  }
}
