@import "../../../styles/mixins/focus.scss";

raul-carousel {
  display: block;
  position: relative;
  font-size: var(--font-size-3);

  .r-carousel {
    &__container {
      overflow: hidden;
      margin-left: auto;
      margin-right: auto;
      padding: 0;
      position: relative;
      list-style-type: none;
      width: calc(100% - 36px);

      &--full {
        width: 100%;

        .r-carousel__slide__container {
          padding: 0;
        }
      }

      &--padding {
        padding-bottom: 3rem;
      }
    }

    &--border {
      border-width: 1px;
      border-color: var(--color-neutral-light);
      border-style: solid;
      border-radius: 0.25rem;
    }

    &--padding {
      padding: 2rem 2rem 0rem 2rem;

      &-mobile {
        padding: 1.5rem 1.5rem 0rem 1.5rem;
      }
    }

    &__wrapper {
      position: relative;
      width: 100%;
      height: 100%;
      display: flex;
    }

    &__slide {
      position: relative;
      width: 100%;
      height: 100%;

      &__container {
        padding-left: 0.75rem;
        padding-right: 0.75rem;
        width: 100%;
        display: block;

        &__card {
          border-style: solid;
          border-color: var(--color-neutral-light);
          border-width: 1px;
          border-radius: 0.25rem;
          padding: 1.5rem;
          display: block;
        }
      }
    }

    $navButtonSize: 2rem;

    &__pagination {
      &__container {
        display: flex;
        position: absolute;
        width: 100%;
        align-items: center;
        justify-content: center;
        margin-top: 1rem;

        &--overlap {
          margin-top: 2rem;
          z-index: 10;
        }

        .r-carousel__button-prev--small,
        .r-carousel__button-next--small {
          border-color: var(--color-neutral-light);
          background-color: var(--color-background);
          color: var(--color-primary);
          z-index: 10;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          border-radius: 9999px;
          opacity: 1;
          height: $navButtonSize;
          width: $navButtonSize;
          transition: opacity 0.25s;

          &:hover {
            border-style: solid;
            border-color: var(--gray-light);
          }
        }

        .r-carousel__button--small-disabled {
          opacity: 0;
          pointer-events: none;
          transition: opacity 0.25s;
        }

        .r-carousel__button-prev--small {
          margin-right: 0.5rem;
        }

        .r-carousel__button-next--small {
          margin-left: -0.5rem;
        }
      }

      &__bullet {
        background-color: var(--color-neutral);
        position: relative;
        display: inline-block;
        border-radius: 9999px;
        width: 0.5rem;
        height: 0.5rem;
        margin: 0rem 0.25rem 0rem 0.25rem;

        &:hover {
          cursor: pointer;
        }

        &--active {
          background-color: var(--color-primary);
        }

        @include focus-visible(2px);
      }
    }

    &__button-next,
    &__button-prev {
      border-width: 1px;
      border-style: solid;
      border-color: var(--color-neutral-light);
      position: absolute;
      background-color: var(--color-background);
      color: var(--color-primary);
      z-index: 10;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 50%;
      border-radius: 9999px;
      opacity: 1;
      pointer-events: auto;
      height: 2rem;
      width: 2rem;
      transform: translateY(-50%);
      height: $navButtonSize;
      width: $navButtonSize;

      &:disabled {
        opacity: 0;
        pointer-events: none;
        transition: opacity 0.25s;
        visibility: hidden;
      }

      @include focus-visible(2px);
    }

    &__button-prev {
      left: 16px;
    }

    &__button-next {
      right: 16px;
    }
  }
}

.r-carousel--navigation-hover>.r-carousel__button-next,
.r-carousel--navigation-hover>.r-carousel__button-prev {
  transition: opacity 0.25s;
  opacity: 0;
  pointer-events: none;
}

.r-carousel--navigation-hover:hover {

  .r-carousel__button-next,
  .r-carousel__button-prev {
    opacity: 1;
    pointer-events: auto;
    transition: opacity 0.25s;
  }
}