import {
  Component,
  Element,
  Prop,
  Event,
  EventEmitter,
  Listen,
  Method,
  Host,
  h,
  Watch,
} from "@stencil/core";
import { FOCUSABLE_ELEMENTS } from "../../../utils/keyboard";

/**
 * @slot - Add the combination of `raul-aside-header`, `raul-aside-body` and `raul-aside-footer` in this slot.
 */

@Component({
  tag: "raul-aside",
  styleUrl: "raul-aside.scss",
})
export class RaulAside {
  asideTrigger!: HTMLElement;
  dialogEl!: HTMLDivElement;
  dummyEl!: HTMLAnchorElement;
  focusableElements!: NodeList;
  secondaryAside!: HTMLRaulAsideElement | null;

  @Element() el!: HTMLRaulAsideElement;

  /**
   * Sets the on screen state of the component.
   */
  @Prop({ mutable: true, reflect: true }) active = false;

  @Watch("active")
  handleActivePropChange(newVal: boolean) {
    if (newVal) {
      this.visible = true;
      this.raulAsideOpen.emit();
      this.focusableElements = this.getFocusableElements();
    } else {
      this.raulAsideClose.emit();
    }
  }

  /**
   * Determines whether the aside can be closed via clicking away or the `Escape` key
   */
  @Prop({ reflect: true }) notDismissible = false;

  /**
   * Sets the width of the dialog box.
   */
  @Prop({ reflect: true }) size: "small" | "medium" | "large" | "xlarge" =
    "medium";

  /**
   * Controls visibility of component.
   */
  @Prop({ mutable: true, reflect: true }) visible = false;

  /**
   * Emitted when the aside opens.
   */
  @Event()
  raulAsideOpen!: EventEmitter<undefined>;

  /**
   * Emitted when the aside closes.
   */
  @Event()
  raulAsideClose!: EventEmitter<undefined>;

  /**
   * Emitted when the aside is ready.
   */
  @Event()
  raulAsideReady!: EventEmitter<undefined>;

  /**
   * Emitted when clicking away or the `Escape` key. Should be used when dismissible is `true`.
   */
  @Event()
  raulAsideClickedOutside!: EventEmitter<undefined>;

  componentWillLoad() {
    this.visible = this.active ? true : false;
  }

  componentDidLoad() {
    this.secondaryAside = this.el.querySelector("raul-aside");
    this.raulAsideReady.emit();
  }

  @Listen("click")
  handleInternalClick(e: MouseEvent) {
    const target = e.target as HTMLElement;
    if (this.isNotSecondaryAside(target)) {
      const dialogEl = target.closest('[role="dialog"]');
      if (dialogEl !== this.dialogEl) {
        if (!this.notDismissible) {
          this.dummyEl.focus();
        } else {
          this.raulAsideClickedOutside.emit();
        }
      }
    }
  }

  @Listen("focusin")
  handleInternalFocus({ target, relatedTarget }: FocusEvent) {
    if (
      !(relatedTarget as HTMLElement)?.closest("raul-aside") ||
      (relatedTarget as HTMLElement)?.closest("raul-aside") !== this.el
    ) {
      this.asideTrigger = relatedTarget as HTMLElement;
    }

    if (
      this.isNotSecondaryAside(target as HTMLElement) &&
      this.dialogEl !== target
    ) {
      !this.notDismissible && target === this.dummyEl
        ? this.close()
        : undefined;
    }
  }

  @Listen("keydown")
  handleInternalKeydown(e: KeyboardEvent) {
    const { key, target, shiftKey } = e;

    if (["Esc", "Escape"].includes(key)) {
      this.isNotSecondaryAside(target as HTMLElement)
        ? !this.notDismissible
          ? this.dummyEl.focus()
          : this.raulAsideClickedOutside.emit()
        : undefined;
      return;
    }

    this.focusableElements = this.getFocusableElements();

    if (
      shiftKey &&
      this.focusableElements &&
      key === "Tab" &&
      this.dialogEl !== target
    ) {
      const lastIndex = this.focusableElements.length - 1;
      if (target === this.focusableElements[0]) {
        e.preventDefault();
        (this.focusableElements[lastIndex] as HTMLElement)?.focus();
      }
      return;
    }

    if (key === "Tab" && this.focusableElements && this.dialogEl !== target) {
      const lastIndex = this.focusableElements.length - 1;
      if (target === this.focusableElements[lastIndex]) {
        e.preventDefault();
        (this.focusableElements[0] as HTMLElement)?.focus();
      }
      return;
    }
  }

  @Listen("raulAsideOpen")
  handleRaulAsideOpen() {
    this.active = true;
  }

  @Listen("transitionstart")
  handleInternalTransitionstart({ target }: TransitionEvent) {
    if (this.active && this.focusableElements && target === this.dialogEl) {
      this.focusableElements = this.getFocusableElements();
      if (this.focusableElements.length > 0)
        (this.focusableElements[0] as HTMLElement)?.focus();
    }
  }

  @Listen("transitionend")
  handleInternalTransitionend({ target }: TransitionEvent) {
    if (!this.active && target === this.dialogEl) {
      this.visible = false;
    }
  }

  isNotSecondaryAside(target: HTMLElement) {
    return !(
      this.secondaryAside &&
      (this.secondaryAside === target.closest("raul-aside") ||
        this.secondaryAside === target)
    );
  }

  getFocusableElements() {
    return this.dialogEl?.querySelectorAll(FOCUSABLE_ELEMENTS.join(", "));
  }

  /**
   * Opens the aside.
   */
  @Method()
  async open(): Promise<void> {
    this.active = true;
    this.visible = true;
  }

  /**
   * Closes the aside.
   */
  @Method()
  async close(): Promise<void> {
    this.active = false;
    this.asideTrigger?.focus();
  }

  render() {
    return (
      <Host
        aria-hidden={this.active ? "false" : "true"}
        data-testid="aside-off-panel"
      >
        <a
          ref={(el) => (this.dummyEl = el as HTMLAnchorElement)}
          class="r-dummy-link"
          tabindex="-1"
          aria-hidden="true"
          href="javascript:undefined"
        ></a>

        <div
          ref={(el) => (this.dialogEl = el as HTMLDivElement)}
          class="r-aside__dialog"
          role="dialog"
          tabindex="-1"
          aria-modal="true"
          aria-labelledby="asideTitle"
          onClick={({ target }) =>
            target === this.dialogEl ? this.dialogEl.focus() : undefined
          }
        >
          <slot />
        </div>

        <slot name="secondary-aside" />
      </Host>
    );
  }
}
