import { Component, h, Prop, Host, State } from "@stencil/core";
import { TimelineEventInterface } from "../../../../utils/interfaces";

/**
 * @slot title - Place the title of the event here.
 * @slot content - Place rich content of the event here.
 * @slot timestamp - Place the timestamp of the event here.
 */

@Component({
  tag: "raul-timeline-event",
})
export class RaulTimelineEvent {
  private maxHeight = 30;
  private titleEl: HTMLDivElement | undefined;
  private subTitleEl: HTMLDivElement | undefined;

  @State() shouldTruncate = false;
  @State() truncated = false;

  /**
   * @deprecated Use the available slots to compose your content instead.
   */
  @Prop() data?: TimelineEventInterface;

  /**
   * Sets the type of the event.
   */
  @Prop() type?: "default" | "success" | "warning" | "error" = "default";

  private handleViewButtonClick() {
    this.truncated = !this.truncated;
  }

  componentDidLoad() {
    if (this.titleEl) {
      this.truncated = this.shouldTruncate =
        this.titleEl.scrollHeight > this.maxHeight;
    }

    if (this.subTitleEl && !this.shouldTruncate) {
      this.truncated = this.shouldTruncate =
        this.subTitleEl.scrollHeight > this.maxHeight;
    }
  }

  render() {
    return (
      <Host
        role="listitem"
        class={{
          "r-timeline__event": true,
          [`r-timeline__event--${this.data?.type}`]: !!this.data?.type, // This needs to be removed eventually
          [`r-timeline__event--${this.type}`]: !!this.type,
        }}
      >
        <div class="r-timeline__marker__wrapper">
          <div class="r-timeline__outer__marker">
            <div class="r-timeline__marker"></div>
          </div>

          <div class="r-timeline__line"></div>
        </div>

        <div class="r-timeline__content">
          <div class={{ "r-timeline__event--truncate": this.truncated }}>
            <div
              class="r-timeline__content__title"
              ref={(el) => (this.titleEl = el)}
            >
              <slot name="title">
                {this.data && this.data.title.text && (
                  <span>
                    {this.data && this.data.title.user && (
                      <a
                        href={this.data.title.user.url}
                        class="r-timeline__content__title__link"
                        onClick={(e) => this.data?.title.user?.onClick(e)}
                      >
                        {this.data.title.user.name}
                      </a>
                    )}
                    {this.data.title?.user?.name ? " " : null}
                    {this.data.title.text}
                  </span>
                )}
              </slot>
            </div>
            <div
              class="r-timeline__content__sub-title"
              ref={(el) => (this.subTitleEl = el)}
            >
              <slot name="content">
                {this.data && this.data.subTitle && (
                  <span>{this.data.subTitle}</span>
                )}
              </slot>
            </div>
            {this.data && this.shouldTruncate && (
              <button
                class="r-timeline__content__view-more__link"
                onClick={() => this.handleViewButtonClick()}
              >
                {this.truncated ? "View More" : "View Less"}
              </button>
            )}
            <div class="r-timeline__content__timestamp">
              <slot name="timestamp">
                {this.data && this.data.timestamp && (
                  <span>{this.data.timestamp}</span>
                )}
              </slot>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
