import { Component, Prop, Event, EventEmitter, h } from "@stencil/core";

@Component({
  tag: "raul-column-sorter",
  styleUrl: "raul-column-sorter.scss",
})
export class RaulColumnSorter {
  /**
   * Sets direction of indicator icon.
   */
  @Prop({ reflect: true, mutable: true }) direction:
    | "ascending"
    | "descending"
    | null = null;

  /**
   * Sets field name that is detailed in the `raulSort` event.
   */
  @Prop({ reflect: true })
  field!: string;

  private handleSort() {
    if (this.direction === null) {
      this.direction = "ascending";
    } else if (this.direction === "ascending") {
      this.direction = "descending";
    } else if (this.direction === "descending") {
      this.direction = null;
    }

    this.raulSort.emit({ direction: this.direction, field: this.field });
  }

  @Event()
  raulSort!: EventEmitter<{
    direction: "ascending" | "descending" | null;
    field: string;
  }>;

  render() {
    return (
      <button
        type="button"
        class="r-column__sorter"
        aria-label="Sort"
        onClick={() => this.handleSort()}
      >
        <span
          class={{
            "r-column__sorter__icon": true,
            "r-column__sorter__icon--ascending": true,
            "r-column__sorter__icon--active": this.direction === "ascending",
          }}
        />

        <span
          class={{
            "r-column__sorter__icon": true,
            "r-column__sorter__icon--descending": true,
            "r-column__sorter__icon--active": this.direction === "descending",
          }}
        />
      </button>
    );
  }
}
