export const positionMenu = (
  toggleEl: HTMLElement,
  dropdownEl: HTMLElement,
  currentPosition: string,
  initialPosition: string
): string => {
  const isOut = isOutOfViewport(dropdownEl);
  const toggleBounding = toggleEl?.getBoundingClientRect();
  const dropdownBounding = dropdownEl?.getBoundingClientRect();
  let positionPrefix: string | null =
    currentPosition?.split("-").length > 1
      ? currentPosition?.split("-")[0]
      : null;
  const viewPortHeight =
    window.innerHeight || document.documentElement.clientHeight;
  const dropdownGap = currentPosition?.includes("bottom")
    ? dropdownBounding.top - toggleBounding.bottom
    : toggleBounding.top - dropdownBounding.bottom;

  if (positionPrefix) {
    positionPrefix = isOut.right
      ? "end"
      : isOut.left
      ? "start"
      : positionPrefix;
  } else {
    if (isOut.right) return "left";
    if (isOut.left) return "right";
  }

  if (
    initialPosition?.includes("top") &&
    !["left", "right"].includes(currentPosition) &&
    toggleBounding.top > dropdownGap + dropdownBounding.height
  )
    return positionPrefix
      ? positionPrefix + "-" + initialPosition.split("-")[1]
      : initialPosition;

  if (isOut.top) return `${positionPrefix ? positionPrefix + "-" : ""}bottom`;

  if (
    initialPosition?.includes("bottom") &&
    !["left", "right"].includes(currentPosition) &&
    viewPortHeight - toggleBounding.bottom >
      dropdownGap + dropdownBounding.height
  )
    return positionPrefix
      ? positionPrefix + "-" + initialPosition.split("-")[1]
      : initialPosition;

  if (isOut.bottom) {
    if (toggleBounding.top < dropdownGap + dropdownBounding.height)
      return `${positionPrefix ? positionPrefix + "-" : ""}bottom`;

    return `${positionPrefix ? positionPrefix + "-" : ""}top`;
  }

  return positionPrefix
    ? positionPrefix + "-" + currentPosition.split("-")[1]
    : currentPosition;
};

interface OutOfViewport {
  top?: boolean;
  bottom?: boolean;
  left?: boolean;
  right?: boolean;
  any?: boolean;
  all?: boolean;
}

export const isOutOfViewport = function (elem: HTMLElement) {
  const bounding = elem.getBoundingClientRect();
  const out: OutOfViewport = {};

  out.top = bounding?.top < 0;
  out.left = bounding?.left < 0;
  out.bottom =
    bounding?.bottom >
    (window.innerHeight || document.documentElement.clientHeight);
  out.right =
    bounding?.right >
    (window.innerWidth || document.documentElement.clientWidth);
  out.any = out.top || out.left || out.bottom || out.right;
  out.all = out.top && out.left && out.bottom && out.right;

  return out;
};
