

raul-progress {
  display: flex;
  flex-direction: column;
  font-size: var(--font-size-3);

  .r-progress {
    &__label {
      display: flex;
      align-items: flex-end;
      font-size: var(--font-size-2);
      font-weight: var(--font-weight-semibold);
      margin-bottom: 0.25rem;
    }

    &__remove-btn {
      margin-left: auto;
      width: 1.25rem;
      height: 1.25rem;
      display: flex;
      align-items: center;
      justify-content: center;

      @mixin focus-visible {
        &:focus {
          outline: none;
        }

        &:focus:not(:focus-visible) {
          outline: none;
        }

        &:focus-visible {
          outline: 2px solid var(--color-primary);
        }
      }
    }

    &__bar {
      background-color: var(--gray-light);
      height: 0.25rem;
      border-radius: 9999px;
      overflow: hidden;
      position: relative;

      &__value {
        background-color: var(--color-primary);
        border-radius: 9999px;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
      }
    }

    &__text {
      font-size: var(--font-size-2);
      margin-top: 0.25rem;
    }
  }

  &[static] {
    flex-direction: row;
    align-items: center;

    .r-progress {
      &__label {
        font-weight: var(--font-weight-normal);
        margin-bottom: 0;
        margin-right: 0.5rem;
      }

      &__bar {
        flex: 0 0 5rem;
      }

      &__text {
        margin-top: 0;
        margin-left: 0.5rem;
      }
    }
  }

  &[color="warning"] .r-progress__bar__value {
    background-color: var(--color-warning);
  }

  &[color="danger"] .r-progress__bar__value {
    background-color: var(--color-destructive);
  }

  &[color="success"] .r-progress__bar__value {
    background-color: var(--color-success);
  }

  &[is-removed] {
    display: none;
  }
}