import {
  Component,
  Prop,
  Element,
  EventEmitter,
  Event,
  h,
  Host,
  Listen,
} from "@stencil/core";
import { HTMLStencilElement } from "@stencil/core/internal";
import { RaulStatusVariant } from "../../../utils/types";

/**
 * @slot cta - Place a custom call to action link in this slot.
 * @slot content - Add alert content in this slot
 * @slot icon - Add a raul-icon component to the left alert content.
 */

@Component({
  tag: "raul-alert",
  styleUrl: "raul-alert.scss",
})
export class RaulAlert {
  @Element() el!: HTMLStencilElement;

  /**
   * @deprecated Use "content" slot to add rich content to alert instead
   */
  @Prop() content?: string;

  /**
   * @deprecated Use the "cta" slot instead.
   */
  @Prop() ctaMessage?: string;

  /**
   * @deprecated Use the "cta" slot instead.
   */
  @Prop() ctaUrl?: string;

  /**
   * A way to remove the alert
   */
  @Prop() dismissible = false;

  /**
   * A header
   */
  @Prop() heading?: string;

  /**
   * @internal
   */
  @Prop({ mutable: true, reflect: true }) isDismissed = false;

  /**
   * Sets removed state.
   */
  @Prop({ mutable: true, reflect: true }) isRemoved = false;

  /**
   * Corners can be rounded or not
   */
  @Prop({ reflect: true }) rounded? = false;

  /**
   * Determines the color of the left bar
   */
  @Prop({ reflect: true }) variant: RaulStatusVariant = "information";
  /**
   * Emitted when the alert is dismissed.
   */
  @Event() raulAlertDismiss!: EventEmitter<undefined>;

  componentWillLoad() {
    if (this.ctaMessage)
      console.warn(
        '[RAUL: Deprecated] "cta-message" is deprecated, use the "cta" slot instead.',
      );
    if (this.ctaUrl)
      console.warn(
        '[RAUL: Deprecated] "cta-url" is deprecated, use the "cta" slot instead.',
      );

    if (this.isRemoved) this.isDismissed = true;

    if (this.content) {
      console.warn(
        '[RAUL: Deprecated] "content" is deprecated, use "content" slot to add rich content to alert instead',
      );
    }
  }

  @Listen("transitionend")
  handleInternalTransitionEnd(event: TransitionEvent) {
    if (event.target === this.el && this.isDismissed) {
      this.isRemoved = true;
      this.raulAlertDismiss.emit();
    }
  }

  dismiss() {
    this.isDismissed = true;
  }

  render() {
    return (
      <Host>
        <slot name="icon"></slot>
        <div class="r-alert__content-wrapper">
          <div class="r-alert__heading">
            {this.heading}
            {this.dismissible && (
              <button
                class="r-alert__heading__close-button"
                onClick={() => this.dismiss()}
                role="button"
                aria-label="Dismiss"
                tabindex={0}
                data-testid="alert-dismiss-button"
                onKeyPress={(e) => {
                  if (e.key === "Enter") {
                    this.dismiss();
                  }
                }}
              >
                <raul-icon icon="close"></raul-icon>
              </button>
            )}
          </div>
          <div class="r-alert__content">
            <slot name="content">{this.content}</slot>
          </div>

          {this.ctaMessage && this.ctaUrl ? (
            <a href={this.ctaUrl}>{this.ctaMessage}</a>
          ) : (
            <slot name="cta" />
          )}
        </div>
      </Host>
    );
  }
}
