@import "../../../styles/mixins/focus.scss";

raul-radio {
  display: inline-flex;
  align-items: center;
  column-gap: 0.25rem;
  vertical-align: middle;
  font-size: var(--font-size-3);

  .r-radio__label {
    cursor: pointer;
    display: flex;
    align-items: center;
    position: relative;

    input[type="radio"] {
      position: absolute;
      height: 0;
      opacity: 0;
      width: 0;
            

      &:checked~.r-radio__label__icon {
        background-color: var(--color-primary);
        border-color: var(--color-primary);

        &::after {
          position: absolute;
          background-color: var(--color-background);
          left: 50%;
          border-radius: 50%;
          top: 50%;

          content: "";
          height: 6px;
          transform: translate(-50%, -50%);
          width: 6px;
        }
      }

      &:focus-visible {
        & ~ .r-radio__label__icon {
          outline: 2px solid var(--color-primary);
          outline-offset: 2px;
        }
      }

      &:disabled {
        & ~ .r-radio__label__icon {
          background-color: var(--color-neutral-light);
          border-color: var(--color-neutral-light);
        }

        &:checked~.r-radio__label__icon::after {
          background-color: var(--color-neutral-dark);
        }
      }
    }

    &__icon {
      background-color: var(--color-background);
      border: 1px solid var(--color-neutral);
      flex: none;
      position: relative;
      border-radius: 9999px;
      height: 20px;
      transition: background-color 0.15s linear, border-color 0.15s linear;
      width: 20px;
    }

    &__text {
      font-size: var(--font-size-3);
      margin-left: 0.5rem;
      word-break: break-all;
    }
  }

  &[small] {
    .r-radio__label {
      &__icon {
        height: 14px;
        width: 14px;
      }

      input[type="radio"]:checked~.r-radio__label__icon::after {
        height: 4px;
        width: 4px;
      }
    }
  }

  &[invalid] {
    .r-radio__label__icon {
      border-color: var(--color-destructive);
    }
  }

  *[data-label-action] {
    @include focus-visible;
  }
}