import { Component, Prop, h, Host } from "@stencil/core";

/**
 * @slot - Place tab content in this slot.
 */
@Component({
  tag: "raul-tab-pane",
})
export class RaulTabPane {
  /**
   * The name of the tab pane.
   */
  @Prop({ reflect: true }) name?: string;

  render() {
    return (
      <Host role="tabpanel" aria-labelledby={`${this.name}-tab`}>
        <slot />
      </Host>
    );
  }
}
