:host {
  --input-helper-icon-size: var(--font-size-4);
  --input-helper-font-size: var(--font-size-2);
  --input-helper-destructive-color: var(--color-destructive);
  --input-helper-warning-color: var(--color-warning);
  --input-helper-success-color: var(--color-success);
}

:host(raul-input-helper-list-item) {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  column-gap: 0.25rem;
  font-size: var(--input-helper-font-size);
  font-family: var(--font-family-roboto);
  width: 100%;
  raul-icon {
    font-size: var(--input-helper-icon-size);
  }
}

:host(raul-input-helper-list-item[variant="destructive"]) {
  color: var(--input-helper-destructive-color);
}

:host(raul-input-helper-list-item[variant="warning"]) raul-icon {
  color: var(--input-helper-warning-color);
}

:host(raul-input-helper-list-item[variant="success"]) raul-icon {
  color: var(--input-helper-success-color);
}
