import {
  Element,
  Component,
  Prop,
  h,
  Event,
  EventEmitter,
} from "@stencil/core";

/**
 * @slot - Add tab label text and badges in this slot.
 */
@Component({
  tag: "raul-tab",
  styleUrl: "raul-tab.scss",
})
export class RaulTab {
  @Element() el!: HTMLElement;

  /**
   * Set the active state for the tab
   */
  @Prop() active = false;

  /**
   * Set the disabled state for the tab
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * Use to set discernible text to the tab when using an icon. Sets the aria-label attribute on the tab.
   */
  @Prop() label?: string;

  /**
   * The name of the tab pane.
   */
  @Prop({ reflect: true }) name?: string;

  /** @internal */
  @Event() raulTabClick!: EventEmitter<undefined>;

  handleClick() {
    if (!this.disabled) {
      this.raulTabClick.emit();
    }
  }

  render() {
    return (
      <button
        role="tab"
        class={{
          "r-tab-item": true,
          "r-tab-item--active": this.active,
          "r-tab-item--disabled": this.disabled,
        }}
        id={this.name ? `${this.name}-tab` : undefined}
        disabled={this.disabled}
        aria-label={this.label}
        aria-selected={this.active ? "true" : "false"}
        onClick={() => this.handleClick()}
      >
        <slot />
      </button>
    );
  }
}
