import {
  Component,
  Prop,
  EventEmitter,
  Event,
  Host,
  h,
  Watch,
} from "@stencil/core";

/**
 * @slot messaging - Place validation, hint text or any other informational text here. Use the `raul-input-helper-list` component in this slot.
 * @slot label-start - Place an element before the label. For action elements use the `data-label-action` attribute on the element.
 * @slot label-end - Place an element at the end of the label. For action elements use the `data-label-action` attribute on the element.
 */

@Component({
  tag: "raul-quantity",
  styleUrl: "raul-quantity.scss",
  shadow: true,
})
export class RaulQuantity {
  inputId = `raul-quantity-${inputIds++}`;
  /**
   * Sets the value of the quantity input
   */
  @Prop({ mutable: true }) value!: number | string;
  @Watch("value")
  handleValuePropChange(newValue) {
    this.raulChange?.emit({ value: newValue });
  }

  /**
   * Sets the name for label and input
   */
  @Prop() name!: string;

  /**
   * Sets the increment or decrement of the +/- buttons
   */
  @Prop() step: number | string = 1;

  /**
   * Sets the minimum allowed value that the quantity can be decremented to
   */
  @Prop() minValue?: number | string;

  /**
   * Sets the maximum allowed value that the quantity can be incremented to
   */
  @Prop() maxValue?: number | string;

  /**
   * Makes the quantity visually invalid.
   */
  @Prop({ reflect: true }) invalid?: boolean = false;

  /**
   * Sets the text above the quantity input
   */
  @Prop() label?: string;

  /**
   * Controls whether this button is disabled.
   */
  @Prop() disabled = false;

  /**
   * Sets aria-describedby on internal input element.
   */
  @Prop() helperId?: string;

  /**
   * Emitted when a button is clicked
   */
  @Event() raulChange!: EventEmitter<{ value: number | string }>;

  private handleInputChange(e) {
    const temp = Number(e.target.value);
    if (temp > Number(this.maxValue) || temp < Number(this.minValue)) {
      e.target.value = this.value;
      return;
    }
    this.value = temp;
  }

  private increment() {
    const temp = Math.max(Number(this.value) + Number(this.step));
    this.value = temp;
  }

  private decrement() {
    const temp = Math.min(Number(this.value) - Number(this.step));
    this.value = temp;
  }

  render() {
    return (
      <Host>
        {this.label ? (
          <div part="label-wrapper">
            <slot name="label-start" />
            <label htmlFor={this.name || this.inputId}>{this.label}</label>
            <slot name="label-end" />
          </div>
        ) : null}
        <div part="field-wrapper">
          <button
            data-testid="decrement-button"
            type="button"
            onClick={() => this.decrement()}
            aria-label="decrement"
            disabled={
              Number(this.value) === Number(this.minValue) || this.disabled
            }
          >
            <raul-icon icon="subtract-2" />
          </button>
          <input
            id={this.name || this.inputId}
            name={this.name}
            type="number"
            aria-describedby={this.helperId}
            min={this.minValue}
            max={this.maxValue}
            step={this.step}
            value={this.value}
            onChange={(e) => this.handleInputChange(e)}
            disabled={this.disabled}
          />
          <button
            data-testid="increment-button"
            type="button"
            onClick={() => this.increment()}
            aria-label="increment"
            disabled={
              Number(this.value) === Number(this.maxValue) || this.disabled
            }
          >
            <raul-icon icon="add-2" />
          </button>
        </div>
        <slot name="messaging" />
      </Host>
    );
  }
}
let inputIds = 0;
