import { Component, Prop, h, Host } from "@stencil/core";
import { ripple } from "../../../utils/animation";
import { ButtonIcons } from "../../../utils/interfaces";

/**
 * @slot - Button label text.
 * @slot icon - Add a raul-icon component to the button.
 */
@Component({
  tag: "raul-button",
  styleUrl: "raul-button.scss",
})
export class ButtonComponent {
  btnTextEl: HTMLElement | undefined;
  btnEl: HTMLButtonElement | HTMLAnchorElement | undefined;

  /**
   * Determines the primary appearance of the button based on its purpose.
   */
  @Prop({ reflect: true }) variant:
    | "primary"
    | "secondary"
    | "reverse"
    | "danger"
    | "text"
    | "control" = "secondary";

  /**
   * Determines the primary appearance of the button based on its purpose.
   */
  @Prop({ reflect: true }) size: "default" | "small" = "default";

  /**
   * Controls the underlying markup based on the use case for the button.
   */
  @Prop() type: "button" | "reset" | "submit" = "button";

  /**
   * Only valid for input types (submit, reset). Pair with name attribute.
   */
  @Prop() value?: string;

  /**
   * Adds name to internal button for form support. Pair with value attribute.
   */
  @Prop() name?: string;

  /**
   * Determines link behavior. Only valid for links.
   */
  @Prop() href?: string;

  /**
   * Determines how links open. Only valid for links.
   */
  @Prop() target?: "_blank" | "_self" | "_parent" | "_top" | string;

  /**
   * Controls whether this button is disabled.
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * Adds `add` icon.
   * Works with all button variants.
   */
  @Prop({ reflect: true }) add = false;

  /**
   * Adds `delete` icon.
   * Works with all button variants.
   */
  @Prop({ reflect: true }) delete = false;

  /**
   * The icon name rendered in the button.
   * Will override add and delete prop icons.
   */
  @Prop({ reflect: true }) icon?: ButtonIcons;

  /**
   * Use when the button only has an icon and no text.
   */
  @Prop({ mutable: true }) iconOnly = false;

  /**
   * Sets `aria-label` attribute on button for screen readers (use when the button only has an icon).
   */
  @Prop() label?: string;

  /**
   * Determines the placement of the icon based on start and end parameters
   * in relation to the button text.
   */
  @Prop() iconPlacement?: "start" | "end" = "start";

  componentDidLoad() {
    if (!this.btnTextEl?.innerHTML) {
      this.iconOnly = true;
    }
  }

  render() {
    const renderAddOrDeleteIcon = () => {
      return this.add ? (
        <raul-icon icon="add-2" />
      ) : this.delete ? (
        <raul-icon icon="bin" />
      ) : null;
    };

    const renderIcon = () => {
      if (this.icon && !(this.add || this.delete)) {
        return <raul-icon icon={iconReference[this.icon]} />;
      }
    };

    const renderDefaultIconSlot = () => (
      <slot name="icon">{renderIcon() || renderAddOrDeleteIcon()}</slot>
    );

    const renderButton = () => {
      const Tag = this.href ? "a" : "button";

      return (
        <Tag
          ref={(el) => (this.btnEl = el)}
          class={{ "r-button--icon-only": this.iconOnly }}
          type={this.type}
          href={this.href && !this.disabled ? this.href : undefined}
          target={this.href && !this.disabled ? this.target : undefined}
          aria-label={this.label}
          aria-disabled={this.disabled ? "true" : null}
          disabled={this.disabled}
          name={this.name}
          value={this.value}
          onMouseDown={(e) => {
            if (this.btnEl) ripple(e, this.btnEl);
          }}
        >
          {this.iconPlacement === "start" ? renderDefaultIconSlot() : null}

          <span hidden={this.iconOnly} ref={(el) => (this.btnTextEl = el)}>
            <slot />
          </span>
          {this.iconPlacement === "end" ? renderDefaultIconSlot() : null}
        </Tag>
      );
    };

    return <Host>{renderButton()}</Host>;
  }
}

const iconReference = {
  filter: "content-filter",
  delete: "bin",
  undo: "undo",
  "right-arrow-v": "arrow-right-v",
  "left-arrow-v": "arrow-left-v",
  refresh: "synchronize-1",
  edit: "pencil-1",
  close: "close",
  download: "data-download-8",
  "right-arrow": "arrow-right-1",
  "left-arrow": "arrow-left-1",
  sync: "synchronize-alert",
  overflow: "navigation-show-more-vertical",
  preview: "visible",
  upload: "data-upload-8",
  save: "floppydisk",
  settings: "cog-gear-settings",
  redo: "redo",
  today: "calendar-check-2",
  comments: "chat-bubble-square-1",
  call: "phone",
  email: "email-envelope",
  "zoom-in": "add-2",
  "zoom-out": "subtract-2",
  collapse: "shrink-diagonal-1",
  expand: "expand-7",
};
