import { Component, Prop, EventEmitter, Event, h } from "@stencil/core";

/**
 * @deprecated - Use raul-dropdown-item instead.
 */

@Component({
  tag: "raul-option",
})
export class RaulOption {
  /**
   * Adds a checkbox to the option.
   */
  @Prop({ reflect: true }) multiple?: boolean;
  /**
   * Sets selected state.
   */
  @Prop({ mutable: true, reflect: true }) selected?: boolean;
  /**
   * Sets the `multiple` checkbox to indeterminate
   */
  @Prop({ reflect: true }) indeterminate?: boolean;
  /**
   * Sets focus state.
   */
  @Prop() focused?: boolean;
  /**
   * Sets disabled state.
   */
  @Prop({ reflect: true }) disabled?: boolean;
  /**
   * Sets value of option.
   */
  @Prop() value?: string;
  /**
   * Sets text label.
   */
  @Prop() text?: string;
  /**
   * Sets description text under text label.
   */
  @Prop() description?: string;
  /**
   * Adds icon to left side of option.
   */
  @Prop() icon?: string;
  /**
   * Sets the icon.
   */
  @Prop() iconKind?: string;
  /**
   * Sets `id` attribute on internal button.
   */
  @Prop() optionId?: string;
  /**
   * Sets the type of option.
   */
  @Prop({ reflect: true }) variant?: "group" | "heading" | "sub-option";

  /**
   * For custom raul-option the following props are not available - multiple, selected, indeterminate, focused.
   */
  @Prop() custom?: boolean;

  /**
   * Emitted when the raul-option is clicked.
   */
  @Event() raulOptionClick!: EventEmitter;

  private handleOptionClick() {
    if (
      this.variant &&
      !this.multiple &&
      ["heading", "group"].includes(this.variant)
    )
      return;

    this.selected = this.multiple
      ? !this.selected
      : this.variant && ["heading", "group"].includes(this.variant)
      ? false
      : true;

    this.raulOptionClick.emit({
      custom: !!this.custom,
      selected: this.selected,
      indeterminate: this.indeterminate,
      focused: this.focused,
      disabled: this.disabled,
      value: this.value,
      text: this.text,
      description: this.description,
      optionId: this.optionId,
      variant: this.variant,
    });
  }

  render() {
    return (
      <button
        class={{
          "r-option": true,
          "r-option--multiple": Boolean(this.multiple && !this.custom),
          "r-option--active": Boolean(this.focused && !this.custom),
          "r-option--disabled": Boolean(this.disabled),
          "r-option--selected": Boolean(this.selected && !this.custom),
          "r-option--group": this.variant === "group",
          "r-option--heading": this.variant === "heading",
          "r-option--sub-option": this.variant === "sub-option",
          "r-option--with-description": !!this.description,
        }}
        role="option"
        tabindex="-1"
        id={this.optionId}
        aria-selected={this.selected ? "true" : "false"}
        aria-disabled={this.disabled ? "true" : null}
        onClick={() => this.handleOptionClick()}
      >
        {this.icon && (
          <raul-icon
            class="r-option__icon"
            icon={this.icon}
            kind={this.iconKind}
          />
        )}

        <div class="r-option__text">
          <div class="r-option__text__title" title={this.text}>
            {this.text}
          </div>

          {this.description && (
            <div class="r-option__text__description" title={this.description}>
              {this.description}
            </div>
          )}
        </div>

        <slot />

        {this.multiple && !this.custom ? (
          <span
            class={{
              "r-option__checkbox": true,
              "r-option__checkbox--checked": Boolean(this.selected),
              "r-option__checkbox--disabled": Boolean(this.disabled),
              "r-option__checkbox--indeterminate": Boolean(this.indeterminate),
            }}
          />
        ) : this.selected && !this.custom ? (
          <span class="r-option__check-mark"></span>
        ) : null}
      </button>
    );
  }
}
