@import "../../../styles/mixins/focus.scss";

body:has(raul-modal[active]) {
  overflow: hidden;
}

raul-modal {
  font-size: var(--font-size-3);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10000;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(55, 71, 79, 0);
  cursor: pointer;
  pointer-events: none;
  transition: background-color 0.15s linear;
  visibility: visible;

  .r-modal__dialog {
    position: absolute;
    bottom: 2.5rem;
    padding: 2rem 1rem;
    margin: 0 0.5rem;
    width: calc(100% - 1rem);
    max-width: 22.5rem;
    min-height: 10.25rem;
    max-height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    border-radius: 0.25rem;
    transform: scale(.95);

    background: rgba(255, 255, 255, 1);
    cursor: default;
    opacity: 0;
    transition: opacity 0.1s linear, transform 0.15s linear;

    @media (min-width: 640px) {
      position: relative;
      bottom: auto;
    }
  }

  &:not([visible]) {
    .r-modal__dialog {
      visibility: hidden;
    }
  }

  &[active] {
    background-color: rgba(55, 71, 79, 0.5);
    pointer-events: initial;

    &>.r-modal__dialog {
      opacity: 1;
      transform: scale(1);
    }
  }

  &[not-dismissible] {
    cursor: default;
  }

  .r-dummy-link {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;

    &:focus {
      outline: none;
    }
  }

  raul-modal-header {
    padding-bottom: 0.5rem;
    display: flex;
    justify-content: space-between;
    font-size: var(--font-size-3);
    line-height: var(--line-height-tight);

    h2 {
      font-size: var(--font-size-5);
      font-weight: var(--font-weight-semibold);
      line-height: var(--line-height-tight);
    }

    p {
      margin-bottom: 0;
      color: rgba(55, 71, 79, .8);
      font-size: var(--font-size-3);
      line-height: var(-line-height-tight);
    }

    .r-modal-header__close {
      width: 1.25rem;
      height: 1.25rem;
      display: flex;
      align-items: center;
      justify-content: center;
      @include focus-visible;
    }
  }

  &:not([variant="media"]) raul-modal-header {
    justify-content: center;

    h2,
    p {
      text-align: center;
    }
  }

  raul-modal-body {
    padding: 0 1rem;
    overflow-y: auto;
    color: rgba(55, 71, 79, .8);
  }

  &:not([variant="media"]) raul-modal-body {
    margin-bottom: 1rem;
    text-align: center;
  }

  raul-modal-footer {
    padding: 1rem 1rem 0;
    display: flex;
    flex-direction: column;
    column-gap: 1rem;
    row-gap: 1rem;

    @media (min-width: 40rem) {
      flex-direction: row;
    }

    raul-button {
      flex: 1 1 0;
    }
  }

  &[variant="media"] {
    .r-modal__dialog {
      top: 0.5rem;
      padding: 2rem;
      height: 100vh;
      max-width: 44rem;
      max-height: calc(100% - 1rem);

      @media (min-width: 640px) {
        top: auto;
        height: auto;
      }
    }

    raul-icon svg {
      display: block;
      height: 1em;
      width: 1em;
    }

    raul-modal-body {
      padding: 0;
    }

    raul-modal-footer {
      padding: 1rem 0 0;
    }
  }
}