@import "../../../styles/mixins/focus.scss";

raul-color-picker {
  display: block;

  .r-color {
    position: relative;

    &__label {
      display: inline-block;
      font-size: var(--font-size-2);
      font-weight: var(--font-weight-medium);
      margin-bottom: 0.25rem;
    }

    &__toggle {
      display: block;
      background-color: var(--transparent);
      border-width: 0;
      cursor: pointer;
      padding: 0;
      text-align: left;
      outline: 2px solid var(--transparent);
      outline-offset: 2px;
      width: 100%;

      &__content {
        border: 1px solid var(--color-neutral);
        display: flex;
        align-items: center;
        font-size: var(--font-size-3);
        height: 2.5rem;
        padding: 0rem 0.75rem 0rem 0.75rem;
        line-height: var(--line-height-tight);
        background-color: var(--color-background);
        border-radius: 0.125rem;
        color: var(--color-base);

        &__text {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          flex-grow: 1;
          font-size: var(--font-size-11);

          @media (min-width: 1024px) {
            font-size: var(--font-size-3);
          }
        }

        &__indicator {
          margin-left: 0.5rem;
          width: 1.25rem;
          height: 1.25rem;
          border-radius: 9999px;
        }
      }

      &:focus {
        outline: 2px solid var(--transparent);
        outline-offset: 2px;

        .r-color__toggle__content {
          border-width: 1px;
          border-color: var(--color-primary);
        }
      }

      &:disabled {
        .r-color__toggle__content {
          background-color: var(--color-neutral-light);
          border-color: var(--color-neutral-light);
          color: var(--color-neutral-dark);
          cursor: not-allowed;
        }
      }
    }

    &__label {
      display: inline-block;
      font-size: var(--font-size-2);
      font-weight: var(--font-weight-medium);
      margin-bottom: 0.25rem;
    }

    &__error {
      display: flex;
      font-size: var(--font-size-3);
      align-items: center;
      color: var(--color-destructive);
      margin-top: 0.5rem;

      &__icon {
        font-size: var(--font-size-11);
        margin-right: 0.25rem;
      }
    }

    &__menu {
      display: flex;
      flex-direction: row;
      border-width: 1px;
      border-radius: 0.125rem;
      border-color: var(--color-neutral-light);
      box-shadow: var(--shadow-level-3);
      position: absolute;
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
      background-color: var(--color-background);
      transition: opacity 0.2s ease, scale 0.2s linear;
      z-index: 100000;
      opacity: 0;
      pointer-events: none;


      @media (min-width: 640px) {
        top: 100%;
        left: 0;
      }

      &__content {
        display: flex;
        flex-direction: column;
        max-height: 20rem;
        width: 100%;
        min-height: 2.5rem;

        @media (max-width: 639px) {
          max-height: none;
        }
      }

      &__body {
        display: flex;
        align-items: center;
        padding: 11px;

        @media (max-width: 639px) {
          padding: 1rem;
          justify-content: space-between;
        }

        .r-color__menu__item > button {
          color: var(--color-background);
          margin: 0.25rem;
          border-radius: 9999px;
          padding: 3px;
          display: block;
          border: 1px solid var(--color-background);

          @include focus-visible;

          span {
            width: 2rem;
            height: 2rem;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 9999px;

            @media (min-width: 768px) {
              width: 1.5rem;
              height: 1.5rem;
            }
          }
        }
      }

      &__footer {
        padding: 1rem 2rem;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 0.5rem;
        border-top: 1px solid var(--color-neutral-light);

        @media (min-width: 768px) {
          padding: 0.5rem 1rem;
        }

        label {
          font-size: var(--font-size-3);
        }

        input[type="color"] {
          appearance: none;
          -moz-appearance: none;
          -webkit-appearance: none;
          border: 0;
          width: 1.5rem;
          height: 1.5rem;
          border-radius: 9999px;
          cursor: pointer;
        }

        input[type="color"]::-moz-color-swatch-wrapper {
          padding: 0;
          width: 2rem;
          height: 2rem;

          @media (min-width: 768px) {
            width: 1.5rem;
            height: 1.5rem;
          }
        }

        input[type="color"]::-webkit-color-swatch-wrapper {
          padding: 0;
          width: 2rem;
          height: 2rem;

          @media (min-width: 768px) {
            width: 1.5rem;
            height: 1.5rem;
          }
        }

        input[type="color"]::-moz-color-swatch {
          border: none;
          border-radius: 9999px;
        }

        input[type="color"]::-webkit-color-swatch {
          border: none;
          border-radius: 9999px;
        }
      }

      @media (max-width: 639px) {
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        transform: scale(0, 0);
        box-shadow: none;
        margin: 0;
        min-width: 0;
        position: fixed;
        border-width: 0;
        border-radius: 0;
      }
    }

    &__mobile-label {
      display: none;

      @media (max-width: 639px) {
        display: block;

        &__content {
          align-items: center;
          border-bottom-width: 1px;
          border-color: var(--color-neutral-light);
          background-color: var(--color-neutral-lightest);
          font-size: var(--font-size-3);
          display: flex;
          height: 2.5rem;

          &__text,
          &__button {
            width: 33.333333%;
          }

          &__text {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            font-weight: var(--font-weight-medium);
            text-align: center;
          }

          &__button {
            padding: 0rem 0.75rem 0rem 0.75rem;
            font-size: var(--font-size-3);
            color: var(--color-primary);
            cursor: pointer;
          }
        }
      }
    }

    &--invalid .r-color__toggle__content {
      border-color: var(--color-destructive);
    }
  }

  &[opened] .r-color__menu {
    opacity: 1;
    pointer-events: inherit;

    @media (max-width: 639px){
      transform: scale(1, 1);
    }
  }

  &[menu-placement="start-top"] .r-color__menu {
    @media (min-width: 640px)  {
      top: auto;
      bottom: 100%;
      left: 0;
    }
  }

  &[menu-placement="start-bottom"] .r-color__menu {
    @media (min-width: 640px) {
      top: 100%;
      bottom: auto;
      left: 0;
    }
  }
}


//Color picker plugin styles rewrite
raul-color-picker {
  &[variant="plugin"] {
    display: block;
    position: relative;
  }

  .pcr-app {
    display: none;
    position: absolute;
    left: 0 !important;
    top: 100% !important;
    transform: translateY(10px);

    &.visible {
      display: flex;

      .pcr-interaction .pcr-result {
        background-color: var(--color-background);
        font-size: var(--font-size-4);
        margin-top: 0.25rem;
        border: 1px solid var(--color-neutral-light);

        &:hover {
          filter: none;
        }

        &:focus {
          outline: 2px solid transparent;
          outline-offset: 2px;
          box-shadow: none;
        }
      }

      @media (min-width: 640px) {
        & {
          font-family: var(--font-family-roboto);

          &[data-theme="monolith"] {
            .pcr-selection {
              .pcr-color-chooser {
                margin-top: 1rem;
              }

              .pcr-color-opacity {
                margin-top: 0.5rem;
              }
            }

            .pcr-color-palette .pcr-picker {
              width: 14px;
              height: 14px;
            }

            .pcr-color-chooser,
            .pcr-color-opacity {
              .pcr-picker {
                width: 0.75rem;
                height: 0.75rem;
              }
            }
          }
        }
      }
    }
  }
}