import {
  Component,
  h,
  Host,
  Prop,
  Element,
  Event,
  EventEmitter,
  Listen,
  State,
  Watch,
} from "@stencil/core";
import { ActionItem } from "../../../utils/interfaces";

@Component({
  tag: "raul-bulk-action-bar",
  styleUrl: "raul-bulk-action-bar.scss",
})
export class RaulBulkActionBar {
  buttonWidths: number[] = [];
  initButtons!: HTMLRaulButtonElement[];

  @Element() el!: HTMLElement;

  @State() bulkButtons: {
    buttonsInTray: ActionItem[];
    buttonsNotInTray: ActionItem[];
  } = { buttonsInTray: [], buttonsNotInTray: [] };
  @State() visible = false;

  /**
   * Renders action buttons
   */
  @Prop() actions: ActionItem[] = [];

  /**
   * Count of selected items to apply actions to.
   */
  @Prop() selectedCount?: number = 0;

  /**
   * Total amount of records that's displayed in the "Select all xxx records" button.
   */
  @Prop() totalRecords?: number = 0;

  /**
   * Sets open state.
   */
  @Prop({ mutable: true, reflect: true }) open = false;

  @Watch("open")
  openChanged(newVal: boolean) {
    if (newVal) {
      this.setBulkButtons();
      this.visible = true;
    } else {
      this.overflow = false;
    }
  }

  /** @internal */
  @Prop({ mutable: true, reflect: true }) overflow = false;

  /**
   * Emits when bulk action bar is closed.
   */
  @Event() bulkActionsClose!: EventEmitter<undefined>;

  /**
   * Emits when "Select all xxx records" button is clicked.
   */
  @Event() bulkActionsSelectAll!: EventEmitter<undefined>;

  componentWillLoad() {
    this.overflow = this.open;
    this.visible = this.open;
  }

  componentDidLoad() {
    const buttons = Array.from(
      this.el.querySelector(".r-bulk-actions__buttons-wrapper")
        ?.childNodes as NodeListOf<HTMLRaulButtonElement>
    );

    setTimeout(() => {
      this.initButtons = [...buttons];
      this.buttonWidths = this.getButtonWidths(this.initButtons);
      if (this.open) this.setBulkButtons();
    });
  }

  @Listen("resize", { target: "window" })
  handleWindowResize() {
    this.setBulkButtons();
  }

  @Listen("transitionend")
  handleInternalTransitionend({ target }: TransitionEvent) {
    if (target === this.el && this.open) {
      this.overflow = true;
    }
    if (target === this.el && !this.open) {
      this.bulkButtons = { buttonsInTray: [], buttonsNotInTray: [] };
      this.visible = false;
    }
  }

  closeBar() {
    this.open = false;
    this.bulkActionsClose.emit();
  }

  getButtonWidths(initButtons: HTMLRaulButtonElement[]) {
    const buttons = initButtons;
    const buttonWidths: number[] = [];

    buttons.forEach((button) => {
      const thisButton = button as HTMLElement;
      let buttonWidth = thisButton.offsetWidth;
      const icon = thisButton.querySelector("raul-icon");
      if (icon) {
        buttonWidth = buttonWidth + icon.offsetWidth;
      }
      buttonWidths.push(buttonWidth);
    });

    return buttonWidths;
  }

  handleClick(onClick: () => void | undefined) {
    if (typeof onClick === "function") {
      onClick();
    }
  }

  handleMoreButtons(
    element: HTMLElement,
    buttonWidths: number[],
    buttons: HTMLRaulButtonElement[]
  ) {
    const leftSideWidth = element.querySelector<HTMLDivElement>(
      ".r-bulk-actions__left"
    )?.offsetWidth;
    const indexesInTray: number[] = [];
    const indexesNotInTray: number[] = [];
    let visibleButtonTotalWidth = 0;
    const maxButtonWidth = Math.max(...buttonWidths);

    buttons?.forEach((_button, index) => {
      visibleButtonTotalWidth =
        visibleButtonTotalWidth + buttonWidths[index] + 8;
      if (
        leftSideWidth &&
        visibleButtonTotalWidth + (maxButtonWidth + 8) > leftSideWidth
      ) {
        indexesInTray.push(index);
      } else {
        indexesNotInTray.push(index);
      }
    });

    const buttonsNotInTray: ActionItem[] = [];
    indexesNotInTray.map((i) => buttonsNotInTray.push(this.actions[i]));
    const buttonsInTray: ActionItem[] = [];
    indexesInTray.map((i) => buttonsInTray.push(this.actions[i]));

    return {
      buttonsInTray,
      buttonsNotInTray,
    };
  }

  setBulkButtons() {
    if (this.initButtons.length > 0) {
      this.bulkButtons = this.handleMoreButtons(
        this.el,
        this.buttonWidths,
        this.initButtons
      );
    }
  }

  render() {
    if (!this.bulkButtons) return null;
    const { buttonsInTray, buttonsNotInTray } = this.bulkButtons;

    const renderButtons = () =>
      buttonsNotInTray?.length > 0
        ? buttonsNotInTray.map((item: ActionItem) => {
            return (
              <raul-button
                variant="reverse"
                size="small"
                icon={item.icon ? item.icon : undefined}
                value={`${item.text}`}
                disabled={item.disabled}
                onClick={() => {
                  if (item.onClick && !item.disabled)
                    this.handleClick(item.onClick);
                }}
              >
                {item.text}
              </raul-button>
            );
          })
        : buttonsNotInTray?.length === 0 &&
          buttonsInTray?.length === 0 &&
          this.actions?.map((item: ActionItem) => (
            <raul-button
              variant="reverse"
              size="small"
              icon={item.icon ? item.icon : undefined}
              value={`${item.text}`}
              disabled={item.disabled}
              onClick={() => {
                if (item.onClick && !item.disabled)
                  this.handleClick(item.onClick);
              }}
            >
              {item.text}
            </raul-button>
          ));

    const renderMoreMenu = () => {
      let items: ActionItem[] = [];
      buttonsInTray?.length > 0
        ? buttonsInTray.forEach((item: ActionItem) => {
            items = [
              ...items,
              {
                text: item.text,
                disabled: item.disabled,
                onClick: item.onClick ? item.onClick : undefined,
              },
            ];
          })
        : null;
      return (
        <raul-menu items={items}>
          <raul-button
            disabled={!this.open}
            class="r-bulk-actions__button-more"
            variant="reverse"
            size="small"
          >
            {buttonsInTray.length === this.actions.length ? "Actions" : "More"}
          </raul-button>
        </raul-menu>
      );
    };

    return (
      <Host visible={this.visible} aria-hidden={this.open ? "false" : "true"}>
        <div class="r-bulk-actions__wrapper">
          <div class="r-bulk-actions__left">
            <div class="r-bulk-actions__buttons-wrapper">{renderButtons()}</div>
            {buttonsInTray?.length > 0 ? (
              <div class="r-bulk-actions__more">{renderMoreMenu()}</div>
            ) : null}
          </div>

          <div class="r-bulk-actions__right">
            <button
              disabled={!this.open}
              type="button"
              data-testid="select-all-button"
              class="r-bulk-actions__select-all"
              onClick={() => this.bulkActionsSelectAll.emit()}
            >
              Select all {this.totalRecords} records
            </button>
            <span
              data-testid="selected-records-count"
              class="r-bulk-actions__selected-count"
            >
              {this.selectedCount} Selected
            </span>
            <button
              disabled={!this.open}
              type="button"
              class="r-bulk-actions__close"
              onClick={() => this.closeBar()}
              aria-label="Close"
            >
              <raul-icon
                data-testid="close-action-bar"
                icon="close"
              ></raul-icon>
            </button>
          </div>
        </div>
      </Host>
    );
  }
}
