import { Component, Event, EventEmitter, Host, Prop, h } from "@stencil/core";

@Component({
  tag: "raul-file-uploader-status",
  styleUrl: "raul-file-uploader-status.scss",
  shadow: true,
})
export class RaulFileUploaderStatus {
  /**
   * Specifies the name of the file.
   */
  @Prop() fileName!: string;

  /**
   * Specifies the description of the file, normally used to not the file size.
   */
  @Prop() description?: string;

  /**
   * Specifies the progress of the file upload.
   */
  @Prop() progress?: number;

  /**
   * Specifies the state of the file upload.
   */
  @Prop({ reflect: true }) state:
    | "default"
    | "error"
    | "success"
    | "uploading" = "default";

  /**
   * Fires when the user clicks the remove button.
   */
  @Event() raulFileUploadRemove!: EventEmitter<void>;

  render() {
    const { state, progress, fileName, description } = this;
    const iconName = state === "error" ? "alert-diamond" : "check-1";

    return (
      <Host>
        <slot>
          {["error", "success"].includes(state) ? (
            <raul-icon part="icon" icon={iconName} kind="consumer" />
          ) : null}
          <div class="raul-file-uploader-status--content">
            <div class="raul-file-uploader-status--heading-bar">
              {fileName ? <span part="file-name">{fileName}</span> : null}
              <button
                type="button"
                aria-label="Remove Upload"
                onClick={() => this.raulFileUploadRemove.emit()}
                data-testid="remove-upload-button"
              >
                <raul-icon icon="remove-2" />
              </button>
            </div>
            {state === "uploading" && progress ? (
              <raul-progress
                part="progress"
                value={progress.toString() || "0"}
              />
            ) : null}
            {state !== "uploading" && description ? (
              <span part="description">{description}</span>
            ) : null}
          </div>
        </slot>
      </Host>
    );
  }
}
