import { Component, Host, Listen, Prop, Watch, h } from "@stencil/core";

/**
 * @slot This can be used add custom markup to the dropdown item. default is the text prop.
 * @part check - The check that indicates the item is selected.
 * @part option - The clickable button.
 */
@Component({
  tag: "raul-dropdown-item",
  styleUrl: "raul-dropdown-item.scss",
  shadow: { delegatesFocus: true },
})
export class RaulDropdownItem {
  optionEl: HTMLButtonElement | undefined;

  /**
   * Disables interaction.
   */
  @Prop({ reflect: true }) disabled = false;

  /** @internal - sets focused state */
  @Prop({ mutable: true }) focused = false;

  @Watch("focused")
  handleFocusedPropUpdate(newVal: boolean) {
    if (newVal) this.optionEl?.focus();
  }

  /** @internal - Sets styles to "grouped" styles. */
  @Prop({ reflect: true }) grouped = false;

  /** @internal - Sets styles to "heading" styles. */
  @Prop({ reflect: true }) heading = false;

  /** @internal - */
  @Prop({ reflect: true }) indeterminate = false;

  /** @internal - Enable multiselect */
  @Prop({ reflect: true }) multi = false;

  /**
   * Selected item.
   */
  @Prop({ reflect: true }) selected = false;

  /**
   * Item text.
   */
  @Prop() text!: string;

  /**
   * Value of the item.
   */
  @Prop({ reflect: true }) value!: string;

  @Listen("focusout")
  handleInternalBlur() {
    this.focused = false;
  }

  render() {
    const { selected, text } = this;
    return (
      <Host role="option" aria-selected={selected.toString()}>
        <button
          ref={(el) => (this.optionEl = el as HTMLButtonElement)}
          part="option"
          type="button"
          tabindex="-1"
          disabled={this.disabled}
        >
          <slot>
            <span>{text}</span>
          </slot>
          <span part="check" aria-hidden="true" />
        </button>
      </Host>
    );
  }
}
