import { Component, h, Host, Prop } from "@stencil/core";

@Component({
  tag: "raul-step-tracker",
  styleUrl: "raul-step-tracker.scss",
})
export class RaulStepTracker {
  @Prop({ reflect: true }) compact = false;

  render() {
    return (
      <Host>
        {this.compact ? <slot name="label" /> : null}
        <div class="r-step-tracker">
          <slot />
        </div>
      </Host>
    );
  }
}
