/**
 * This plugin adjusts the positioning of the calendar after flatpicker initially
 * sets it. This is a fix for a flatpicker issue with top fixed positioned eleemnts
 * such as the Omnibar header.
 */

import { Instance as FpInstance } from "flatpickr/dist/types/instance";
import { Plugin } from "flatpickr/dist/types/options";

interface Config {
  appendTo: HTMLElement;
}

export default (config: Config): Plugin =>
  (fp: FpInstance) => {
    const handlePreCalendarPosition = async () => {
      await Promise.resolve();
      const {
        calendarContainer,
        config: fpConfig,
        _positionElement: positionElement,
      } = fp;
      const { appendTo } = fpConfig;
      const { top } = appendTo!.getBoundingClientRect();
      const { bottom } = positionElement.getBoundingClientRect();
      calendarContainer.style.top = `${bottom - top}px`;
      calendarContainer.style.left = `0px`;
    };

    const register = () => {
      fp.loadedPlugins.push("raulAppendPlugin");
    };

    return {
      appendTo: config.appendTo,
      onReady: register,
      onPreCalendarPosition: handlePreCalendarPosition,
    };
  };
