import {
  Component,
  Prop,
  State,
  Element,
  Event,
  EventEmitter,
  Listen,
  h,
  Host,
  Watch,
} from "@stencil/core";
import { Snackbar } from "./Snackbar";
import { RaulStatusVariant } from "../../../utils/types";

@Component({
  tag: "raul-snackbar",
  styleUrl: "raul-snackbar.scss",
})
export class RaulSnackbar {
  timeoutTimer!: number;
  refreshTimer!: number;
  timeoutLeft!: number;
  timeoutStartedAt!: number;
  mediaQuery: MediaQueryList = window.matchMedia(
    "(prefers-reduced-motion: reduce)",
  );

  @Element() el!: HTMLElement;

  @State() hide = false;
  @State() reducedMotion = false;
  @State() show = true;

  @Watch("show")
  handleShowStateChange() {
    if (this.reducedMotion && !this.show) {
      this.hide = true;
      this.snackbarDismissed.emit();
    }
  }

  /**
   * The second line of text
   */
  @Prop() content?: string;

  /**
   * A callback function that will be called on CTA click if an url was not provided
   */
  @Prop() ctaCallback?: () => void;

  /**
   * The call-to-action text
   */
  @Prop() ctaMessage?: string;

  /**
   * A call-to-action url
   */
  @Prop() ctaUrl?: string;

  /**
   * Determines wether the snackbar has a close button. Defaults to `false`
   */
  @Prop() dismissible = false;

  /**
   * The first line of text
   */
  @Prop() heading?: string;

  /**
   * The number of ms after which the snackbar self-destructs
   */
  @Prop() timeout?: number;

  /**
   * Determines the color of the left bar
   */
  @Prop({ reflect: true }) variant: RaulStatusVariant = "information";

  /**
   * Displays icon to the left of the snackbar content
   */
  @Prop() showIcon = false;

  /**
   * Emits when snackbar is dismissed by user.
   */
  @Event() snackbarDismissed!: EventEmitter<undefined>;

  /**
   * Emits when toast has timed out.
   */
  @Event() timedOut!: EventEmitter<undefined>;

  componentWillLoad() {
    this.reducedMotion = this.mediaQuery.matches;
    this.mediaQuery.addEventListener("change", (ev) =>
      this.queryChangeHandler(ev),
    );
    // Initialize self-destruct mechanism
    if (this.timeout) {
      this.timeoutTimer = window.setTimeout(() => {
        this.timedOut.emit();
        this.dismiss();
      }, this.timeout);

      this.timeoutStartedAt = new Date().getTime();
    }
  }

  disconnectedCallback() {
    clearTimeout(this.timeoutTimer);
    clearInterval(this.refreshTimer);
  }

  @Listen("mouseenter")
  @Listen("focusin")
  handleMouseenter() {
    if (this.timeout) {
      const timeout = this.timeoutLeft ? this.timeoutLeft : this.timeout;
      const timeoutPausedAt = new Date().getTime();

      this.timeoutLeft = timeout - (timeoutPausedAt - this.timeoutStartedAt);
      clearTimeout(this.timeoutTimer);
    }
  }

  @Listen("mouseleave")
  @Listen("focusout")
  handleMouseleave() {
    if (this.timeout) {
      this.timeoutTimer = window.setTimeout(() => {
        this.timedOut.emit();
        this.dismiss();
      }, this.timeoutLeft);

      this.timeoutStartedAt = new Date().getTime();
    }
  }

  @Listen("transitionend")
  handleInternalTransitionEnd({ propertyName, target }: TransitionEvent) {
    if (!this.show && propertyName === "opacity" && target === this.el) {
      this.hide = true;
      this.snackbarDismissed.emit();
    }
  }

  dismiss() {
    this.show = false;
  }

  queryChangeHandler(event: MediaQueryListEvent) {
    this.reducedMotion = event.matches;
  }

  render() {
    return (
      <Host show={this.show} hidden={this.hide}>
        <Snackbar
          dismissible={this.dismissible}
          variant={this.variant}
          heading={typeof this.heading === "string" ? this.heading : ""}
          content={this.content}
          ctaMessage={this.ctaMessage}
          ctaUrl={this.ctaUrl}
          showIcon={this.showIcon}
          ctaCallback={this.ctaCallback}
          onCloseCallback={() => this.dismiss()}
        />
      </Host>
    );
  }
}
