import {
  Component,
  h,
  Prop,
  Event,
  EventEmitter,
  State,
  Watch,
} from "@stencil/core";
import { SelectOptionInterface } from "../../../utils/interfaces";
/**
 * @deprecated - Use raul-dropdown instead (See time pattern example in raul-dropdown).
 */
@Component({
  tag: "raul-time-picker",
})
export class RaulTimePicker {
  /**
   * Options object passed as prop to select component.
   */
  @State() options: SelectOptionInterface[] = [];

  /**
   * Start time, also supports 24h format `12:00`.
   */
  @Prop() startTime = "00:00";

  @Watch("startTime")
  handleStartTimeChange(newVal: string) {
    this.startTime = newVal;
    this.value = "";
    this.options = this.generateOptions();
  }

  /**
   * End time, also supports 24h format `23:00`.
   */
  @Prop() endTime = "23:59";

  @Watch("endTime")
  handleEndTimeChange(newVal: string) {
    this.endTime = newVal;
    this.value = "";
    this.options = this.generateOptions();
  }

  /**
   * Time intervals in minutes.
   */
  @Prop() step = 60;

  @Watch("step")
  handleStepChange() {
    this.value = "";
    this.options = this.generateOptions();
  }

  /**
   * Minimum time, any time before this time will be disabled.
   */
  @Prop() minTime?: string;

  @Watch("minTime")
  handleMinTimeChange() {
    this.value = "";
    this.options = this.generateOptions();
  }

  /**
   * Disabled hours in the select drop-down.
   */
  @Prop() disabledHours: string[] = [];

  @Watch("disabledHours")
  handleDisabledHoursChange() {
    this.options = this.generateOptions();
  }

  /**
   * If `true`, the user cannot interact with the select.
   */
  @Prop({ reflect: true }) disabled = false;

  /**
   * The text label.
   */
  @Prop({ reflect: true }) label?: string;

  /**
   * Optional hint text.
   */
  @Prop() hint?: string;

  /**
   * Makes the select visually invalid and shows the feedback message.
   */
  @Prop() error?: string;

  /**
   * The text to display when the select value is empty.
   */
  @Prop({ reflect: true }) placeholder = "Select a time";

  /**
   * The value of the select.
   */
  @Prop({ mutable: true }) value?: string;

  @Watch("value")
  handleValueChange(newVal: string) {
    this.value = newVal;
    this.options = this.generateOptions();
  }

  /**
   * Add text input support. (v0.32+)
   */
  @Prop({ mutable: true }) textInput = false;

  /**
   * Sets 24 hour support. (v0.32+) Will automatically be set when setting startTime and endTime if AM or PM are included in the string.
   */
  @Prop({ mutable: true }) twentyFourHour = false;

  /**
   * Emitted when the select value changes. As of v0.32 isValid boolean is available for time format validation.
   */
  @Event() raulChange!: EventEmitter<{
    value: string | undefined;
    isValid: boolean;
  }>;

  componentWillLoad() {
    console.warn(
      `[RAUL: Deprecated] "raul-time-picker" is deprecated. Use "raul-dropdown" instead (See time pattern example in "raul-dropdown").`
    );

    if (
      this.textInput &&
      this.value &&
      this.value !== "" &&
      this.value.substr(0, 2).includes(":")
    ) {
      this.value = `0${this.value}`;
    }
    if (
      this.textInput &&
      this.startTime &&
      !(this.startTime.includes("AM") || this.startTime.includes("PM"))
    ) {
      this.twentyFourHour = true;
    }

    this.options = this.generateOptions();
  }

  private handleRaulChange(event: CustomEvent) {
    event.stopPropagation();
    this.value = event.detail.value as string;
    this.raulChange.emit({
      value: this.value,
      isValid: this.isValid(this.value),
    });
  }

  private generateHours(
    increment: number,
    format: string,
    startTime = "00:00",
    endTime = "23:59"
  ) {
    const is12Hour = format === "twelve-hour";
    const hourMinuteRegex =
      /(?<hours>\d+):(?<minutes>\d+)\s?(?<meridiem>AM|PM)?/;
    const startMatch = startTime.match(hourMinuteRegex);
    const endMatch = endTime.match(hourMinuteRegex);
    if (!startMatch || !startMatch.groups)
      throw new Error("Invalid start time");
    if (!endMatch || !endMatch.groups) throw new Error("Invalid end time");

    let startHour = parseInt(startMatch.groups.hours);
    const startMinute = parseInt(startMatch.groups.minutes);
    let endHour = parseInt(endMatch.groups.hours);
    const endMinute = parseInt(endMatch.groups.minutes);

    const minuteInHour = 60;
    const increments = minuteInHour / increment;
    const result: string[] = [];

    if (is12Hour) {
      const startMeridiem = startMatch.groups.meridiem;
      const endMeridiem = endMatch.groups.meridiem;
      if (startMeridiem === "PM" && startHour !== 12) startHour += 12;
      if (endMeridiem === "PM" && endHour !== 12) endHour += 12;
    }

    for (let i = startHour; i <= endHour; i++) {
      if (i === endHour + 1) break;

      const hour = is12Hour
        ? ((i - 1) % 12) + 1 || 12
        : i.toString().padStart(2, "0");
      const times: string[] = [];

      if (i === startHour) {
        const startIncrement = Math.ceil(startMinute / increment);
        for (let j = startIncrement; j < increments; j++) {
          const minute = (j * increment).toString().padStart(2, "0");
          const time = is12Hour
            ? `${hour}:${minute} ${startHour < 12 ? "AM" : "PM"}`
            : `${hour}:${minute}`;
          times.push(time);
        }
      } else if (i === endHour) {
        const endIncrement = Math.floor(endMinute / increment);
        for (let j = 0; j <= endIncrement; j++) {
          const minute = (j * increment).toString().padStart(2, "0");
          const time = is12Hour
            ? `${hour}:${minute} ${i < 12 ? "AM" : "PM"}`
            : `${hour}:${minute}`;
          times.push(time);
        }
      } else {
        for (let j = 0; j < increments; j++) {
          const minute = (j * increment).toString().padStart(2, "0");
          const time = is12Hour
            ? `${hour}:${minute} ${i < 12 ? "AM" : "PM"}`
            : `${hour}:${minute}`;
          times.push(time);
        }
      }
      times.forEach((time) => result.push(time));
    }

    return result;
  }

  private generateOptions() {
    const times = this.generateHours(
      this.step,
      this.twentyFourHour ? "twenty-four-hour" : "twelve-hour",
      this.startTime,
      this.endTime
    );

    const options: { value: string; text: string; disabled: boolean }[] = [];
    times.forEach((time: string) => {
      const isDisabled: boolean =
        Boolean(this.disabledHours?.indexOf(time) > -1) ||
        Boolean(
          this.minTime &&
            Date.parse(`01/01/2000 ${time}`) <
              Date.parse(`01/01/2000 ${this.minTime}`)
        );

      options.push({
        value: time,
        text: time,
        disabled: isDisabled,
      });
    });

    return options;
  }

  isValid(value: string) {
    if (this.twentyFourHour) {
      return Boolean(
        value &&
          value.includes(":") &&
          value.length === 5 &&
          !isNaN(Number(value.substr(0, 2))) &&
          !isNaN(Number(value.substr(3, 3)))
      );
    }
    return Boolean(
      value &&
        value.includes(":") &&
        !isNaN(Number(value.substr(0, 2))) &&
        value.length === 8 &&
        !isNaN(Number(value.substr(3, 3))) &&
        (value.toLowerCase().includes(" am") ||
          value.toLowerCase().includes(" pm"))
    );
  }

  render() {
    return (
      <raul-select
        onRaulChange={(e) => this.handleRaulChange(e)}
        variant={
          this.twentyFourHour ? "time-picker-twenty-four" : "time-picker"
        }
        disabled={this.disabled}
        options={this.options}
        value={
          this.textInput && !this.value
            ? this.twentyFourHour
              ? "--:--"
              : "--:-- --"
            : this.value
        }
        label={this.label}
        hint={this.hint}
        placeholder={this.placeholder}
        error={this.error}
        text-input={this.textInput}
        searchable
      />
    );
  }
}
