@import "../../../styles/mixins/focus.scss";

raul-toggles {
  display: block;
  font-size: var(--font-size-3);

  .r-toggles {
    height: 2.5rem;
    overflow: hidden;

    &__list {
      display: flex;
      height: 4rem;
      overflow-x: auto;
    }

    &__item {
      display: flex;
      column-gap: 0.5rem;
      align-items: center;
      justify-content: center;
      height: 2.5rem;
      padding-right: 1rem;
      padding-left: 1rem;
      border: 1px solid var(--color-neutral);
      border-right-color: var(--transparent);
      background-color: var(--color-background);
      font-size: var(--font-size-3);
      white-space: nowrap;

      &:first-child {
        border-top-left-radius: 0.125rem;
        border-bottom-left-radius: 0.125rem;
      }

      &:last-child {
        border-top-right-radius: 0.125rem;
        border-bottom-right-radius: 0.125rem;
        border-right-color: var(--color-neutral);
      }

      &:hover:not(:disabled) {
        color: var(--color-primary);
        text-shadow: 0 0 1px var(--color-primary);
      }

      @include focus-visible(-2px);

      &.r-toggles__item--active:not(.r-toggles__item--disabled) {
        border-color: var(--color-primary);
        background-color: var(--color-primary);
        color: var(--color-background);
        text-shadow: 0 0 1px var(--color-background);

        &:focus-visible {
          outline-color: rgba(255, 255, 255, 1);
          outline-width: 1px;
          outline-offset: -3px;
        }
      }

      &.r-toggles__item--disabled {
        background-color: var(--gray-lightest);
        color: var(--color-neutral-dark);
        cursor: not-allowed;
      }
    }
  }

  &[full-width] {
    .r-toggles {
      &__item {
        flex: 1 1 0%;
      }
    }
  }

  &[size="small"] {
    .r-toggles {
      &__item {
        height: 2rem;
      }
    }
  }
}
