import {
  Component,
  Prop,
  h,
  Element,
  State,
  EventEmitter,
  Event,
  Listen,
} from "@stencil/core";

/**
 * @slot - Place a HTML input in this slot.
 * @slot endAccessory - Place an element (like a button) in this slot to add to the end of the input field.
 * @slot label-start - Place an element before the label. For action elements use the `data-label-action` attribute on the element.
 * @slot label-end - Place an element at the end of the label. For action elements use the `data-label-action` attribute on the element.
 * @slot messaging - Place validation, hint text or any other informational text here. Use the `raul-input-helper-list` component in this slot.
 */

@Component({
  tag: "raul-input",
  styleUrl: "raul-input.scss",
})
export class RaulInput {
  @Element() el!: HTMLInputElement;
  inputEl!: HTMLInputElement;
  symbolEl!: HTMLDivElement;

  @State() inputId = `raul-input-${inputIds++}`;
  @State() value = "";
  @State() focused = false;
  @State() disabled = false;

  /**
   * Input's type.
   */
  @Prop() type: "text" | "search" = "text";

  /**
   * Input's label text.
   */
  @Prop() label?: string;

  /**
   * @deprecated Use the "messaging" slot with the raul-input-helper-list component instead
   */
  @Prop() hint?: string;

  /**
   * @deprecated Use "invalid" and the "messaging" slot with the raul-input-helper-list component instead
   */

  @Prop() error?: string;

  /**
   *  Makes the input visullay invlaid
   */

  @Prop({ reflect: true }) invalid?: boolean = false;

  /**
   * Toggles error input text on input keyup event. Sets to original error message if input has no characters.
   */
  @Prop() updateErrorOnChange?: boolean;

  /**
   * Allows for compact floating label support. Requires label property.
   */
  @Prop({ reflect: true }) compact = false;

  /**
   * Used to indicate if input value is added (only relevant when compact is in use).
   */
  @Prop() notEmpty = false;

  /**
   * Symbol in the beginning of input.
   */
  @Prop() prependSymbol?: string;

  /**
   * Symbol in the end of input.
   */
  @Prop() appendSymbol?: string;

  /**
   * Emits when search is cleared.
   */
  @Event() raulClearSearch!: EventEmitter<undefined>;

  @Listen("change")
  handleInternalChange({ target }: Event) {
    if (target === this.inputEl) {
      this.value = this.inputEl.value;
    }
  }

  @Listen("focusin")
  handleInternalFocus({ target }: Event) {
    if (target === this.inputEl) {
      this.focused = true;
    }
  }

  @Listen("focusout")
  handleInternalBlur({ target }: Event) {
    if (target === this.inputEl) {
      this.focused = false;
    }
  }

  @Listen("keyup")
  handleInternalKeyup({ target }: Event) {
    if (target === this.inputEl) {
      this.value = this.inputEl.value;
    }
  }

  componentWillLoad() {
    const input = this.el.querySelector("input");
    if (input) {
      this.inputEl = input;
    }

    if (this.inputEl?.id) this.inputId = this.inputEl.id;
    if (!this.inputEl.id) this.inputEl.id = this.inputId;
    this.disabled = this.inputEl?.disabled;

    if (this.error) {
      console.warn(
        '[RAUL: Deprecated] "error" is deprecated, use "invalid" and the "messaging" slot with the raul-input-helper-list component instead',
      );
    }

    if (this.hint) {
      console.warn(
        '[RAUL: Deprecated] "hint" is deprecated, use the "messaging" slot with the raul-input-helper-list component instead',
      );
    }
  }

  componentDidLoad() {
    this.handleErrorChange();
  }

  handleErrorChange() {
    if (!this.updateErrorOnChange) return;

    const originalError = this.error;
    const input = this.el.querySelector("input");
    if (input) {
      this.inputEl = input;
    }

    this.inputEl.addEventListener("keyup", () => {
      this.error = this.inputEl.value === "" ? originalError : "";
    });
  }

  clearSearch() {
    this.inputEl.value = "";
    this.value = this.inputEl.value;
    this.inputEl.focus();
    this.raulClearSearch.emit();
  }

  render() {
    const clearSearchIcon = () => {
      return (
        <button
          type="button"
          data-testid="clear-search"
          class="r-form-element__control__clear-search-icon"
          onClick={() => this.clearSearch()}
        >
          <raul-icon icon="close" />
        </button>
      );
    };

    const label = () => (
      <div
        class={{
          "r-form-element__label": true,
          "r-form-element__label--float":
            this.compact &&
            (this.value !== "" || this.focused || this.notEmpty),
        }}
      >
        <slot name="label-start" />
        <label htmlFor={this.inputId} data-testid="input-label">
          {this.label}
        </label>
        <slot name="label-end" />
      </div>
    );

    return (
      <div
        class={{
          "r-form-element": true,
          "r-form-element--invalid": !!this.error || !!this.invalid,
          "r-form-element--search": this.type === "search",
          "r-form-element--symbol-prepend": !!this.prependSymbol,
          "r-form-element--symbol-append": !!this.appendSymbol,
          "r-form-element--compact": this.compact,
          "r-form-element--focused": this.focused && this.compact,
        }}
        data-testid="form-element"
      >
        {this.label ? label() : null}
        <div
          class={{
            "r-form-element__control": true,
            "r-form-element__control--focused": this.focused && this.compact,
            "r-form-element__control--disabled": this.disabled,
          }}
        >
          {this.type === "search" && (
            <raul-icon
              icon="search"
              class="r-form-element__control__search-icon"
            />
          )}

          <slot />

          <slot name="endAccessory" />

          {this.type === "search" && this.value?.length > 0
            ? clearSearchIcon()
            : null}

          {(this.prependSymbol || this.appendSymbol) && (
            <div
              class={{
                "r-form-element__control__symbol-prepend": !!this.prependSymbol,
                "r-form-element__control__symbol-append": !!this.appendSymbol,
              }}
              ref={(el) => (this.symbolEl = el as HTMLDivElement)}
            >
              {this.appendSymbol ? this.appendSymbol : null}
              {this.prependSymbol ? this.prependSymbol : null}
            </div>
          )}
        </div>
        <slot name="messaging">
          {this.hint && <small class="r-form-element__hint">{this.hint}</small>}

          {this.error && (
            <div class="r-form-element__error">
              <raul-icon
                icon="interface-alert-diamond"
                class="r-form-element__error__icon"
              />{" "}
              {this.error}
            </div>
          )}
        </slot>
      </div>
    );
  }
}

let inputIds = 0;
