@import "../../../../styles/mixins/focus.scss";

raul-step {
  display: inline-block;
  width: 100%;

  .r-step {
    display: flex;
    position: relative;
    align-items: left;
    justify-content: left;
    width: 100%;
    height: 2rem;
    padding-right: 1.5rem;
    padding-left: 2rem;
    border-radius: 0;
    background-color: var(--color-background);
    color: var(--black);
    font-weight: var(--font-weight-medium);
    font-size: var(--font-size-2);
    text-align: center;
    white-space: nowrap;

    @include focus-visible(0.125rem);

    &:after {
      z-index: 1;
      position: absolute;
      top: 0;
      right: -0.625rem;
      border-top: 1rem solid transparent;
      border-bottom: 1rem solid transparent;
      border-left: 0.625rem solid;
      border-left-color: var(--color-background);
      content: "";
    }

    &:before {
      position: absolute;
      top: 0;
      left: 0;
      border-top: 1rem solid transparent;
      border-bottom: 1rem solid transparent;
      border-left: 0.625rem solid;
      border-left-color: var(--color-neutral-lightest);
      content: "";
    }
  }

  &[past] .r-step,
  &[current] .r-step {
    color: var(--color-background);
  }

  &[past] .r-step {
    background: var(--charcoal-dark);

    &:after {
      border-left-color: var(--charcoal-dark);
    }

    &:hover {
      background-color: var(--color-primary);

      &:after {
        border-left-color: var(--color-primary);
      }
    }
  }

  &[current] .r-step {
    background: var(--color-primary);

    &:after {
      border-left-color: var(--color-primary);
    }
  }
}

raul-step-tracker[compact] raul-step {
  .r-step {
    height: 0.375rem;
    overflow: hidden;
    border-radius: 9999px;
    text-indent: -9999px;
    white-space: wrap;

    &::after,
    &::before {
      display: none;
    }
  }

  &:not([past]):not([current]) .r-step {
    background-color: var(--color-neutral);
  }
}
