import {
  Component,
  h,
  Prop,
  State,
  Event,
  EventEmitter,
  Listen,
  Element,
  Watch,
} from "@stencil/core";
import { VerticalMenuInterface } from "../../../utils/interfaces";
import { isScreen } from "../../../utils/dom";
import cloneDeep from "lodash/cloneDeep";
import debounce from "lodash/debounce";
import { FOCUSABLE_ELEMENTS } from "../../../utils/keyboard";

const findParent = (
  items: VerticalMenuInterface[],
  name: string,
  parent?: VerticalMenuInterface
): VerticalMenuInterface | undefined => {
  for (const item of items) {
    const result: VerticalMenuInterface | undefined =
      item.name === name
        ? parent
        : item.items && findParent(item.items, name, item);
    if (result) return result;
  }
};

const findItem = (
  items: VerticalMenuInterface[],
  name: string
): VerticalMenuInterface | undefined => {
  for (const item of items) {
    const result =
      item.name === name ? item : item.items && findItem(item.items, name);
    if (result) return result;
  }
};

@Component({
  tag: "raul-vertical-menu",
  styleUrl: "raul-vertical-menu.scss",
})
export class RaulVerticalMenu {
  mobileContainer!: HTMLDivElement;
  mobileMenuButton!: HTMLButtonElement;
  backButton!: HTMLButtonElement;
  mediaQuery: MediaQueryList = window.matchMedia(
    "(prefers-reduced-motion: reduce)"
  );

  @Element() el!: HTMLRaulVerticalMenuElement;

  @State() currentItems: VerticalMenuInterface[] | null = null;
  @State() previousItems: VerticalMenuInterface[] | null = null;
  @State() depth = 0;
  @State() backButtonName?: string;
  @State() selectedItemTitle!: string;
  @State() isMobile = false;
  @State() modalVisibility = false;
  @State() modalHidden = true;
  @State() direction: "left" | "right" | null = null;
  @State() isMoving = false;
  @State() path: Array<VerticalMenuInterface[]> = [];
  @State() reducedMotion = false;

  /**
   * Default value for selected item
   */
  @Prop({ mutable: true }) value!: string;

  /**
   * Menu title
   */
  @Prop() menuTitle!: string;

  /**
   * Menu items
   */
  @Prop() items!: VerticalMenuInterface[];

  @Watch("items")
  handleItemsChange() {
    this.initMenuValues();
  }

  componentWillLoad() {
    this.initMenuValues();
    // this.reducedMotion = this.mediaQuery.matches;
    this.reducedMotion = true;
    // this.mediaQuery.addEventListener("change", (ev) =>
    //   // this.queryChangeHandler(ev)
    // );
  }

  componentDidUpdate() {
    if (this.direction) {
      if (!this.reducedMotion) {
        this.isMoving = true;
      } else {
        const parentToFocus = this.backButtonName
          ? (this.el.querySelector(
              `.r-vertical-menu_${this.backButtonName}-button`
            ) as HTMLAnchorElement)
          : null;
        if (this.backButton) this.backButton.focus();
        if (parentToFocus) parentToFocus.focus();
        this.direction = null;
      }
    }
    this.setContainerHeight();
  }

  // disconnectedCallback() {
  //   this.mediaQuery.removeEventListener("change", (ev) =>
  //     this.queryChangeHandler(ev)
  //   );
  // }

  /**
   * Emitted when an menu item is clicked with the name prop value in detail
   */
  @Event() raulVerticalMenuClick!: EventEmitter<string>;

  @Listen("resize", { target: "window" })
  handleDebouncedResize() {
    this.isMobile = !isScreen("sm");
    this.setContainerHeight();
  }

  @Listen("keydown")
  handleInternalKeydown(e: KeyboardEvent) {
    const { key, shiftKey, target } = e;
    if (this.modalVisibility && this.isMobile) {
      const focusableElements = this.mobileContainer.querySelectorAll(
        FOCUSABLE_ELEMENTS.join(", ")
      );

      if (["Esc", "Escape"].includes(key)) {
        this.handleModalVisibility();
      }

      if (shiftKey && key === "Tab") {
        const lastIndex = focusableElements.length - 1;
        if (target === focusableElements[0]) {
          e.preventDefault();
          (focusableElements[lastIndex] as HTMLElement).focus();
        }
        return;
      }

      if (key === "Tab") {
        const lastIndex = focusableElements.length - 1;
        if (target === focusableElements[lastIndex]) {
          e.preventDefault();
          (focusableElements[0] as HTMLElement)?.focus();
        }
      }
    }
  }

  @Listen("transitionend")
  async handleInternalTransitionEnd(e: TransitionEvent) {
    const target = e.target as HTMLElement;
    if (target === this.mobileContainer) {
      if (this.modalVisibility) {
        this.modalHidden = false;
        const focusableElements = this.mobileContainer.querySelectorAll(
          FOCUSABLE_ELEMENTS.join(", ")
        );
        (focusableElements[0] as HTMLElement)?.focus();
      } else {
        this.modalHidden = true;
      }
    }

    if (target.classList.contains("r-vertical-menu__container__ul--current")) {
      if (this.direction === "left") {
        this.currentItems = this.depth === 0 ? this.items : this.previousItems;
        this.previousItems = this.depth === 0 ? null : this.path[this.depth];
        const parentToFocus = this.backButtonName
          ? (this.el.querySelector(
              `.r-vertical-menu_${this.backButtonName}-button`
            ) as HTMLAnchorElement)
          : null;
        if (parentToFocus) parentToFocus.focus();
      } else {
        if (this.backButton) this.backButton.focus();
      }
      this.isMoving = false;
      this.direction = null;
    }
  }

  private initMenuValues() {
    if (this.items) {
      this.currentItems = cloneDeep(this.items);
      this.handleDebouncedResize = debounce(this.handleDebouncedResize, 200);
      this.isMobile = !isScreen("sm");

      if (this.value) {
        const activeItem = findItem(this.items, this.value);
        if (activeItem) this.selectedItemTitle = activeItem.title;
      }
    }
  }

  handleClick(e: MouseEvent, currentItems: VerticalMenuInterface): void {
    e.preventDefault();
    if (currentItems.items) {
      this.handleClickNext(currentItems);
    } else {
      this.value = currentItems.name;
      this.selectedItemTitle = currentItems.title;
      if (this.isMobile) this.modalVisibility = false;
    }
    this.raulVerticalMenuClick.emit(currentItems.name);
  }

  private handleClickNext(currentItems: VerticalMenuInterface): void {
    this.direction = "right";
    if (currentItems.items && this.currentItems) {
      this.path.push(this.currentItems);
      this.depth++;
      this.previousItems = this.path[this.depth - 1];
      this.currentItems = currentItems.items;
    }
  }

  private async handleClickBack(e: MouseEvent) {
    e.preventDefault();
    const target = e.target as HTMLButtonElement;
    this.backButtonName = target.closest("button")?.name;
    this.direction = "left";
    this.depth--;
    this.depth > 0
      ? this.path.splice(this.depth, this.depth)
      : (this.path = []);
    if (this.reducedMotion) {
      this.currentItems = this.depth === 0 ? this.items : this.previousItems;
      this.previousItems = this.depth === 0 ? null : this.path[this.depth];
    }
  }

  private handleModalVisibility() {
    this.modalVisibility = !this.modalVisibility;
    this.mobileMenuButton.focus();
  }

  private setContainerHeight() {
    if (this.currentItems) {
      const menuContainer = this.el.querySelector(
        ".r-vertical-menu__container"
      );
      let liHeight = 0;
      this.el
        .querySelectorAll(".r-vertical-menu__container__ul--current li")
        .forEach((node) => (liHeight += (node as HTMLElement).offsetHeight));

      menuContainer?.setAttribute("style", `height: ${liHeight + 2}px`);
    }
  }

  queryChangeHandler(e: MediaQueryListEvent) {
    this.reducedMotion = (e.target as MediaQueryList).matches;
  }

  render() {
    const renderHamburgerIcon = () => {
      return (
        <div class="r-vertical-menu__mobile__icon">
          <span></span>
          <span></span>
          <span></span>
        </div>
      );
    };

    const renderHeading = (text: string) => {
      return text && <h2 class="r-vertical-menu__heading">{text}</h2>;
    };

    const renderText = (cssClass: string, text: string) => {
      return text && <div class={{ [cssClass]: true }}>{text}</div>;
    };

    const renderContainer = () => (
      <div
        class={{
          "r-vertical-menu__container": true,
          "r-vertical-menu--moving": this.isMoving,
          [`r-vertical-menu--${this.direction}`]: this.direction !== null,
        }}
      >
        {this.previousItems ? renderUL(this.previousItems) : null}
        {this.currentItems ? renderUL(this.currentItems) : null}
      </div>
    );

    const renderMenu = () => {
      if (this.isMobile) {
        return (
          <div class="r-vertical-menu">
            <button
              ref={(el) => (this.mobileMenuButton = el as HTMLButtonElement)}
              type="button"
              class="r-vertical-menu__mobile"
              onClick={() => this.handleModalVisibility()}
            >
              {renderHamburgerIcon()}

              {renderText("r-vertical-menu__mobile__title", this.menuTitle)}

              {renderText(
                "r-vertical-menu__mobile__sub-title",
                this.selectedItemTitle
              )}
            </button>

            <div
              ref={(el) => (this.mobileContainer = el as HTMLDivElement)}
              class={{
                "r-vertical-menu__mobile__modal": true,
                "r-vertical-menu__mobile__modal__visible": this.modalVisibility,
                "r-vertical-menu__mobile__modal__hidden": this.modalHidden,
              }}
            >
              <div class="r-vertical-menu__mobile__topnav">
                {renderHeading(this.menuTitle)}

                <button
                  type="button"
                  class="r-vertical-menu__mobile__topnav__close-button"
                  onClick={() => this.handleModalVisibility()}
                >
                  Close
                </button>
              </div>

              {renderContainer()}
            </div>
          </div>
        );
      } else {
        return (
          <div class="r-vertical-menu">
            {renderHeading(this.menuTitle)}

            {renderContainer()}
          </div>
        );
      }
    };

    const renderUL = (items: VerticalMenuInterface[]) => {
      const parent = findParent(this.items, items[0].name, undefined);
      return (
        <ul
          class={{
            "r-vertical-menu__container__ul": true,
            "r-vertical-menu__container__ul--previous":
              this.previousItems === items,
            "r-vertical-menu__container__ul--current":
              this.currentItems === items,
          }}
          key={this.depth}
          data-testid={
            this.currentItems === items ? "vertical-menu-current" : null
          }
        >
          {parent && (
            <li>
              <button
                class="r-vertical-menu__container__item r-vertical-menu__container__item--back-button"
                name={parent.name}
                onClick={(e) => this.handleClickBack(e)}
                ref={(el) => (this.backButton = el as HTMLButtonElement)}
              >
                <span class="r-vertical-menu__container__item__arrow-back">
                  <svg
                    aria-hidden="true"
                    focusable="false"
                    width="16px"
                    height="12px"
                    viewBox="0 0 16 12"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="currentColor"
                  >
                    <defs>
                      <polygon
                        id="path-2-raul"
                        points="17.707 13.707 16.293 12.293 13 15.586 13 5 11 5 11 15.586 7.707 12.293 6.293 13.707 12 19.414"
                      ></polygon>
                    </defs>
                    <g
                      id="Symbols"
                      stroke="none"
                      stroke-width="1"
                      fill-rule="evenodd"
                    >
                      <g
                        id="Icons/Left"
                        transform="translate(-4.000000, -6.000000)"
                      >
                        <mask id="mask-2" fill="white">
                          <use href="#path-2-raul"></use>
                        </mask>
                        <use
                          id="Icon"
                          fill="#202737"
                          transform="translate(12.000000, 12.207000) rotate(90.000000) translate(-12.000000, -12.207000) "
                          href="#path-2-raul"
                        ></use>
                      </g>
                    </g>
                  </svg>
                </span>
                {renderText(
                  "r-vertical-menu__container__item__title-back",
                  parent?.title
                )}
              </button>
            </li>
          )}

          {items &&
            items.map((item: VerticalMenuInterface) => (
              <li
                class={{
                  "r-vertical-menu--active": this.value === item.name,
                }}
              >
                <a
                  href={`/${item.name}`}
                  class={{
                    "r-vertical-menu__container__item": true,
                    [`r-vertical-menu_${item.name}-button`]: true,
                  }}
                  onClick={(e) => this.handleClick(e, item)}
                  aria-current={this.value === item.name ? "true" : "false"}
                >
                  {renderText(
                    "r-vertical-menu__container__item__title",
                    item.title
                  )}

                  {item.subTitle &&
                    renderText(
                      "r-vertical-menu__container__item__sub-title",
                      item.subTitle
                    )}

                  {item.items && (
                    <span class="r-vertical-menu__container__item__arrow-next">
                      <svg
                        aria-hidden="true"
                        fill="currentColor"
                        focusable="false"
                        version="1.1"
                        xmlns="http://www.w3.org/2000/svg"
                        x="0px"
                        y="0px"
                        viewBox="-393 295 24 24"
                        enable-background="new -393 295 24 24"
                      >
                        <rect
                          id="Rectangle-path"
                          x="-393"
                          fill="none"
                          y="295"
                          width="24"
                          height="24"
                        ></rect>
                        <polygon
                          id="Shape"
                          points="-383.5,299.5 -385.1,301.1 -379.2,307 -385.1,312.9 -383.5,314.5 -376,307 "
                        ></polygon>
                      </svg>
                    </span>
                  )}
                </a>
              </li>
            ))}
        </ul>
      );
    };

    return renderMenu();
  }
}
