raul-grid-row {
  display: flex;
  width: 100%;

  &.raul-grid-row--movable,
  &.raul-grid-row--movable-by-handle {
    display: block;

    &:hover {
      background-color: var(--color-primary-highlight);

      raul-handle {
        opacity: 1;
        margin: 1.25rem 0rem 1.25rem 0rem;
      }
    }
  }

  &.raul-grid-row--movable:hover {
    cursor: move;
  }

  &.raul-grid-row--movable-by-handle:hover {
    raul-handle {
      cursor: move;
    }
  }
}