@import "../../../styles/mixins/focus.scss";
@import "../../../styles/mixins/label-styles.scss";
:host {
  --rating-radio-font-size-small: var(--font-size-3);
  --rating-radio-font-size-medium: var(--font-size-5);
  --rating-radio-font-size-large: var(--font-size-8);
  --rating-radio-color-selected: var(--yellow);
  --rating-radio-color-default: var(--color-neutral);
  --rating-radio-color-hover: var(--yellow);
}

:host(raul-rating) {
  display: block;

  &::part(field-wrapper) {
    display: flex;
    column-gap: 0.35rem;
    align-items: center;
  }

  &::part(label-wrapper) {
    @include label-styles;
  }

  .raul-rating--radio-container {
    display: flex;

    @include focus-visible;
  }

  .raul-rating--radio {
    color: var(--rating-radio-color-default);
    cursor: pointer;

    @include focus-visible;
  }

  .raul-rating--radio--selected {
    color: var(--rating-radio-color-selected);
  }
  .raul-rating--radio--hovered {
    color: var(--rating-radio-color-selected);
  }
}
:host(raul-rating[readonly]) {
  .raul-rating--radio {
    cursor: default;
  }
}
:host(raul-rating[size="small"]) {
  &::part(field-wrapper) {
    font-size: var(--rating-radio-font-size-small);
  }
  .raul-rating--radio {
    font-size: 1rem;
  }
}
:host(raul-rating[size="medium"]) {
  &::part(field-wrapper) {
    font-size: var(--rating-radio-font-size-medium);
  }
  .raul-rating--radio {
    font-size: 1.5rem;
  }
}
:host(raul-rating[size="large"]) {
  &::part(field-wrapper) {
    font-size: var(--rating-radio-font-size-large);
  }
  .raul-rating--radio {
    font-size: 2.5rem;
  }
}
:host(raul-rating[compact]) {
  &::part(field-wrapper) {
    font-size: var(--rating-radio-font-size-small);
  }
  .raul-rating--radio {
    font-size: 1rem;
  }
}
