import { Component, h, Prop, Host, EventEmitter, Event } from "@stencil/core";

@Component({
  tag: "raul-step",
  styleUrl: "raul-step.scss",
})
export class RaulStep {
  /**
   * Past variant of step.
   */
  @Prop({ reflect: true }) past = false;

  /**
   * Current variant of step.
   */
  @Prop({ reflect: true }) current = false;

  /**
   * Step id.
   */
  @Prop() stepId?: string;

  /**
   * Event emitted on click.
   */
  @Event() raulStepClick!: EventEmitter<{ value: string }>;

  private handleClick() {
    if (this.stepId) this.raulStepClick.emit({ value: this.stepId });
  }

  render() {
    return (
      <Host>
        <button
          type="button"
          class={{
            "r-step": true,
            "r-step--past": this.past,
            "r-step--current": this.current,
          }}
          onClick={() => this.handleClick()}
        >
          <slot />
        </button>
      </Host>
    );
  }
}
